import { createHttpEffect } from '@servicenow/ui-effect-http';

export const fetchDashboardsListEffect = (
	successActionType,
	errorActionType,
	startActionType
) =>
	createHttpEffect('/api/now/builder/dashboards', {
		method: 'GET',
		successActionType,
		errorActionType,
		startActionType,
		batch: false
	});

export function extractScreensFromDashboards(dashboards) {
	return dashboards.flatMap(extractScreensFromDashboard);
}

function extractScreensFromDashboard(dashboard) {
	return [
		{
			...dashboard.screen,
			_appRouteId: dashboard.id,
			sysId: dashboard.screen.id,
			type: 'screen'
		},
		...(dashboard.children || []).flatMap((child) =>
			extractScreensFromDashboard(child)
		)
	];
}

export function annotateDashboards(dashboards) {
	return dashboards.map((dashboard) => ({
		...dashboard,
		sysId: dashboard.id,
		children: dashboard.children ? annotateDashboards(dashboard.children) : []
	}));
}
