import { createHttpEffect } from '@servicenow/ui-effect-http';
import debounce from 'lodash/debounce';
import {
	APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_FAILED,
	APPLICATIONS_REQUESTED,
	APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_SENT,
	APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_SUCCEEDED,
	APPLICATION_SEARCHED,
	APPLICATIONS_REQUESTED_BEHAVIOR_LOADING,
	APPLICATIONS_REQUESTED_BEHAVIOR_APPLICATION_LIST_UPDATED,
	UPDATE_VALIDATION_LIST,
	UPDATE_DEFAULT_APPLICATION,
	APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_REQUESTED,
	APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_FAILED,
	APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_SUCCEEDED,
	APPLICATIONS_REQUESTED_BEHAVIOR_APPLICATION_UPDATED
} from './constants';
import { effectProxy } from 'sn-uxf-builder-middleware';

export const fetchApplicationsEffect = createHttpEffect(
	'/api/now/ui/concoursepicker/application',
	{
		successActionType: APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_SUCCEEDED,
		errorActionType: APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_FAILED,
		method: 'GET',
		batch: false
	}
);

export const updateApplicationEffect = createHttpEffect(
	'/api/now/ui/concoursepicker/application',
	{
		successActionType: APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_SUCCEEDED,
		errorActionType: APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_FAILED,
		method: 'PUT',
		batch: false,
		dataParam: ['data']
	}
);

export const applicationScopeQuery = `query {
		GlideRecord_Query {
			sys_scope(queryConditions: "sys_id={{sysId}}") {
				_results {
					sysId: sys_id {
						value
					},
					name {
						value
					}
				}
			}
		}
	}
`;

const applicationSearchedHandler = ({ action, updateState, state }) => {
	const { searchString = '' } = action.payload;

	updateState({
		applications: state.pristineApplications.filter((application) => {
			return application.label
				.toLowerCase()
				.includes(searchString.toLowerCase());
		}),
		lastSearchString: searchString
	});
};

const applicationsRequestedHandler = ({ dispatch, updateState, action }) => {
	const { payload = {} } = action;
	const { defaultScopeId = null } = payload;

	updateState({
		isLoadingApplications: true,
		defaultScopeId
	});
	dispatch(APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_SENT);
	dispatch(APPLICATIONS_REQUESTED_BEHAVIOR_LOADING, { loading: true });
};

const applicationsRequestedRequestFailed = ({ dispatch, updateState }) => {
	updateState({ isLoadingApplications: false });
	dispatch(APPLICATIONS_REQUESTED_BEHAVIOR_LOADING, { loading: false });
	dispatch(UPDATE_DEFAULT_APPLICATION, {
		defaultScope: { id: 'global', label: 'Global' }
	});
};

const applicationsRequestedRequestSucceededHandler = ({
	action,
	updateState,
	dispatch,
	state
}) => {
	const { current, list } = action.payload.result;
	const { lastSearchString, defaultScopeId } = state;
	const applications = list.map((application) => {
		return {
			id: application.sysId,
			label: application.name,
			className: application.className
		};
	});
	const pristineApplications = list.map((application) => {
		return {
			id: application.sysId,
			label: application.name,
			className: application.className
		};
	});
	const userScope = applications.find(({ id }) => id === current);
	const defaultScope = applications.find(({ id }) => id === defaultScopeId);

	updateState({
		applications,
		pristineApplications,
		isLoadingApplications: false,
		userScope
	});

	dispatch(APPLICATIONS_REQUESTED_BEHAVIOR_APPLICATION_LIST_UPDATED);

	if (defaultScope || userScope) {
		dispatch(UPDATE_DEFAULT_APPLICATION, {
			defaultScope: userScope || defaultScope
		});
		updateState({
			defaultScopeId: null
		});
	} else {
		dispatch(UPDATE_DEFAULT_APPLICATION, {
			defaultScope: { id: 'global', label: 'Global' }
		});
	}

	dispatch(UPDATE_VALIDATION_LIST, {
		key: 'selectedApplication',
		value: lastSearchString
	});
	dispatch(APPLICATIONS_REQUESTED_BEHAVIOR_LOADING, { loading: false });
};

const updateApplicationSucceededHandler = ({
	action,
	updateState,
	dispatch,
	state
}) => {
	const { pristineApplications } = state;
	const { app_id } = action.payload.result;
	const userScope = pristineApplications.find(({ id }) => id === app_id);

	updateState({
		userScope
	});

	dispatch(APPLICATIONS_REQUESTED_BEHAVIOR_APPLICATION_UPDATED, userScope);
};

export default {
	[APPLICATION_SEARCHED]: debounce(applicationSearchedHandler, 250),
	[APPLICATIONS_REQUESTED]: applicationsRequestedHandler,
	[APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_SENT]: effectProxy(
		fetchApplicationsEffect
	),
	[APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_SUCCEEDED]: applicationsRequestedRequestSucceededHandler,
	[APPLICATIONS_REQUESTED_BEHAVIOR_REQUEST_FAILED]: applicationsRequestedRequestFailed,
	[APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_REQUESTED]: effectProxy(
		updateApplicationEffect
	),
	[APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_SUCCEEDED]: updateApplicationSucceededHandler
};
