import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

// Internal components
import '../sn-experience-settings-link';

// NDS
import '@servicenow/now-button';

const view = ({ properties: { label, size } }) => {
	return (
		<div>
			<now-button-bare
				label={label}
				icon-start="open-link-right-outline"
				variant="secondary"
				tooltipContent=""
				size={size}></now-button-bare>
		</div>
	);
};

createCustomElement('sn-experience-settings-link', {
	view,
	properties: {
		sysId: {
			default: ''
		},
		table: {
			required: true,
			schema: { type: 'string' }
		},
		label: {
			required: false,
			default: t('Advanced settings'),
			schema: { type: 'string' }
		},
		size: {
			default: 'md'
		}
	},
	actionHandlers: {
		'NOW_BUTTON_BARE#CLICKED': {
			stopPropagation: true,
			effect: ({ properties: { sysId, table } }) => {
				const uri = encodeURIComponent(`/${table}.do?sys_id=${sysId}`);
				const url = `nav_to.do?uri=${uri}`;
				window.open(`${window.location.origin}/${url}`);
			}
		}
	}
});
