import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import styles from './uib-fields-props-sync-dialog.scss';
import '@servicenow/now-button';
import '@servicenow/now-uxf-page';

export const UIB_FIELDS_PROPS_SYNC_DIALOG_SYNC_CLICKED =
	'UIB_FIELDS_PROPS_SYNC_DIALOG#SYNC_CLICKED';
export const UIB_FIELDS_PROPS_SYNC_DIALOG_DISMISS =
	'UIB_FIELDS_PROPS_SYNC_DIALOG#DIMISS';
/* prettier-ignore */
const headerLabel = t('Do you want to sync the required parameters for your URL?');
const actionLabel = t('Sync');
const appendToButtonPayload = { category: 'sync' };
const successMessage = (name) =>
	t('The required parameters for {0} are now in sync with the page.', name);
const startMessage = (name) => t('Syncing fields to {0}...', name);

createCustomElement('uib-fields-props-sync-dialog', {
	styles,
	view: () => {
		return (
			<now-uxf-bare-modal opened={true} size="sm">
				<div className="container">
					<div className="header-label">{headerLabel}</div>
					<span className="dialog-message">
						<p>
							{
								/* prettier-ignore */
								t('The current variant you are working on is part of a different scope than that of your page.')
							}
						</p>
						<p>
							{
								/* prettier-ignore */
								t("To ensure this variant works properly, we recommend syncing to this page's required parameters.")
							}
						</p>
					</span>
					<now-button
						variant="primary"
						size="md"
						append-to-payload={appendToButtonPayload}
						label={actionLabel}
					/>
				</div>
			</now-uxf-bare-modal>
		);
	},
	transformState: (state) => {
		const { objectToDelete } = state.properties;

		return {
			...state,
			name: objectToDelete ? objectToDelete.name : null
		};
	},
	properties: {
		outOfSync: {
			default: []
		},
		name: {
			default: null
		}
	},
	actionHandlers: {
		'NOW_UXF_BARE_MODAL#DISMISSED': ({ dispatch }) => {
			dispatch(UIB_FIELDS_PROPS_SYNC_DIALOG_DISMISS);
		},
		['NOW_BUTTON#CLICKED']: {
			stopPropagation: true,
			effect: ({ dispatch, properties, action }) => {
				const { category } = action.payload;
				if (category === 'sync') {
					const { outOfSync, name } = properties;
					outOfSync.forEach((name) =>
						dispatch(snUXFActions.PAGE_PROPERTIES_CREATED, {
							name,
							label: name
						})
					);
					dispatch(snUXFActions.PAGE_SAVE_REQUESTED, null, {
						customSuccessMessage: successMessage(name),
						customStartMessage: startMessage(name)
					});
					dispatch(UIB_FIELDS_PROPS_SYNC_DIALOG_DISMISS);
				}
			}
		}
	}
});
