import { createCustomElement } from '@servicenow/ui-core';
import '../uib-screen-audiences';
import styles from './uib-audiences-modal.scss';
import { t } from 'sn-translate';
import { AUDIENCE_LOADING_SET } from '../../behaviors/audiencesBehavior/constants';

export const UIB_AUDIENCES_MODAL_DISMISSED =
	'UIB_AUDIENCES_MODAL#MODAL_DISMISSED';
const INTERNAL_DISMISS = 'UIB_AUDIENCES_MODAL#INTERNAL_MODAL_DISMISSED';

export const view = ({
	initialLoad,
	hasAudiences,
	properties: {
		screenId,
		userScope,
		userDomain,
		supportDomains,
		applications,
		supportScope
	}
}) => {
	return (
		<now-modal
			manage-opened={true}
			opened={true}
			size="lg"
			footer-actions={
				initialLoad
					? null
					: [
							{
								label: t('Done'),
								variant: 'primary',
								clickActionType: INTERNAL_DISMISS
							}
					  ]
			}
			headerLabel={
				initialLoad
					? null
					: hasAudiences
					? t('Edit audiences')
					: t('Add audiences')
			}>
			<div className={`container ${initialLoad ? 'loading' : ''}`}>
				{initialLoad ? null : (
					<div className="subheader">
						<p className="first">
							{
								/* prettier-ignore */
								t("Select one or more audiences to determine who sees this page. If you don't see an audience in the list, you can create an audience in the platform.")
							}
						</p>
					</div>
				)}
				<uib-screen-audiences
					screen-id={screenId}
					user-scope={userScope}
					user-domain={userDomain}
					support-domains={supportDomains}
					support-scope={supportScope}
					applications={applications}
				/>
			</div>
		</now-modal>
	);
};

createCustomElement('uib-audiences-modal', {
	view,
	styles,
	initialState: {
		initialLoad: true,
		hasAudiences: false
	},
	properties: {
		screenId: {
			default: null
		},
		returnToSuccessModal: {
			default: false
		},
		userScope: {
			default: null
		},
		userDomain: {
			default: null
		},
		supportDomains: {
			default: null
		},
		applications: {
			default: null
		},
		supportScope: {
			default: false
		}
	},
	actionHandlers: {
		['NOW_MODAL#OPENED_SET']: ({ dispatch, action }) => {
			action.stopPropagation();
			dispatch(UIB_AUDIENCES_MODAL_DISMISSED);
		},
		[INTERNAL_DISMISS]: ({ dispatch, properties, action }) => {
			action.stopPropagation();
			const { returnToSuccessModal } = properties;
			dispatch(UIB_AUDIENCES_MODAL_DISMISSED, { returnToSuccessModal });
		},
		[AUDIENCE_LOADING_SET]: ({ updateState, state, action }) => {
			action.stopPropagation();
			const { initialLoad } = state;
			const { value, audienceList } = action.payload;
			if (initialLoad && !value) {
				updateState({
					initialLoad: value,
					hasAudiences: audienceList && audienceList.length
				});
			}
		}
	}
});
