import get from 'lodash/get';
import every from 'lodash/every';
import { t } from 'sn-translate';
import processors from './property-processors';
import isEmpty from 'lodash/isEmpty';
import { deserializeAndExecuteScript } from 'sn-uxf-builder-middleware';
import eventProcessors from './event-mappings-processors';

export const SCRIPT_SHORTHAND_PATTERN = new RegExp(
	`^javascript\\(([\\n\\S\\d\\W\\w]*)\\)(\\!)?$`
);

/**
 * @param queryString
 */
export function parseFilterCountFromConditionString(queryString) {
	return typeof queryString === 'string'
		? queryString.split('^').length.toString()
		: '0';
}

/**
 * @param value
 */
export function getDependentPropertyId(value) {
	if (value && typeof value === 'string' && !value.match(/^@\w+\.\w+/)) {
		return get(value.match(/^[@]([^@\s]\w+)/), '[1]', null);
	}
	return null;
}

/**
 * @param currentProperties
 * @param dependentProperty
 * @param key
 */
export function getValueFromDependentProperty(
	currentProperties,
	dependentProperty,
	key
) {
	const dependentProp = currentProperties.find(
		(prop) => prop.id === dependentProperty
	);
	return get(dependentProp, `${key}`, null);
}

/**
 * @param value
 */
export function extractScriptFromPropertyValue(value) {
	if (typeof value !== 'string') {
		return null;
	}
	const matcher = value.match(SCRIPT_SHORTHAND_PATTERN);
	if (matcher) {
		return matcher[1];
	}
	return null;
}

/**
 * @param value
 */
export function extractBindingPath(value) {
	if (value && typeof value === 'string' && value.match(/^@(.+)$/)) {
		return value.slice(1);
	}
	return null;
}

/**
 * @param propName
 * @param global
 */
export function buildUrlTokenPattern(propName = null, global = true) {
	const tokenName = propName || '.+';
	return new RegExp(
		`(\\{\\{\\@(${tokenName}(\\.(.+))?)\\}\\})`,
		global ? 'g' : ''
	);
}

/**
 * @param url
 * @param properties
 * @param resolvedBindings
 * @param clientScriptApi
 */
export function hydrateUrlWithDependentValues(
	url,
	properties,
	resolvedBindings,
	clientScriptApi
) {
	return properties.reduce((newUrl, property) => {
		let currentUrl = newUrl;
		const tokenPattern = buildUrlTokenPattern(property.name || property.id);
		const matcher = newUrl.match(tokenPattern);
		if (matcher) {
			const { value } = property;
			const defaultValue =
				typeof value === 'string' && value.startsWith('@') ? null : value;
			let valueToUse = get(
				resolvedBindings,
				extractBindingPath(value),
				defaultValue
			);
			const inlineScript = extractScriptFromPropertyValue(value);
			if (inlineScript) {
				valueToUse = deserializeAndExecuteScript(inlineScript, clientScriptApi);
			}
			matcher.forEach((token) => {
				if (typeof valueToUse === 'object') {
					const tokenPathMatcher = token.match(
						buildUrlTokenPattern(property.name, false)
					);
					valueToUse = get(valueToUse, tokenPathMatcher[4], valueToUse);
				}
				currentUrl = currentUrl.replace(tokenPattern, valueToUse);
			});
		}
		return currentUrl;
	}, url);
}

/**
 * @param queryString
 * @param resolvedDynamicBindings
 * @param currentSelectedProperties
 */
export function getDynamicValues(
	queryString,
	resolvedDynamicBindings,
	currentSelectedProperties
) {
	const regexPattern = /({{@.+?}})/g;

	const matcher =
		typeof queryString === 'string' ? queryString.match(regexPattern) : null;

	if (matcher === null) {
		return [];
	}

	return matcher.map((matchString) => {
		if (!matchString) {
			return null;
		}
		const propertyName = matchString.match(/[^{{@]+(?=\}\})/g)[0].split('.')[0];

		const propertyValue = get(
			currentSelectedProperties.find(
				(property) => propertyName === property.id
			),
			'value',
			null
		);
		const propertyPath =
			typeof propertyValue === 'string' && propertyValue.startsWith('@')
				? propertyValue.substring(1).split('.')
				: null;
		return propertyPath
			? get(resolvedDynamicBindings, propertyPath, null)
			: propertyValue || null;
	});
}
/**
 * @typedef ExternalConfigButton
 * @property {string} url
 * @property {string} label
 */

/**
 * creates URLs for the modal experience for each config
 *
 * @param {object} component
 * @param {object[]} currentSelectedProperties
 * @param resolvedDynamicBindings
 * @param clientScriptApi
 * @returns {ExternalConfigButton[]}
 */
export function getExternalConfigurationButtons(
	component,
	currentSelectedProperties,
	resolvedDynamicBindings,
	clientScriptApi
) {
	if (!component) {
		return null;
	}

	return component.externalConfigurationSources.map((config) => {
		const {
			queryString,
			table,
			view = 'default',
			url = '/'
		} = config.configProperties;
		const query = queryString || url;
		const dynamicValues = getDynamicValues(
			query,
			resolvedDynamicBindings,
			currentSelectedProperties
		);
		const disabled = !every(dynamicValues);
		let finalUrl;
		switch (config.type) {
			case 'LIST':
				finalUrl = `/${table}_list.do?sysparm_view=${view}&sysparm_titleless=true&sysparm_skipmsgs=true&sysparm_query=${encodeURI(
					hydrateUrlWithDependentValues(
						queryString,
						currentSelectedProperties,
						resolvedDynamicBindings,
						clientScriptApi
					)
				)}`;
				break;
			case 'FORM':
				finalUrl = `/${table}.do?sysparm_view=${view}&${encodeURI(
					hydrateUrlWithDependentValues(
						queryString,
						currentSelectedProperties,
						resolvedDynamicBindings,
						clientScriptApi
					)
				)}`;
				break;
			case 'URL':
				finalUrl = hydrateUrlWithDependentValues(
					url,
					currentSelectedProperties,
					resolvedDynamicBindings,
					clientScriptApi
				);
				break;
			default:
				finalUrl = '/';
		}

		return {
			url: finalUrl,
			label: config.label,
			disabled
		};
	});
}

/**
 * @param elementProperties
 */
export function createElementInstanceNameMap(elementProperties) {
	const elementArr = Object.entries(elementProperties);
	return elementArr.reduce((acc, cur) => {
		const [elementId, elementProps] = cur;
		const instanceNameProp = elementProps.find(
			(prop) => prop.id === '__instance_name__'
		);
		acc[elementId] = instanceNameProp.value;
		return acc;
	}, {});
}

/**
 * takes in the currently selected element's properties and enriches it
 *
 * @param {object[]} currentProperties - The manifest of UXF element properties
 * @param {object} state - The current Seismic component state
 * @param {object} processorOverrides - Optional overrides for the default element config panel property processors
 * @param clientScriptApi
 * @returns {object[]}
 */
export function processCurrentSelectedProperties(
	currentProperties,
	state,
	processorOverrides = {},
	clientScriptApi
) {
	let cumulativeCurrentProperties = [...currentProperties];
	return currentProperties.map((property) => {
		const typeProcessorName = property.type.toLowerCase();

		const PROPERTY_PROCESSOR_SEQUENCE = [
			'data_binding',
			'dependent_meta',
			'scripted',
			typeProcessorName,
			'disabled'
		];

		const mappedProcessors = mapProcessorOverrides(
			processors,
			processorOverrides
		);
		const newProp = PROPERTY_PROCESSOR_SEQUENCE.reduce((acc, processName) => {
			if (!mappedProcessors[processName]) {
				return acc;
			}
			return mappedProcessors[processName].apply(this, [
				acc,
				cumulativeCurrentProperties,
				state,
				clientScriptApi
			]);
		}, property);
		cumulativeCurrentProperties = [
			...cumulativeCurrentProperties.filter(
				(prop) => prop.name !== newProp.name
			),
			newProp
		];
		return newProp;
	});
}

/**
 * @param currentEvents
 */
export function processCurrentSelectedEvents(currentEvents) {
	if (isEmpty(currentEvents)) {
		return currentEvents;
	}
	return currentEvents.map(
		({ id, apiName, eventName, label, payloadShape }) => ({
			id,
			apiName,
			label: label || eventName,
			payloadShape
		})
	);
}

/**
 * @param currentEventMappings
 */
export function processCurrentSelectedEventMappings(currentEventMappings) {
	if (isEmpty(currentEventMappings)) {
		return currentEventMappings;
	}
	return currentEventMappings.map((eventMapping) => ({
		id: eventMapping.eventMappingId,
		eventId: eventMapping.eventId
	}));
}

/**
 * @param eventHandlers
 */
export function processEventHandlers(eventHandlers) {
	return eventHandlers.map(
		({
			id,
			apiName,
			label,
			name,
			sourceName,
			type,
			sourceId,
			payloadFields,
			relay,
			ancestor
		}) => {
			const sourceAndOperationName =
				type === 'DATABROKER_OP' ? `:${sourceId}:${name}` : '';
			return {
				id: `${id}${sourceAndOperationName}`,
				apiName,
				label: `${t('{0}', label)} (${t('{0}', sourceName)})`,
				payloadFields,
				type,
				relay,
				ancestor
			};
		}
	);
}

/**
 * @param eventMappings
 * @param eventMappingHandlers
 */
export function getCurrentSelectedEventMappingHandlers(
	eventMappings = [],
	eventMappingHandlers = {}
) {
	return eventMappings.reduce((handlers, mapping) => {
		const eventMappingId = mapping.id || '';
		if (eventMappingHandlers[eventMappingId]) {
			handlers[eventMappingId] = eventMappingHandlers[eventMappingId];
		}
		return handlers;
	}, {});
}

/**
 * @param eventMappingHandlers
 * @param state
 * @param processorOverrides
 * @param selectablePropAddress
 */
export function processEventMappingHandlers(
	eventMappingHandlers,
	state,
	processorOverrides = {},
	selectablePropAddress
) {
	return eventMappingHandlers
		? Object.entries(eventMappingHandlers).reduce(
				(acc, [eventMappingId, handlers]) => {
					return {
						...acc,
						[eventMappingId]: handlers.map((handler) => {
							const valuesToProcess = handler.values || [];
							const updatedState = {
								...state,
								handlerId: handler.handlerId,
								selectablePropAddress
							};
							const overrides = { ...processorOverrides, ...eventProcessors };

							return {
								...handler,
								values: processCurrentSelectedProperties(
									valuesToProcess,
									updatedState,
									overrides
								)
							};
						})
					};
				},
				{}
		  )
		: {};
}

/**
 * @param processors
 * @param overrides
 */
export function mapProcessorOverrides(processors, overrides) {
	return Object.keys(processors).reduce((acc, processorName) => {
		if (overrides[processorName]) {
			acc[processorName] = overrides[processorName];
		} else {
			acc[processorName] = processors[processorName];
		}

		return acc;
	}, {});
}

/**
 * @param properties
 */
export function transformDispatchedEventProperties(properties) {
	return JSON.stringify(
		properties.map((property) => ({
			name: property.name,
			fieldType: property.type,
			label: property.label,
			interfaceApiName: property.interfaceApi || '',
			typeMetadata: property.typeMetadata || null,
			mandatory: false,
			readOnly: false
		}))
	);
}

