import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-checkbox';
import '@servicenow/now-text-link';
import '@servicenow/now-alert';
import '@servicenow/now-button';
import styles from './uib-audiences-form.scss';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

export const AUDIENCE_FORM_ADD_AUDIENCE = 'SN_UIB_AUDIENCE_FORM#ADD_AUDIENCE';
export const AUDIENCE_FORM_EDIT_AUDIENCE = 'SN_UIB_AUDIENCE_FORM#EDIT_AUDIENCE';
export const AUDIENCE_FORM_DELETE_AUDIENCE =
	'SN_UIB_AUDIENCE_FORM#DELETE_AUDIENCE';

const refreshFormEffect = ({ updateState, properties, state }) => {
	const { audience, userScope } = properties;
	const { applicationSysId, disabled, newForm, transactionLoad } = state;
	if (audience) {
		const { active, order, applicability, scope } = audience;
		updateState({
			audienceSysId: applicability.id,
			audienceLabel: applicability.label,
			order,
			active,
			applicationSysId: scope.id,
			applicationLabel: scope.label,
			showInvalid: false,
			isDirty: false,
			disabled: newForm
				? true
				: scope.id === applicationSysId
				? disabled
				: true,
			newForm: false,
			loading:
				newForm || transactionLoad ? true : scope.id !== applicationSysId,
			transactionLoad: ''
		});
	} else {
		updateState({
			audienceSysId: null,
			audienceLabel: '',
			order: 0,
			active: true,
			applicationSysId: userScope ? userScope.id : null,
			applicationLabel: userScope ? userScope.label : '',
			showInvalid: false,
			isDirty: false,
			disabled: false,
			newForm: true,
			loading: false,
			transactionLoad: ''
		});
	}
};

const disableButton = ({ audienceSysId, order, active, audience, isDirty }) => {
	if (!(audienceSysId && isDirty)) {
		return true;
	}

	if (audience) {
		return (
			audienceSysId === audience.applicability.id &&
			order === audience.order &&
			active === audience.active
		);
	}

	return false;
};

const view = ({
	audienceSysId,
	audienceLabel,
	applicationSysId,
	applicationLabel,
	order,
	active,
	disabled,
	loading,
	newForm,
	showInvalid,
	isDirty,
	transactionLoad,
	properties: {
		audienceChoices,
		audience,
		applications,
		protectedRecordMessage,
		size,
		userScope,
		supportScope
	}
}) => {
	const buttonSize = size === 'fullscreen' ? 'md' : 'sm';
	const escapedApplicationLabel = escapeScopeLabel(applicationLabel);
	const escapedAudienceLabel = escapeScopeLabel(audienceLabel);

	return (
		<div className="container">
			{transactionLoad ? (
				<LoadingStage size={'sm'} loadingStageContent={transactionLoad} />
			) : (
				<div>
					{protectedRecordMessage ? (
						<now-alert
							status="warning"
							content={{
								type: 'jsx',
								value: protectedRecordMessage
							}}
							icon="circle-exclamation-outline"
						/>
					) : audience && audience.scope ? (
						<sn-uib-transaction-alert
							applications={applications}
							userScope={userScope}
							requestScopeId={applicationSysId}
							requestScopeName={escapedApplicationLabel}
							recordProtectionPolicy={audience.sysPolicy}
							supportScope={supportScope}
							requestItemType={'Audience'}
						/>
					) : null}
					{!loading ? (
						<div className="body">
							<now-input
								className="application"
								label={t('Application scope')}
								value={escapedApplicationLabel}
								disabled={true}
								readonly={true}
								helper-content={
									/* prettier-ignore */
									t('The audience record is created in this default application scope. To switch scope, use the application scope picker.')
								}
							/>
							<div className="audience">
								<div className="input-field">
									<now-typeahead
										label={t('Audience')}
										className="audience-typeahead"
										items={audienceChoices}
										manage-invalid
										manage-value
										invalid={!audienceSysId && showInvalid}
										disabled={disabled}
										required={!disabled}
										selected-item={audienceSysId}
										value={escapedAudienceLabel}
										append-to-payload={{ field: 'audience' }}
										search="contains"
									/>
									{audienceSysId ? (
										<now-button-iconic
											bare
											icon="open-link-fill"
											tooltip-content={t('Open record in platform')}
											on-click={(e) => {
												e.stopPropagation();
												e.preventDefault();
												window.open(
													`/sys_ux_applicability.do?sys_id=${audienceSysId}`,
													'_blank'
												);
											}}
										/>
									) : null}
								</div>
								<now-text-link
									label={t('Open audiences in platform')}
									href="/sys_ux_applicability_list.do"
									opensWindow={true}
									underliend={true}
								/>
							</div>
							<div className="order-active">
								<now-input
									label={t('Order')}
									type="number"
									disabled={disabled}
									required={!disabled}
									value={order}
									helper-content={
										/* prettier-ignore */
										t('To give higher priority to this record, enter a lower number.')
									}
								/>
								<div className="active">
									<now-checkbox
										disabled={disabled}
										label={t('Active')}
										checked={active}
										manage-checked
									/>
								</div>
							</div>
						</div>
					) : (
						<LoadingStage
							size={'sm'}
							loadingStageContent={t('Loading audience record...')}
						/>
					)}
					{!disabled && !loading ? (
						<div
							className={`footer${!audience || newForm ? ' --flex-end' : ''}`}>
							{audience && !newForm ? (
								<now-button
									className="delete"
									label={t('Delete')}
									variant="primary-negative"
									append-to-payload={{ id: 'delete' }}
									size={buttonSize}
								/>
							) : null}
							<div className="end-buttons">
								{isDirty ? (
									<now-button
										label={t('Reset')}
										className="reset"
										variant="secondary"
										append-to-payload={{ id: 'reset' }}
										size={buttonSize}
									/>
								) : null}
								<now-button
									className="add-edit"
									label={t('Save')}
									variant="primary"
									size={buttonSize}
									append-to-payload={{ id: audience ? 'update' : 'add' }}
									disabled={disableButton({
										audienceSysId,
										order,
										active,
										audience,
										isDirty
									})}
								/>
							</div>
						</div>
					) : null}
				</div>
			)}
		</div>
	);
};

export default createCustomElement('uib-audiences-form', {
	view,
	styles,
	initialState: {
		audienceSysId: null,
		audienceLabel: '',
		order: 0,
		applicationSysId: null,
		active: true,
		showInvalid: false,
		isDirty: false,
		disabled: true,
		newForm: true,
		loading: true,
		transactionLoad: ''
	},
	properties: {
		audienceChoices: {
			default: []
		},
		audience: {
			default: null
		},
		protectedRecordMessage: {
			default: false
		},
		size: {
			default: 'fullscreen'
		},
		userScope: {
			default: null
		},
		applications: {
			default: null
		},
		supportScope: {
			default: false
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: refreshFormEffect,
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			updateState,
			properties,
			state
		}) => {
			action.stopPropagation();
			const { audience } = properties;
			const { name, value } = action.payload;

			if (name === 'audience') {
				refreshFormEffect({ updateState, properties, state });
			}

			if (name === 'protectedRecordMessage' && audience) {
				const disabled = Boolean(value);
				updateState({
					loading: !disabled,
					disabled: disabled
				});
			}
		},
		['NOW_BUTTON#CLICKED']: ({
			dispatch,
			action,
			state,
			updateState,
			properties
		}) => {
			action.stopPropagation();
			const {
				audienceSysId,
				order,
				active,
				applicationSysId,
				audienceLabel
			} = state;
			const { id } = action.payload;
			let transactionLoad;
			let loading;
			switch (id) {
				case 'add':
					dispatch(AUDIENCE_FORM_ADD_AUDIENCE, {
						audienceSysId,
						order,
						active,
						applicationSysId
					});
					transactionLoad = t('Adding {0}...', audienceLabel);
					loading = true;
					break;
				case 'update':
					dispatch(AUDIENCE_FORM_EDIT_AUDIENCE, {
						audienceSysId,
						order,
						active,
						applicationSysId
					});
					transactionLoad = t('Saving {0}...', audienceLabel);
					loading = true;
					break;
				case 'delete':
					dispatch(AUDIENCE_FORM_DELETE_AUDIENCE, { applicationSysId });
					transactionLoad = t('Deleting {0}...', audienceLabel);
					loading = true;
					break;
				case 'reset':
					refreshFormEffect({ updateState, properties, state });
					break;
			}
			updateState({
				isDirty: false,
				transactionLoad,
				loading
			});
		},
		['NOW_INPUT#INPUT']: ({ action, updateState, state }) => {
			action.stopPropagation();
			const { fieldValue } = action.payload;
			const { order } = state;
			if (order !== fieldValue) {
				updateState({
					order: parseFloat(fieldValue),
					isDirty: true
				});
			}
		},
		['NOW_TYPEAHEAD#SELECTED_ITEM_SET']: ({ action, updateState, state }) => {
			const { value, field } = action.payload;
			const { audienceSysId } = state;
			switch (field) {
				case 'audience':
					if (audienceSysId !== value) {
						updateState({
							audienceSysId: value,
							isDirty: true
						});
					}
					break;
			}
		},
		['NOW_TYPEAHEAD#VALUE_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			const { value, field } = action.payload;
			switch (field) {
				case 'audience':
					updateState({
						audienceLabel: value,
						isDirty: true
					});
					break;
			}
		},
		['NOW_TYPEAHEAD#INVALID_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			const { value: showInvalid } = action.payload;
			updateState({
				showInvalid
			});
		},
		['NOW_CHECKBOX#CHECKED_SET']: ({ action, updateState, state }) => {
			action.stopPropagation();
			const { value } = action.payload;
			const { active } = state;
			if (active !== value) {
				updateState({
					active: value,
					isDirty: true
				});
			}
		},
		['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: ({
			action,
			updateState
		}) => {
			action.stopPropagation();
			const { isNecessaryTransactionInProgress } = action.payload;
			updateState({
				disabled: !isNecessaryTransactionInProgress,
				loading: false
			});
		},
		['SN_UIB_TRANSACTION_ALERT#REQUEST_STARTED']: ({ action, updateState }) => {
			action.stopPropagation();
			updateState({
				loading: true
			});
		}
	}
});
