import { actionTypes } from '@servicenow/ui-core';
export const VALIDATION_CHANGED = 'VALIDATION_BEHAVIOR#VALIDATION_CHANGED';
export const UPDATE_VALIDATION_LIST =
	'VALIDATION_BEHAVIOR#UPDATE_VALIDATION_LIST';

export const onChildDataChanged = ({
	action,
	state,
	properties,
	dispatch,
	updateState,
	updateProperties
}) => {
	const { key, value } = action.payload;
	const { validationList, config } = state.behaviors.validationBehavior;

	const configItem = config.find((item) => item.id === key);

	if (configItem) {
		const { isValid, validationError } = getFieldValidationState(
			configItem.validators,
			{ state, properties },
			configItem.id,
			value
		);
		const validationErrors = validationError
			? { ...properties.validationErrors, [key]: validationError }
			: { ...properties.validationErrors, [key]: null };
		const updatedList = updateValidationList(key, isValid, validationList);
		const pageIsValid = getPageValidationState(updatedList);

		updateState({
			operation: 'set',
			path: 'behaviors.validationBehavior.validationList',
			value: updatedList
		});

		updateProperties({ validationErrors });

		dispatch(VALIDATION_CHANGED, { pageIsValid });
	}
};

export const onConnect = ({
	action,
	dispatch,
	state,
	properties,
	updateState
}) => {
	const { options } = action.payload;
	const { config = [] } = options;

	const validationList = getValidationList(config, { state, properties });
	const pageIsValid = getPageValidationState(validationList);

	updateState({
		operation: 'set',
		path: 'behaviors.validationBehavior',
		value: { validationList, config }
	});

	dispatch(VALIDATION_CHANGED, { pageIsValid });
};

/**
 * @param {configList[]} config - the config passed in to the behavior
 * @param {object} coeffects - contains the page's state and properties
 * @returns {validationList[]} - the map of all field ids and their validation state
 *
 */

export const getValidationList = (config, { state, properties }) => {
	const validationList = config.map((item) => {
		const { isValid } = getFieldValidationState(
			item.validators,
			{ state, properties },
			item.id,
			properties[item.id]
		);
		return {
			id: item.id,
			isValid
		};
	});
	return validationList;
};

/**
 * @typedef FieldValidationState
 * @property {boolean} isValid
 * @property {string|null} validationError
 */

/**
 * @param {validators[]} validators - the map of validators and their corresponding options & error messages
 * @param {object} coeffects - contains the page's state and properties
 * @param coeffects.state
 * @param coeffects.properties
 * @param {id} id - the config id
 * @param {string} value - the current value of the field
 * @returns {FieldValidationState} - returns the validation state for the field
 */
export const getFieldValidationState = (
	validators,
	{ state, properties },
	id,
	value
) => {
	let validationError = null;

	const isValid = validators.every((current) => {
		const currentValidation = current.validator({
			properties,
			state,
			value,
			options: { ...current.options, id }
		});

		validationError = currentValidation ? null : current.errorMessage;
		return currentValidation;
	});

	return { isValid, validationError };
};

/**
 * @param {validationList[]} - the map of all field ids and their validation state
 * @returns {boolean} - returns the final validation state for the page
 */

export const getPageValidationState = (validationList) => {
	return validationList.every(({ isValid }) => isValid);
};

/**
 * @param {string} id - the field id
 * @param {boolean} isValid - the computed validation state of the field
 * @param {validationList[]} validationList - the map of all field ids and their validation state
 * @returns {validationList[]} - the updated validation list
 */

export const updateValidationList = (id, isValid, validationList) => {
	return validationList.map((field) => {
		const shouldUpdateField = field.id === id;
		return {
			...field,
			isValid: shouldUpdateField ? isValid : field.isValid
		};
	});
};

export const validationBehavior = {
	name: 'validationBehavior',
	/**
	 * @typedef validationList
	 * @property {string} id
	 * @property {boolean} isValid
	 */

	/**
	 * @typedef configList
	 * @property {string} id
	 * @property {validators[]} validators
	 */

	/**
	 * @typedef validators
	 * @property {string} errorMessage the error message to display if the validation fails
	 * @property {object} options options passed to the validator function
	 * @property {Function} validator the function the field is validated against
	 */
	initialState: {
		/**
		 * contains a list of input field ids, their current values and their computed validation state (isValid)
		 *
		 * @type {validationList[]}
		 */
		validationList: [],
		/**
		 * consists of a list of field ids and validators
		 *
		 * @type {configList[]}
		 */
		config: []
	},

	properties: {
		validationErrors: {
			default: {},
			selectable: true
		}
	},

	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: onConnect,
		[UPDATE_VALIDATION_LIST]: onChildDataChanged
	}
};
export default validationBehavior;
