import '@servicenow/now-input';
import '@servicenow/now-alert';
import '@servicenow/now-typeahead';
import { t } from 'sn-translate';
import get from 'lodash/get';
import 'sn-ui-builder-components';

const appendToNamePayload = { input: 'page-name' };
const appendToRoutePayload = { input: 'route' };
const appendToOrderPayload = { input: 'order' };
const appendToIconPayload = { input: 'icon' };

/**
 * @param root0
 * @param root0.itemType
 * @param root0.itemName
 * @param root0.editMode
 * @param root0.disabled
 * @param root0.routeType
 * @param root0.scope
 * @param root0.domain
 * @param root0.supportDomains
 * @param root0.routeConfigurationEnabled
 * @param root0.showAlert
 * @param root0.selectedPageIsTab
 * @param root0.order
 * @param root0.icon
 * @param root0.validations
 * @param root0.runtimeUrl
 * @param root0.isNewPage
 * @param root0.dynamicTabDataSource
 * @param root0.dynamicTabRoutes
 * @param root0.isDynamicTab
 * @param root0.showUrlPreview
 * @param root0.allowSharedPageDefinition
 */
export default function PagePropertiesForm({
	itemType,
	itemName,
	editMode,
	disabled,
	routeType = {},
	scope,
	domain,
	supportDomains,
	routeConfigurationEnabled,
	showAlert,
	selectedPageIsTab,
	order,
	icon,
	validations,
	runtimeUrl,
	isNewPage,
	dynamicTabDataSource,
	dynamicTabRoutes,
	isDynamicTab,
	showUrlPreview,
	allowSharedPageDefinition
}) {
	const capitalizedItemType = itemType[0].toUpperCase() + itemType.slice(1);
	const isDashboardBuilder = itemType === 'dashboard';
	return (
		<div className="form-container">
			<form>
				<div>
					{showAlert ? renderAlert(selectedPageIsTab) : null}
					<div
						className={
							routeConfigurationEnabled && !isDynamicTab
								? 'two-column-flex name'
								: 'name'
						}>
						<now-input
							label={t('Name of {0}', itemType)}
							value={itemName}
							append-to-payload={appendToNamePayload}
							required
							disabled={disabled}
							helper-content={
								isDashboardBuilder
									? /* prettier-ignore */
									  t('Enter a unique name. For example, for a user dashboard, you may enter "User Dashboard."')
									: /* prettier-ignore */
									  t('Enter a unique name. For example, for a user registration page, you may enter "User Registration."')
							}
							placeholder={t('Enter a name for your {0}', itemType)}
							name="page-name"
						/>
						{routeConfigurationEnabled ? (
							!isDynamicTab ? (
								<now-input
									label={t('Path')}
									value={routeType.content}
									append-to-payload={appendToRoutePayload}
									required
									disabled={disabled}
									/* prettier-ignore */
									helper-content={t('A path acts as a unique identifier for your page or tab. A path can be appended to the instance address to determine the location of a page in your instance.')}
									placeholder={t('Enter a path for your {0}', itemType)}
									messages={routeType.messages}
									name="route"
								/>
							) : (
								<div className="two-column-flex">
									<div className="column">
										<now-typeahead
											items={dynamicTabRoutes}
											search="contains"
											value={routeType.content}
											label={t('Path')}
											append-to-payload={appendToRoutePayload}
											required
											disabled={disabled}
											/* prettier-ignore */
											helper-content={t('Path should match one of the route field in the linked data resource.')}
											/* prettier-ignore */
											placeholder={t('Enter a path name for your {0}',itemType)}
											messages={routeType.messages}
											name="route"
										/>
									</div>
									<div className="column">
										<now-input
											label={t('Data Source')}
											value={dynamicTabDataSource}
											disabled={true}
											helper-content={t('Data source imported from parent')}
											name="dataSource"
										/>
									</div>
								</div>
							)
						) : null}
					</div>
					{!selectedPageIsTab && showUrlPreview ? (
						<sn-uib-url-preview
							runtime-url={runtimeUrl}
							path={routeType.content}
							isNewPage={isNewPage}
						/>
					) : null}
					{allowSharedPageDefinition && !editMode ? (
						<div className="page-selector">
							<uib-page-definition-selector />
						</div>
					) : null}
					{selectedPageIsTab ? (
						<div className="two-column-flex">
							<div className="column">
								<sn-uib-icon-property-input
									id={'viewport-icon-id'}
									placeholder={t('Icon')}
									label={t('Icon')}
									icons="@uib-app/icons"
									value={icon}
									disabled={disabled}
									required={get(validations, 'icon.required', false)}
									append-to-payload={appendToIconPayload}
									isStatic={true}
									dataBindingEnabled={false}
									scriptingEnabled={false}
								/>
							</div>
							<div className="column">
								<now-input
									label={t('Order')}
									value={order}
									append-to-payload={appendToOrderPayload}
									required={get(validations, 'order.required', false)}
									type="number"
									disabled={disabled}
									/* prettier-ignore */
									helper-content={t('To give higher priority to this record, enter a lower number.')}
									placeholder={t('Enter an order for your {0}', itemType)}
									name="order"
								/>
							</div>
						</div>
					) : null}
				</div>
				<div className="two-column-flex app-domain">
					<div className="column">
						<now-input
							label={t('Application scope')}
							value={scope && scope.label ? scope.label : ''}
							disabled
							readonly
							helper-content={
								isDashboardBuilder
									? /* prettier-ignore */
									  t('The dashboard records are created in this default application scope. To switch scope, use the application scope picker.')
									: /* prettier-ignore */
									  t('The page or tab records are created in this default application scope. To switch scope, use the application scope picker.')
							}
						/>
					</div>
					{!editMode && supportDomains ? (
						<div className="column">
							<now-input
								label={t('Domain')}
								value={
									domain && domain.label ? domain.label.split('/').pop() : ''
								}
								disabled
								readonly
								helper-content={
									isDashboardBuilder
										? /* prettier-ignore */
										  t('The dashboard records are created in this domain. To switch domain scope, use the domain scope picker.')
										: /* prettier-ignore */
										  t('The variant is created in this domain. To switch domain scope, use the domain scope picker.')
								}
							/>
						</div>
					) : null}
				</div>
			</form>
		</div>
	);
}

/**
 * @param selectedPageIsTab
 */
export function renderAlert(selectedPageIsTab) {
	return (
		<now-alert
			status="critical"
			icon="circle-exclamation-outline"
			/* prettier-ignore */
			content={t('This {0} has another {1} with the same {2} which is not allowed. Please change the {2} name.',selectedPageIsTab ? 'viewport element' : 'application', selectedPageIsTab ? 'tab' : 'page', selectedPageIsTab ? 'ID' : 'path')}
		/>
	);
}
