import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-modal';

import '../uib-page-info-data-manager';

import { lifecycleHandlers } from './actionHandlers/lifecycleHandlers';
import { pageHandlers } from './actionHandlers/pageHandlers';
import { dataHandlers } from './actionHandlers/dataHandlers';
import { modalHandlers } from './actionHandlers/modalHandlers';

import styles from './uib-create-new-page.scss';

const renderPage = (state) => {
	const { context, page, properties } = state;
	const { pageName, selectedApplication, selectedProtectionChoice } = context;
	const { disabled, userScope } = properties;

	const editMode = page === 'sn-uib-edit-page-info';

	switch (page) {
		case 'sn-uib-splash-page':
			return <sn-uib-splash-page />;
		case 'sn-uib-page-info':
		case 'sn-uib-edit-page-info':
		case 'sn-uib-copy-page-info':
			return (
				<uib-page-info-data-manager
					page-name={pageName}
					selected-application={editMode ? selectedApplication : userScope}
					selected-protection-choice={selectedProtectionChoice}
					is-edit-mode={editMode}
					disabled={disabled}
				/>
			);
		default:
			return null;
	}
};

createCustomElement('uib-create-new-page', {
	view: (state) => {
		const { footerActions, headerLabel, loading } = state;

		return (
			<now-modal
				manage-opened={true}
				opened={true}
				size="lg"
				header-label={headerLabel}
				footer-actions={footerActions}>
				<div aria-busy={loading}>{renderPage(state)}</div>
			</now-modal>
		);
	},
	properties: {
		pageCreateType: {
			default: 'sn-uib-splash-page'
		},
		appsSysId: {
			default: null
		},
		appScopeId: {
			default: null
		},
		pageState: {
			default: {}
		},
		pageSysId: {
			default: {}
		},
		pageRegistrySysId: {
			default: {}
		},
		disabled: {
			default: false
		},
		modalInfo: {
			default: null
		},
		userScope: {
			default: {}
		}
	},
	initialState: {
		context: {},
		headerLabel: '',
		page: 'sn-uib-splash-page',
		footerActions: [],
		tabs: [],
		loading: false
	},
	actionHandlers: {
		...lifecycleHandlers,
		...pageHandlers,
		...dataHandlers,
		...modalHandlers
	},
	styles
});
