import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { snUXFActions, isReferenced } from 'sn-uxf-builder-middleware';
import styles from './uib-page-properties-modal.scss';
import isEmpty from 'lodash/isEmpty';
import { validationErrorMessage } from '../uib-advanced-url-configuration-modal';
import get from 'lodash/get';
import omitBy from 'lodash/omitBy';
import '@servicenow/now-button';
import '@servicenow/now-content-tree';
import '@servicenow/now-checkbox';
import '@servicenow/now-empty-state';
import '@servicenow/now-input';
import '@servicenow/now-select';
import '@servicenow/now-textarea';
import '@servicenow/now-popover';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';
import {
	updatePagePropsAndContentTree,
	generateFieldTypeOptions,
	disableFieldAndDelete,
	disableButtons,
	getSelectedItem,
	addButtonPayload,
	deleteButtonPayload,
	resetButtonPayload,
	saveButtonPayload,
	popoverPayload,
	createButtonPayload,
	buttonCreateLabel,
	headerLabel,
	propertiesLabel,
	noPropertiesYet,
	addPropertyLabel,
	nameLabel,
	typeLabel,
	tooltipLabel,
	translatableLabel,
	deleteLabel,
	resetLabel,
	createLabel,
	saveLabel,
	nameHelperContent,
	typeHelperContent,
	tooltipHelperContent,
	subheaderText,
	tooltipPlaceholder,
	footerActions,
	UIB_PAGE_PROPERTIES_MODAL_DELETE_ALL_PARAMS_CLICKED,
	UIB_PAGE_PROPERTIES_MODAL_DELETE_PROP_CLICKED,
	UIB_PAGE_PROPERTIES_MODAL_POPOVER_CREATE_CLICKED,
	UIB_PAGE_PROPERTIES_MODAL_POPOVER_URL_ADD_CLICKED,
	UIB_PAGE_PROPERTIES_MODAL_DISMISSED,
	UIB_PAGE_PROPERTIES_MODAL_SAVE,
	UIB_PAGE_PROPERTIES_MODAL_CANCEL,
	UIB_PAGE_PROPERTIES_MODAL_UPDATE_PROPS,
	getPopoverItems
} from './utils';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const view = ({
	selectedProperty,
	contentTreeItems,
	isReadOnly,
	propName,
	propNameMessages,
	propType,
	propTranslatable,
	propTooltip,
	isDirty,
	unmappedPageParameters,
	unmappedPageFields,
	popoverOpen,
	newForm,
	pagePropsWithFlags,
	properties: {
		userScope,
		applications,
		domains,
		supportScope,
		isDomainExpanded,
		overrideActionDisabled,
		supportDomains,
		currentDomain,
		pageScopeName,
		pageScopeId,
		pageDomainName,
		pageDomainId,
		pageState
	}
}) => {
	const requestPageDefinitionScopeId = get(pageState, 'rootElement.scope');
	const requestPageDefinitionScopeName = get(
		pageState,
		'rootElement.scopeName'
	);
	const pageDefProtectionPolicy = get(pageState, 'rootElement.sysPolicy', null);

	return (
		<now-modal
			manage-opened={true}
			opened={true}
			size="lg"
			footer-actions={footerActions(isReadOnly)}
			headerLabel={headerLabel}>
			<div className={'wrapper'}>
				<sn-uib-transaction-alert
					applications={applications}
					domains={domains}
					userScope={userScope}
					userDomain={currentDomain}
					supportDomains={supportDomains}
					requestScopeId={pageScopeId}
					requestScopeName={pageScopeName}
					requestDomainId={pageDomainId}
					requestDomainName={pageDomainName}
					requestPageDefinitionScopeId={requestPageDefinitionScopeId}
					requestPageDefinitionScopeName={requestPageDefinitionScopeName}
					recordProtectionPolicy={pageState.sysPolicy}
					pageDefProtectionPolicy={pageDefProtectionPolicy}
					supportScope={supportScope}
					isSharedPageDef={pageState.usingSharedPageDefinition}
					isDomainExpanded={isDomainExpanded}
					overrideActionDisabled={overrideActionDisabled}
					requestItemType="Variant"
				/>
				<div className="subheader">{subheaderText}</div>
				<div className={'container'}>
					<div className="first-column">
						<div className="header">
							<div className="header-text"> {propertiesLabel} </div>
							{isEmpty(unmappedPageParameters) &&
							isEmpty(unmappedPageFields) ? (
								<now-button-bare
									label={buttonCreateLabel}
									iconStart="plus-outline"
									size="sm"
									variant="tertiary"
									append-to-payload={addButtonPayload}
									disabled={isReadOnly}
								/>
							) : (
								<now-popover
									manage-opened
									opened={popoverOpen}
									append-to-payload={popoverPayload}
									hideTail>
									<now-button-bare
										label={buttonCreateLabel}
										iconStart="plus-outline"
										size="sm"
										variant="tertiary"
										slot="trigger"
										disabled={isReadOnly}
									/>
									<sn-uib-menu-elements
										slot="content"
										items={getPopoverItems(
											unmappedPageFields,
											unmappedPageParameters
										)}
									/>
								</now-popover>
							)}
						</div>
						{contentTreeItems && contentTreeItems.length ? (
							<div className="properties-list">
								<now-content-tree
									manage-selected-items
									items={contentTreeItems}
									size="sm"
									overflow="truncate"
									trigger-icon="caret"
									selected-items={getSelectedItem(
										selectedProperty,
										newForm,
										pagePropsWithFlags
									)}
								/>
							</div>
						) : (
							<div className="empty-container">
								<now-empty-state
									headerText={noPropertiesYet}
									bodyText={null}
									icon={'user-plus-outline'}
								/>
								<now-button
									label={addPropertyLabel}
									size="sm"
									variant="secondary"
									append-to-payload={addButtonPayload}
								/>
							</div>
						)}
					</div>
					<div className="second-column">
						{newForm || selectedProperty ? (
							<div className="column-container">
								<now-input
									required
									manage-invalid
									autofocus={!selectedProperty}
									helper-content={nameHelperContent}
									label={nameLabel}
									value={propName}
									invalid={propNameMessages && !!propNameMessages.length}
									messages={propNameMessages}
									disabled={disableFieldAndDelete(
										selectedProperty,
										isReadOnly,
										'name'
									)}
								/>
								<now-select
									label={typeLabel}
									required
									disabled={disableFieldAndDelete(
										selectedProperty,
										isReadOnly,
										'type'
									)}
									items={generateFieldTypeOptions(
										selectedProperty && selectedProperty.fieldType
									)}
									selected-item={propType || 'string'}
									helper-content={typeHelperContent}
								/>
								{propType === 'string' || !propType ? (
									<now-checkbox
										label={translatableLabel}
										checked={propTranslatable}
										disabled={disableFieldAndDelete(
											selectedProperty,
											isReadOnly,
											'translatable'
										)}
									/>
								) : null}
								<now-textarea
									label={tooltipLabel}
									helper-content={tooltipHelperContent}
									disabled={disableFieldAndDelete(
										selectedProperty,
										isReadOnly,
										'tooltip'
									)}
									autoresize
									placeholder={tooltipPlaceholder}
									resize="vertical"
									show-borders
									value={propTooltip}
								/>
								<div className="footer">
									<div className="delete-button">
										{!disableFieldAndDelete(
											selectedProperty,
											isReadOnly,
											'delete-button'
										) ? (
											<now-button
												label={deleteLabel}
												variant="tertiary"
												size="sm"
												append-to-payload={deleteButtonPayload}
											/>
										) : null}
									</div>
									<div className="end-buttons">
										{!disableButtons(
											selectedProperty,
											propName,
											propType,
											propTooltip,
											propTranslatable,
											isDirty,
											propNameMessages,
											'reset-button'
										) ? (
											<now-button
												label={resetLabel}
												append-to-payload={resetButtonPayload}
												variant="tertiary"
												size="sm"
											/>
										) : null}
										<now-button
											label={selectedProperty ? saveLabel : createLabel}
											append-to-payload={
												selectedProperty
													? saveButtonPayload
													: createButtonPayload
											}
											variant="secondary"
											size="sm"
											disabled={disableButtons(
												selectedProperty,
												propName,
												propType,
												propTranslatable,
												propTooltip,
												isDirty,
												propNameMessages,
												'save-button'
											)}
										/>
									</div>
								</div>
							</div>
						) : null}
					</div>
				</div>
			</div>
		</now-modal>
	);
};

createCustomElement('uib-page-properties-modal', {
	view,
	styles,
	initialState: {
		selectedProperty: null,
		contentTreeItems: [],
		pagePropsWithFlags: {},
		propName: '',
		propNameMessages: [],
		propType: 'string',
		propTranslatable: false,
		propTooltip: null,
		isDirty: false,
		unmappedPageParameters: [],
		unmappedPageFields: [],
		popoverOpen: false,
		newForm: false,
		isReadOnly: true
	},
	properties: {
		pageState: {
			default: null
		},
		appRoutes: {
			default: null
		},
		selectedPageId: {
			default: null
		},
		userScope: {
			default: null
		},
		supportDomain: {
			default: false
		},
		currentDomain: {
			default: null
		},
		applications: {
			default: []
		},
		fieldsParamsEnabled: {
			default: false
		},
		pageScopeName: {
			default: null,
			computed({ properties: { pageState } }) {
				const scopeName = get(pageState, 'scopeName', null);
				return scopeName !== null ? escapeScopeLabel(scopeName) : null;
			}
		},
		pageScopeId: {
			default: null,
			computed({ properties: { pageState } }) {
				return get(pageState, 'scope', null);
			}
		},
		domains: {
			default: []
		},
		supportScope: {
			default: false
		},
		isDomainExpanded: {
			default: false
		},
		overrideActionDisabled: {
			default: false
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: updatePagePropsAndContentTree,
		[COMPONENT_PROPERTY_CHANGED]: ({
			updateState,
			properties,
			action,
			state
		}) => {
			const { name } = action.payload;
			if (name === 'pageState') {
				updatePagePropsAndContentTree({ updateState, properties, state });
				const { selectedProperty = {} } = state;
				if (
					!get(
						properties,
						'pageState.rootElement.macroponentProperties',
						[]
					).find((prop) => prop.id === selectedProperty.id)
				) {
					updateState({
						selectedProperty: null,
						propName: '',
						propNameMessages: [],
						propTranslatable: false,
						propType: 'string',
						propTooltip: '',
						isDirty: false
					});
				}
			}
		},
		['NOW_MODAL#OPENED_SET']: ({ dispatch, action }) => {
			action.stopPropagation();
			dispatch(UIB_PAGE_PROPERTIES_MODAL_DISMISSED);
		},
		['NOW_POPOVER#OPENED_SET']: ({ updateState, action, state }) => {
			action.stopPropagation();
			const { isReadOnly } = state;
			if (!isReadOnly) {
				const { value: popoverOpen, popover } = action.payload;
				if (popover === 'create') {
					updateState({
						popoverOpen
					});
				}
			}
		},
		[UIB_PAGE_PROPERTIES_MODAL_CANCEL]: ({ dispatch }) => {
			dispatch(snUXFActions.PAGE_STATE_RESET);
			dispatch(UIB_PAGE_PROPERTIES_MODAL_DISMISSED);
		},
		['NOW_CONTENT_TREE#ITEM_CLICKED']: ({ action, updateState, state }) => {
			action.stopPropagation();
			const { pagePropsWithFlags } = state;
			const { item } = action.payload;
			const selectedProperty = pagePropsWithFlags.find(
				({ id }) => item.id === id
			);
			if (selectedProperty) {
				updateState({
					newForm: false,
					selectedProperty,
					propName: selectedProperty.name,
					propNameMessages: [],
					propType: selectedProperty.fieldType || 'string',
					propTooltip: selectedProperty.description,
					propTranslatable: get(selectedProperty, 'typeMetadata.translatable'),
					isDirty: false
				});
			}
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ updateState, action }) => {
			action.stopPropagation();
			const { button } = action.payload || {};
			if (button === 'add-prop') {
				updateState({
					newForm: true,
					selectedProperty: null,
					propName: '',
					propNameMessages: [],
					propType: 'string',
					propTooltip: '',
					propTranslatable: false,
					isDirty: false
				});
			}
		},
		['NOW_BUTTON#CLICKED']: ({
			updateState,
			action,
			state,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const {
				selectedProperty,
				propName: name,
				propType: fieldType,
				propTooltip: description,
				propTranslatable: translatable
			} = state;
			const { button } = action.payload || {};
			switch (button) {
				case 'add-prop':
					updateState({
						selectedProperty: null,
						propName: '',
						propNameMessages: [],
						propType: 'string',
						propTranslatable: false,
						propTooltip: '',
						isDirty: false
					});
					break;
				case 'reset-prop':
					updateState({
						propName: selectedProperty ? selectedProperty.name : '',
						propNameMessages: [],
						propType: selectedProperty
							? selectedProperty.fieldType || 'string'
							: 'string',
						propTooltip: selectedProperty ? selectedProperty.description : null,
						propTranslatable: selectedProperty
							? get(selectedProperty.typeMetadata, 'translatable')
							: false,
						isDirty: false
					});
					break;
				case 'create-prop':
					dispatch(snUXFActions.PAGE_PROPERTIES_CREATED, {
						name,
						fieldType,
						description,
						translatable
					});
					updateState({
						selectedProperty: null,
						propName: '',
						propNameMessages: [],
						propType: 'string',
						propTranslatable: false,
						propTooltip: '',
						isDirty: false
					});
					break;
				case 'save-prop':
					dispatch(snUXFActions.PAGE_PROPERTIES_CHANGED, {
						name,
						fieldType,
						description,
						translatable,
						pagePropertyId: selectedProperty.id,
						value: selectedProperty.value,
						testValue: selectedProperty.testValue
					});
					updateState({
						isDirty: false
					});
					break;
				case 'delete-prop': {
					const hasReferences = isReferenced(
						'pageProperty',
						selectedProperty.name,
						properties.pageState
					);
					if (hasReferences) {
						dispatch(
							'SN_UIB_PAGE_PROPERTIES_MODAL#DELETE_REFERENCED_PROPERTY',
							{
								selectedProperty
							}
						);
					} else {
						dispatch(snUXFActions.PAGE_PROPERTIES_DELETED, {
							pagePropertyId: selectedProperty.id
						});
					}
					break;
				}
			}
		},
		['NOW_INPUT#INPUT']: ({ updateState, action, properties, state }) => {
			action.stopPropagation();
			const { pageState } = properties;
			const { selectedProperty } = state;
			const { fieldValue: propName } = action.payload;
			const pageProps = omitBy(
				get(pageState, 'rootElement.macroponentProperties', {}),
				({ id }) => selectedProperty && id === selectedProperty.id
			);
			updateState({
				propName,
				propNameMessages: validationErrorMessage(
					propName,
					'property',
					pageProps
				),
				isDirty: true
			});
		},
		['NOW_CHECKBOX#CHECKED_SET']: ({ updateState, action }) => {
			action.stopPropagation();
			const { value: propTranslatable } = action.payload;
			updateState({
				propTranslatable,
				isDirty: true
			});
		},
		['NOW_TEXTAREA#INPUT']: ({ updateState, action }) => {
			action.stopPropagation();
			const { fieldValue } = action.payload;
			updateState({
				propTooltip: fieldValue || null,
				isDirty: true
			});
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({ updateState, action, state }) => {
			action.stopPropagation();
			const { propTranslatable } = state;
			const { value: propType } = action.payload;
			updateState({
				propType,
				propTranslatable:
					propType === 'string' || !propType ? propTranslatable : false,
				isDirty: true
			});
		},
		[UIB_PAGE_PROPERTIES_MODAL_DELETE_PROP_CLICKED]: ({
			properties,
			dispatch,
			action
		}) => {
			action.stopPropagation();
			const { item } = action.payload;
			const selectedProperty = get(
				properties,
				'pageState.rootElement.macroponentProperties',
				[]
			).find((prop) => prop.id === item.id);
			const hasReferences = isReferenced(
				'pageProperty',
				selectedProperty.name,
				properties.pageState
			);
			if (hasReferences) {
				dispatch('SN_UIB_PAGE_PROPERTIES_MODAL#DELETE_REFERENCED_PROPERTY', {
					selectedProperty
				});
			} else {
				dispatch(snUXFActions.PAGE_PROPERTIES_DELETED, {
					pagePropertyId: selectedProperty.id
				});
			}
		},
		[UIB_PAGE_PROPERTIES_MODAL_DELETE_ALL_PARAMS_CLICKED]: ({
			updateState,
			dispatch,
			action,
			state
		}) => {
			action.stopPropagation();
			const { selectedProperty, pagePropsWithFlags } = state;
			if (selectedProperty && selectedProperty.isParam) {
				updateState({
					selectedProperty: null,
					propName: '',
					propTranslatable: false,
					propType: 'string',
					propNameMessages: [],
					propTooltip: '',
					isDirty: false
				});
			}
			pagePropsWithFlags.forEach((prop) => {
				if (prop.isParam) {
					dispatch(snUXFActions.PAGE_PROPERTIES_DELETED, {
						pagePropertyId: prop.id
					});
				}
			});
		},
		[UIB_PAGE_PROPERTIES_MODAL_SAVE]: ({ dispatch, action }) => {
			action.stopPropagation();
			dispatch(snUXFActions.PAGE_SAVE_REQUESTED);
			dispatch(UIB_PAGE_PROPERTIES_MODAL_DISMISSED);
		},
		[UIB_PAGE_PROPERTIES_MODAL_POPOVER_URL_ADD_CLICKED]: ({
			updateState,
			dispatch,
			action
		}) => {
			action.stopPropagation();
			const { itemLabel } = action.payload || {};
			if (itemLabel) {
				dispatch(snUXFActions.PAGE_PROPERTIES_CREATED, {
					name: itemLabel,
					fieldType: 'string'
				});
			}
			updateState({
				popoverOpen: false
			});
		},
		[UIB_PAGE_PROPERTIES_MODAL_POPOVER_CREATE_CLICKED]: ({
			updateState,
			action
		}) => {
			action.stopPropagation();
			updateState({
				newForm: true,
				selectedProperty: null,
				propName: '',
				propTranslatable: false,
				propType: 'string',
				propNameMessages: [],
				propTooltip: null,
				popoverOpen: false
			});
		},
		['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: ({
			updateState,
			action,
			state,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const { isReadOnly } = state;
			const { pageState } = properties;
			const pageDefProtectionPolicy = get(
				pageState,
				'rootElement.sysPolicy',
				null
			);
			const usingSharedPageDefinition = get(
				pageState,
				'usingSharedPageDefinition',
				null
			);
			const macroponentIsReadyOnly =
				['read', 'protected'].includes(pageDefProtectionPolicy) ||
				usingSharedPageDefinition;
			const { isNecessaryTransactionInProgress } = action.payload;

			if (isReadOnly === isNecessaryTransactionInProgress) {
				updateState({
					isReadOnly:
						!isNecessaryTransactionInProgress || macroponentIsReadyOnly
				});
				dispatch(UIB_PAGE_PROPERTIES_MODAL_UPDATE_PROPS, {
					switchingReadOnly: true,
					isReadOnly:
						!isNecessaryTransactionInProgress || macroponentIsReadyOnly
				});
			}
		},
		[UIB_PAGE_PROPERTIES_MODAL_UPDATE_PROPS]: ({
			updateState,
			properties,
			state,
			action
		}) => {
			updatePagePropsAndContentTree(
				{ updateState, properties, state },
				action.payload
			);
		}
	}
});
