export default (context, appConfigId) => {
	const {
		pageName: name = '',
		pageRegistrySysId = '',
		selectedApplication = { id: 'global', label: 'Global' },
		selectedProtectionChoice = { id: '' }
	} = context;

	const scope = selectedApplication.id;
	// Parse sysPolicy back to an empty string if value is "none"
	const sysPolicy =
		selectedProtectionChoice.id === 'none' ? '' : selectedProtectionChoice.id;

	const payload = {
		scope,
		name,
		sysPolicy,
		applicability: {
			applications: [],
			appConfigId
		}
	};

	if (pageRegistrySysId) {
		payload.applicability.applications = [pageRegistrySysId];
	}

	return payload;
};
