import debounce from 'lodash/debounce';

export const NULLIFIED_C3_CONFIG = {
	propertyId: null,
	value: null,
	initialValue: null,
	pendingValue: null,
	srcUrl: null,
	label: null,
	pending: false,
	meta: null,
	scrollable: null
};

export const extendedPropertiesHandlers = {
	'UIB_TINYMCE#INITIALIZED': ({ updateState }) => {
		updateState({
			// This is a weird height value which is perfect for the modal height in 8080 but it differ in 8081.
			//  Any value above this causes a weird scroll when being used in voiceover even
			//  though we are adding overflow:hidden. Voice Over doesn't respect that.
			c3ContentHeight: '32em',
			c3ContentInitialized: true
		});
	},
	'UIB_TINYMCE#CONTENT_CHANGED': debounce(
		({ action, properties, updateProperties }) => {
			updateProperties({
				c3PropertyConfig: {
					...properties.c3PropertyConfig,
					pendingValue: action.payload,
					pending: true
				}
			});
		},
		300
	),
	'UIB_CONDITION_BUILDER#INITIALIZED': ({ updateState }) => {
		updateState({
			c3ContentHeight: '40em',
			c3ContentInitialized: true
		});
	},
	'UIB_CONDITION_BUILDER#QUERY_CHANGED': ({
		action,
		properties,
		updateProperties
	}) => {
		updateProperties({
			c3PropertyConfig: {
				...properties.c3PropertyConfig,
				pendingValue: action.payload.query,
				pending: true
			}
		});
	},
	'SN_UIB_C3_PROPERTY_INPUT#OPENED_SET': ({
		action,
		dispatch,
		updateProperties,
		updateState
	}) => {
		const {
			payload: {
				id,
				value,
				label,
				url,
				meta,
				scrollable,
				redirectFocusTo,
				c3ContentHeight,
				c3FooterActions
			}
		} = action;

		updateProperties({
			c3PropertyConfig: {
				propertyId: id,
				srcUrl: url,
				initialValue: value,
				pendingValue: value,
				value,
				label,
				pending: false,
				meta,
				scrollable,
				redirectFocusTo
			}
		});

		updateState({
			c3ContentHeight,
			c3FooterActions
		});
		dispatch('SN_UIB_APP#C3_MODAL_OPENED');
	},
	'UIB_APP#C3_DONE_CLICKED': {
		effect: () => null,
		interceptors: [
			(context) => {
				return {
					...context,
					effects: [
						// Effect 1: Update the value for UXF
						{
							effect: ({ properties, dispatch, updateProperties }) => {
								updateProperties({
									c3PropertyConfig: {
										...properties.c3PropertyConfig,
										value: properties.c3PropertyConfig.pendingValue
									}
								});
								dispatch('SN_UIB_APP#C3_MODAL_CLOSED');
							},
							args: [context.coeffects]
						}
					]
				};
			}
		]
	},
	'UIB_APP#C3_CANCEL_CLICKED': ({
		dispatch,
		updateProperties,
		updateState
	}) => {
		updateProperties({
			c3PropertyConfig: { ...NULLIFIED_C3_CONFIG }
		});
		updateState({
			c3ContentHeight: 'auto',
			c3ContentInitialized: false
		});
		dispatch('SN_UIB_APP#C3_MODAL_CLOSED');
	},
	'NOW_MODAL#OPENED_SET': ({ action, updateProperties, updateState }) => {
		if (action.payload.value) {
			return;
		}
		updateProperties({ c3PropertyConfig: { ...NULLIFIED_C3_CONFIG } });
		updateState({
			c3ContentHeight: 'auto',
			c3ContentInitialized: false
		});
	}
};
