import { createHttpEffect } from '@servicenow/ui-effect-http';
import cloneDeep from 'lodash/cloneDeep';
import {
	SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUESTED,
	SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_STARTED,
	SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_SUCCEEDED,
	SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_FAILED
} from './constants';
import { effectProxy } from 'sn-uxf-builder-middleware';

export const fetchScreenEffect = createHttpEffect(
	'/api/now/uxframework/screens/:sysId',
	{
		method: 'GET',
		pathParams: ['sysId'],
		startActionType: SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_STARTED,
		successActionType: SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_SUCCEEDED,
		errorActionType: SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_FAILED,
		batch: false
	}
);

const screenRequestStartedHandler = ({ updateState }) => {
	updateState({
		isLoadingScreenDetails: true
	});
};

const screenRequestSucceededHandler = ({ action, updateState }) => {
	const {
		payload: { result = {} }
	} = action;
	const {
		name,
		id: sysId,
		applicationName: scopeName,
		scope: scopeId,
		isActive: active,
		order,
		screenCondition,
		domainId,
		domainName,
		macroponentInstance,
		sysPolicy
	} = result;

	const pageDefinition =
		macroponentInstance.macroponentDefinitions[
			macroponentInstance.rootMacroponentId
		];
	const pageDefinitionScopeId = pageDefinition.scope;
	const pageDefinitionScopeName = pageDefinition.scopeName;

	const screen = {
		name,
		sysId,
		scopeName,
		scopeId,
		active,
		order,
		screenCondition,
		domainId,
		domainName,
		pageDefinitionScopeId,
		pageDefinitionScopeName,
		macroponentInstance,
		sysPolicy
	};

	updateState({
		isLoadingScreenDetails: false,
		screen,
		pristineScreen: cloneDeep(screen)
	});
};

const screenRequestFailedHandler = ({ updateState }) => {
	updateState({
		isLoadingScreenDetails: false
	});
};

export default {
	[SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUESTED]: effectProxy(fetchScreenEffect),
	[SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_STARTED]: screenRequestStartedHandler,
	[SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_SUCCEEDED]: screenRequestSucceededHandler,
	[SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUEST_FAILED]: screenRequestFailedHandler
};
