import '@servicenow/now-text-link';
import { t } from 'sn-translate';
import { forceTransitionTo } from '../../uib-router';
import PAGE_TITLE, { PAGE_TITLE_SUFFIX } from '../../../utils/pageTitle';
import { screenNames } from '../../../utils/routingConstants';

export function shouldShowRouteNotFound(
	routeName,
	routeState,
	applicationType
) {
	return (
		routeState.show404 &&
		[
			`${applicationType}.page`,
			`${applicationType}.unknownPath`,
			`${applicationType}.page.variant`,
			`${applicationType}.dashboard`
		].indexOf(routeName) > -1
	);
}

function RouteNotFoundLayout(_, children) {
	// eslint-disable-line no-unused-vars
	return (
		<div className="route-not-found">
			<div className="not-found">{children}</div>
		</div>
	);
}

export default function RouteNotFound({
	routeName,
	routeState = {},
	applicationType
}) {
	const {
		appsSysId,
		pageSysId,
		variantSysId,
		dashboardId,
		pageIsValid
	} = routeState;
	PAGE_TITLE.set(t('Page Not Found'), PAGE_TITLE_SUFFIX);

	switch (routeName) {
		case `${applicationType}.unknownPath`:
			return (
				<RouteNotFoundLayout>
					{t('Click here to navigate to {0}', applicationType)}
					&nbsp;
					<now-text-link
						label={appsSysId}
						href="javascript:void(0)"
						on-click={() => {
							forceTransitionTo(`${applicationType}`, { appsSysId });
						}}
					/>
				</RouteNotFoundLayout>
			);

		case `${applicationType}.page`:
			return (
				<RouteNotFoundLayout>
					{t('Cannot find page {0}', pageSysId)}
					<br />
					{t('Click here to navigate to {0}', applicationType)}
					&nbsp;
					<now-text-link
						label={appsSysId}
						href="javascript:void(0)"
						on-click={() => {
							forceTransitionTo(`${applicationType}`, { appsSysId });
						}}
					/>
				</RouteNotFoundLayout>
			);

		case `${applicationType}.page.variant`:
			return (
				<RouteNotFoundLayout>
					{pageIsValid
						? t('Cannot find variant {0}', variantSysId)
						: t('Cannot find page {0}', pageSysId)}
					<br />
					{t('Click here to navigate to {0}', pageIsValid ? 'page ' : 'app ')}
					&nbsp;
					<now-text-link
						label={pageIsValid ? pageSysId : appsSysId}
						href="javascript:void(0)"
						on-click={() => {
							forceTransitionTo(
								pageIsValid ? `${applicationType}.page` : `${applicationType}`,
								pageIsValid ? { appsSysId, pageSysId } : { appsSysId }
							);
						}}
					/>
				</RouteNotFoundLayout>
			);

		case screenNames.dashboardsDashboard:
			return (
				<RouteNotFoundLayout>
					{t('Cannot find dashboard {0}', dashboardId)}
					<br />
					{t('Click here to navigate to ')}
					&nbsp;
					<now-text-link
						label={t('dashboards home')}
						href="javascript:void(0)"
						on-click={() => {
							forceTransitionTo(screenNames.dashboardsHome, {});
						}}
					/>
				</RouteNotFoundLayout>
			);
	}
}
