import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import '@servicenow/now-modal';
import 'sn-ui-builder-components';

const getDialogContent = (type, context) => {
	switch (context) {
		case 'reset':
			return {
				headerLabel: t('Reset layout to original?'),
				/* prettier-ignore */
				content: t('This layout code has been customized and any changes made will be lost when reset. This cannot be undone'),
				footerActions: [
					{
						clickActionType: 'UIB_NEW_LAYOUT_CONFIRMATION#APPLY_CLICKED',
						variant: 'primary-negative',
						label: t('Reset layout')
					},
					{
						clickActionType: 'UIB_NEW_LAYOUT_CONFIRMATION#CANCEL_CLICKED',
						variant: 'secondary',
						label: t('Cancel')
					}
				]
			};
		default:
			return {
				headerLabel: t('Confirm layout change'),
				/* prettier-ignore */
				content: t("Applying this template will completely overwrite your {0}'s current layout.", type),
				footerActions: [
					{
						clickActionType: 'UIB_NEW_LAYOUT_CONFIRMATION#APPLY_CLICKED',
						variant: 'primary-negative',
						label: t('Apply')
					},
					{
						clickActionType: 'UIB_NEW_LAYOUT_CONFIRMATION#CANCEL_CLICKED',
						variant: 'secondary',
						label: t('Cancel')
					}
				]
			};
	}
};

createCustomElement('uib-new-layout-confirmation', {
	view: (state) => {
		const { opened, type, context } = state.properties;
		const { headerLabel, content, footerActions } = getDialogContent(
			type,
			context
		);
		return (
			<now-modal
				manageOpened={true}
				opened={opened}
				size="sm"
				header-label={headerLabel}
				content={content}
				footer-actions={footerActions}
			/>
		);
	},
	properties: {
		/**
		 * Whether the delete confirmation modal is currently open
		 *
		 * @type {boolean}
		 */
		opened: {
			default: false
		},
		/**
		 * The type of template being changed
		 *
		 * @type {string}
		 */
		type: {
			default: null
		},
		/**
		 * The context of the layout change, either reset or default
		 *
		 * @type {string}
		 */
		context: {
			default: null
		},
		/**
		 * The action handler to use if the apply button is clicked in the modal
		 *
		 * @type {Function}
		 */
		applyActionHandler: {
			default: () => {}
		}
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': ({ dispatch }) => {
			dispatch('UIB_NEW_LAYOUT_CONFIRMATION#CLOSED');
		},
		'UIB_NEW_LAYOUT_CONFIRMATION#CANCEL_CLICKED': ({ dispatch }) => {
			dispatch('UIB_NEW_LAYOUT_CONFIRMATION#CLOSED');
		},
		'UIB_NEW_LAYOUT_CONFIRMATION#APPLY_CLICKED': ({ dispatch, properties }) => {
			const { applyActionHandler } = properties;
			applyActionHandler();
			dispatch('UIB_NEW_LAYOUT_CONFIRMATION#CLOSED');
		}
	}
});
