import get from 'lodash/get';

export const flattenAppRoutes = (appRoutes) => {
	return Array.isArray(appRoutes)
		? appRoutes.flatMap((route) => [
				route,
				...flattenAppRoutes(
					route.page.variants
						? route.page.variants.flatMap((variant) => variant.viewports)
						: []
				)
		  ])
		: [];
};

/**
 * Find a variant amongst the app routes by id.
 *
 * @param {string} variantId The sys_id of the variant you are looking for
 * @param {Array<object>} appRoutes The raw app routes object
 * @param isDashboardBuilder
 * @returns {object|undefined} The variant that has a matching sys_id
 */
export const findVariantInAppRoutes = (
	variantId,
	appRoutes = [],
	isDashboardBuilder = false
) => {
	if (!isDashboardBuilder) {
		const flattenedAppRoutes = flattenAppRoutes(appRoutes);
		return flattenedAppRoutes.flatMap((route) => {
			const { page = {} } = route;
			if (!page) {
				return null;
			}
			const { variants = [] } = page;
			return variants.filter((v) => v.sysId === variantId);
		})[0];
	} else if (isDashboardBuilder) {
		const flattenedDashboardRoutes = flattenDashboardRoutes(appRoutes);
		return (
			flattenedDashboardRoutes.find(
				(route) => route.screen && route.screen.id === variantId
			) || null
		);
	} else {
		return null;
	}
};

/**
 * Util function that puts all nested macroponent defs into a single object
 *
 * @param {object} appRoute - The root app route being inspected
 * @param {object} defs - The running hash of all mcp defs collected down the tree
 */
export const flattenAppRouteForMcps = (appRoute, defs = {}) => {
	let defsToUse = defs;
	const variants = get(appRoute, 'page.variants', []);
	variants.forEach((variant) => {
		defsToUse = {
			...defsToUse,
			...get(variant, 'macroponentInstance.macroponentDefinitions', {})
		};
		if (variant.viewports && variant.viewports.length) {
			variant.viewports.forEach((vp) => {
				defsToUse = {
					...defsToUse,
					...flattenAppRouteForMcps(vp, defsToUse)
				};
			});
		}
	});
	return defsToUse;
};

/**
 * Takes an object of mcp defs and gets all the data brokers
 *
 * @param {object} mcpDefs - object of all the mcp definitions to be processed
 */
export const getAllDescendentDataBrokers = (mcpDefs) => {
	return Object.values(mcpDefs).reduce((acc, def) => {
		if (def.data && def.data.length) {
			return [...acc, ...def.data];
		}
		return acc;
	}, []);
};

export const flattenDashboardRoutes = (routes) => {
	return Array.isArray(routes)
		? routes.flatMap((route) => [
				route,
				...flattenDashboardRoutes(route.children)
		  ])
		: [];
};
