import find from 'lodash/find';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import {
	BRANDING_COMPONENT_BOOTSTRAPPED,
	ITEM_SELECTED,
	PAGE_BRANDING,
	SETTINGS_UPDATED,
	TABLE_PANEL,
	TABLE_THEME,
	URLDOC,
	WORKSPACE
} from '../../constants';

import '../sn-experience-settings-link';

// NDS
import '@servicenow/now-heading';
import '@servicenow/now-select';
import '@servicenow/now-text-link';

import styles from '../../styles/forms.scss';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const view = ({
	properties: { panelSysId, primaryThemeId, themes, applicationType, readonly }
}) => {
	const selectedItem = find(themes, (o) => o.id === primaryThemeId);
	const page =
		applicationType === WORKSPACE
			? 'UIB_Workspace_BrandingTheming_Settings'
			: 'UIB_Portal_BrandingTheming_Settings';
	return (
		<div>
			<now-heading
				label={t('Branding and theming')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{t('Select a theme for this experience.')}{' '}
				{
					/* prettier-ignore */
					t('Themes apply branding to an experience to give it a consistent look and feel across all pages.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="fieldset">
				{themes && !readonly ? (
					<now-select
						className="field m-width"
						label={t('Themes')}
						items={themes}
						selectedItem={primaryThemeId}
						required
					/>
				) : (
					<now-input
						label={t('Themes')}
						value={selectedItem ? selectedItem.label : ''}
						required={true}
						readonly={readonly}
					/>
				)}

				<sn-experience-settings-link
					sys-id="-1"
					table={TABLE_THEME}
					label={t('Create a theme')}
					size="sm"></sn-experience-settings-link>
			</div>
			<sn-experience-settings-link
				sys-id={panelSysId}
				table={TABLE_PANEL}></sn-experience-settings-link>
		</div>
	);
};

createCustomElement('sn-experience-settings-branding', {
	view,
	// Properties are not required, because are lazy-loaded on component init
	properties: {
		panelSysId: {
			schema: { type: 'string' },
			default: ''
		},
		themes: {
			schema: { type: 'array' },
			default: []
		},
		primaryThemeId: {
			schema: { type: 'string' },
			default: ''
		},
		applicationType: {
			default: ''
		},
		readonly: {
			schema: { type: 'boolean' }
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(BRANDING_COMPONENT_BOOTSTRAPPED);
		},
		[ITEM_SELECTED]: ({
			action: {
				stopPropagation,
				payload: { value }
			},
			dispatch
		}) => {
			stopPropagation();
			dispatch(SETTINGS_UPDATED, {
				page: PAGE_BRANDING,
				name: 'primaryThemeId',
				value
			});
		}
	},
	styles
});
