import { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import '@servicenow/now-icon';
import { divide } from 'lodash';

export default ({ deleteItem, references }) => (
	<Fragment>
		<div>
			<strong>{deleteItem}</strong> is referenced by other items on this page.
			Deleting it will remove the following references:
		</div>
		<p>
			{references.map((ref) => (
				<div style={{ margin: '1rem', display: 'flex', alignItems: 'center' }}>
					{ref.icon ? (
						<now-icon
							icon={ref.icon}
							size="md"
							style={{ marginRight: '.5rem' }}
						/>
					) : null}
					<span>{ref.name}</span>
				</div>
			))}
		</p>
		<div>
			Any scripts that reference <strong>{deleteItem}</strong> will not be
			changed.
		</div>
	</Fragment>
);
