import { createHttpEffect } from '@servicenow/ui-effect-http';
import { effectProxy } from 'sn-uxf-builder-middleware';
import { t } from 'sn-translate';
import {
	SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUESTED,
	SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUEST_SUCCEEDED,
	SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUEST_FAILED
} from './constants';

const definitionsRequestHttpEffect = createHttpEffect(
	'/api/now/table/sys_ux_macroponent',
	{
		method: 'GET',
		batch: false,
		queryParams: ['sysparm_query'],
		successActionType: SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUEST_SUCCEEDED,
		failureActionType: SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUEST_FAILED
	}
);

const definitionsRequestSucceeded = ({ action, updateState }) => {
	const { result } = action.payload;
	updateState({
		pageDefinitions: [{ id: 'none', label: t('None') }].concat(
			result.map(({ sys_id: id, name: label, sys_policy: sysPolicy }) => ({
				id,
				label,
				sysPolicy
			}))
		),
		isLoadingPageDefinitions: false
	});
};

const definitionsRequestFailed = ({ updateState }) => {
	updateState({
		isLoadingPageDefinitions: false
	});
};

export default {
	[SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUESTED]: effectProxy(
		definitionsRequestHttpEffect
	),
	[SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUEST_SUCCEEDED]: definitionsRequestSucceeded,
	[SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUEST_FAILED]: definitionsRequestFailed
};
