const BASE_URL = '/api/now/v1/context_doc_url/';

const resources = {
	['dashboard-builder']: 'help-dashboard-builder',
	['now-code-editor']: 'now-code-editor-ovw',
	['uib-overview']: 'UI_builder_overview',
	['uib-release-notes']: 'UI_Builder_Release_Notes_xyb',
	['custom-components']: 'Custom_Components_RID',
	['uib-quickstart']: 'ui_builder_quick_start_topic',
	['screen-conditions']: 'Edit_Screen_Conditions_UIB',
	['uib-page-templates']: 'UIB_page_templates'
};

/**
 * Helper to construct resource URLs to product documentation
 *
 * @param {string} key - The UIB internal key that maps to a resource Id
 * @returns {string} - The constructed URL
 */
export default function (key) {
	return `${BASE_URL}${resources[key]}`;
}
