import get from 'lodash/get';
import {
	getDependentPropertyId,
	getValueFromDependentProperty,
	extractScriptFromPropertyValue
} from '../properties';

import { deserializeAndExecuteScript } from 'sn-uxf-builder-middleware';

/**
 * Processor that takes in the currently configured property
 * and configures it for dependent typeMetadata
 * @param {object} property - The current UXF element property being configured
 * @param {object[]} properties - The current set of UXF properties for the element being configured
 * @param {object} state - The current element configuration panel Seismic state
 * @return {object} - The configured property object
 */
// eslint-disable-next-line no-unused-vars
export default function (property, properties, state, clientScriptApi) {
	const { typeMeta } = property;

	const newTypeMeta = { ...typeMeta };
	const otherPropertyKeys = {};
	/**
	 * First: Get all the typeMeta fields and resolve any dependent
	 * property value (typeMeta values that start with a '@')
	 */
	typeMeta &&
		Object.entries(typeMeta).forEach(([key, data]) => {
			let dependentPropertyName = null;
			if (key === 'reference') {
				dependentPropertyName = getDependentPropertyId(
					get(data, 'table', null)
				);
			} else {
				dependentPropertyName =
					typeof data === 'string' ? getDependentPropertyId(data) : null;
			}
			if (dependentPropertyName) {
				// TODO: For now, we are disabling static configuration of a property if
				// it has a dependent property whose value is a selectable property address.
				const dependentPropIsStatic = getValueFromDependentProperty(
					properties,
					dependentPropertyName,
					'isStatic'
				);

				const dependentPropIsScripted = getValueFromDependentProperty(
					properties,
					dependentPropertyName,
					'isScripted'
				);

				const dependentPropValue = getValueFromDependentProperty(
					properties,
					dependentPropertyName,
					'value'
				);

				if (dependentPropIsScripted) {
					const inlineScript = extractScriptFromPropertyValue(
						dependentPropValue
					);
					newTypeMeta[key] =
						inlineScript && clientScriptApi
							? deserializeAndExecuteScript(inlineScript, clientScriptApi)
							: dependentPropValue;
				} else {
					newTypeMeta[key] = dependentPropValue;
				}

				if (dependentPropIsStatic === false) {
					otherPropertyKeys.isStatic = false;
					otherPropertyKeys.alwaysBound = true;
					otherPropertyKeys.value = property.value || '@';
				}
			}
		});

	return {
		...property,
		...otherPropertyKeys,
		typeMeta: newTypeMeta
	};
}
