import { createCustomElement } from '@servicenow/ui-core';

export default createCustomElement('uib-modal-wrapper', {
	view: ({ properties: { modalProps } }) => {
		return (
			<now-modal {...modalProps}>
				<slot />
			</now-modal>
		);
	},
	properties: {
		modalProps: {
			default: {}
		},
		closeActionName: {
			default: null
		}
	},
	initialState: {
		layout: null,
		templateName: ''
	},
	actionHandlers: {
		['NOW_MODAL#OPENED_SET']: ({
			dispatch,
			properties: { closeActionName }
		}) => {
			if (closeActionName) {
				dispatch(closeActionName);
			}
		}
	}
});
