import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './uib-page-configuration-modal.module.scss';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import '@servicenow/now-input';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import { validationErrorMessage } from '../uib-advanced-url-configuration-modal';
import PagePropertiesForm from './partials/page-properties-form'; // eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';
import { tabFieldsValid, flattenRoute } from '../uib-new-create-page/utils';
import get from 'lodash/get';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const { NEW_PAGE_SAVED, DASHBOARDS_EDIT_REQUEST } = snUXFActions;

export const UIB_PAGE_CONFIGURATION_MODAL_MODAL_DISMISSED =
	'UIB_PAGE_CONFIGURATION_MODAL#MODAL_DISMISSED';
export const UIB_PAGE_CONFIGURATION_MODAL_DELETE_CLICKED =
	'UIB_PAGE_CONFIGURATION_MODAL#DELETE_CLICKED';
export const DISMISS = 'UIB_PAGE_CONFIGURATION_MODAL#CANCEL_CLICKED';
export const SAVE = 'UIB_PAGE_CONFIGURATION_MODAL#PAGE_SAVED';
const SAVE_BUTTON_CLICKED = 'UIB_PAGE_CONFIGURATION_MODAL#SAVE_CLICKED';
const DELETE_BUTTON_CLICKED =
	'UIB_PAGE_CONFIGURATION_MODAL#INTERNAL_DELETE_CLICKED';
const ICON_PROPERTY_INPUT_PROPERTY_CHANGED =
	'ICON_PROPERTY_INPUT#PROPERTY_CHANGED';

const transformScope = (scope) => {
	if (scope) {
		if (scope instanceof Object) {
			return {
				...scope,
				label: escapeScopeLabel(scope.label)
			};
		} else {
			return {
				id: scope
			};
		}
	}
	return scope;
};

const footerActions = (
	name,
	route,
	initialItemName,
	initialRouteTypeContent,
	showAlert,
	disabled,
	isSavingPage,
	selectedPageIsTab,
	order,
	icon,
	validation,
	initialIcon,
	initialOrder
) => {
	if (isSavingPage) {
		return null;
	} else if (disabled) {
		return [
			{
				label: 'Done',
				variant: 'primary',
				clickActionType: DISMISS
			}
		];
	}
	const emptyName = name.trim() === '';
	const routeError =
		route.content.trim() === '' || (route.messages && route.messages.length);
	const sameName = name === initialItemName;
	const sameRoute = route.content === initialRouteTypeContent;
	const sameIcon = icon === initialIcon;
	const sameOrder = order === initialOrder;

	const checks = [
		emptyName,
		routeError,
		disabled,
		showAlert,
		sameName && sameRoute && sameIcon && sameOrder,
		!tabFieldsValid(selectedPageIsTab, validation, icon, order)
	];
	const isDisabled = checks.some((check) => check);

	return [
		{
			label: t('Save'),
			variant: 'primary',
			clickActionType: SAVE_BUTTON_CLICKED,
			disabled: isDisabled
		},
		{ label: t('Cancel'), variant: 'secondary', clickActionType: DISMISS },
		{
			label: t('Delete'),
			variant: 'primary-negative',
			clickActionType: DELETE_BUTTON_CLICKED
		}
	];
};
const view = (state) => {
	const {
		loaded,
		itemName,
		routeType,
		scope,
		isSavingPage,
		disabled,
		initialItemName,
		initialRouteTypeContent,
		order,
		initialOrder,
		icon,
		initialIcon
	} = state;

	const {
		appRoutes,
		page,
		itemType,
		routeConfigurationEnabled,
		userScope,
		applications,
		parentInfo: { elementProperties },
		selectedPageIsTab,
		currentPages,
		runtimeUrl,
		isNewPage,
		showUrlPreview,
		supportScope
	} = state.properties;

	const showAlert = Boolean(
		(selectedPageIsTab
			? flattenRoute(elementProperties, currentPages)
			: appRoutes
		).find(
			(route) =>
				route.routeType === routeType.content &&
				(selectedPageIsTab
					? route.id !== page.page.sysId
					: route.sysId !== page.sysId)
		)
	);

	const validation = get(
		elementProperties,
		'viewportConfiguration.validation',
		{}
	);

	return (
		<now-modal
			heading="Edit page properties"
			manage-opened={true}
			opened={true}
			size="lg"
			header-label={!isSavingPage ? t('Edit settings for {0}', itemName) : null}
			footer-actions={footerActions(
				itemName,
				routeType,
				initialItemName,
				initialRouteTypeContent,
				showAlert,
				disabled,
				isSavingPage,
				selectedPageIsTab,
				order,
				icon,
				validation,
				initialIcon,
				initialOrder
			)}>
			<div
				aria-busy={true}
				className={`${itemType} flex ${
					!loaded || isSavingPage ? 'saving' : ''
				}`}>
				{scope && !isSavingPage ? (
					<sn-uib-transaction-alert
						applications={applications}
						userScope={userScope}
						requestScopeId={scope.id}
						requestScopeName={escapeScopeLabel(scope.label)}
						recordProtectionPolicy={page.sysPolicy}
						supportScope={supportScope}
						requestItemType={'Page'}
					/>
				) : null}
				{!loaded || isSavingPage ? (
					<LoadingStage
						size={'sm'}
						loadingStageContent={
							!isSavingPage ? t('Loading...') : t('Saving {0}...', itemName)
						}
					/>
				) : (
					<div className="content">
						<PagePropertiesForm
							routeType={routeType}
							itemName={itemName}
							itemType={itemType}
							disabled={disabled}
							scope={scope}
							routeConfigurationEnabled={routeConfigurationEnabled}
							editMode={true}
							showAlert={showAlert}
							order={order}
							icon={icon}
							selectedPageIsTab={selectedPageIsTab}
							validations={validation}
							runtimeUrl={runtimeUrl}
							isNewPage={isNewPage}
							showUrlPreview={showUrlPreview}
						/>
					</div>
				)}
			</div>
		</now-modal>
	);
};

const updateStateFromProps = (updateState, props) => {
	const { routeType, itemName, scope, icon, order } = props;

	updateState([
		{
			operation: 'set',
			path: 'routeType.content',
			value: routeType
		},
		{
			operation: 'set',
			path: 'initialRouteTypeContent',
			value: routeType
		},
		{
			operation: 'set',
			path: 'itemName',
			value: itemName
		},
		{
			operation: 'set',
			path: 'initialItemName',
			value: itemName
		},
		{
			operation: 'set',
			path: 'scope',
			value: transformScope(scope)
		},
		{
			operation: 'set',
			path: 'icon',
			value: icon
		},
		{
			operation: 'set',
			path: 'initialIcon',
			value: icon
		},
		{
			operation: 'set',
			path: 'order',
			value: order
		},
		{
			operation: 'set',
			path: 'initialOrder',
			value: order
		}
	]);
};

createCustomElement('uib-page-configuration-modal', {
	styles,
	view,
	initialState: {
		itemName: '',
		routeType: { content: '', messages: [] },
		loaded: false,
		scope: null,
		disabled: true,
		isSavingPage: false,
		initialItemName: '',
		initialRouteTypeContent: '',
		icon: '',
		order: 0,
		initialIcon: '',
		initialOrder: 0
	},
	properties: {
		page: {
			default: null
		},
		appsSysId: {
			default: null
		},
		appRoutes: {
			default: false
		},
		itemType: {
			default: null
		},
		routeConfigurationEnabled: {
			default: null
		},
		userScope: {
			default: null
		},
		userDomain: {
			default: null
		},
		supportDomains: {
			default: null
		},
		applications: {
			default: null
		},
		selectedPageIsTab: {
			default: false
		},
		parentInfo: {
			default: {}
		},
		currentPages: {
			default: []
		},
		runtimeUrl: {
			default: {}
		},
		isNewPage: {
			default: false
		},
		isDashboardBuilder: {
			default: false
		},
		showUrlPreview: {
			default: false
		},
		supportScope: {
			default: false
		}
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': closeModal,
		[DISMISS]: closeModal,
		[DELETE_BUTTON_CLICKED]: {
			stopPropagation: true,
			effect: ({ properties, dispatch }) => {
				const {
					page: {
						page: { name, sysId }
					}
				} = properties;
				dispatch(UIB_PAGE_CONFIGURATION_MODAL_MODAL_DISMISSED);
				dispatch(UIB_PAGE_CONFIGURATION_MODAL_DELETE_CLICKED, { name, sysId });
			}
		},
		[COMPONENT_BOOTSTRAPPED]: ({ updateState, properties }) => {
			if (properties.page) {
				const {
					routeType,
					icon,
					order,
					page: { name: itemName, scope }
				} = properties.page;

				updateStateFromProps(updateState, {
					routeType,
					itemName,
					scope,
					icon,
					order
				});
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const {
				payload: { name, value, oldValue }
			} = action;

			if (name === 'page' && !oldValue) {
				const {
					routeType,
					page: { name: itemName }
				} = value;

				updateStateFromProps(updateState, {
					routeType,
					itemName
				});
			}
		},
		'NOW_INPUT#INPUT': ({ updateState, action }) => {
			const { fieldValue, input } = action.payload;

			switch (input) {
				case 'page-name':
					updateState({
						operation: 'set',
						path: 'itemName',
						value: fieldValue
					});
					break;
				case 'route':
					updateState({
						routeType: {
							content: fieldValue,
							messages: validationErrorMessage(fieldValue, 'path')
						}
					});
					break;
				case 'order':
					updateState({
						order: Number(fieldValue)
					});
					break;
			}
		},
		[ICON_PROPERTY_INPUT_PROPERTY_CHANGED]: ({
			action: { stopPropagation, payload },
			updateState
		}) => {
			stopPropagation();
			const { value } = payload;
			updateState({ icon: value });
		},
		['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: ({
			updateState,
			action
		}) => {
			action.stopPropagation();
			const { isNecessaryTransactionInProgress } = action.payload;
			updateState({
				loaded: true,
				disabled: !isNecessaryTransactionInProgress
			});
		},
		[SAVE_BUTTON_CLICKED]: ({ dispatch, state, properties, updateState }) => {
			const {
				itemName: pageName,
				routeType,
				scope: { id: sysparm_transaction_scope },
				icon,
				order
			} = state;
			const { itemType, isDashboardBuilder } = properties;
			const {
				page: { sysId },
				fields: pageFields,
				optionalParameters: parameters
			} = properties.page;

			const fields = pageFields
				? Object.values(pageFields).map(({ name }) => ({ name }))
				: [];

			let flow = itemType;

			if (itemType === 'tab') {
				flow = isDashboardBuilder ? 'dashboard' : 'page';
			}

			switch (flow) {
				case 'page':
					dispatch(NEW_PAGE_SAVED, {
						appId: properties.appsSysId,
						pageId: sysId,
						data: {
							pageName,
							routeType: routeType.content,
							fields,
							parameters,
							icon,
							order
						},
						sysparm_transaction_scope
					});
					updateState({
						isSavingPage: true
					});

					break;
				case 'dashboard':
					dispatch(DASHBOARDS_EDIT_REQUEST, {
						appId: properties.appsSysId,
						dashboardId: sysId,
						data: {
							icon,
							order,
							name: pageName
						}
					});
					dispatch(UIB_PAGE_CONFIGURATION_MODAL_MODAL_DISMISSED);
					break;
			}
		}
	}
});

/**
 * @param root0
 * @param root0.dispatch
 */
function closeModal({ dispatch }) {
	dispatch(UIB_PAGE_CONFIGURATION_MODAL_MODAL_DISMISSED);
}
