import '@servicenow/now-button';
import '@servicenow/now-empty-state';
import 'sn-ui-builder-components';
import { snUibUrlPreview } from 'sn-ui-builder-components';
import { t } from 'sn-translate';

export default ({
	fields,
	disabled,
	selectedPageIsTab,
	runtimeUrl,
	showUrlPreview
}) => {
	return (
		<div>
			<span>
				{
					/* prettier-ignore */
					t('Use required parameters for pieces of data that your {0} will require. Components can bind to the value of a required parameter. Often, required parameters are visible in the URL. Some common examples of fields are table, sysID, and query.', selectedPageIsTab ? 'tab' : 'page'	)
				}
			</span>
			<PageFieldsForm
				fields={fields}
				disabled={disabled}
				runtimeUrl={runtimeUrl}
				showUrlPreview={showUrlPreview}
			/>
		</div>
	);
};

/**
 * @param root0
 * @param root0.fields
 * @param root0.disabled
 * @param root0.runtimeUrl
 * @param root0.showUrlPreview
 */
export function PageFieldsForm({
	fields,
	disabled,
	runtimeUrl,
	showUrlPreview
}) {
	const appendToPayload = { button: 'add-field' };
	return (
		<div className="set-height">
			<form className="set-height">
				{showUrlPreview ? (
					<sn-uib-url-preview runtime-url={runtimeUrl} fields={fields} />
				) : null}
				<span className="parameters-heading">
					<span className="label">{t('Required parameters')}</span>
					<now-button-bare
						variant="secondary"
						icon-start="plus-outline"
						label={t('Add')}
						disabled={disabled}
						append-to-payload={appendToPayload}
					/>
				</span>
				{fields && fields.length ? (
					<sn-uib-draggable-list items={fields} disabled={disabled} />
				) : (
					<now-empty-state
						header-text={t('No required parameters, yet')}
						body-text={false}
					/>
				)}
			</form>
		</div>
	);
}
