import get from 'lodash/get';
import debounce from 'lodash/debounce';
import { snUXFActions, contextTypes } from 'sn-uxf-builder-middleware';

const makeDebouncedActionHandler = (ms, fn) => debounce(fn, ms);

const transformActionType = (actionType) => ({ action, dispatch }) =>
	dispatch(actionType, action.payload);

const debouncedElementPropertyUpdated = debounce((payload, dispatch) => {
	dispatch(snUXFActions.ELEMENT_PROPERTY_UPDATED, payload);
}, 250);

const sendNewPropertyValueBase = transformActionType(
	snUXFActions.ELEMENT_PROPERTY_UPDATED
);

const debounceSendNewPropertyValue = makeDebouncedActionHandler(
	500,
	sendNewPropertyValueBase
);

const sendNewPropertyValue = ({ action, dispatch, properties }) => {
	const { noDebounce } = action.payload;

	if (noDebounce) {
		sendNewPropertyValueBase({ action, dispatch, properties });
	} else {
		debounceSendNewPropertyValue({ action, dispatch, properties });
	}
};

const requestReferenceResults = (
	dispatch,
	payload = {},
	properties = [],
	propertyId
) => {
	const property = properties.find(({ id }) => id === propertyId) || {};
	dispatch('UIB_ELEMENT_CONFIGURATION_PANEL#REFERENCE_REQUESTED', {
		data: {
			...payload,
			referenceFilter: get(property, 'typeMeta.reference.referenceFilter', null)
		}
	});
};

const elementPropertyReferenceSearched = ({ action, dispatch, properties }) => {
	const { id: propertyId } = action.payload;
	const { selectedElementId, elementProperties = {} } = properties;
	const currentProperties = elementProperties[selectedElementId] || [];
	requestReferenceResults(
		dispatch,
		action.payload,
		currentProperties,
		propertyId
	);
};

const elementEventMappingHandlerReferenceSearched = ({
	action,
	dispatch,
	properties
}) => {
	const { eventMappingId, handlerId, id: propertyId } = action.payload;
	const { eventMappingHandlers = {} } = properties;
	const handlers = eventMappingHandlers[eventMappingId] || [];
	const currentHandler =
		handlers.find((handler) => handler.handlerId === handlerId) || {};
	requestReferenceResults(
		dispatch,
		action.payload,
		currentHandler.values || [],
		propertyId
	);
};

const requestReferenceDisplayValues = ({ action, dispatch }) => {
	// When the selected element in the config panel changes, dispatch the GraphQL
	// query to fetch the display values for all of the configured reference pickers.
	const { references = [] } = action.payload || {};
	references.length &&
		dispatch('UIB_ELEMENT_CONFIGURATION_PANEL#DISPLAY_VALUES_REQUESTED', {
			references
		});
};

const requestFieldValues = ({ action, dispatch }) => {
	const { table } = action.payload;

	dispatch('UIB_ELEMENT_CONFIGURATION_PANEL#FIELD_VALUES_REQUESTED', {
		table
	});
};

const resetAndSendNewPropertyValue = ({ action, dispatch }) => {
	action.stopPropagation();

	// Using the dynamic property input for isHidden which isnt configured statically
	let newPropertyValue;
	if (action.payload.propertyId === '__isHidden__') {
		newPropertyValue = '';
	} else {
		// TODO: dispatch a separate action up to UXF once they support storing metadata
		// about whether a property is statically or dynamically configured. For now, we
		// will just reset the property value when the property's binding is switched.
		newPropertyValue = action.payload.value ? null : '@';
	}
	const payload = {
		propertyId: action.payload.propertyId,
		elementId: action.payload.elementId,
		value: newPropertyValue
	};
	dispatch(snUXFActions.ELEMENT_PROPERTY_UPDATED, payload);
};

const requestNewEventMapping = transformActionType(
	snUXFActions.ELEMENT_EVENT_MAPPING_CREATED
);

const requestNewEventMappingHandler = transformActionType(
	snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_CREATED
);

const requestEventMappingHandlerDelete = transformActionType(
	snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_DELETED
);

const requestEventMappingHandlerChange = ({ action, dispatch }) => {
	dispatch(snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_CHANGED, action.payload);
};

const requestEventMappingHandlerChangeImmediate = transformActionType(
	snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_CHANGED
);

const requestElementMetadataChange = ({ action, dispatch }) => {
	action.payload.context === 'component' &&
		dispatch(snUXFActions.ELEMENT_METADATA_UPDATE_REQUESTED, action.payload);
};

const sendNewScriptedPropertyValue = ({ action, dispatch }) => {
	const { propertyId, elementId, value, compatibilityMode } = action.payload;
	const outboundPayload = {
		propertyId,
		elementId,
		value: value ? `javascript(${value})${compatibilityMode ? '!' : ''}` : ''
	};
	dispatch(snUXFActions.ELEMENT_PROPERTY_UPDATED, outboundPayload);
};

const sendNewScriptedHandlerValue = ({ action, dispatch }) => {
	action.stopPropagation();
	const {
		payload: { script, compatibilityMode }
	} = action;
	dispatch(snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_CHANGED, {
		...action.payload,
		scripted: true,
		value: script ? `javascript(${script})${!compatibilityMode ? '!' : ''}` : ''
	});
};

const navigateToExternalConfig = ({ action }) => {
	return window.open(action.payload.url);
};

/**
 * @param tab
 * @param appRoutes
 */
function pageSysIdForTab(tab, appRoutes) {
	const allTabs = getVariantsFromRoutes(appRoutes);
	const foundTab = allTabs.find((t) => t.sysId === tab.id);
	return foundTab ? foundTab.pageSysId : undefined;
}

/**
 * @param routes
 */
function getVariantsFromRoutes(routes) {
	return Array.isArray(routes)
		? routes.flatMap((route) => [
				...route.page.variants,
				...getVariantsFromRoutes(
					route.page.variants.flatMap((variant) => variant.viewports)
				)
		  ])
		: [];
}

const layoutTemplateSelected = ({ action, dispatch }) => {
	const { isLayoutModified, layoutTemplateId, elementId } = action.payload;
	const dispatchFn = () => {
		dispatch(snUXFActions.ELEMENT_LAYOUT_CHANGED, {
			elementId,
			layoutTemplateId
		});
	};
	if (!isLayoutModified) {
		dispatchFn();
	} else {
		dispatch('SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED', {
			type: 'element',
			applyActionHandler: dispatchFn
		});
	}
};

const layoutTemplateReset = ({ action, dispatch }) => {
	const { layoutTemplateId, elementId } = action.payload;
	const dispatchFn = () => {
		dispatch(snUXFActions.ELEMENT_LAYOUT_CHANGED, {
			elementId,
			layoutTemplateId
		});
	};

	dispatch('SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED', {
		type: 'element',
		context: 'reset',
		applyActionHandler: dispatchFn
	});
};

const editTabContents = ({ action, properties, dispatch }) => {
	const { tab } = action.payload;
	const {
		page: {
			uiTypeParameters: { appConfig }
		},
		applicationType,
		appConfig: { appRoutes }
	} = properties;
	dispatch('SN_UIB_APP#PAGE_REQUESTED', {
		sysId: tab.id,
		appsSysId: appConfig,
		pageSysId: pageSysIdForTab(tab, appRoutes),
		name: tab.label,
		applicationType
	});
};

const propertyChanged = ({ action, dispatch }) => {
	debouncedElementPropertyUpdated(action.payload, dispatch);
};
const propertyChangedImmediate = transformActionType(
	snUXFActions.ELEMENT_PROPERTY_UPDATED
);

const styleChanged = transformActionType(snUXFActions.ELEMENT_STYLES_UPDATED);
const slotStyleChanged = transformActionType(snUXFActions.SLOT_STYLES_CHANGED);

const testValueChanged = transformActionType(
	snUXFActions.ELEMENT_IS_HIDDEN_TEST_VALUE_CHANGED
);

const triggeringElementSelected = ({ action, dispatch }) => {
	const { element } = action.payload;
	dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
		elementId: element.id,
		elementInstanceName: element.instanceName
	});
};

const bindingsRequested = ({ action, dispatch }) => {
	const { element: contextId, id: propertyId } = action.payload;
	dispatch(snUXFActions.SN_UXF_CONTEXT_BINDINGS_REQUESTED, {
		contextType: contextTypes.elementProperty,
		contextId,
		propertyId
	});
};

const requestDAEventMappingModal = ({ action, dispatch, properties }) => {
	action.stopPropagation();
	dispatch('UIB_ELEMENT_CONFIGURATION_PANEL#DA_MAPPING_MODAL_REQUESTED', {
		elementId: properties.selectedElementId
	});
};

export default {
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#LAYOUT_TEMPLATE_SELECTED']: layoutTemplateSelected,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#RESET_LAYOUT']: layoutTemplateReset,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#PROPERTY_CHANGED']: propertyChanged,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#STYLE_CHANGED']: styleChanged,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#SLOT_STYLE_CHANGED']: slotStyleChanged,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#PROPERTY_CHANGED_IMMEDIATE']: propertyChangedImmediate,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#PROPERTY_CHANGED']: sendNewPropertyValue,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#PROPERTY_SCRIPT_CHANGED']: sendNewScriptedPropertyValue,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#REFERENCE_SEARCHED']: elementPropertyReferenceSearched,
	['SN_UIB_EVENT_CONFIG_PANE#HANDLER_VALUE_REFERENCE_SEARCHED']: elementEventMappingHandlerReferenceSearched,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL#ELEMENT_CHANGED']: requestReferenceDisplayValues,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#FIELD_PROPERTY_TABLE_SELECTED']: requestFieldValues,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#PROPERTY_BINDING_TOGGLED']: resetAndSendNewPropertyValue,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#EXTERNAL_CONFIG_BUTTON_CLICKED']: navigateToExternalConfig,
	['SN_UIB_PROPERTIES_CONFIGURATION_PANE#DA_MAPPING_BUTTON_CLICKED']: requestDAEventMappingModal,
	['EVENT_CONFIG_PANE#EVENT_MAPPING_ADDED']: requestNewEventMapping,
	['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_ADDED']: requestNewEventMappingHandler,
	['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_DELETED']: requestEventMappingHandlerDelete,
	['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_CHANGED']: requestEventMappingHandlerChange,
	['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE']: requestEventMappingHandlerChangeImmediate,
	['EVENT_CONFIG_PANE#HANDLER_SCRIPTED_VALUE_CHANGED']: sendNewScriptedHandlerValue,
	['SN_UIB_IS_HIDDEN_PROPERTY_INPUT#TEST_VALUE_CHANGED']: testValueChanged,
	['NOW_UXF_TAB_SET_BUILDTIME#EDIT_TAB_CONTENTS']: editTabContents,
	['EVENT_CONFIG_PANE#TRIGGERING_ELEMENT_SELECTED']: triggeringElementSelected,
	['SN_UIB_ELEMENT_CONFIGURATION_PANEL_BINDINGS_REQUESTED']: bindingsRequested,
	['SN_UIB_INSTANCE_NAME_EDITOR#VALUES_CHANGED']: requestElementMetadataChange
};
