import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { snUXFActions } from 'sn-uxf-builder-middleware';

import 'sn-devx-components';
import '@servicenow/now-modal';
import '@servicenow/now-alert';
import 'sn-ui-builder-components';

const layoutModalFooterActions = [
	{
		label: t('Apply'),
		variant: 'primary',
		clickActionType: 'UIB_LAYOUT_MODAL_FOOTER_APPLY#CLICKED'
	},
	{
		label: t('Cancel'),
		variant: 'secondary',
		clickActionType: 'UIB_LAYOUT_MODAL_FOOTER_CANCEL#CLICKED'
	}
];
export default createCustomElement('uib-layout-modal', {
	view: ({ properties: { showModal, templates } }) => {
		return showModal ? (
			<now-modal
				header-label={t('Select a layout template')}
				opened
				size="lg"
				footerActions={layoutModalFooterActions}>
				<now-alert
					/* prettier-ignore */
					content={t('Changing the layout template will reset all the changes on the stage')}
					header={t('Warning')}
					icon="circle-exclamation-fill"
					status="critical"
				/>
				<sn-uib-layout-templates templates={templates} />
			</now-modal>
		) : null;
	},
	properties: {
		showModal: {
			default: false
		},
		templates: {
			default: []
		},
		selectedLayoutId: {
			default: null
		}
	},
	initialState: {
		host: null
	},
	actionHandlers: {
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) => {
			dispatch('UIB_LAYOUT_MODAL_CLOSED_BUTTON#CLICKED');
		},
		['SN_UIB_LAYOUT_TEMPLATE#CLICKED']: ({
			dispatch,
			action,
			updateProperties
		}) => {
			const { value } = action.payload;
			updateProperties({
				selectedLayoutId: value
			});
			dispatch('UIB_LAYOUT_MODAL_LAYOUT#SELECTED', { value });
		},
		['UIB_LAYOUT_MODAL_FOOTER_APPLY#CLICKED']: ({
			updateProperties,
			dispatch,
			state
		}) => {
			if (state.properties.selectedLayoutId) {
				dispatch(snUXFActions.PAGE_TEMPLATE_SELECTED, {
					pageTemplateId: state.properties.selectedLayoutId
				});
			}
			updateProperties({ selectedLayoutId: null });
			dispatch('UIB_LAYOUT_MODAL_CLOSED_BUTTON#CLICKED');
		},
		['UIB_LAYOUT_MODAL_FOOTER_CANCEL#CLICKED']: ({ dispatch }) => {
			dispatch('UIB_LAYOUT_MODAL_CLOSED_BUTTON#CLICKED');
		}
	}
});
