import {
	CHECKBOX_CHECKED,
	NAVIGATION_COMPONENT_BOOTSTRAPPED,
	PAGE_NAVIGATION,
	SETTINGS_UPDATED,
	TABLE_CONFIG,
	URLDOC
} from '../../constants';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

// Internal components
import '../sn-experience-settings-link';

import styles from '../../styles/forms.scss';

// NDS
import '@servicenow/now-checkbox';
import '@servicenow/now-heading';
import '@servicenow/now-text-link';

const page = 'UIB_Portal_NavigationMenu_Settings';

const view = ({
	properties: {
		headerSysId,
		menu,
		menuSysId,
		footerTop,
		footerBottom,
		footerSysId,
		readonly
	}
}) => {
	return (
		<div>
			<now-heading
				label={t('Header')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{
					/* prettier-ignore */
					t(' This typically displays a logo, an avatar and anything else you have to set as part of your header.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<sn-experience-settings-link
				label={t('Advanced header settings')}
				sys-id={headerSysId}
				table={TABLE_CONFIG}></sn-experience-settings-link>
			<div className="separator"></div>
			<now-heading
				label={t('Menu')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{
					/* prettier-ignore */
					t('You can choose to display a menu as part of this portal. A menu provides a way for users to navigate to key pages in your portal.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="fieldset">
				<now-checkbox
					label={t('Navigation menu')}
					append-to-payload={{ name: 'menu' }}
					checked={menu}
					readonly={readonly}
				/>
			</div>
			<sn-experience-settings-link
				label={t('Advanced navigation menu settings')}
				sys-id={menuSysId}
				table={TABLE_CONFIG}></sn-experience-settings-link>
			<div className="separator"></div>
			<now-heading
				label={t('Footer')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{
					/* prettier-ignore */
					t('You can choose to display a footer as part of this portal. A footer provides an expanded view of any important links that you want to display.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="fieldset">
				<now-checkbox
					label={t('Primary footer')}
					append-to-payload={{ name: 'footerTop' }}
					checked={footerTop}
					readonly={readonly}
				/>
			</div>
			<div className="fieldset">
				<now-checkbox
					label={t('Secondary footer')}
					append-to-payload={{ name: 'footerBottom' }}
					checked={footerBottom}
					readonly={readonly}
				/>
			</div>
			<sn-experience-settings-link
				label={t('Advanced footer settings')}
				sys-id={footerSysId}
				table={TABLE_CONFIG}></sn-experience-settings-link>
		</div>
	);
};

const inputChangedEffect = ({
	action: {
		payload: { name, value }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, { page: PAGE_NAVIGATION, name, value });
};

createCustomElement('sn-experience-settings-navigation', {
	view,
	properties: {
		headerSysId: {
			default: ''
		},
		menu: {
			schema: { type: 'boolean' },
			default: false
		},
		menuSysId: {
			default: ''
		},
		footerTop: {
			schema: { type: 'boolean' },
			default: false
		},
		footerBottom: {
			schema: { type: 'boolean' },
			default: false
		},
		footerSysId: {
			default: ''
		},
		readonly: {
			schema: { type: 'boolean' }
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(NAVIGATION_COMPONENT_BOOTSTRAPPED);
		},
		[CHECKBOX_CHECKED]: {
			stopPropagation: true,
			effect: inputChangedEffect
		}
	},
	styles
});
