import { createCustomElement } from '@servicenow/ui-core';
import { Fragment, createRef } from '@servicenow/ui-renderer-snabbdom';
import workspaceLookup from '../../utils/workspaceLookup';
import 'sn-ui-builder-components';

import styles from './_uib-element.scss';

createCustomElement('uib-element', {
	view: (state, dispatch) => {
		const { isHovered, selected, element, properties } = state;
		const {
			elementId,
			elementInstanceName,
			parentElementId,
			elementComponentIcon,
			disabled,
			isResizing,
			isDragging,
			elementConfirmingDelete,
			isWorkspace,
			component,
			allowIsolatedEditing
		} = properties;

		const dragProps = {
			id: elementId,
			elementInstanceName,
			elementComponentIcon,
			isHovered,
			selected,
			disabled,
			'active-direction': '@dnd-provider/activeDirection',
			'is-dragging': '@dnd-provider/isDragging',
			'parent-id': elementId,
			'is-root': parentElementId === null,
			'keyboard-drag': '@dnd-provider/keyboardDrag',
			'active-id': '@dnd-provider/activeId',
			'parent-node-id': `@${elementId}_information/parent-node-id`,
			'element-styles': '@uxf/elementStyles'
		};

		// eslint-disable-next-line no-unused-vars
		const DragWrapper = disabled
			? 'sn-uib-read-only-element-wrapper'
			: workspaceLookup(
					isWorkspace,
					'dragWrapper',
					'sn-uib-dnd-element-wrapper'
			  );
		const elemRef = createRef();
		return (
			<div
				class={{ selected, alwaysBlue: true }}
				ref={elemRef}
				on-click={(event) => {
					event.stopPropagation();
					dispatch('UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED', {
						type: 'element',
						elementId,
						elementInstanceName,
						component
					});
				}}
				hook-insert={() => {
					dispatch('UIB_ELEMENT#ELEMENT_ROOT_RENDERED', {
						parentElementId,
						id: elementId
					});
				}}
				on-mouseover={(event) => {
					event.stopPropagation();

					if (isResizing || isDragging) {
						return;
					}

					dispatch('UIB_ELEMENT#ELEMENT_MOUSE_OVER', {
						elementId
					});
				}}>
				<DragWrapper {...dragProps} className="drag-wrapper">
					<sn-uib-element
						confirm={elementConfirmingDelete === elementId}
						isWorkspace={isWorkspace}
						element={element}
						focused={selected}
						allowIsolatedEditing={allowIsolatedEditing}
						elementRef={`@${elementId}_information/elementRef`}
						keymaps={`@${elementId}_information/element`}
						resize-info={`@${elementId}_information/resize`}
						elementType={`@${elementId}_information/type`}
						rank={`@${elementId}_information/rank`}
						wrapperProps={`@${elementId}_information/wrapperProps`}
						elementWrapper="@uib-layout-adapter/elementWrapper"
						hovered={isHovered}
						disabled={disabled}>
						<slot />
					</sn-uib-element>
				</DragWrapper>
			</div>
		);
	},
	transformState: (state) => {
		const {
			elementId,
			hoveredElementId,
			selectedElementId,
			elementInstanceName,
			component
		} = state.properties;

		const isHovered = hoveredElementId && hoveredElementId === elementId;
		const selected = selectedElementId && selectedElementId === elementId;

		return {
			...state,
			isHovered,
			selected,
			element: {
				id: elementId,
				displayName: elementInstanceName,
				component
			}
		};
	},
	properties: {
		selectedElementId: {
			default: null
		},
		hoveredElementId: {
			default: null
		},
		elementId: {
			default: null
		},
		parentElementId: {
			default: null
		},
		elementInstanceName: {
			default: null
		},
		isWorkspace: {
			default: false
		},
		elementComponentIcon: {
			default: 'building-fill'
		},
		isResizing: {
			default: false
		},
		isDragging: {
			default: false
		},
		elementConfirmingDelete: {
			default: null
		},
		/**
		 * Whether the element is disabled
		 *
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * the Component config for the element
		 *
		 * @type {object}
		 */
		component: {
			default: null
		},
		allowIsolatedEditing: {
			default: false
		}
	},
	styles
});
