import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import get from 'lodash/get';

import {
	BRANDING_FETCH_SUCCEEDED,
	BRANDING_UPDATE_SUCCEEDED,
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_UPDATE_SUCCEEDED
} from '../constants';

const query = `
query snExperienceSettingsConnected($sysId: ID!) {
  snExpSettings {
    settings {
      theme(sysId: $sysId) {
		panelSysId
		primaryThemeId
        themes {
			id
			label
		}
      }
    }
  }
}`;

export const brandingFetchRequestedEffect = createGraphQLEffect(query, {
	variableList: ['sysId'],
	successActionType: BRANDING_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const brandingFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const { panelSysId, primaryThemeId, themes } = get(
		payload,
		`data.snExpSettings.settings.theme`,
		[]
	);
	updateState({
		branding: {
			panelSysId,
			primaryThemeId,
			themes,
			updated: false
		}
	});
};

const mutation = `
mutation snExperienceSettingsConnected($sysId: ID!, $primaryThemeId: ID!) {
  snExpSettings {
    settings {
      themeUpdate(sysId:$sysId primaryThemeId:$primaryThemeId) {
		panelSysId
		primaryThemeId
		themes {
			id
			label
		}
      }
    }
  }
}`;

export const brandingUpdateRequestedEffect = createGraphQLEffect(mutation, {
	variableList: ['sysId', 'primaryThemeId'],
	successActionType: BRANDING_UPDATE_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const brandingUpdateSucceededEffect = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	const { panelSysId, primaryThemeId, themes } = get(
		payload,
		`data.snExpSettings.settings.themeUpdate`,
		[]
	);
	updateState({
		branding: {
			panelSysId,
			primaryThemeId,
			themes,
			updated: false
		}
	});
	dispatch(SETTINGS_UPDATE_SUCCEEDED);
};
