import get from 'lodash/get';

/**
 *
 * @param {*} rootElementId
 * @param {*} elementId
 * @param {*} elementProperties
 * @param {*} selectedPage
 * @param {*} selectedPageVariants
 * @param {*} routeState
 */
const pageStateToTabModal = (
	rootElementId,
	elementId,
	elementProperties,
	selectedPage,
	selectedPageVariants,
	routeState = {}
) => {
	const scope = get(selectedPage, 'scope', {});
	const selectedPageSelectedVariant = selectedPageVariants.find(
		(e) => e.sysId === routeState.variantSysId
	);

	const model = {
		compositionElementId: elementId || '',
		elementProperties,
		macroponentId: rootElementId || '',
		scope: get(scope, 'id', ''),
		scopeName: get(scope, 'label', ''),
		screenId: get(selectedPageSelectedVariant, 'sysId', ''),
		screenName: get(selectedPageSelectedVariant, 'name', ''),
		screenType: get(selectedPageSelectedVariant, 'pageSysId', '')
	};

	return model;
};

export default pageStateToTabModal;
