import get from 'lodash/get';
import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	getPayloadPropertiesFromHandlerDefinition,
	getUxValueFromShorthand
} from 'sn-uxf-builder-middleware';

const FETCH_EVENT_MAPPINGS_URL = '/api/now/table/sys_ux_addon_event_mapping';

const eventMappingListRequested = createHttpEffect(FETCH_EVENT_MAPPINGS_URL, {
	method: 'GET',
	batch: false,
	queryParams: [
		'sysparm_query',
		'sysparm_display_value',
		'sysparm_fields',
		'sysparm_query_no_domain'
	],
	successActionType: 'EVENT_MAPPINGS_LIST_REQUEST_SUCCEEDED',
	errorActionType: 'EVENT_MAPPINGS_LIST_REQUEST_FAILED'
});

const buildDomainOverridesList = (
	eventMappings,
	parentId,
	isDomainExpanded
) => {
	if (!isDomainExpanded) {
		const em = eventMappings.find((em) => em.sysId === parentId);
		return em.overrides
			? [
					em.overrides,
					...buildDomainOverridesList(eventMappings, em.overrides, false)
			  ]
			: [];
	}

	const childMappings = eventMappings.filter((em) => em.overrides === parentId);
	if (childMappings.length === 0) {
		return [];
	}

	return childMappings.reduce((acc, cur) => {
		acc.push(
			cur.sysId,
			...buildDomainOverridesList(eventMappings, cur.sysId, true)
		);
		return acc;
	}, []);
};

const eventMappingListRequestedSucceeded = ({
	action,
	properties,
	state,
	updateState
}) => {
	const { macroponentEventHandlers } = properties;

	const eventMappingList = get(action, 'payload.result', []).map((em) => {
		const handlerId = em.target_event.value;
		const payload = JSON.parse(em.target_payload_mapping.value);

		const transformedPayloadMapping = payload
			? getPayloadPropertiesFromHandlerDefinition(
					payload.container || payload.value || {},
					handlerId,
					macroponentEventHandlers
			  )
			: {};

		return {
			eventId: em.source_da.value,
			name: em.name.value,
			description: em.description.value,
			handlerId,
			handlerName: em.target_event.display_value,
			sysId: em.sys_id.value,
			handlerValues: transformedPayloadMapping,
			scope: {
				id: get(em, 'sys_scope.value', ''),
				label: get(em, 'sys_scope.display_value', '')
			},
			domain: {
				id: get(em, 'sys_domain.value', ''),
				label: get(em, 'sys_domain.display_value', '')
			},
			overrides: get(em, 'sys_overrides.value', '')
		};
	});

	if (action.meta.overridePickerListRequested) {
		const { selectedOverrideTarget } = state;
		const { isDomainExpanded } = properties;

		const domainOverridesIdList = [selectedOverrideTarget].concat(
			buildDomainOverridesList(
				eventMappingList,
				selectedOverrideTarget,
				isDomainExpanded
			)
		);
		const overridePickerList = domainOverridesIdList.map((mappingId) => {
			return eventMappingList.find((mapping) => mapping.sysId === mappingId);
		});

		updateState({
			operation: 'set',
			path: 'overridePickerList',
			value: overridePickerList
		});
	} else {
		const { selectedEventId, selectedMappingId } = action.meta;

		if (selectedMappingId && selectedEventId === state.selectedEvent.id) {
			const selectedMapping = eventMappingList.find(
				(em) => em.sysId === selectedMappingId
			);
			updateState([
				{
					operation: 'set',
					path: 'eventMappings',
					value: eventMappingList
				},
				{
					operation: 'set',
					path: 'initialLoad',
					value: false
				},
				{
					operation: 'set',
					path: 'selectedMapping',
					value: selectedMapping
				}
			]);
		} else {
			updateState([
				{
					operation: 'set',
					path: 'eventMappings',
					value: eventMappingList
				},
				{
					operation: 'set',
					path: 'initialLoad',
					value: false
				}
			]);
		}
	}
};

const eventMappingListRequestedFailed = ({ updateState }) => {
	updateState({
		operation: 'set',
		path: 'initialLoad',
		value: false
	});
};

export const modalActionHandlers = {
	['FETCH_EVENT_MAPPINGS_LIST_REQUESTED']: eventMappingListRequested,
	['EVENT_MAPPINGS_LIST_REQUEST_SUCCEEDED']: eventMappingListRequestedSucceeded,
	['EVENT_MAPPINGS_LIST_REQUEST_FAILED']: eventMappingListRequestedFailed
};

const UPDATE_EVENT_MAPPING_URL =
	'/api/now/table/sys_ux_addon_event_mapping/:recordId';

const deleteEventMappingRequested = createHttpEffect(UPDATE_EVENT_MAPPING_URL, {
	method: 'DELETE',
	batch: false,
	pathParams: ['recordId'],
	queryParams: ['sysparm_transaction_scope', 'sysparm_query_no_domain'],
	successActionType: 'DELETE_EVENT_MAPPING_SUCCEEDED'
});

const deleteEventMappingSucceeded = ({ dispatch, properties, updateState }) => {
	console.log('DELETE SUCCEEDED!');
	dispatch('FETCH_EVENT_MAPPINGS_LIST_REQUESTED', {
		sysparm_query: `parent_macroponent=${properties.rootElementId}^source_element_id=${properties.elementId}`,
		sysparm_display_value: 'all',
		sysparm_query_no_domain: properties.isDomainExpanded
	});
	updateState([
		{
			operation: 'set',
			path: 'displayForm',
			value: false
		},
		{
			operation: 'set',
			path: 'selectedEvent',
			value: null
		},
		{
			operation: 'set',
			path: 'selectedMapping',
			value: null
		}
	]);
};

const updateEventMappingRequested = ({ action, dispatch }) => {
	const {
		selectedHandler,
		handlerValues,
		eventMappingName,
		description,
		eventMapping
	} = action.payload;

	const handlerValueObj = handlerValues.reduce((acc, cur) => {
		acc[cur.id] = getUxValueFromShorthand(cur.value);
		return acc;
	}, {});

	const targetPayloadMapping = {
		type: 'MAP_CONTAINER',
		container: handlerValueObj
	};

	dispatch('UPDATE_EVENT_MAPPING_HTTP_REQUESTED', {
		recordId: eventMapping.sysId,
		sysparm_transaction_scope: eventMapping.scope.id,
		sysparm_query_no_domain: true,
		data: {
			name: eventMappingName,
			description,
			target_event: selectedHandler.id,
			target_payload_mapping: JSON.stringify(targetPayloadMapping, null, 2)
		}
	});
};

const updateEventMappingHttpRequested = createHttpEffect(
	UPDATE_EVENT_MAPPING_URL,
	{
		method: 'PUT',
		batch: false,
		pathParams: ['recordId'],
		queryParams: ['sysparm_transaction_scope', 'sysparm_query_no_domain'],
		dataParam: ['data'],
		successActionType: 'UPDATE_EVENT_MAPPING_SUCCEEDED'
	}
);

const updateEventMappingSucceeded = ({ action, dispatch, properties }) => {
	dispatch(
		'FETCH_EVENT_MAPPINGS_LIST_REQUESTED',
		{
			sysparm_query: `parent_macroponent=${properties.rootElementId}^source_element_id=${properties.elementId}`,
			sysparm_display_value: 'all',
			sysparm_query_no_domain: properties.isDomainExpanded
		},
		{
			selectedMappingId: action.payload.result.sys_id,
			selectedEventId: action.payload.result.source_da.value
		}
	);
};

const CREATE_EVENT_MAPPING_URL = '/api/now/table/sys_ux_addon_event_mapping';

const createEventMappingRequested = ({ action, dispatch, properties }) => {
	const {
		selectedHandler,
		handlerValues,
		eventMappingName,
		description,
		applicationScope,
		event,
		overrides
	} = action.payload;

	const {
		rootElementId: parentMacroponentId,
		elementId: sourceElementId
	} = properties;

	const handlerValueObj = handlerValues.reduce((acc, cur) => {
		acc[cur.id] = getUxValueFromShorthand(cur.value);
		return acc;
	}, {});

	const targetPayloadMapping = {
		type: 'MAP_CONTAINER',
		container: handlerValueObj
	};

	dispatch('CREATE_EVENT_MAPPING_HTTP_REQUESTED', {
		sysparm_transaction_scope: applicationScope.id,
		data: {
			name: eventMappingName,
			description,
			sys_overrides: overrides,
			parent_macroponent: parentMacroponentId,
			source_da: event.id,
			source_element_id: sourceElementId,
			target_event: selectedHandler.id,
			target_payload_mapping: JSON.stringify(targetPayloadMapping, null, 2)
		}
	});
};

const createEventMappingHttpRequested = createHttpEffect(
	CREATE_EVENT_MAPPING_URL,
	{
		method: 'POST',
		batch: false,
		queryParams: ['sysparm_transaction_scope'],
		dataParam: ['data'],
		successActionType: 'CREATE_EVENT_MAPPING_SUCCEEDED'
	}
);

const createEventMappingSucceeded = ({ action, dispatch, properties }) => {
	console.log('CREATE SUCCEEDED!');
	dispatch(
		'FETCH_EVENT_MAPPINGS_LIST_REQUESTED',
		{
			sysparm_query: `parent_macroponent=${properties.rootElementId}^source_element_id=${properties.elementId}`,
			sysparm_display_value: 'all',
			sysparm_query_no_domain: properties.isDomainExpanded
		},
		{
			selectedMappingId: action.payload.result.sys_id,
			selectedEventId: action.payload.result.source_da.value
		}
	);
};

export const formActionHandlers = {
	['DELETE_EVENT_MAPPING_REQUESTED']: deleteEventMappingRequested,
	['DELETE_EVENT_MAPPING_SUCCEEDED']: deleteEventMappingSucceeded,
	['CREATE_EVENT_MAPPING_REQUESTED']: createEventMappingRequested,
	['CREATE_EVENT_MAPPING_HTTP_REQUESTED']: createEventMappingHttpRequested,
	['CREATE_EVENT_MAPPING_SUCCEEDED']: createEventMappingSucceeded,
	['UPDATE_EVENT_MAPPING_REQUESTED']: updateEventMappingRequested,
	['UPDATE_EVENT_MAPPING_HTTP_REQUESTED']: updateEventMappingHttpRequested,
	['UPDATE_EVENT_MAPPING_SUCCEEDED']: updateEventMappingSucceeded
};
