/* eslint-disable no-undef */
const get = require('lodash/get');
/* eslint-enable */

/**
 * See App Engine Studio docs for more info:
 * https://servicenow.sharepoint.com/:w:/s/AppEngineStudio/Eb70tfob3MtOupks7123MwsBsVRAj-Bg3UsYIm4c_1rkhQ?e=F9Qe1F
 */

/**
 * Returns App Engine Studio API object if present
 *
 * @returns {(boolean|object)}
 */
function getAesApi() {
	return get(window, 'frameElement.appEngineStudioBuilderAPI', false);
}

/**
 * Used to indicate that UIB is being rendered inside App Engine Studio iframe
 *
 * @returns {boolean}
 */
function isRenderedInAes() {
	const aesApi = getAesApi();

	if (!aesApi) {
		return false;
	}

	return aesApi.isAppEngineStudio;
}

/**
 * Update various state properties on enclosing tab
 *
 * @param {{ title: string, subtitle: string, hasUnsavedChanges: boolean }} payload
 * @returns {undefined}
 */
function updateAesTabState(payload) {
	const aesApi = getAesApi();

	if (!aesApi) {
		return;
	}

	if (typeof aesApi.updateTabState !== 'function') {
		return;
	}

	aesApi.updateTabState(payload);
}

/**
 *  Update various sessionStorage properties on enclosing tab.
 *  Allows App Engine Studio to put user on page they left off on after page reload.
 *
 * @param {{ title: string, subtitle: string, url: string }} payload
 * @returns {undefined}
 */
function updateAesTabSession(payload) {
	const aesApi = getAesApi();

	if (!aesApi) {
		return;
	}

	if (typeof aesApi.updateTabSession !== 'function') {
		return;
	}

	aesApi.updateTabSession(payload);
}

/* eslint-disable no-undef */
module.exports = {
	getAesApi,
	isRenderedInAes,
	updateAesTabState,
	updateAesTabSession
};
/* eslint-enable */
