import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import styles from './_uib-empty-slot.scss';
import { droppableBehavior, strategyManager } from 'sn-ui-builder-components';
import { keyboardNavBehavior } from 'sn-ui-builder-components/src/behaviors/droppable/utils';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	createFocusRef
} = DevxKeyFocusBehavior;

const view = (state, dispatch) => {
	const {
		behaviors: {
			droppable: { active }
		}
	} = state;

	const { elementId, isDragging, keymaps } = state.properties;

	const listeners = isDragging
		? keyboardNavBehavior({
				id: elementId,
				dispatch,
				hoverAction: 'UIB_DND_DROPZONE#DRAG_ENTER',
				blurAction: 'UIB_DND_DROPZONE#CLEAR_REGIONS'
		  })
		: {};

	const parent = keymaps ? keymaps.parent : 'quick-link-stage';
	return (
		<div
			class={{
				'empty-slot': true,
				'drop-zone-active': active
			}}
			ref={createFocusRef(`${elementId}_EMPTY`, {
				Escape: `${parent}`
			})}
			hook={{
				insert: ({ elm }) => {
					dispatch('UIB_DND_DROPZONE#MOUNTED', {
						el: elm
					});

					strategyManager.set(elementId, 'emptySlot', elm);
				}
			}}
			{...listeners}
		/>
	);
};

export default createCustomElement('uib-empty-slot', {
	actionHandlers: {
		'UIB_DND_DROPZONE#DROPPED': {
			effect: ({
				action,
				dispatch,
				state: {
					properties: { elementId, slotName }
				}
			}) => {
				const {
					payload: { dataTransfer }
				} = action;

				dispatch('EMPTY_SLOT#DROPPED', {
					dataTransfer,
					parentId: elementId,
					slotName
				});
			}
		}
	},
	actions: {
		'UIB_DND_DROPZONE#DROPPED': {
			stopPropagation: true,
			private: true
		}
	},
	behaviors: [droppableBehavior, keyFocusBehavior],
	properties: {
		elementId: {
			default: null
		},
		pageType: {
			default: null
		},
		slotName: {
			default: null
		},
		isDragging: {
			default: false
		},
		keymaps: { default: {} }
	},
	renderer: { type: snabbdom },
	styles,
	view
});
