import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';
import { t } from 'sn-translate';
import styles from './_uib-default-slotted.scss';

export const UNREGISTERED_SLOTTED_ELEMENT =
	'UIB_DEFAULT_SLOTTED#PLACED_IN_UNREGISTERED';
const CHECK_INTERVAL = 'UIB_DEFAULT_SLOTTED#CHECK_INTERVAL';
const TAG_MISSING = 'TAG_MISSING';
const COMPONENTS_LOADING = 'COMPONENTS_LOADING';
const EVERYTHING_OKAY = 'EVERYTHING_OKAY';
const TWENTY_SECONDS = 20000;

// eslint-disable-next-line no-unused-vars
const Loading = () => {
	return (
		<section className="loading">
			<section className="message">{t('Loading...')}</section>
			<now-loader className="loader" size="lg" />
		</section>
	);
};

const checkInASecond = (dispatch) => {
	setTimeout(() => dispatch(CHECK_INTERVAL), 1000);
};

const isTagMissing = (tag) => {
	// returns undefined if not found
	const elementIsRegistered = !!window.customElements.get(tag);
	const elementIsCustom = tag.includes('-');
	return elementIsCustom && !elementIsRegistered;
};

createCustomElement('uib-default-slotted', {
	view: ({ properties }) => {
		const { status, elementId } = properties;

		switch (status) {
			case COMPONENTS_LOADING:
				return <Loading />;
			case TAG_MISSING:
				return <sn-uib-missing-component element-id={elementId} />;
			default:
				return <slot />;
		}
	},
	actionHandlers: {
		[CHECK_INTERVAL]: {
			effect: ({
				state: {
					host,
					mountedTimestamp,
					properties: { elementId }
				},
				dispatch,
				updateProperties
			}) => {
				const tag = get(host, 'parentNode.tagName', '').toLowerCase();
				if (!tag) {
					// assume we're in a shadow root in which case we can just render normally
					return;
				}
				if (!isTagMissing(tag)) {
					updateProperties({
						status: EVERYTHING_OKAY
					});
					return;
				}

				if (Date.now() - mountedTimestamp < TWENTY_SECONDS) {
					checkInASecond(dispatch);
				} else {
					updateProperties({
						status: TAG_MISSING
					});
					dispatch(UNREGISTERED_SLOTTED_ELEMENT, { elementId });
				}
			},
			stopPropagation: true
		},
		[actionTypes.COMPONENT_CONNECTED]: ({
			action: {
				payload: { host }
			},
			updateProperties,
			updateState,
			dispatch,
			state: {
				mountedTimestamp,
				properties: { elementId }
			}
		}) => {
			const tag = get(host, 'parentNode.tagName', '').toLowerCase();
			if (!tag) {
				// assume we're in a shadow root in which case we can just render normally
				return;
			}

			if (!isTagMissing(tag)) {
				return;
			}
			if (Date.now() - mountedTimestamp > TWENTY_SECONDS) {
				updateProperties({
					status: TAG_MISSING
				});
				dispatch(UNREGISTERED_SLOTTED_ELEMENT, { elementId });
			} else {
				updateProperties({
					status: COMPONENTS_LOADING
				});
				checkInASecond(dispatch);
			}
			updateState({
				path: 'mountedTimestamp',
				value: Date.now(),
				operation: 'set'
			});
			updateState({ path: 'host', value: host, operation: 'set' });
		}
	},
	properties: {
		elementId: {
			default: ''
		},
		mountedTimestamp: {
			default: 0
		},
		status: {
			default: EVERYTHING_OKAY
		}
	},
	styles
});
