import { t } from 'sn-translate';

/**
 * @type {HTMLCollectionOf<HTMLTitleElement>}
 * @private
 */
const TITLES = document.head.getElementsByTagName('title');

/**
 * Exported for testing purposes only
 * @type {string}
 * @private
 */
export const DEFAULT_TITLE = getTitle();

/**
 * @type {string}
 * @private
 */
let DELIMITER = ' | ';

/**
 * @type {string}
 * @public
 */
export const PAGE_TITLE_SUFFIX = t('NOW UI Builder');

/**
 * Exported for testing purposes only
 * @returns {HTMLTitleElement}
 * @private
 */
export function getTitleTag() {
	let [titleTag] = TITLES;

	if (!titleTag) {
		titleTag = document.createElement('title');
		document.head.appendChild(titleTag);
	}

	return titleTag;
}

/**
 * Sets the delimiter used by `setTitle`
 * @param {string} delimiter
 * @public
 */
export function setDelimiter(delimiter) {
	DELIMITER = delimiter;
}

/**
 * Get the `<title>` tag's text
 * @returns {string}
 * @public
 */
export function getTitle() {
	return getTitleTag().textContent;
}

/**
 * Set the `<title>` tag's text
 * @param  {string[]} segments Segments of the page title
 * @public
 */
export function setTitle(...segments) {
	const titleTag = getTitleTag();
	titleTag.textContent = segments.join(DELIMITER);
}

/**
 * Reset the `<title>` tag's text to value on page load
 * @public
 */
export function resetTitle() {
	setTitle(DEFAULT_TITLE);
}

export default { get: getTitle, set: setTitle, reset: resetTitle };
