export const pageListSorter = (pageA, pageB) => {
	const pageAName = pageA.name.toUpperCase();
	const pageBName = pageB.name.toUpperCase();
	const pageAActive = pageA.pageStatus === 'active';
	const pageBActive = pageB.pageStatus === 'active';
	const pageAOrder = pageA.order;
	const pageBOrder = pageB.order;

	if (pageAOrder < pageBOrder) {
		return -1;
	}

	if (pageAOrder > pageBOrder) {
		return 1;
	}

	if (pageAName < pageBName) {
		return -1;
	}
	if (pageAName > pageBName) {
		return 1;
	}

	return pageBActive - pageAActive;
};
