import { C3_TYPES } from '../../../utils/properties';

/**
 * Processor that takes in the currently configured property
 * and configures it for C3 type
 * @param {object} property - The current UXF element property being configured
 * @param {object[]} properties - The current set of UXF properties for the element being configured
 * @param {object} state - The current element configuration panel Seismic state
 * @return {object} - The configured property object
 */
export default function (property) {
	const { type } = property;
	const typeLowerCased = type.toLowerCase();

	const c3Config = '@sn-uib-data-configuration-shelf/c3PropertyConfig';
	const icon = C3_TYPES[typeLowerCased].icon;
	const url = C3_TYPES[typeLowerCased].url;
	const scrollable = C3_TYPES[typeLowerCased].scrollable;
	const redirectFocusTo = C3_TYPES[typeLowerCased].redirectFocusTo;

	return {
		...property,
		c3Config,
		icon,
		url,
		scrollable,
		redirectFocusTo
	};
}
