import { t } from 'sn-translate';
import { snUXFActions, isReferenced } from 'sn-uxf-builder-middleware';
import { CONFIRM_DIALOG_INTERACTION_COMPLETE } from '../../uib-confirm-dialog';
import { urlRoot, getRuntimeUrl } from '../../../utils/urlBuilders';
import { UIB_CREATE_OVERRIDE_DIALOG_INTERACTION_COMPLETE } from '../../uib-create-override-dialog';
import { SN_UIB_APP_UNLOCK_TO_CREATE_OVERRIDE_CLICKED } from './uibAppActions';
import { UIB_PAGE_CONFIGURATION_MODAL_DELETE_CLICKED } from '../../uib-page-configuration-modal';
import { UIB_STAGE_UNLOCK_TO_CREATE_OVERRIDE_CLICKED } from '../../uib-stage/uib-stage';
import { applicationTypes } from '../../../utils/routingConstants';
import { UIB_SCREEN_CONFIGURATION_MODAL_DELETE_CLICKED } from '../../uib-screen-configuration-modal';
import { UIB_DUPLICATE_PREVENTED_OK_CLICKED } from '../../uib-duplicate-prevented';
import DeleteReferencedItems from '../partials/DeleteReferencedItems'; // eslint-disable-line no-unused-vars

const getOverrideCreateActionType = (applicationType) => {
	switch (applicationType) {
		case applicationTypes.dashboards:
			return snUXFActions.DASHBOARDS_CREATE_OVERRIDE_REQUEST;
		default:
			return snUXFActions.NEW_PAGE_VARIANT_CREATE_OVERRIDE_REQUEST;
	}
};

const getBindingReferencesAndModalContent = (
	deleting,
	{ id, name },
	pageState
) => {
	const references = isReferenced(deleting, id, pageState);
	const content = references ? (
		<DeleteReferencedItems deleteItem={name} references={references} />
	) : null;
	return {
		references,
		content
	};
};

const executeBindingReferenceRemovals = (dispatch, references) => {
	if (!references) {
		return;
	}
	references.forEach((ref) =>
		ref.action ? dispatch(ref.action, ref.payload) : null
	);
};

export const dialogHandlers = {
	['NOW_ALERT#ACTION_CLICKED']: ({ action, updateProperties }) => {
		const { type } = action.payload.action;

		if (type === 'dismiss') {
			updateProperties({
				alertProps: null
			});
		}
	},
	['SN_UIB_APP#ALERT_CLEARED_BY_TIMEOUT']: ({ updateProperties }) => {
		updateProperties({
			alertProps: null
		});
	},
	[UIB_PAGE_CONFIGURATION_MODAL_DELETE_CLICKED]: ({
		updateProperties,
		properties,
		action
	}) => {
		const { appsSysId: appId, isDashboardBuilder } = properties;
		const { sysId, name } = action.payload;

		if (isDashboardBuilder) {
			updateProperties({
				showDeleteConfirmation: true,
				deleteConfirmationProps: {
					headerLabel: t('Delete {0}', name || t('Dashboard')),
					objectToDelete: {
						name
					},
					deleteActionHandler: (dispatch) => {
						dispatch(
							snUXFActions.DASHBOARDS_DELETE_REQUEST,
							{
								dashboardId: sysId
							},
							{ deletedDashboardId: sysId }
						);
					}
				}
			});
		} else {
			updateProperties({
				showDeleteConfirmation: true,
				deleteConfirmationProps: {
					headerLabel: t('Delete {0}', name),
					objectToDelete: {
						name,
						pageSysId: sysId,
						appId
					},
					deleteActionHandler: (dispatch, objectToDelete) => {
						dispatch(snUXFActions.NEW_PAGE_DELETE_REQUEST, objectToDelete);
					}
				}
			});
		}
	},
	[UIB_SCREEN_CONFIGURATION_MODAL_DELETE_CLICKED]: ({
		action,
		updateProperties,
		properties
	}) => {
		const { appsSysId, selectedPage, selectedPageIsTab } = properties;
		const {
			payload: { sysId: variantSysId, name }
		} = action;

		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: selectedPageIsTab
					? t('Delete tab variant')
					: t('Delete variant'),
				objectToDelete: {
					name: name,
					variantSysId,
					// Page sys id here refers to the new concept `page` or screen_type
					pageSysId: selectedPage.sysId,
					appId: appsSysId
				},
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(
						snUXFActions.NEW_PAGE_VARIANT_DELETE_REQUEST,
						objectToDelete
					);
				}
			}
		});
	},
	['SN_UIB_PAGE_PROPERTIES_MODAL#DELETE_REFERENCED_PROPERTY']: ({
		action,
		properties,
		updateProperties
	}) => {
		// this handles both the trash icon and the delete button
		const {
			payload: { selectedProperty }
		} = action;

		const { references, content } = getBindingReferencesAndModalContent(
			'pageProperty',
			{ ...selectedProperty, id: selectedProperty.name },
			properties.pageState
		);

		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete page property'),
				content,
				objectToDelete: selectedProperty,
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(snUXFActions.PAGE_PROPERTIES_DELETED, {
						pagePropertyId: objectToDelete.id
					});
					executeBindingReferenceRemovals(dispatch, references);
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	['SN_UIB_DATA_BROKER_CONFIG_PANEL#DELETE_BUTTON_CLICKED']: ({
		action,
		properties,
		updateProperties
	}) => {
		const {
			payload: { selectedDataBroker }
		} = action;

		const { references, content } = getBindingReferencesAndModalContent(
			'dataBrokerInstance',
			selectedDataBroker,
			properties.pageState
		);

		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete data resource instance'),
				content,
				objectToDelete: selectedDataBroker,
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(snUXFActions.DATA_BROKER_INSTANCE_DELETED, {
						dataBrokerId: objectToDelete.id
					});
					executeBindingReferenceRemovals(dispatch, references);
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	['SN_UIB_COMPONENT_TREE_PANEL#ELEMENT_DELETED']: ({
		action,
		updateProperties
	}) => {
		const {
			payload: { elementId, name, elementToSelect }
		} = action;
		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete Element'),
				objectToDelete: { elementId, name },
				deleteActionHandler: (dispatch) => {
					dispatch(snUXFActions.ELEMENT_DELETED, {
						elementId,
						elementToSelect
					});
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_DELETED']: ({
		action,
		properties,
		updateProperties
	}) => {
		const {
			payload: { id, name }
		} = action;

		const { references, content } = getBindingReferencesAndModalContent(
			'clientScript',
			{ id, name },
			properties.pageState
		);

		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete client script'),
				content,
				objectToDelete: id,
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(snUXFActions.CLIENT_SCRIPTS_SCRIPT_DELETED, {
						id: objectToDelete
					});
					executeBindingReferenceRemovals(dispatch, references);
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	['SN_UIB_STATE_CONFIGURATION_PANEL#PARAMETER_DELETED']: ({
		action,
		properties,
		updateProperties
	}) => {
		const {
			payload: { id, name }
		} = action;

		const { content } = getBindingReferencesAndModalContent(
			'clientStateParameter',
			{ id: name, name },
			properties.pageState
		); // client state params already do reference deletes!

		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete client state parameter'),
				content,
				objectToDelete: { id, name },
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(snUXFActions.CLIENT_STATE_PARAMETER_DELETED, {
						id: objectToDelete.id
					});
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	['SN_UIB_ELEMENT_CONTROLS#EXPLODE_BUTTON_CLICKED']: ({
		action,
		updateProperties
	}) => {
		updateProperties({
			showConfirmDialog: true,
			confirmDialogProps: {
				headerLabel: t('Confirm component deconstruction'),
				/* prettier-ignore */
				message: t('You are about to deconstruct this component into individual components.  Proceed?'),
				confirmAction: {
					actionType: snUXFActions.ELEMENT_EXPLODED,
					payload: action.payload
				}
			}
		});
	},
	[CONFIRM_DIALOG_INTERACTION_COMPLETE]: ({ updateProperties }) => {
		updateProperties({
			showConfirmDialog: false
		});
	},
	['UIB_DELETE_CONFIRMATION#CLOSED']: ({ updateProperties }) => {
		updateProperties({
			showDeleteConfirmation: false,
			deleteConfirmationProps: null
		});
	},
	['SN_UIB_APP#ACTIVATE_CONFIRMATION_REQUESTED']: ({ updateProperties }) => {
		updateProperties({
			showActivationConfirmation: true
		});
	},
	['SN_UIB_APP#DEACTIVATE_CONFIRMATION_REQUESTED']: ({ updateProperties }) => {
		updateProperties({
			showActivationConfirmation: true
		});
	},
	['SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED']: ({
		action,
		updateProperties
	}) => {
		updateProperties({
			showNewLayoutConfirmation: true,
			newLayoutConfirmationProps: action.payload
		});
	},
	['UIB_NEW_LAYOUT_CONFIRMATION#CLOSED']: ({ updateProperties }) => {
		updateProperties({
			showNewLayoutConfirmation: false,
			newLayoutConfirmationProps: null
		});
	},
	'SN_UIB_PAGE_HEADER#GO_TO_SCREEN_COLLECTION': ({ properties }) => {
		const {
			appRoutes,
			routeState: { pageSysId }
		} = properties;
		const parentRoute = appRoutes.find(
			(route) => route.page.sysId === pageSysId
		);
		if (!parentRoute) {
			return;
		}
		window.open(
			`${urlRoot()}/sys_ux_screen_type.do?sys_id=${parentRoute.page.sysId}`,
			'_blank'
		);
	},
	'SN_UIB_PAGE_HEADER#GO_TO_SCREEN': ({ properties }) => {
		window.open(
			`${urlRoot()}/sys_ux_screen.do?sys_id=${
				properties.routeState.variantSysId
			}`,
			'_blank'
		);
	},
	'SN_UIB_PAGE_HEADER#GO_TO_MACROPONENT': ({ properties }) => {
		window.open(
			`${urlRoot()}/sys_ux_macroponent.do?sys_id=${
				properties.pageState.rootElement.id
			}`,
			'_blank'
		);
	},
	'SN_UIB_PAGE_HEADER#GO_TO_APP_CONFIG': ({ properties }) => {
		const { appsSysId } = properties;
		window.open(
			`${urlRoot()}/sys_ux_app_config.do?sys_id=${appsSysId}`,
			'_blank'
		);
	},
	'SN_UIB_LAYOUT_CONTEXT_MENU#OPEN_IN_PLATFORM': () => {
		window.open(`${urlRoot()}/sys_uib_template_list.do`);
	},
	'SN_UIB_DATA_BROKER_CONFIG_PANEL#OPEN_DATA_BROKER_DEF_CLICKED': ({
		action
	}) => {
		const { definition = {} } = action.payload;
		const urlPathMap = {
			GRAPHQL: 'graphql',
			TRANSFORM: 'transform',
			CLIENT_STATE: 'proxy',
			COMPOSITE: 'composite',
			REST: 'rest'
		};

		if (!urlPathMap[definition.type]) {
			return;
		}
		window.open(
			`${urlRoot()}/sys_ux_data_broker_${
				urlPathMap[definition.type]
			}.do?sys_id=${definition.id}`,
			'_blank'
		);
	},
	'SN_UIB_DATA_BROKER_DEFINITIONS#CREATE_NEW_CLICKED': ({ action }) => {
		const { selectedType = 'graphql' } = action.payload;
		window.open(
			`${urlRoot()}/sys_ux_data_broker_${selectedType}.do?sys_id=-1`,
			'_blank'
		);
	},
	['SN_UIB_PAGE_MANAGEMENT_PANEL#OPEN_PAGE_CLICKED']: ({
		action,
		properties
	}) => {
		action.stopPropagation();
		const runtimeUrl = getRuntimeUrl(properties);
		window.open(runtimeUrl.absolute, '_blank');
	},
	['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_DELETE_REQUESTED']: ({
		action,
		updateProperties
	}) => {
		const {
			payload: { context, handler }
		} = action;
		let deleteAction = snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_DELETED;

		switch (context) {
			case 'screen':
				deleteAction = snUXFActions.SCREEN_EVENT_MAPPING_HANDLER_DELETED;
				break;
			case 'data-broker':
				deleteAction =
					snUXFActions.DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_DELETED;
				break;
			case 'page':
				deleteAction = snUXFActions.PAGE_INTERNAL_EVENT_MAPPING_HANDLER_DELETED;
				break;
		}

		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete event handler'),
				objectToDelete: handler,
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(deleteAction, {
						...action.payload,
						handlerId: objectToDelete.id
					});
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	['PAGE_EVENT_CONFIG_PANE#MACROPONENT_CONTRACT_EVENT_DELETE_REQUESTED']: ({
		action,
		updateProperties
	}) => {
		action.stopPropagation();
		updateProperties({
			showDeleteConfirmation: true,
			deleteConfirmationProps: {
				headerLabel: t('Delete event'),
				objectToDelete: {
					...action.payload.event,
					name: action.payload.event.label
				},
				deleteActionHandler: (dispatch, objectToDelete) => {
					dispatch(snUXFActions.MCP_CONTRACT_EVENT_DELETED, {
						...action.payload,
						eventId: objectToDelete.id
					});
					updateProperties({
						showDeleteConfirmation: false,
						deleteConfirmationProps: null
					});
				}
			}
		});
	},
	[SN_UIB_APP_UNLOCK_TO_CREATE_OVERRIDE_CLICKED]: ({
		updateProperties,
		action,
		properties: { applicationType }
	}) => {
		const { sysId, scope } = action.payload;
		updateProperties({
			showCreateOverrideDialog: true,
			createOverrideDialogProps: {
				confirmAction: {
					actionType: getOverrideCreateActionType(applicationType),
					payload: {
						sysId,
						sysparm_transaction_scope: scope
					}
				}
			}
		});
	},
	[UIB_STAGE_UNLOCK_TO_CREATE_OVERRIDE_CLICKED]: ({ dispatch, action }) => {
		dispatch(SN_UIB_APP_UNLOCK_TO_CREATE_OVERRIDE_CLICKED, action.payload);
	},
	[UIB_CREATE_OVERRIDE_DIALOG_INTERACTION_COMPLETE]: ({ updateProperties }) => {
		updateProperties({
			showCreateOverrideDialog: false
		});
	},
	['SN_UIB_APP#DUPLICATE_PREVENTED_OPENED']: ({ updateProperties, action }) => {
		updateProperties({
			duplicatePreventedProps: action.payload
		});
	},
	[UIB_DUPLICATE_PREVENTED_OK_CLICKED]: ({ updateProperties }) => {
		updateProperties({
			duplicatePreventedProps: null
		});
	}
};
