export const ROOT_DOMAIN = 'global';

function mapDomain(domain) {
	const hierarchy = domain.label.split('/');

	return {
		id: domain.id,
		fullLabel: domain.label,
		label: hierarchy[hierarchy.length - 1],
		hierarchy,
		children: []
	};
}

function findDomainParent(domainTree, hierarchy, currentLevel = 1) {
	const parentLabel = hierarchy.slice(0, currentLevel).join('/');

	const domainParent = domainTree.find((d) => d.fullLabel === parentLabel);
	if (!domainParent || hierarchy.length === currentLevel) {
		return domainParent;
	}

	const nextLevel = currentLevel + 1;
	const nextDomainParent = findDomainParent(
		domainParent.children,
		hierarchy,
		nextLevel
	);

	// If nextDomainParent is empty and is the last level, return current parent
	if (!nextDomainParent && hierarchy.length === nextLevel) {
		return domainParent;
	}

	return nextDomainParent;
}

export function mountDomainTree(domains) {
	const allDomains = domains.filter((d) => d.id !== ROOT_DOMAIN).map(mapDomain);

	// Sorting the domains so the parent domain will be added to domain tree before its children
	allDomains.sort(function (d1, d2) {
		return d1.hierarchy.length - d2.hierarchy.length;
	});

	const domainTree = allDomains.reduce((domainTree, d) => {
		if (d.hierarchy.length < 2) {
			domainTree.push(d); // add to the root
		} else {
			const domainParent = findDomainParent(
				allDomains,
				d.hierarchy.slice(0, -1)
			);
			if (domainParent) {
				domainParent.children.push(d);
			} else {
				domainTree.push(d);
			}
		}

		return domainTree;
	}, []);

	const rootDomain = domains.find((d) => d.id === ROOT_DOMAIN);

	if (rootDomain) {
		return [
			{
				id: rootDomain.id,
				label: rootDomain.label,
				children: domainTree
			}
		];
	} else {
		return domainTree;
	}
}
