import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import find from 'lodash/find';

import {
	CHECKBOX_CHECKED,
	ITEM_SELECTED,
	PAGE_UTILITIES,
	SETTINGS_UPDATED,
	TABLE_CONFIG,
	URLDOC,
	UTILITIES_COMPONENT_BOOTSTRAPPED
} from '../../constants';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

// Internal components
import '../sn-experience-settings-link';

import styles from '../../styles/forms.scss';

// NDS
import '@servicenow/now-checkbox';
import '@servicenow/now-heading';
import '@servicenow/now-input';
import '@servicenow/now-select';
import '@servicenow/now-text-link';

const page = 'UIB_Workspace_Utility_Settings';

const view = ({
	properties: {
		headerSysId,
		searchSysId,
		notifications,
		globalSearch,
		selectedSearchSource,
		searchSources,
		readonly
	}
}) => {
	const selectedItem = find(
		searchSources,
		(o) => o.id === selectedSearchSource
	);

	return (
		<div>
			<now-heading
				label={t('Notifications')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{
					/* prettier-ignore */
					t('Control whether your end-user sees notifications as they interact with this workspace.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="fieldset">
				<now-checkbox
					label={t('Turn on notifications')}
					readonly={readonly}
					append-to-payload={{ name: 'notifications' }}
					checked={notifications}
				/>
			</div>

			<sn-experience-settings-link
				sys-id={headerSysId}
				table={TABLE_CONFIG}
				/* prettier-ignore */
				label={t('Advanced notification settings')}></sn-experience-settings-link>
			<div className="separator"></div>
			<now-heading
				label={t('Global search')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{
					/* prettier-ignore */
					t('You can show or hide the search functionality in this workspace. If you show search, you can define what search results appear to your end-user.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="flex-column fieldset">
				<now-checkbox
					label="Show global search"
					readonly={readonly}
					append-to-payload={{ name: 'globalSearch' }}
					checked={globalSearch}
				/>
				{!readonly ? (
					<now-select
						label={t('Search source')}
						items={searchSources}
						selectedItem={selectedSearchSource}
						append-to-payload={{ name: 'selectedSearchSource' }}
					/>
				) : (
					<now-input
						label={t('Search source')}
						value={selectedItem ? selectedItem.label : ''}
						readonly={readonly}
					/>
				)}
			</div>
			<sn-experience-settings-link
				sys-id={searchSysId}
				table={TABLE_CONFIG}
				/* prettier-ignore */
				label={t('Advance global search settings')}></sn-experience-settings-link>
		</div>
	);
};

const inputChangedEffect = ({
	action: {
		payload: { name, value }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, { page: PAGE_UTILITIES, name, value });
};

createCustomElement('sn-experience-settings-utilities', {
	view,
	properties: {
		headerSysId: {
			default: ''
		},
		searchSysId: {
			default: ''
		},
		notifications: {
			schema: { type: 'boolean' },
			default: false
		},
		globalSearch: {
			schema: { type: 'boolean' },
			default: false
		},
		selectedSearchSource: {
			schema: { type: 'string' },
			default: ''
		},
		searchSources: {
			schema: { type: 'array' },
			default: []
		},
		readonly: {
			schema: { type: 'boolean' }
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(UTILITIES_COMPONENT_BOOTSTRAPPED);
		},
		[CHECKBOX_CHECKED]: {
			stopPropagation: true,
			effect: inputChangedEffect
		},
		[ITEM_SELECTED]: {
			stopPropagation: true,
			effect: inputChangedEffect
		}
	},
	styles
});
