import get from 'lodash/get';
import utils from './utils';
import { componentInterfaceSchema } from './schemas';

const { createElementInstanceNameMap, createDataBrokerInstanceNameMap } = utils;

export default {
	elementProperties: {
		default: null
	},
	elementStyles: {
		default: null
	},
	elementLayouts: {
		default: null
	},
	layoutTemplates: {
		default: null
	},
	page: {
		default: null
	},
	selectedElementId: {
		default: null
	},
	selectedSlotName: {
		default: null
	},
	selectedSlotParent: {
		default: null
	},
	selectedElementInstanceName: {
		computed({ properties }) {
			const { selectedElementId, elementProperties = {} } = properties;
			const currentProps = elementProperties[selectedElementId] || [];
			const instanceNameProp = currentProps.find(
				(prop) => prop.id === '__instance_name__'
			);
			return get(instanceNameProp, 'value', '');
		}
	},
	tables: {
		default: []
	},
	icons: {
		default: []
	},
	referenceDisplayValues: {
		default: null
	},
	referenceResults: {
		default: null
	},
	referenceSearching: {
		default: null
	},
	tableFieldValues: {
		default: null
	},
	c3PropertyConfig: {
		default: null
	},
	error: {
		default: false
	},
	elementConfigurationPanelExpanded: {
		default: true
	},
	disabled: {
		default: false
	},
	referenceDisplayValuesErrors: {
		default: {}
	},
	applicationType: {
		default: null
	},
	dataBindingEnabled: {
		default: true,
		selectable: true
	},
	scriptedPropertiesEnabled: {
		default: true
	},
	stylingPropertiesEnabled: {
		default: true,
		selectable: true
	},
	dataBrokersEnabled: {
		default: false
	},
	eventMappingEnabled: {
		default: true,
		selectable: true
	},
	/**
	 * the Component config for the element
	 * @type {object}
	 */
	component: {
		default: null
	},
	/**
	 * The map of all element IDs to their user-facing instance names
	 * @type {object}
	 */
	elementInstanceNameMap: {
		computed({ properties }) {
			return createElementInstanceNameMap(properties.elementProperties);
		}
	},
	/**
	 * List of events all current elements have available
	 * @type {object}
	 * @example {'element_id_123': [{ id: '345', eventName: 'SOMETHING_HAPPENED', payloadShape: { ... } }]}
	 */
	elementEvents: {
		default: null
	},
	/**
	 * List of eventMappings all current elements have available
	 * @type {object}
	 * @example {'element_id_123': [{ eventId: '345', eventMappingId: '234' }, {...} ]}
	 */
	elementEventMappings: {
		default: null
	},
	/**
	 * List of available event handlers (ie declarative actions or events)
	 * @type {object[]}
	 */
	eventHandlers: {
		default: []
	},
	/**
	 * List of Event mapping handlers and their value params
	 * @type {object[]}
	 */
	eventMappingHandlers: {
		default: []
	},
	/**
	 * The list of data broker instances and their properties
	 * @type {object[]}
	 */
	dataBrokerInstances: {
		default: []
	},
	/**
	 * The map of all data broker instance IDs to their user-facing instance names
	 * @type {object}
	 */
	dataBrokerInstanceNameMap: {
		computed({ properties }) {
			return createDataBrokerInstanceNameMap(properties.dataBrokerInstances);
		}
	},
	interfaceConfigurationComponent: {
		/**
		 *  the interface name and its corresponding component tag
		 * @typedef componentInterface
		 * @property {string} interfaceName
		 * @property {string}  componentId
		 */

		/**
		 *  a map containing component interfaces
		 * @typedef {Object.<string, componentInterface>} componentInterfaceMap
		 */

		/**
		 * @type {componentInterfaceMap}
		 */
		default: {},
		schema: { type: 'object', schema: componentInterfaceSchema }
	},
	appConfig: {
		/**
		 * @typedef AppConfig
		 * @property {Object[]} appRoutes
		 * @property {Object[]} bindings
		 * @property {Object} dataBrokerInstanceNameMap
		 * @property {Element[]} modalElements
		 * @property {string} elementId
		 */
		/**
		 * a config passed in from emotive-builder that contains relevant data
		 * @type {AppConfig}
		 */
		default: {}
	},
	/**
	 * @typedef bindingInterface
	 * @property {string} label
	 * @property {any} value
	 */

	/**
	 * A list of available context bindings
	 * @type {Array.<bindingInterface>}
	 */
	bindings: {
		default: []
	},

	/**
	 * Event handler element map
	 */
	eventHandlerElementMap: {
		schema: {
			type: 'object'
		},
		default: {}
	},

	/**
	 * Used to determine if the instance name should be edited in
	 * the header or configuration pane.
	 */
	editInstanceNameInHeader: {
		schema: { type: 'boolean' },
		default: true
	},
	/**
	 * Script templates for clientTransformScripts
	 */
	clientTransformScriptTemplates: {
		default: {}
	},
	/**
	 * Client script api for resolving dynamic bindings
	 */
	clientScriptApi: {
		default: {}
	},
	/**
	 * Elements in the compositions of the ancestor mcp hierarchy
	 */
	ancestorElements: {
		default: {}
	},
	parentElements: {
		default: {}
	},
	/**
	 * Can viewport variants be created?
	 */
	viewportVariantsEnabled: {
		default: false
	},
	isMacroponentReadOnly: {
		default: false
	},
	dynamicTabsEnabled: {
		default: true
	},
	isEmptyState: {
		default: false
	},
	isPageDefinitionEventsDisabled: {
		default: false
	},
	isPageDefinitionLayoutDisabled: {
		default: false
	},
	hideComponentPropertyBindingsEnabled: {
		default: false
	}
};
