import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import isNil from 'lodash/isNil';
import get from 'lodash/get';
import has from 'lodash/has';

export const DISMISS = 'UIB_NEW_CREATE_PAGE_MODAL#CANCEL_CLICKED';
export const CREATE = 'UIB_NEW_CREATE_PAGE_MODAL#CREATE_CLICKED';

export const isDisabled = ({
	itemName,
	routeType,
	showAlert,
	selectedPageIsTab,
	icon,
	order,
	validations
}) => {
	const hasAlert = showAlert;
	const emptyItemName = !itemName || itemName.trim() === '';
	const routeTypeError =
		!routeType ||
		routeType.content.trim() === '' ||
		(routeType.messages && routeType.messages.length);
	const checks = [
		hasAlert,
		emptyItemName,
		routeTypeError,
		!tabFieldsValid(selectedPageIsTab, validations, icon, order)
	];

	return checks.some((check) => check);
};

export const tabFieldsValid = (selectedPageIsTab, validations, icon, order) => {
	let tabFieldsValid = true;

	if (selectedPageIsTab) {
		for (const key in validations) {
			if (validations[key] && validations[key].required) {
				if (key === 'icon' && isEmpty(icon)) {
					tabFieldsValid = false;
				} else if (key === 'order' && isNil(order)) {
					tabFieldsValid = false;
				}
			}

			if (!tabFieldsValid) {
				return false;
			}
		}
	}

	return tabFieldsValid;
};

export const footerActions = (disabled) => {
	return [
		{
			label: t('Create'),
			variant: 'primary',
			clickActionType: CREATE,
			disabled
		},
		{ label: t('Cancel'), variant: 'secondary', clickActionType: DISMISS }
	];
};

export const getUniqueRoutes = (repeatedTabData) => {
	if (!Array.isArray(repeatedTabData) || isEmpty(repeatedTabData)) {
		return [];
	}
	const uniqueRoute = [...new Set(repeatedTabData.map((item) => item.route))];
	return uniqueRoute.map((elem) => ({
		id: elem,
		label: elem
	}));
};

/**
 * Function retrieves the highest order from currentPages or viewportRoutes
 * returns the highest order + 100
 *
 * @param {Array} currentPages - array of the siblings to the page you're on
 * @param {object} parentInfo - parent info object that contains viewportRoutes when selectedPageIsTab
 * @returns {number} returns the next higher order to use for page/tab creation
 */
export const getUpdatedOrder = (currentPages, parentInfo = {}) => {
	let lastRoute;
	if (get(parentInfo, 'elementProperties.viewportRoutes', false)) {
		const values = Object.values(parentInfo.elementProperties.viewportRoutes);
		[lastRoute] = values.slice(-1)[0];
	} else {
		if (!Array.isArray(currentPages)) {
			return 0;
		}
		[lastRoute] = currentPages.slice(-1);
	}
	return !isEmpty(lastRoute) ? lastRoute.order + 100 : 0;
};

export const flattenRoute = (elementProperties, currentPages) => {
	return has(elementProperties, 'viewportRoutes')
		? Object.values(get(elementProperties, 'viewportRoutes', {})).flatMap(
				(route) => route
		  )
		: currentPages;
};
