import '@servicenow/now-modal';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import FieldModalContent from './partials/FieldModalContent';
import ParameterModalContent from './partials/ParameterModalContent';
import styles from './uib-advanced-url-configuration-modal.scss';
import { createDraggableFieldItem, validationErrorMessage } from './utils';
import cuid from 'cuid';
import isEqual from 'lodash/isEqual';
import partition from 'lodash/partition';
import { t } from 'sn-translate';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const CANCEL_BUTTON_CLICKED =
	'UIB_ADVANCED_URL_CONFIGURATION_MODAL#CANCEL_BUTTON_CLICKED';
const SAVE_BUTTON_CLICKED =
	'UIB_ADVANCED_URL_CONFIGURATION_MODAL#SAVE_BUTTON_CLICKED';
export const UIB_ADVANCED_URL_CONFIGURATION_MODAL_DISMISSED =
	'UIB_ADVANCED_URL_CONFIGURATION_MODAL#MODAL_DISMISSED';

const footerActions = (disableSave) => [
	{
		label: t('Save'),
		variant: 'primary',
		clickActionType: SAVE_BUTTON_CLICKED,
		disabled: disableSave
	},
	{
		label: t('Cancel'),
		variant: 'tertiary',
		clickActionType: CANCEL_BUTTON_CLICKED
	}
];

const getModalContent = (
	type,
	fields,
	parameters,
	pristineState,
	isNecessaryTransactionInProgress,
	selectedPageIsTab,
	runtimeUrl,
	showUrlPreview
) => {
	let disableSave = false;
	if (type === 'fields') {
		const updatedFields = fields.map(({ label }) => ({ name: label }));
		const pristineFields = pristineState.fields.map(({ label }) => ({
			name: label
		}));
		const invalidFields = fields.filter(
			(value) =>
				!value.label ||
				value.label.trim() === '' ||
				(value.messages && Boolean(value.messages.length))
		);
		disableSave =
			invalidFields.length !== 0 ||
			!isNecessaryTransactionInProgress ||
			isEqual(updatedFields, pristineFields);
		return {
			disableSave,
			headerLabel:
				fields && fields.length !== 0
					? t('Edit required parameters')
					: t('Add required parameters'),
			modalBody: (
				<FieldModalContent
					fields={fields}
					disabled={!isNecessaryTransactionInProgress || selectedPageIsTab}
					selectedPageIsTab={selectedPageIsTab}
					runtimeUrl={runtimeUrl}
					showUrlPreview={showUrlPreview}
				/>
			)
		};
	}
	if (type === 'parameters') {
		const updatedParameters = Object.keys(parameters)
			.map((key) => parameters[key].name)
			.sort();
		const pristineParameters = Object.keys(pristineState.parameters)
			.map((key) => pristineState.parameters[key].name)
			.sort();
		const invalidParameters = Object.entries(parameters).filter(
			([, value]) =>
				!value.name ||
				value.name.trim() === '' ||
				(value.messages && Boolean(value.messages.length))
		);
		disableSave =
			invalidParameters.length !== 0 ||
			!isNecessaryTransactionInProgress ||
			isEqual(updatedParameters, pristineParameters);
		return {
			disableSave,
			headerLabel:
				parameters && Object.keys(parameters).length !== 0
					? t('Edit optional parameters')
					: t('Add optional parameters'),
			modalBody: (
				<ParameterModalContent
					parameters={parameters}
					disabled={!isNecessaryTransactionInProgress}
					runtimeUrl={runtimeUrl}
					showUrlPreview={showUrlPreview}
				/>
			)
		};
	}
};

const appendToTransactionMessagePayload = { form: 'urlConfig' };

const view = (state) => {
	const {
		fields,
		parameters,
		pristineState,
		properties: {
			configType,
			route: { scope, sysPolicy },
			userScope,
			applications,
			selectedPageIsTab,
			runtimeUrl,
			showUrlPreview,
			supportScope
		},
		isNecessaryTransactionInProgress
	} = state;
	const modalProps = getModalContent(
		configType,
		fields,
		parameters,
		pristineState,
		isNecessaryTransactionInProgress,
		selectedPageIsTab,
		runtimeUrl,
		showUrlPreview
	);

	return (
		<now-modal
			header-label={modalProps.headerLabel}
			footer-actions={footerActions(modalProps.disableSave)}
			manage-opened={true}
			opened={true}
			size="lg">
			<div className="full-content">
				<sn-uib-transaction-alert
					applications={applications}
					userScope={userScope}
					requestScopeId={scope.id}
					requestScopeName={escapeScopeLabel(scope.label)}
					recordProtectionPolicy={sysPolicy}
					supportScope={supportScope}
					requestItemType={'Page'}
				/>
				{modalProps.modalBody}
			</div>
		</now-modal>
	);
};

createCustomElement('uib-advanced-url-configuration-modal', {
	view,
	styles,
	initialState: {
		fields: [],
		parameters: {},
		pristineState: {
			fields: [],
			parameters: {}
		},
		isNecessaryTransactionInProgress: false
	},
	properties: {
		appSysId: {},
		configType: {
			default: 'fields'
		},
		returnToSuccessModal: {
			default: false
		},
		route: {},
		applications: {
			default: null
		},
		userScope: {
			default: null
		},
		userDomain: {
			default: null
		},
		supportDomains: {
			default: null
		},
		selectedPageIsTab: {
			default: false
		},
		runtimeUrl: {
			default: {}
		},
		showUrlPreview: {
			default: false
		},
		supportScope: {
			default: false
		}
	},
	actionHandlers: {
		[CANCEL_BUTTON_CLICKED]: {
			effect: ({ dispatch, properties }) => {
				const { returnToSuccessModal } = properties;
				dispatch(UIB_ADVANCED_URL_CONFIGURATION_MODAL_DISMISSED, {
					returnToSuccessModal
				});
			},
			stopPropagation: true
		},
		[COMPONENT_BOOTSTRAPPED]: ({ properties, updateState }) => {
			const { route } = properties;
			if (!route) {
				return;
			}

			const { fields, optionalParameters } = route;
			const parameters = optionalParameters ? optionalParameters : {};

			const mappedFields = fields
				? Object.keys(fields).map((field, idx) => {
						const { id, name } = fields[field];
						return createDraggableFieldItem(id, name, [], idx * 100);
				  })
				: [];

			updateState({
				fields: mappedFields,
				parameters,
				pristineState: {
					fields: mappedFields,
					parameters
				}
			});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const { name, newValue, oldValue } = action.payload;

			if (name === 'route' && !isEqual(oldValue, newValue)) {
				const { fields, optionalParameters } = newValue;
				const mappedFields = fields
					? Object.keys(fields).map((field, idx) => {
							const { id, name } = fields[field];
							return createDraggableFieldItem(id, name, [], idx * 100);
					  })
					: [];

				updateState({
					fields: mappedFields,
					parameters: optionalParameters,
					pristineState: {
						fields: mappedFields,
						parameters: optionalParameters
					}
				});
			}
		},
		'NOW_BUTTON_BARE#CLICKED': {
			effect: ({ action, state, updateState }) => {
				if (action.payload.button === 'add-field') {
					const { fields } = state;
					const id = cuid();
					const newField = {
						id,
						order: fields.length * 100,
						label: '',
						messages: [],
						type: 'field',
						actions: [
							{
								icon: 'pencil-outline',
								label: t('Rename'),
								id: 'edit-field'
							},
							{
								icon: 'trash-outline',
								label: t('Delete'),
								id: 'delete-field',
								alwaysVisible: true
							}
						]
					};
					updateState({
						operation: 'set',
						path: `fields[${fields.length}]`,
						value: newField
					});
				} else if (action.payload.button === 'add-parameter') {
					const newParam = { name: '', messages: [] };
					newParam.id = cuid();
					updateState({
						operation: 'set',
						path: `parameters[${newParam.id}]`,
						value: newParam
					});
				}
			},
			stopPropagation: true
		},
		'NOW_BUTTON_ICONIC#CLICKED': {
			effect: ({ action, state, updateState }) => {
				const { button, id } = action.payload;
				if (button === 'delete-parameter') {
					const parameters = { ...state.parameters };
					delete parameters[id];
					updateState({ parameters });
				}
			},
			stopPropagation: true
		},
		'NOW_INPUT#INPUT': {
			effect: ({ action, updateState, state }) => {
				const { fieldValue, id: fieldId, input } = action.payload;
				const { fields, parameters } = state;
				let fieldIndex;

				if (input === 'field') {
					fieldIndex = fields.findIndex(({ id }) => id === fieldId);
					updateState({
						operation: 'set',
						path: `fields[${fieldIndex}]`,
						value: {
							...fields[fieldIndex],
							label: fieldValue,
							messages: validationErrorMessage(fieldValue, 'field', {
								...fields,
								...parameters
							})
						}
					});
				} else if (input === 'parameter') {
					updateState({
						operation: 'set',
						path: `parameters[${fieldId}]`,
						value: {
							id: fieldId,
							name: fieldValue,
							messages: validationErrorMessage(fieldValue, 'parameter', {
								...parameters,
								...fields
							})
						}
					});
				}
			},
			stopPropagation: true
		},
		'NOW_MODAL#OPENED_SET': {
			effect: ({ dispatch }) => {
				dispatch(UIB_ADVANCED_URL_CONFIGURATION_MODAL_DISMISSED);
			},
			stopPropagation: true
		},
		[SAVE_BUTTON_CLICKED]: {
			effect: ({ dispatch, properties, state }) => {
				const {
					appSysId,
					configType,
					returnToSuccessModal,
					route
				} = properties;
				const { parameters } = state;
				const fields = state.fields.map(({ label }) => ({ name: label }));

				Object.keys(parameters).map((key) => {
					delete parameters[key].messages;
				});

				const {
					sysId,
					name,
					routeType,
					scope: { id: sysparm_transaction_scope }
				} = route;

				dispatch(
					snUXFActions.NEW_PAGE_SAVED,
					{
						appId: appSysId,
						pageId: sysId,
						data: {
							pageName: name,
							routeType,
							fields,
							parameters: state.parameters
						},
						sysparm_transaction_scope
					},
					{
						returnToSuccessModal,
						routeConfigType: configType,
						showLoadingModal: true
					}
				);

				dispatch(UIB_ADVANCED_URL_CONFIGURATION_MODAL_DISMISSED);
			},
			stopPropagation: true
		},
		'SN_UIB_DRAGGABLE_LIST#ITEM_MOVED': {
			effect: ({ state, updateState, action }) => {
				const {
					payload: { item, dragIndex }
				} = action;
				const { fields } = state;
				const [before, after] = partition(fields, (i) => {
					return i.order < dragIndex;
				});
				const newItems = [
					...before.filter((i) => i.id !== item.id),
					item,
					...after.filter((i) => i.id !== item.id)
				].map((item, idx) => ({
					...item,
					order: idx * 100
				}));

				updateState({
					fields: newItems
				});
			},
			stopPropagation: true
		},
		'SN_UIB_PILLAR_WRAPPER#ACTION_CLICKED': {
			effect: ({ action, updateState, state }) => {
				const { actionId, itemId } = action.payload;
				const { fields } = state;

				if (actionId === 'delete-field') {
					const newFields = fields.filter(({ id }) => id !== itemId);
					updateState({
						operation: 'set',
						path: 'fields',
						value: newFields
					});
				}
			},
			stopPropagation: true
		},
		['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: {
			effect: ({ action, state, updateState }) => {
				const {
					payload: { isNecessaryTransactionInProgress }
				} = action;
				if (
					isNecessaryTransactionInProgress !==
					state.isNecessaryTransactionInProgress
				) {
					updateState({ isNecessaryTransactionInProgress });
				}
			},
			stopPropagation: true
		}
	}
});
