/**
 * Processor that takes in the currently configured property
 * and configures it for a table_name type
 * @param {object} property - The current UXF property being configured
 * @param {object[]} properties - The current set of UXF properties
 * @param {object} state - The current page configuration panel Seismic state
 * @return {object} - The configured property object
 */
export default function (property) {
	property.tables = '@sn-uib-page-configuration-panel/tables';
	return property;
}
