import { t } from 'sn-translate';
/**
 * Processor that takes in the currently configured property and configures it for string types
 * @param {object} property - The current UXF element property being configured
 * @param {object[]} properties - The current set of UXF properties for the element being configured
 * @param {object} state - The current element configuration panel Seismic state
 * @return {object} - The configured property object
 */
export default function (property) {
	return {
		...property,
		validation: {
			pattern: '^(@|@[^\\/]+|https?:\\/\\/[^\\/]+/.*|\\.{0,2}\\/.*)$',
			message: t('Invalid URL syntax')
		},
		mode: 'url'
	};
}
