import { amb } from './amb/glide-amb-client-bundle.min';
import { resetAuthToken } from '../../utils/getAuthToken';

/**
 * A context-specific alias to mimic ambEventEffect until this issue is resolved:
 * https://code.devsnc.com/dev/sn-seismic/issues/446
 */
class AmbClientProxy {
	handlerIdMap = {};
	client = amb.getClient();

	dispatchOnEvent(actionType, dispatch) {
		const { client, handlerIdMap } = this;

		const eventKey = actionType.replace(/^AMB_EVENT_/, '');
		const { [eventKey]: eventName } = client.getEvents();

		if (!eventName) {
			throw new Error(
				`[ambClientProxy] No amb event found for '${actionType}'`
			);
		}

		if (handlerIdMap.hasOwnProperty(actionType)) {
			return;
		}

		handlerIdMap[actionType] = client.subscribeToEvent(eventName, () => {
			dispatch(actionType);
		});
	}

	unsubscribeFromAction(actionType) {
		const {
			client,
			handlerIdMap,
			handlerIdMap: { [actionType]: handlerId }
		} = this;

		client.unsubscribeFromEvent(handlerId);
		delete handlerIdMap[actionType];
	}

	unsubscribeFromActions() {
		const actionTypes = Object.keys(this.handlerIdMap);

		for (const actionType of actionTypes) {
			this.unsubscribeFromAction(actionType);
		}
	}
}

/* eslint-disable no-undef */
const ambClientProxy =
	process.env.NODE_ENV !== 'development' && new AmbClientProxy();
/* eslint-enable */

/**
 * Encapsulates funcitonality described by the effect-amb documentation:
 * https://code.devsnc.com/dev/sn-seismic/blob/master/packages/effects/effect-amb/api.md#createambeventeffect
 */
const ambStatusBehavior = {
	name: 'ambStatusBehavior',

	initialState: {
		isSessionLoggedIn: true,
		isAuthTokenActive: true
	},

	properties: {
		sessionLoggedIn: {
			default: true,
			selectable: true
		}
	},

	// Using deprecated API here since COMPONENT_CONNECTED wasn't firing for some reason
	// TODO: @benjamin.fischer investigate why seismic lifecycle actions aren't firing in this context
	onConnect(_, dispatch) {
		if (ambClientProxy) {
			ambClientProxy.dispatchOnEvent('AMB_EVENT_SESSION_LOGGED_IN', dispatch);
			ambClientProxy.dispatchOnEvent('AMB_EVENT_SESSION_LOGGED_OUT', dispatch);
			ambClientProxy.dispatchOnEvent('AMB_EVENT_SESSION_INVALIDATED', dispatch);

			const isGckValid = typeof window.g_ck === 'string';
			dispatch('AMB_STATUS_BEHAVIOR#SET_STATUS', {
				isAuthTokenActive: isGckValid,
				isSessionLoggedIn: isGckValid
			});
		}
	},

	onDisconnect() {
		if (ambClientProxy) {
			ambClientProxy.unsubscribeFromActions();
		}
	},

	actions: {
		['AMB_STATUS_BEHAVIOR#SET_STATUS']: { private: true }
	},

	actionHandlers: {
		['AMB_EVENT_SESSION_LOGGED_IN']: ({ dispatch }) => {
			dispatch('AMB_STATUS_BEHAVIOR#LOGGED_IN');
		},

		['AMB_EVENT_SESSION_LOGGED_OUT']: ({ dispatch }) => {
			dispatch('AMB_STATUS_BEHAVIOR#LOGGED_OUT');
		},

		['AMB_EVENT_SESSION_INVALIDATED']: ({ dispatch }) => {
			dispatch('AMB_STATUS_BEHAVIOR#LOGGED_OUT');
		},

		['AMB_STATUS_BEHAVIOR#SET_STATUS']: ({
			state,
			properties,
			action,
			updateState,
			updateProperties
		}) => {
			const { isAuthTokenActive, isSessionLoggedIn } = action.payload || {};
			const sessionLoggedIn = isAuthTokenActive && isSessionLoggedIn;

			if (properties.sessionLoggedIn !== sessionLoggedIn) {
				updateProperties({ sessionLoggedIn });
			}

			updateState({
				operation: 'set',
				path: 'behaviors.ambStatusBehavior',
				value: {
					...state.behaviors.ambStatusBehavior,
					isAuthTokenActive,
					isSessionLoggedIn
				}
			});
		},

		// Intended to be called after being logged in via the login modal
		['AMB_STATUS_BEHAVIOR#LOGGED_IN']: ({ state, action, dispatch }) => {
			const behaviorState = state.behaviors.ambStatusBehavior;
			const payload = {
				isAuthTokenActive: behaviorState.isAuthTokenActive,
				isSessionLoggedIn: true
			};
			const { token } = action.payload || {};

			// Making this check ensures that the LoginModal stays open long enough to retrieve the new g_ck.
			if (!behaviorState.isAuthTokenActive && typeof token === 'string') {
				resetAuthToken(token);
				payload.isAuthTokenActive = true;

				// Doing this because we can't seem to re-establish a valid connection once it's lost.
				// So, we'll just ignore invalidated messages after the first one fires
				ambClientProxy.unsubscribeFromAction('AMB_EVENT_SESSION_INVALIDATED');
			}

			dispatch('AMB_STATUS_BEHAVIOR#SET_STATUS', payload);
		},

		['AMB_STATUS_BEHAVIOR#LOGGED_OUT']: ({ dispatch }) => {
			dispatch('AMB_STATUS_BEHAVIOR#SET_STATUS', {
				isAuthTokenActive: false,
				isSessionLoggedIn: false
			});
		}
	}
};

export default ambStatusBehavior;
