import snakeCase from 'lodash/snakeCase';
import { flattenAppRoutes } from './appRoutes';

/**
 *
 */
export function urlRoot() {
	return window.__localDomain ? window.__localDomain : '';
}

const validationRegex = /^[ .~a-zA-Z0-9_-]*$/;

/**
 * @param properties
 * @param encodeURI
 */
export function getRuntimeUrl(properties, encodeURI = false) {
	const {
		appRoutes,
		appsURL,
		routeState: { pageSysId },
		selectedPageIsTab
	} = properties;

	const runtimeUrl = {
		absolute: undefined,
		isInvalidAbsoluteUrl: false,
		root: urlRoot(),
		appsURL,
		page: undefined,
		fields: [],
		params: []
	};

	const parentRoute = flattenAppRoutes(appRoutes).find(
		(route) => route.page.sysId === pageSysId
	);
	let path;
	if (parentRoute) {
		runtimeUrl.page = `/${parentRoute.routeType}`;
		path = `${runtimeUrl.page}`;

		if (parentRoute.fields) {
			Object.values(parentRoute.fields).map((field) => {
				const pageProperty = properties.pageProperties.find(
					({ name }) => name === field.name
				);
				if (pageProperty) {
					const value = pageProperty.testValue || pageProperty.value;

					runtimeUrl.fields.push({ name: field.name, value, pageProperty });
					path += `/${encodeURI ? encodeURIComponent(value) : value}`;

					if (!value || !value.match(validationRegex)) {
						runtimeUrl.isInvalidAbsoluteUrl = true;
					}
				}
			});
		}

		if (parentRoute.optionalParameters) {
			let paramsPath = '/params';
			let hasParamsWithValues = false;
			Object.values(parentRoute.optionalParameters).map((optionalParameter) => {
				const pageProperty = properties.pageProperties.find(
					({ name }) => name === optionalParameter.name
				);
				if (pageProperty) {
					const paramName = snakeCase(optionalParameter.name).replace(
						/_/g,
						'-'
					);
					const value = pageProperty.testValue || pageProperty.value;

					runtimeUrl.params.push({
						name: optionalParameter.name,
						value,
						pageProperty
					});

					if (value) {
						paramsPath += `/${paramName}/${
							encodeURI ? encodeURIComponent(value) : value
						}`;
						hasParamsWithValues = true;
					}
				}
			});
			if (hasParamsWithValues) {
				path += paramsPath;
			}
		}

		if (!selectedPageIsTab) {
			runtimeUrl.absolute = `${urlRoot()}${appsURL}${path}`;
		}
	}

	return runtimeUrl;
}
