import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import get from 'lodash/get';

import {
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_UPDATE_ERROR_OCCURRED,
	TOOLBAR_FETCH_SUCCEEDED,
	TOOLBAR_UPDATE_SUCCEEDED
} from '../constants';

const query = `
query snExperienceSettingsConnected($sysId: ID!) {
  snExpSettings {
    settings {
      toolbar(sysId: $sysId) {
		sysId
		routes {
			id
			label
		}
		buttons {
			sysId
			order
			icon
			label
			route
			viewportRoute
			group
		}
      }
    }
  }
}`;

export const toolbarFetchRequestedEffect = createGraphQLEffect(query, {
	variableList: ['sysId'],
	successActionType: TOOLBAR_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const toolbarFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const { sysId, routes, buttons } = get(
		payload,
		`data.snExpSettings.settings.toolbar`,
		{}
	);

	updateState({
		toolbar: {
			sysId,
			routes,
			buttons,
			updated: false
		}
	});
};

const mutation = `
mutation snExperienceSettingsConnected($sysId: ID! $buttons: [snExpSettings_settings_ButtonInput] ) {
  snExpSettings {
    settings {
      toolbarUpdate(sysId:$sysId buttons:$buttons) {
        sysId
		routes {
			id
			label
		}
		buttons {
			sysId
			order
			icon
			label
			route
			viewportRoute
			group
		}
      }
    }
  }
}`;

export const toolbarUpdateRequestedEffect = createGraphQLEffect(mutation, {
	variableList: ['sysId', 'buttons'],
	successActionType: TOOLBAR_UPDATE_SUCCEEDED,
	errorActionType: SETTINGS_UPDATE_ERROR_OCCURRED
});

export const toolbarUpdateSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const { sysId, routes, buttons } = get(
		payload,
		`data.snExpSettings.settings.toolbarUpdate`,
		{}
	);
	updateState({
		toolbar: {
			sysId,
			routes,
			buttons,
			updated: false
		}
	});
};
