import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import {
	SEARCH_FETCH_SUCCEEDED,
	SEARCH_UPDATE_SUCCEEDED,
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_UPDATE_SUCCEEDED
} from '../constants';
import get from 'lodash/get';

const props = `
        sysId
        searchPublic
        searchPrivate
`;

const query = `
query snExperienceSettingsConnected($sysId: ID!) {
  snExpSettings {
    settings {
      search(sysId: $sysId) {
        ${props}
      }
    }
  }
}`;

export const searchFetchRequestedEffect = createGraphQLEffect(query, {
	variableList: ['sysId'],
	successActionType: SEARCH_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const searchFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const { sysId, searchPublic, searchPrivate } = get(
		payload,
		`data.snExpSettings.settings.search`,
		{}
	);
	updateState({
		search: {
			sysId,
			searchPublic,
			searchPrivate,
			updated: false
		}
	});
};

const mutation = `
mutation snExperienceSettingsConnected($sysId: ID! $searchPublic: Boolean! $searchPrivate: Boolean!) {
  snExpSettings {
    settings {
      searchUpdate(sysId:$sysId searchPublic:$searchPublic searchPrivate:$searchPrivate) {
        ${props}
      }
    }
  }
}`;

export const searchUpdateRequestedEffect = createGraphQLEffect(mutation, {
	variableList: ['sysId', 'searchPublic', 'searchPrivate'],
	successActionType: SEARCH_UPDATE_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const searchUpdateSucceededEffect = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	const { sysId, searchPublic, searchPrivate } = get(
		payload,
		`data.snExpSettings.settings.searchUpdate`,
		[]
	);

	updateState({
		search: {
			sysId,
			searchPublic,
			searchPrivate,
			updated: false
		}
	});
	dispatch(SETTINGS_UPDATE_SUCCEEDED);
};
