import { createCustomElement } from '@servicenow/ui-core';
import styles from './uib-sidebar-nav.scss';
import { t } from 'sn-translate';
import { transformPageList } from '../uib-page-management-panel/utils';

export const SIDEBAR_NAV_TAB_CHANGED = 'UIB_SIDEBAR_NAV#TAB_CHANGED';
export const SIDEBAR_NAV_DATATAB_CHANGED = 'UIB_SIDEBAR_NAV#DATATAB_CHANGED';
import { applicationTypes } from '../../utils/routingConstants';

const getTabs = (
	isPagesLoading,
	isPageSelected,
	isVariantSelected,
	applicationType,
	isEmptyState
) => () => {
	const tabs = [];
	const isDisabled =
		!isPageSelected ||
		isPagesLoading ||
		(!isVariantSelected && applicationType === applicationTypes.app);

	if (applicationType === applicationTypes.app) {
		tabs.push({
			id: 'home',
			icon: 'home',
			label: t('UI Builder home page'),
			disabled: false
		});
	}
	tabs.push({
		id: 'pages',
		icon: 'document-blank',
		label: t('Pages'),
		disabled: isEmptyState
	});
	tabs.push({
		id: 'components',
		icon: 'component',
		label: t('Components'),
		disabled: isDisabled
	});

	return tabs;
};

const getDataTabs = (
	databrokersEnabled,
	clientStateEnabled,
	clientScriptsEnabled,
	isVariantSelected
) => {
	const dataTabs = [];
	if (databrokersEnabled && isVariantSelected) {
		dataTabs.push({
			id: 'data-brokers',
			icon: 'database',
			label: t('Data')
		});
	}

	if (clientStateEnabled && isVariantSelected) {
		dataTabs.push({
			id: 'client-state',
			icon: 'address-card',
			label: t('Client state')
		});
	}

	if (clientScriptsEnabled && isVariantSelected) {
		dataTabs.push({
			id: 'client-scripts',
			icon: 'code',
			label: t('Client scripts')
		});
	}

	return dataTabs;
};

const view = (state) => {
	const {
		tabs,
		dataTabs,
		properties: { selectedTab, selectedDataTab, isEmptyState }
	} = state;

	const circleClasses = (tab, selectedTab) => ({
		['tab-button-bg']: true,
		selected: selectedTab === tab.id && !isEmptyState
	});

	return (
		<div className="tab-sidebar-wrapper">
			<div className="tab-sidebar">
				{tabs.map((tab) => (
					<div class={circleClasses(tab, selectedTab)}>
						<now-button
							icon={`${tab.icon}${
								selectedTab === tab.id ? '-fill' : '-outline'
							}`}
							bare={true}
							className={tab.id}
							disabled={tab.disabled}
							tooltip-content={tab.label}
							variant={selectedTab === tab.id ? 'inherit' : 'inherit'}
							append-to-payload={{ tab: tab.id }}
							size="md"
						/>
					</div>
				))}
			</div>
			<div className="tab-sidebar">
				{dataTabs.map((dataTab) => (
					<div class={circleClasses(dataTab, selectedDataTab)}>
						<now-button
							icon={`${dataTab.icon}${
								selectedDataTab === dataTab.id ? '-fill' : '-outline'
							}`}
							bare={true}
							className={dataTab.id}
							disabled={dataTab.disabled}
							tooltip-content={dataTab.label}
							variant={selectedDataTab === dataTab.id ? 'inherit' : 'inherit'}
							append-to-payload={{ dataTab: dataTab.id }}
							size="md"
						/>
					</div>
				))}
			</div>
		</div>
	);
};

createCustomElement('uib-sidebar-nav', {
	view,
	properties: {
		pages: {
			default: []
		},
		isWorkspaceApp: {
			default: false
		},
		isPagesLoading: {
			default: false
		},
		selectedTab: {
			default: 'pages'
		},
		selectedDataTab: {
			default: 'data-brokers'
		},
		applicationType: {
			default: null
		},
		routes: {
			default: []
		},
		pageVariantsEnabled: {
			default: false
		},
		databrokersEnabled: {
			default: false
		},
		componentTreeEnabled: {
			default: false
		},
		clientStateEnabled: {
			default: false
		},
		clientScriptsEnabled: {
			default: false
		},
		isPageSelected: {
			schema: {
				type: 'boolean'
			},
			default: false
		},
		isVariantSelected: {
			default: false
		},
		isEmptyState: {
			default: false
		},
		isPageStateDirty: {
			default: false
		}
	},
	transformState: (state) => {
		const { pagesList } = transformPageList(state);
		const {
			isPagesLoading,
			databrokersEnabled,
			clientScriptsEnabled,
			isPageSelected,
			clientStateEnabled,
			isVariantSelected,
			applicationType,
			isEmptyState
		} = state.properties;
		return {
			...state,
			tabs: getTabs(
				isPagesLoading,
				isPageSelected,
				isVariantSelected,
				applicationType,
				isEmptyState
			)(pagesList),
			dataTabs: getDataTabs(
				databrokersEnabled,
				clientStateEnabled,
				clientScriptsEnabled,
				isVariantSelected
			)
		};
	},
	actionHandlers: {
		'NOW_BUTTON#CLICKED': ({ action, properties, dispatch }) => {
			const selectedTab = action.payload.tab;
			const selectedDataTab = action.payload.dataTab;

			if (selectedTab === 'home') {
				dispatch('UIB_SIDEBAR_NAV#HOME_CLICKED');
			} else if (selectedTab) {
				dispatch(SIDEBAR_NAV_TAB_CHANGED, {
					value: properties.selectedTab === selectedTab ? null : selectedTab
				});
				dispatch('COLLAPSIBLE_PANEL#PANEL_TOGGLED', {
					panel: 'page-management',
					value: properties.selectedTab !== selectedTab
				});
			}

			selectedDataTab &&
				dispatch(SIDEBAR_NAV_DATATAB_CHANGED, { value: selectedDataTab });
		}
	},
	styles
});
