import { createHttpEffect } from '@servicenow/ui-effect-http';

import {
	DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_REQUESTED,
	DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_FAILED,
	DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_SUCCEEDED,
	DOMAIN_BEHAVIOR_DOMAINS_RETRIEVED,
	DOMAIN_BEHAVIOR_UPDATE_DOMAIN_REQUESTED,
	DOMAIN_BEHAVIOR_UPDATE_DOMAIN_FAILED,
	DOMAIN_BEHAVIOR_UPDATE_DOMAIN_SUCCEEDED,
	DOMAIN_BEHAVIOR_DOMAIN_UPDATED
} from './constants';

import { mountDomainTree } from './utils';

import { effectProxy } from 'sn-uxf-builder-middleware';

export const fetchDomainsEffect = createHttpEffect(
	'/api/now/ui/concoursepicker/domain',
	{
		successActionType: DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_SUCCEEDED,
		errorActionType: DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_FAILED,
		method: 'GET',
		batch: false
	}
);

export const updateDomainEffect = createHttpEffect(
	'/api/now/ui/concoursepicker/domain',
	{
		successActionType: DOMAIN_BEHAVIOR_UPDATE_DOMAIN_SUCCEEDED,
		errorActionType: DOMAIN_BEHAVIOR_UPDATE_DOMAIN_FAILED,
		method: 'PUT',
		batch: false,
		dataParam: ['data']
	}
);

const retrieveDomainsSucceededHandler = ({ action, updateState, dispatch }) => {
	const { current, list } = action.payload.result;
	const domains = list.map((domain) => {
		return {
			id: domain.value,
			label: domain.label
		};
	});

	const payload = {
		domains,
		domainTree: mountDomainTree(domains),
		currentDomain: {
			id: current.value,
			label: current.label
		}
	};

	updateState(payload);
	dispatch(DOMAIN_BEHAVIOR_DOMAINS_RETRIEVED, payload);
};

const updateDomainSucceededHandler = ({ action, updateState, dispatch }) => {
	const { current } = action.payload.result;

	const payload = {
		currentDomain: {
			id: current.value,
			label: current.label
		}
	};

	updateState(payload);

	dispatch(DOMAIN_BEHAVIOR_DOMAIN_UPDATED, payload);
};

export default {
	[DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_REQUESTED]: effectProxy(fetchDomainsEffect),
	[DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_SUCCEEDED]: retrieveDomainsSucceededHandler,
	[DOMAIN_BEHAVIOR_UPDATE_DOMAIN_REQUESTED]: effectProxy(updateDomainEffect),
	[DOMAIN_BEHAVIOR_UPDATE_DOMAIN_SUCCEEDED]: updateDomainSucceededHandler
};
