import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import {
	snUibDataBrokerConfigurationPanel,
	snUIBClientStateConfigurationPanel
} from 'sn-ui-builder-components';
import { contextTypes, snUXFActions } from 'sn-uxf-builder-middleware';
import get from 'lodash/get';
import difference from 'lodash/difference';
import {
	dataBrokerPropertyChanged,
	resetAndSendNewPropertyValue,
	dataBrokerPropertyReferenceSearched,
	dataBrokerEventMappingHandlerReferenceSearched,
	requestReferenceDisplayValues,
	requestFieldValues,
	renderClientStateDataBroker,
	clientScriptCodeChanged,
	clientScriptCreated,
	clientScriptNameChanged,
	clientScriptIncludesChanged,
	deleteHandlerForTriggeringElement,
	clientStateParameterCreated,
	clientStateParameterChanged,
	requestDataBrokerMetadataChange,
	dataBrokerInstanceEvaluationModeChanged,
	dataBrokerEventMappingAdded,
	dataBrokerEventMappingHandlerAdded,
	dataBrokerEventMappingHandlerDeleted,
	dataBrokerEventMappingHandlerChanged,
	sendNewScriptedHandlerValue
} from './actionHandlers';
import {
	createDataBrokerInstanceNameMap,
	getSelectablePropsFromPageState,
	isDataBrokerTypeClientState
} from './utils';
import {
	getCurrentSelectedEventMappingHandlers,
	processCurrentSelectedProperties,
	processEventHandlers,
	processCurrentSelectedEventMappings,
	processEventMappingHandlers
} from '../../utils/properties';
import {
	flattenAppRouteForMcps,
	getAllDescendentDataBrokers
} from '../../utils/appRoutes';
import processorOverrides from './processors';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import '../uib-data-broker-client-state-renderer';
const { createFocusRef } = DevxKeyFocusBehavior;
const QUICK_LINK_DATA_BROKER_REF = createFocusRef(
	'quick-link-data-broker-config-panel'
);

createCustomElement('uib-data-configuration-shelf', {
	view: (state) => {
		const {
			properties,
			currentSelectedProperties,
			currentSelectablePropPreview,
			currentSelectablePropExecutionTime,
			selectedDataBrokerIsAncestor,
			currentSelectedEventMappings,
			currentEventHandlers,
			currentEventMappingHandlers
		} = state;
		const {
			clientStateOutput,
			selectedDataBrokerId,
			dataBrokerInstances,
			bindings,
			dataBrokerInstanceNameMap,
			parentDataBrokerInstances,
			selectedDataTab,
			appConfig,
			interfaceConfigurationComponent,
			clientTransformScriptTemplates
		} = properties;

		const clientStateSelectableProps =
			getSelectablePropsFromPageState(state) || [];

		return (
			<Fragment>
				<sn-uib-data-configuration-shelf
					component-id="sn-uib-data-configuration-shelf"
					{...properties}
					currentSelectedProperties={currentSelectedProperties}
					currentSelectablePropPreview={
						isDataBrokerTypeClientState(
							[...dataBrokerInstances, ...parentDataBrokerInstances],
							selectedDataBrokerId
						)
							? clientStateOutput
							: currentSelectablePropPreview
					}
					currentSelectablePropExecutionTime={
						currentSelectablePropExecutionTime
					}
					selectedDataBrokerIsAncestor={selectedDataBrokerIsAncestor}
					bindings={bindings}
					ref={QUICK_LINK_DATA_BROKER_REF}
					dataBrokerInstanceNameMap={dataBrokerInstanceNameMap}
					selected-data-tab={selectedDataTab}
					eventHandlers={currentEventHandlers}
					eventMappings={currentSelectedEventMappings}
					eventMappingHandlers={currentEventMappingHandlers}
					appConfig={appConfig}
					interfaceConfigurationComponent={interfaceConfigurationComponent}
					clientTransformScriptTemplates={clientTransformScriptTemplates}
				/>
				<div style={{ display: 'none' }}>
					{clientStateSelectableProps.map((elem) => (
						// Todo: Find a better way to handle the client state data broker output
						// currently this is the only way to get the selectable props from the sn-form-data-connected to display it in the output section.
						<uib-data-broker-client-state-renderer
							currentSelectedProp={elem}
							selectableProps={`@${selectedDataBrokerId}/${elem}`}
						/>
					))}
				</div>
			</Fragment>
		);
	},
	transformState: (state) => {
		const {
			properties: {
				dataBrokerInstances,
				parentDataBrokerInstances,
				selectedDataBrokerId,
				dataBrokerEventMappings = {},
				eventHandlers = [],
				eventMappingHandlers = {}
			}
		} = state;

		const localDataBroker = dataBrokerInstances.find(
			(dataBroker) => dataBroker.id === selectedDataBrokerId
		);
		const ancestorDataBroker = parentDataBrokerInstances.find(
			(dataBroker) => dataBroker.id === selectedDataBrokerId
		);
		const selectedDataBrokerIsAncestor = !localDataBroker && ancestorDataBroker;

		const selectedDataBroker = localDataBroker
			? localDataBroker
			: ancestorDataBroker || null;
		const currentProperties = selectedDataBroker
			? Object.values(selectedDataBroker.properties)
			: [];
		const currentSelectablePropPreview = get(
			selectedDataBroker,
			'selectablePropPreview',
			''
		);
		const currentSelectablePropExecutionTime = get(
			selectedDataBroker,
			'selectablePropExecutionTime',
			''
		);

		const currentSelectedProperties = processCurrentSelectedProperties(
			currentProperties,
			state,
			processorOverrides
		);
		selectedDataBrokerIsAncestor &&
			currentSelectedProperties.forEach((property) => {
				property.disabled = true;
			});

		const currentEventMappings =
			dataBrokerEventMappings && selectedDataBrokerId
				? dataBrokerEventMappings[selectedDataBrokerId]
				: [];
		const currentSelectedEventMappings = processCurrentSelectedEventMappings(
			currentEventMappings
		);
		const currentEventHandlers = processEventHandlers(eventHandlers);

		const selectedEventMappingHandlers = getCurrentSelectedEventMappingHandlers(
			currentSelectedEventMappings,
			eventMappingHandlers
		);
		const currentEventMappingHandlers = processEventMappingHandlers(
			selectedEventMappingHandlers,
			state,
			processorOverrides,
			'sn-uib-data-configuration-shelf'
		);

		return {
			...state,
			selectedDataBrokerIsAncestor,
			currentSelectedProperties,
			currentSelectablePropPreview,
			currentSelectablePropExecutionTime,
			currentSelectedEventMappings,
			currentEventHandlers,
			currentEventMappingHandlers
		};
	},
	properties: {
		/**
		 * the list of client state data broker reference
		 *
		 * @type {Array}
		 */
		clientStateDataBrokersRef: {
			default: []
		},
		/**
		 * the list of data broker definitions
		 *
		 * @type {Array}
		 */
		dataBrokerDefinitions: {
			default: null
		},
		/**
		 * the list of data broker instances and their properties
		 *
		 * @type {Array}
		 */
		dataBrokerInstances: {
			default: []
		},
		/**
		 * the ID of the data broker that is currently being configured
		 *
		 * @type {string}
		 */
		selectedDataBrokerId: {
			default: null
		},
		/**
		 * whether the component is disabled
		 *
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 * The map of all data broker instance IDs to their user-facing instance names
		 *
		 * @type {object}
		 */
		dataBrokerInstanceNameMap: {
			computed({
				properties: { dataBrokerInstances, ancestorDataBrokerInstances }
			}) {
				return createDataBrokerInstanceNameMap([
					...dataBrokerInstances,
					...ancestorDataBrokerInstances
				]);
			}
		},
		referenceResults: {
			default: null
		},
		referenceDisplayValues: {
			default: null
		},
		referenceSearching: {
			default: null
		},
		tables: {
			default: null
		},
		tableFieldValues: {
			default: null
		},
		c3PropertyConfig: {
			default: null
		},
		clientStateOutput: {
			default: {}
		},
		isMacroponentReadOnly: {
			default: false
		},
		/**
		 * the list of parent data broker instances and their properties
		 *
		 * @type {Array}
		 */
		parentDataBrokerInstances: {
			default: []
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 *
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * Array of current client scripts
		 */
		scripts: {
			schema: {
				type: 'array'
			},
			default: []
		},
		isScriptsBusy: {
			schema: {
				type: 'boolean'
			},
			default: false
		},
		/**
		 * Event handler element map
		 */
		eventHandlerElementMap: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		/**
		 * List of all the client state parameters configured on the current screen
		 */
		clientStateParameters: {
			schema: {
				type: 'array'
			},
			default: []
		},
		/**
		 * An object containing the current client state data
		 */
		clientStateData: {
			schema: {
				type: 'object'
			},
			default: {}
		},

		/**
		 * the list of parent data broker instances and their properties
		 */
		ancestorDataBrokerInstances: {
			schema: { type: 'array' },
			default: []
		},
		selectedDataTab: {
			default: null
		},
		dataBrokerEventMappings: {
			default: null
		},
		eventHandlers: {
			default: []
		},
		eventMappingHandlers: {
			default: null
		},
		appConfig: {
			default: null
		},
		interfaceConfigurationComponent: {
			default: null
		},
		clientTransformScriptTemplates: {
			default: null
		},
		appRoutes: {
			default: []
		},
		pageSysId: {
			default: null
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			dispatch
		}) => {
			const { name, value, previousValue } = action.payload;
			const { selectedDataBrokerId } = properties;
			if (
				name === 'dataBrokerInstances' &&
				value.length > previousValue.length
			) {
				const newDataBrokerInstance = difference(
					value.map((db) => db.id),
					previousValue.map((db) => db.id)
				);
				dispatch(
					'UIB_DATA_CONFIGURATION_SHELF#NEW_DATABROKER_INSTANCE_DETECTED',
					{ dataBrokerId: newDataBrokerInstance[0] }
				);
			}
		},
		['SN_UIB_DATA_BROKER_DEFINITIONS#DEFINITION_SELECTED']: ({
			action,
			dispatch,
			properties
		}) => {
			const { payload } = action;
			const { appRoutes, pageSysId } = properties;
			const currentAppRoute = appRoutes.find(
				(appRoute) => get(appRoute, 'page.sysId', null) === pageSysId
			);
			const descendentDataBrokers = currentAppRoute
				? getAllDescendentDataBrokers(flattenAppRouteForMcps(currentAppRoute))
				: [];
			dispatch(snUXFActions.DATA_BROKER_INSTANCE_CREATED, {
				dataBrokerDefinitionId: payload.sysId,
				descendentDataBrokers
			});
		},
		['SN_UIB_DATA_BROKER_CONFIG_PANEL#PROPERTY_CHANGED']: dataBrokerPropertyChanged,
		['SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_ADDED']: dataBrokerEventMappingAdded,
		['SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_HANDLER_ADDED']: dataBrokerEventMappingHandlerAdded,
		['SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_HANDLER_DELETED']: dataBrokerEventMappingHandlerDeleted,
		['SN_UIB_DATA_BROKER_CONFIG_PANEL#EVENT_MAPPING_HANDLER_CHANGED']: dataBrokerEventMappingHandlerChanged,
		['SN_UIB_DATA_BROKER_EDITOR#PROPERTY_BINDING_TOGGLED']: resetAndSendNewPropertyValue,
		['SN_UIB_DATA_BROKER_EDITOR#REFERENCE_SEARCHED']: dataBrokerPropertyReferenceSearched,
		['SN_UIB_DATA_BROKER_EDITOR#HANDLER_VALUE_REFERENCE_SEARCHED']: dataBrokerEventMappingHandlerReferenceSearched,
		['SN_UIB_DATA_BROKER_EDITOR#DATA_BROKER_CHANGED']: requestReferenceDisplayValues,
		['SN_UIB_DATA_BROKER_EDITOR#FIELD_PROPERTY_TABLE_SELECTED']: requestFieldValues,
		['CLIENT_DATA_BROKER#RENDERED']: renderClientStateDataBroker,
		['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_CREATED']: clientScriptCreated,
		['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_NAME_CHANGED']: clientScriptNameChanged,
		['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_INCLUDES_CHANGED']: clientScriptIncludesChanged,
		['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#SCRIPT_CODE_CHANGED']: clientScriptCodeChanged,
		['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#TRIGGERING_ELEMENT_DELETE']: deleteHandlerForTriggeringElement,
		['SN_UIB_SCRIPTS_CONFIGURATION_PANEL#TRIGGERING_ELEMENT_SELECTED']: ({
			dispatch,
			action
		}) => {
			const { element } = action.payload;
			dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
				elementId: element.id,
				elementInstanceName: element.instanceName
			});
		},
		['SN_UIB_STATE_CONFIGURATION_PANEL#PARAMETER_CREATED']: clientStateParameterCreated,
		['SN_UIB_STATE_CONFIGURATION_PANEL#PARAMETER_CHANGED']: clientStateParameterChanged,
		[snUibDataBrokerConfigurationPanel.DATA_BROKER_CONFIG_PANEL_CONTEXT_BINDINGS_REQUESTED]: ({
			action,
			dispatch
		}) => {
			const {
				selectedDataBroker: { id: contextId },
				propertyId
			} = action.payload;
			dispatch(snUXFActions.SN_UXF_CONTEXT_BINDINGS_REQUESTED, {
				contextType: contextTypes.dataBrokerInput,
				contextId,
				propertyId
			});
		},
		[snUIBClientStateConfigurationPanel.STATE_CONFIG_PANEL_PARAMETER_CONTEXT_BINDINGS_REQUESTED]: ({
			action,
			dispatch
		}) => {
			const { propertyId } = action.payload;
			dispatch(snUXFActions.SN_UXF_CONTEXT_BINDINGS_REQUESTED, {
				contextType: contextTypes.clientStateInitialValue,
				propertyId
			});
		},
		['SN_UIB_INSTANCE_NAME_EDITOR#VALUES_CHANGED']: requestDataBrokerMetadataChange,
		['SN_UIB_DATA_BROKER_EDITOR#EVALUATION_MODE_CHANGED']: dataBrokerInstanceEvaluationModeChanged,
		['SN_UIB_DATA_BROKER_CONFIG_PANEL#DATA_BROKER_SELECTED']: ({
			updateProperties
		}) => {
			updateProperties({ clientStateOutput: {} });
		},
		['SN_UIB_DATA_BROKER_EDITOR#EVENT_MAPPING_HANDLER_SCRIPTED_VALUE_CHANGED']: sendNewScriptedHandlerValue
	}
});
