import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import cuid from 'cuid';
import styles from './uib-new-create-page.module.scss';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import {
	PagePropertiesForm // eslint-disable-line no-unused-vars,
} from '../uib-page-configuration-modal/';
import {
	UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED,
	UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED
} from '../uib-page-definition-selector';
import { validationErrorMessage } from '../uib-advanced-url-configuration-modal';
import { t } from 'sn-translate';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import {
	footerActions,
	isDisabled,
	DISMISS,
	CREATE,
	getUniqueRoutes,
	getUpdatedOrder,
	flattenRoute
} from './utils';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';

export const UIB_NEW_CREATE_PAGE_MODAL_DISMISSED =
	'UIB_NEW_CREATE_PAGE#MODA_DISMISSED';
const ICON_PROPERTY_INPUT_PROPERTY_CHANGED =
	'ICON_PROPERTY_INPUT#PROPERTY_CHANGED';

const formatPath = (name) =>
	name.toLowerCase().replace(/[\s\W]/g, (match) => (match === ' ' ? '-' : ''));

const getResolvedDataSource = (parentInfo) => {
	let resolvedDataSource = get(parentInfo, 'resolvedDataSource', []);
	if (isEmpty(resolvedDataSource)) {
		resolvedDataSource = get(
			parentInfo,
			'elementProperties.repeatedTabData',
			[]
		);
	}
	return resolvedDataSource;
};

const view = ({
	itemName,
	routeType,
	isCreating,
	icon,
	order,
	pageDefinition,
	properties: {
		appRoutes,
		itemType,
		routeConfigurationEnabled,
		userScope,
		currentDomain,
		supportDomains,
		selectedPageIsTab,
		currentPages,
		runtimeUrl,
		parentInfo,
		isDynamicTabCreateClicked,
		allowSharedPageDefinition,
		showUrlPreview
	}
}) => {
	const { elementProperties } = parentInfo;
	const showAlert = Boolean(
		(selectedPageIsTab
			? flattenRoute(elementProperties, currentPages)
			: appRoutes
		).find((route) => {
			return (
				route.routeType === routeType.content &&
				(route.parentId ? route.parentId === parentInfo.screenId : true)
			);
		})
	);
	const validation = get(
		elementProperties,
		'viewportConfiguration.validation',
		{}
	);

	const dynamicTabRoutes = isDynamicTabCreateClicked
		? getUniqueRoutes(getResolvedDataSource(parentInfo))
		: [];
	const dynamicTabDataSource = get(elementProperties, 'dynamicTabData', '');
	const disabled = isDisabled({
		itemName,
		routeType,
		showAlert,
		selectedPageIsTab,
		icon,
		order,
		validations: validation,
		pageDefinition
	});

	const newItemType =
		itemType === 'tab' && isDynamicTabCreateClicked
			? `data driven ${itemType}`
			: itemType;

	return (
		<now-modal
			manage-opened={true}
			opened={true}
			size="lg"
			footer-actions={!isCreating ? footerActions(disabled) : null}
			headerLabel={!isCreating ? t('Create a {0}', newItemType) : null}>
			<div
				aria-busy={true}
				className={`${itemType} flex ${isCreating ? 'creating' : ''}`}>
				{isCreating ? (
					<LoadingStage
						size={'sm'}
						loadingStageContent={
							itemName ? t('Creating {0}...', itemName) : t('Loading...')
						}
					/>
				) : (
					<div className="content">
						<div className="subheader">
							{newItemType === 'dashboard'
								? t("Let's set up this {0}.", newItemType)
								: t("First, let's set up this {0}.", newItemType)}
						</div>
						<PagePropertiesForm
							itemType={newItemType}
							itemName={itemName}
							routeType={routeType}
							order={order}
							icon={icon}
							routeConfigurationEnabled={routeConfigurationEnabled}
							scope={{
								id: userScope.id,
								label: escapeScopeLabel(userScope.label)
							}}
							supportDomains={supportDomains}
							domain={currentDomain}
							showAlert={showAlert}
							selectedPageIsTab={selectedPageIsTab}
							validations={validation}
							runtimeUrl={runtimeUrl}
							isNewPage
							dynamicTabRoutes={dynamicTabRoutes}
							isDynamicTab={isDynamicTabCreateClicked}
							dynamicTabDataSource={dynamicTabDataSource}
							showUrlPreview={showUrlPreview}
							allowSharedPageDefinition={allowSharedPageDefinition}
						/>
					</div>
				)}
			</div>
		</now-modal>
	);
};

createCustomElement('uib-new-create-page', {
	view,
	styles,
	initialState: {
		routeType: { content: '', messages: [] },
		order: 0,
		icon: '',
		itemName: '',
		isCreating: false,
		pageDefinition: null,
		usingSharedPageDefinition: false,
		copyPageDefinition: false
	},
	properties: {
		appsSysId: {
			default: null
		},
		appRoutes: {
			default: []
		},
		itemType: {
			default: null
		},
		routeConfigurationEnabled: {
			default: null
		},
		isComponentBuilder: {
			default: false
		},
		userScope: {
			default: {}
		},
		currentDomain: {
			default: {}
		},
		selectedPageIsTab: {
			default: false
		},
		parentInfo: {
			default: {}
		},
		supportDomains: {
			default: false
		},
		currentPages: {
			default: []
		},
		runtimeUrl: {
			default: {}
		},
		isDashboardBuilder: {
			default: false
		},
		isDynamicTabCreateClicked: {
			default: false
		},
		allowSharedPageDefinition: {
			default: false
		},
		showUrlPreview: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties,
			updateState,
			dispatch
		}) => {
			const {
				routeConfigurationEnabled,
				isDynamicTabCreateClicked,
				parentInfo,
				currentPages,
				selectedPageIsTab
			} = properties;

			if (!routeConfigurationEnabled) {
				const updates = {
					routeType: {
						content: cuid(),
						messages: []
					}
				};

				if (selectedPageIsTab) {
					updates.order = getUpdatedOrder(currentPages);
				}

				updateState(updates);
			} else if (selectedPageIsTab) {
				updateState({
					order: getUpdatedOrder(currentPages, parentInfo)
				});
			}

			if (isDynamicTabCreateClicked) {
				const dynamicTabData = get(
					parentInfo,
					'elementProperties.dynamicTabData',
					{}
				);
				dispatch('SN_UXF_VIEWPORT#RESOLVE_DATA_SOURCE', {
					value: dynamicTabData,
					isolationMode: true
				});
			}
		},
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) => {
			dispatch(UIB_NEW_CREATE_PAGE_MODAL_DISMISSED);
		},
		[DISMISS]: ({ dispatch }) => {
			dispatch(UIB_NEW_CREATE_PAGE_MODAL_DISMISSED);
		},
		['NOW_TYPEAHEAD#VALUE_SET']: ({ updateState, action }) => {
			action.stopPropagation();
			const { input, value } = action.payload;
			if (input === 'route') {
				updateState({
					routeType: {
						content: value,
						messages: validationErrorMessage(value, 'path')
					}
				});
			}
		},
		['NOW_INPUT#INPUT']: ({ updateState, action, state, properties }) => {
			const { input, fieldValue } = action.payload;
			const { routeType, itemName } = state;
			const { isDynamicTabCreateClicked } = properties;
			let messages;

			switch (input) {
				case 'page-name':
					if (
						routeType.content === formatPath(itemName) &&
						!isDynamicTabCreateClicked
					) {
						const formattedRouteTypeContent = formatPath(fieldValue);
						messages = validationErrorMessage(
							formattedRouteTypeContent,
							'path'
						);
						updateState({
							routeType: {
								content: formattedRouteTypeContent,
								messages
							}
						});
					}
					updateState({
						operation: 'set',
						path: 'itemName',
						value: fieldValue
					});
					break;
				case 'route':
					messages = validationErrorMessage(fieldValue, 'path');
					updateState({
						routeType: {
							content: fieldValue,
							messages
						}
					});
					break;
				case 'order':
					updateState({
						order: fieldValue
					});
					break;
			}
		},
		[ICON_PROPERTY_INPUT_PROPERTY_CHANGED]: ({
			action: { stopPropagation, payload },
			updateState
		}) => {
			stopPropagation();
			const { value } = payload;
			updateState({ icon: value });
		},
		[CREATE]: ({ state, dispatch, properties, updateState }) => {
			let shouldDismiss;
			const {
				itemType,
				selectedPageIsTab,
				parentInfo,
				isDashboardBuilder,
				isComponentBuilder,
				isDynamicTabCreateClicked
			} = properties;
			const {
				itemName,
				routeType,
				icon,
				order,
				scope,
				usingSharedPageDefinition,
				pageDefinition,
				copyPageDefinition
			} = state;
			let actionLabel;
			let builderType;
			let builderData = {};
			let pageDefinitionData = {};
			let flow = itemType;

			if (itemType === 'tab') {
				flow = isDashboardBuilder ? 'dashboard-tab' : 'page';
			}

			switch (flow) {
				case 'page':
					actionLabel = snUXFActions.NEW_CREATE_PAGE;
					builderType = isComponentBuilder ? 'components' : 'apps';
					builderData = isComponentBuilder
						? {
								parentMacroponent: properties.appsSysId,
								parentMacroponentCompositionElementId:
									properties.appRoutes[0].parentElementId
						  }
						: builderData;
					pageDefinitionData = usingSharedPageDefinition
						? {
								rootMacroponentId: pageDefinition,
								copyRootMacroponent: copyPageDefinition
						  }
						: pageDefinitionData;
					updateState({
						isCreating: true
					});
					break;
				case 'dashboard':
					actionLabel = snUXFActions.DASHBOARDS_CREATE_REQUEST;
					shouldDismiss = true;
					break;
				case 'dashboard-tab':
					actionLabel = snUXFActions.DASHBOARDS_CREATE_TAB_REQUEST;
					shouldDismiss = true;
					break;
			}
			let fields = [];
			if (isDynamicTabCreateClicked) {
				const resolvedDataSource = getResolvedDataSource(parentInfo);
				const route = routeType.content;
				if (!isEmpty(route)) {
					const tabDataWithLabel = resolvedDataSource.find(
						(tab) => tab.route === route
					);
					if (tabDataWithLabel && tabDataWithLabel.fields) {
						const fieldKeys = Object.keys(tabDataWithLabel.fields);
						if (fieldKeys) {
							fields = fieldKeys.map((field) => ({ name: field }));
						}
					}
				}
			}
			const data = {
				name: itemName,
				route: routeType.content,
				scope,
				isActive: true,
				order: Number(order),
				icon,
				fields,
				...builderData,
				...pageDefinitionData
			};

			/*
				when creating a page, if we set the parentMacroponent and
				parentMacroponentCompositionElementId fields on the route
				we are essentially creating a "child" route
				a.k.a "tab" a.k.a "viewport route".
			*/
			if (selectedPageIsTab) {
				if (isEmpty(parentInfo)) {
					throw new Error('parentInfo is needed to create a new tab');
				}
				data.parentMacroponent = parentInfo.macroponentId;
				data.parentMacroponentCompositionElementId =
					parentInfo.compositionElementId;
			}
			dispatch(actionLabel, {
				builderType,
				appId: properties.appsSysId,
				data
			});
			if (shouldDismiss) {
				dispatch(UIB_NEW_CREATE_PAGE_MODAL_DISMISSED);
			}
		},
		[UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED]: {
			stopPropagation: true,
			effect: ({ updateState, action }) => {
				const { value: pageDefinition } = action.payload;
				updateState({
					pageDefinition,
					usingSharedPageDefinition: pageDefinition !== 'none'
				});
			}
		},
		[UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED]: {
			stopPropagation: true,
			effect: ({ updateState, action }) => {
				const { value: copyPageDefinition } = action.payload;
				updateState({
					copyPageDefinition
				});
			}
		}
	}
});
