import { replaceWith } from '../behaviors/router';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import get from 'lodash/get';
import {
	fetchWorkspaceConfigEffect,
	fetchDashboardsConfigEffect
} from '../dataSources';
import {
	fetchNewAppConfigEffect,
	flattenQueryResult
} from '../../../utils/appRouteState';
import {
	extractScreensFromDashboards,
	annotateDashboards
} from '../../../utils/dashboards-list';
import { screenNames } from '../../../utils/routingConstants';

export const routerGraphQLHandlers = {
	['UIB_ROUTER#APPLICATION_CONFIG_REQUESTED']: fetchNewAppConfigEffect(
		'UIB_ROUTER#APPLICATION_CONFIG_REQUEST_SUCCEEDED',
		'UIB_ROUTER#CONFIG_REQUEST_FAILED'
	),
	['UIB_ROUTER#WORKSPACE_CONFIG_REQUESTED']: fetchWorkspaceConfigEffect,
	['UIB_ROUTER#CONFIG_LOADED']: ({
		dispatch,
		action,
		updateProperties,
		state,
		updateState
	}) => {
		const {
			routeState: {
				appsSysId,
				pageSysId,
				variantSysId,
				dashboardId,
				queryParams
			},
			routeName
		} = action.meta;
		let newRouteName;

		const { appsURL, appRoutes } = action.payload;

		dispatch('UIB_APP#APP_LOADED', {
			...action.payload,
			routeState: get(action, 'meta.routeState', {}),
			configLoaded: true
		});
		if (state.createNew) {
			updateState({
				operation: 'set',
				path: 'createNew',
				value: false
			});
			return;
		}

		const params = {};
		if (appsSysId) {
			params.appsSysId = appsSysId;
		}
		if (pageSysId) {
			params.pageSysId = pageSysId;
		}

		if (variantSysId) {
			params.variantSysId = variantSysId;
		}

		if (queryParams) {
			params.queryParams = queryParams;
		}

		if (dashboardId) {
			params.dashboardId = dashboardId;
		} else if (
			routeName === screenNames.dashboardsHome &&
			appRoutes &&
			appRoutes.length &&
			!get(queryParams, 'isolatedEditMode', false)
		) {
			newRouteName = screenNames.dashboardsDashboard;
			params.dashboardId = appRoutes[0].id;
		}

		updateProperties({
			...params,
			appsURL
		});
		replaceWith(dispatch, newRouteName || routeName, params);
	},

	['UIB_ROUTER#WORKSPACE_CONFIG_REQUEST_SUCCEEDED']: ({ dispatch, action }) => {
		const result = get(
			action,
			'payload.data.glideRecord.sys_aw_master_config.results',
			[]
		);

		const { applicationType, routeState } = action.meta;

		if (!result.length) {
			dispatch('UIB_APP#APP_LOADED', {
				applicationType: 'workspace',
				routeName: 'workspace',
				routeState: { ...routeState, show404: true }
			});
			return;
		}

		const {
			name,
			sys_id = {},
			sys_scope = {},
			compiled_page_registry = {}
		} = result[0];

		const pageRegistrySysId = compiled_page_registry.value;

		dispatch(snUXFActions.PAGE_LIST_REQUESTED, { pageRegistrySysId });

		dispatch(
			'UIB_ROUTER#CONFIG_LOADED',
			{
				appsName: name.value,
				pageRegistryName: name.value,
				appsSysId: sys_id.value,
				appsScope: get(sys_scope, 'displayValue', ''),
				appsScopeId: get(sys_scope, 'value', ''),
				applicationType,
				pageRegistrySysId,
				isLoading: false
			},
			action.meta
		);
	},

	['UIB_ROUTER#CONFIG_REQUEST_FAILED']: ({ dispatch, action }) => {
		const { applicationType, routeState = {}, routeName } = action.meta;
		dispatch('UIB_APP#APP_LOADED', {
			applicationType,
			routeState: { ...routeState, show404: true }
		});
		if (!routeState.show404) {
			replaceWith(dispatch, routeName, { ...routeState, show404: true });
		}
	},
	['UIB_ROUTER#APPLICATION_CONFIG_REQUEST_SUCCEEDED']: ({
		properties,
		action,
		dispatch
	}) => {
		if (properties.isComponentBuilder) {
			dispatch(
				'UIB_ROUTER#COMPONENT_CONFIG_REQUEST_SUCCEEDED',
				action.payload,
				action.meta
			);
			return;
		}
		try {
			const flattenedResult = flattenQueryResult(action.payload.result);

			const { applicationType, routeState } = action.meta;

			const {
				appConfig: { sysId, appURL }
			} = flattenedResult;

			if (!(sysId && appURL)) {
				// The selected app doesn't exist
				// It should redirect to the RouteNotFound page
				dispatch('UIB_APP#APP_LOADED', {
					applicationType: 'app',
					routeName: 'app',
					routeState: { ...routeState, show404: true }
				});
				return;
			}

			dispatch(snUXFActions.DATA_BROKER_DEFINITIONS_REQUESTED);
			dispatch(snUXFActions.TEMPLATES_REQUESTED);
			dispatch(snUXFActions.CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED);

			dispatch(
				'UIB_ROUTER#CONFIG_LOADED',
				{
					appsName: flattenedResult.appConfig.name,
					appsURL: flattenedResult.appConfig.appURL,
					appsSysId: flattenedResult.appConfig.sysId,
					appsScope: flattenedResult.appConfig.scopeName,
					appsScopeId: flattenedResult.appConfig.scopeId,
					pageRegistrySysId: flattenedResult.appConfig.pageRegistryId,
					pageRegistryScopeName:
						flattenedResult.appConfig.pageRegistryScopeName,
					pageRegistryScopeId: flattenedResult.appConfig.pageRegistryScopeId,
					pageRegistryName: flattenedResult.appConfig.pageRegistryName,
					applicationType,
					isLoading: false,
					isPagesLoading: false,
					appRoutes: flattenedResult.appConfig.routes
				},
				action.meta
			);
		} catch (e) {
			dispatch('UIB_ROUTER#CONFIG_REQUEST_FAILED', {}, action.meta);
		}
	},
	['UIB_ROUTER#COMPONENT_CONFIG_REQUEST_SUCCEEDED']: ({ action, dispatch }) => {
		try {
			const flattenedResult = flattenQueryResult(action.payload.result, true);

			const { applicationType, routeState } = action.meta;

			const {
				macroponent: { sysId }
			} = flattenedResult;
			if (!sysId) {
				// The selected macroponent doesn't exist
				// It should redirect to the RouteNotFound page
				dispatch('UIB_APP#APP_LOADED', {
					applicationType: 'components',
					routeName: 'components',
					routeState: { ...routeState, show404: true }
				});
				return;
			}

			dispatch(snUXFActions.CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED);
			dispatch(snUXFActions.DATA_BROKER_DEFINITIONS_REQUESTED);
			dispatch(snUXFActions.TEMPLATES_REQUESTED);

			dispatch(
				'UIB_ROUTER#CONFIG_LOADED',
				{
					appsName: flattenedResult.macroponent.name,
					appsURL: '',
					appsSysId: flattenedResult.macroponent.sysId,
					appsScope: flattenedResult.macroponent.scopeName,
					appsScopeId: flattenedResult.macroponent.scopeId,
					applicationType,
					isLoading: false,
					appRoutes: flattenedResult.macroponent.routes
				},
				action.meta
			);
		} catch (e) {
			dispatch('UIB_ROUTER#CONFIG_REQUEST_FAILED', {}, action.meta);
		}
	},
	['UIB_ROUTER#DASHBOARDS_CONFIG_REQUESTED']: fetchDashboardsConfigEffect,
	['UIB_ROUTER#DASHBOARDS_CONFIG_REQUEST_SUCCEEDED']: ({
		action,
		dispatch
	}) => {
		const {
			result: { dashboards = [] }
		} = action.payload;

		const screens = extractScreensFromDashboards(dashboards);

		dispatch(snUXFActions.DASHBOARDS_SCREENS_LOADED, { screens });
		dispatch(snUXFActions.CONFIGURATION_COMPONENT_BY_INTERFACE_REQUESTED);
		dispatch(snUXFActions.DATA_BROKER_DEFINITIONS_REQUESTED);
		dispatch(snUXFActions.TEMPLATES_REQUESTED);

		dispatch(
			'UIB_ROUTER#CONFIG_LOADED',
			{
				appsName: 'Dashboards',
				applicationType: 'dashboards',
				isLoading: false,
				appRoutes: annotateDashboards(dashboards)
			},
			action.meta
		);
	}
};
