import '@servicenow/now-checkbox';
import '@servicenow/now-input';
import '@servicenow/now-textarea';
import '@servicenow/now-alert';
import { t } from 'sn-translate';
import { escapeScopeLabel } from '../../../utils/escapeScopeLabel';
import getHelpResourceUrl from '../../uib-app/utils/getHelpResourceUrl';

const renderAlert = (screenValidationErrorMessage) => {
	return (
		<now-alert
			status="critical"
			icon="circle-exclamation-outline"
			content={screenValidationErrorMessage}
		/>
	);
};

const conditionsHelperText = (
	<div>
		<span>
			{' '}
			{
				/* prettier-ignore */
				t('Add criteria for when to display the page or tab by entering an encoded query string.')
			}{' '}
		</span>
		<a href={getHelpResourceUrl('screen-conditions')} target="_blank">
			{' '}
			{t('Learn more.')}{' '}
		</a>
	</div>
);

export default ({
	screen,
	disabled,
	validationErrors,
	supportDomains,
	screenValidationErrorMessage
}) => {
	if (!screen) {
		return null;
	}

	return (
		<div>
			<div className="sub-text">
				{
					/* prettier-ignore */
					t('By setting these conditions, you are adding additional criteria to determine when this page will display')
				}
			</div>
			<form className="property-form-view set-height">
				<div className="form-row">
					{screenValidationErrorMessage
						? renderAlert(screenValidationErrorMessage)
						: null}
					<now-textarea
						label={t('Variant conditions')}
						helper-content={conditionsHelperText}
						value={screen.screenCondition}
						disabled={disabled}
						manage-value
						show-borders
						append-to-payload={{ input: 'screenCondition' }}
					/>
					<p className="screen-condition-help-text">
						{
							/* prettier-ignore */
							t('e.g. table=incident^sys_id!=-1 will only show this variant if the table field is incident, and the sys_id field is not -1')
						}
					</p>
				</div>
				<div className="form-row half">
					<now-input
						label={t('Order')}
						type="number"
						/* prettier-ignore */
						helper-content={t('To give higher priority to this record, enter a lower number.')}
						value={screen.order.toString()}
						disabled={disabled}
						required
						manage-value
						messages={validationErrors.order.messages}
						append-to-payload={{ input: 'order' }}
					/>
				</div>
				<div className="two-column-flex">
					<div className="column">
						<now-input
							label={t('Application scope')}
							value={escapeScopeLabel(screen.scopeName)}
							disabled
							readonly
							/* prettier-ignore */
							helper-content={t('The variant is created in this default application scope. To switch scope, use the application scope picker.')}
						/>
					</div>
					{supportDomains ? (
						<div className="column">
							<now-input
								label={t('Domain')}
								value={
									screen.domainName ? screen.domainName.split('/').pop() : ''
								}
								disabled
								readonly
								/* prettier-ignore */
								helper-content={t('The variant is created in this domain. To switch domain scope, use the domain scope picker.')}
							/>
						</div>
					) : null}
				</div>
			</form>
		</div>
	);
};
