import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import get from 'lodash/get';

import {
	GENERAL_FETCH_SUCCEEDED,
	GENERAL_UPDATE_SUCCEEDED,
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_UPDATE_ERROR_OCCURRED,
	SETTINGS_UPDATE_SUCCEEDED,
	TABLE_EXPERIENCE,
	TABLE_PANEL
} from '../constants';

const results = `
title {
	value
}
path {
	value
}
admin_panel {
	value
}
  _query {
	  queryWithPanelDetails: GlideRecord_Query {
		__typename
		sys_ux_app_config(queryConditions: "sys_id=$$parent.admin_panel$$") {
			_results {
				description {
					value
				 }
			}
		}
	  }
  }
`;

const generalQuery = `
query($sysId: String!) {
	GlideRecord_Query {
	  __typename
	  ${TABLE_EXPERIENCE}(sys_id: $sysId) {
		_results {
		  ${results}
		}
	  }
	}
  }
`;

const generalMutation = `
mutation($sysId: ID! $title: String! $description: String $path: String!) {
	snExpSettings {
		settings {
			generalUpdate(sysId:$sysId title:$title description:$description path:$path) {
				title
				description
				path
			}
		}
	}
}
`;

export const generalFetchRequestedEffect = createGraphQLEffect(generalQuery, {
	variableList: ['sysId'],
	successActionType: GENERAL_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const generalUpdateRequestedEffect = createGraphQLEffect(
	generalMutation,
	{
		variableList: ['sysId', 'path', 'title', 'description'],
		successActionType: GENERAL_UPDATE_SUCCEEDED,
		errorActionType: SETTINGS_UPDATE_ERROR_OCCURRED
	}
);

export const generalFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const { path, title } = get(
		payload,
		`data.GlideRecord_Query.${TABLE_EXPERIENCE}._results[0]`,
		{}
	);
	const { description } = get(
		payload,
		`data.GlideRecord_Query.${TABLE_EXPERIENCE}._results[0]._query.queryWithPanelDetails.${TABLE_PANEL}._results[0]`,
		{}
	);
	updateState({
		general: {
			path: path.value || '',
			title: title.value || '',
			description: description.value || '',
			updated: false
		}
	});
};

export const generalUpdateSucceededEffect = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	const { path, title, description } = get(
		payload,
		`data.snExpSettings.settings.generalUpdate`,
		{}
	);
	updateState({
		general: {
			path: path || '',
			title: title || '',
			description: description || '',
			updated: false
		}
	});

	dispatch(SETTINGS_UPDATE_SUCCEEDED);
};
