import { createCustomElement } from '@servicenow/ui-core';
import 'sn-ui-builder-components';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import '@servicenow/now-modal';
import { transformDispatchedEventProperties } from '../../utils/properties';
import { t } from 'sn-translate';

const renderModal = (
	pendingValues,
	{ headerLabel, componentName, componentProps, footerActions }
) => {
	const TagName = componentName;

	return (
		<now-modal
			opened={true}
			manage-opened={true}
			header-label={t('{0}', headerLabel)}
			size="lg"
			footerActions={footerActions}>
			{TagName ? (
				<TagName {...componentProps} pendingValues={pendingValues} />
			) : (
				'no valid component name was found.'
			)}
		</now-modal>
	);
};

export default createCustomElement('uib-component-modal', {
	view: ({ pendingValues, properties: { modalConfig } }) =>
		modalConfig ? renderModal(pendingValues, modalConfig) : null,
	initialState: {
		pendingValues: {}
	},
	properties: {
		/**
		 * The config object containing modal properties
		 *
		 * @type {{ headerLabel: string, componentName: string, componentProps: object, footerActions: Array<object>, variantScope: string}}
		 */
		modalConfig: { default: null }
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': ({ action, dispatch }) => {
			const { payload } = action;
			if (!payload.value) {
				dispatch('SN_UIB_APP#CLOSE_COMPONENT_MODAL');
			}
		},
		['SN_UIB_DISPATCHED_EVENTS_MODAL#CANCEL_CLICKED']: ({
			updateState,
			dispatch
		}) => {
			updateState({ pendingValues: {} });
			dispatch('SN_UIB_APP#CLOSE_COMPONENT_MODAL');
		},
		['SN_UIB_DISPATCHED_EVENTS_MODAL#PENDING_VALUES_UPDATED']: ({
			action,
			updateState
		}) => {
			updateState({ pendingValues: action.payload.pendingValues });
		},
		['SN_UIB_DISPATCHED_EVENTS_MODAL#OK_CLICKED']: ({
			state,
			properties,
			updateState,
			dispatch
		}) => {
			const {
				modalConfig: {
					componentProps: { mode, eventId, context },
					variantScope
				}
			} = properties;
			if (!state.pendingValues.name) {
				updateState({ pendingValues: {} });
				dispatch('SN_UIB_APP#CLOSE_COMPONENT_MODAL');
				return;
			}
			const actionToDispatch =
				mode === 'edit'
					? snUXFActions.MCP_CONTRACT_EVENT_UPDATED
					: snUXFActions.MCP_CONTRACT_EVENT_CREATED;

			const transformedEventPayload = {
				event: {
					event_name: state.pendingValues.name,
					label: state.pendingValues.label,
					props: transformDispatchedEventProperties(state.pendingValues.props)
				}
			};
			if (mode === 'edit') {
				transformedEventPayload.eventId = eventId;
			}

			if (variantScope) {
				transformedEventPayload.sysparm_transaction_scope = variantScope;
			}

			dispatch(actionToDispatch, transformedEventPayload, {
				context,
				screenEventMappingHandler:
					mode !== 'edit' ? state.pendingValues.parentHandler : null
			});
			updateState({ pendingValues: {} });
			dispatch('SN_UIB_APP#CLOSE_COMPONENT_MODAL');
		}
	}
});
