import { actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';
import omit from 'lodash/omit';
import { t } from 'sn-translate';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import { applicationsRequestActions } from '../../../behaviors/applicationsRequestBehavior';
import { domainsBehaviorActions } from '../../../behaviors/domainsBehavior';
import { applicationTypes, screenNames } from '../../../utils/routingConstants';
import { getElementByProperty } from '../../../utils/tree.js';
import { forceReplaceWith, forceTransitionTo } from '../../uib-router';
import { SN_UIB_APP_RETRIEVE_UIB_CONFIG_REQUESTED } from './uibAppActions';
import { globalEditConfirmDialogProps } from '../../uib-confirm-dialog/globalEditConfirm';
import { findRedirect, routerAlertHandler } from '../utils/routerRedirectUtils';
import { installTheme } from '@devsnc/library-uxf';
import { flattenAppRoutes } from '../../../utils/appRoutes';
import qpUtils from '../../../utils/queryParams';

import uibTheme from '../uib-theme.json';

/**
 * @param root0
 * @param root0.rawParentInfo
 * @param root0.applicationType
 * @param root0.pages
 * @param root0.appRoutes
 */
function caculateParentInfo({
	rawParentInfo,
	applicationType,
	pages,
	appRoutes
}) {
	if (applicationType === applicationTypes.dashboards) {
		const screen = pages.find((page) => page.sysId === rawParentInfo.screenId);
		const dashboard =
			screen && appRoutes.find((route) => route.id === screen._appRouteId);
		return !dashboard
			? rawParentInfo
			: {
					...rawParentInfo,
					dashboardId: dashboard.id,
					screenName: dashboard.name
			  };
	}
	return rawParentInfo;
}

const enableScreenTransactionWorkspace = (properties, updateProperties) => {
	const { applicationType } = properties;
	if (applicationType === applicationTypes.workspace) {
		updateProperties({
			isScreenTransactionAllowed: true
		});
	}
};

export const lifecycleHandlers = {
	[actionTypes.COMPONENT_CONNECTED]: () => {
		installTheme({ theme: uibTheme, assets: [] }, document.head, 'uib-theme');
	},
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
		dispatch,
		properties,
		updateProperties,
		updateState
	}) => {
		const params = window.location.search
			.slice(1)
			.split('&')
			.reduce((acc, s) => {
				const [k, v] = s.split('=');
				return Object.assign(acc, { [k]: v });
			}, {});

		if (params.domainExpanded === 'true') {
			updateState({ isDomainExpanded: true });
		}

		dispatch(applicationsRequestActions.APPLICATIONS_REQUESTED);
		dispatch(domainsBehaviorActions.DOMAIN_BEHAVIOR_RETRIEVE_DOMAINS_REQUESTED);
		dispatch(SN_UIB_APP_RETRIEVE_UIB_CONFIG_REQUESTED);
		dispatch('UIB_APP#LOAD_ICONS');
		enableScreenTransactionWorkspace(properties, updateProperties);
	},
	[actionTypes.COMPONENT_DOM_READY]: ({ properties, updateProperties }) => {
		if (properties.isComponentBuilder) {
			updateProperties({
				showConfirmDialog: true,
				confirmDialogProps: globalEditConfirmDialogProps(false)
			});
		}
	},
	[actionTypes.COMPONENT_RENDERED]: ({ properties, updateProperties }) => {
		let { routeState = {} } = properties;
		const selectedElementId = get(routeState, 'queryParams.selectedElement');
		const pageState = get(properties, 'pageState');

		if (
			selectedElementId &&
			pageState &&
			properties.selectedElementId !== selectedElementId
		) {
			const component = get(
				getElementByProperty(pageState.rootElement, selectedElementId, 'id'),
				'component'
			);
			if (component) {
				routeState = omit(routeState, 'queryParams.selectedElement');
				updateProperties({
					selectedElementId: selectedElementId,
					selectedViewportComponent: component,
					routeState: routeState
				});
			}
		}
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
		properties,
		action,
		updateState,
		updateProperties
	}) => {
		if (action.payload.name === 'rawParentInfo') {
			const { appRoutes, pages, applicationType } = properties;
			updateState({
				parentInfo: caculateParentInfo({
					rawParentInfo: action.payload.value,
					appRoutes,
					pages,
					applicationType
				})
			});
		}
		if (action.payload.name === 'applicationType') {
			enableScreenTransactionWorkspace(properties, updateProperties);
		}
		if (action.payload.name === 'routeState') {
			updateState({
				dataBrokerPanelExpanded: false
			});
		}
	},
	['ROUTER_BEHAVIOR#ROUTE_ENTERED']: ({ action, updateProperties }) => {
		const { routeName, routeState = {} } = action.payload;

		updateProperties({
			routeName,
			routeState,
			pageSysId: routeState.pageSysId
		});
	},
	['ROUTER_BEHAVIOR#ROUTE_NOT_FOUND']: ({ updateProperties }) => {
		updateProperties({
			routeName: undefined,
			routeState: { show404: true }
		});
	},
	['UIB_APP#APP_LOADED']: ({
		action,
		dispatch,
		properties,
		updateProperties
	}) => {
		updateProperties(action.payload);

		// We don't want to redirect away from isolatedEditMode when in isolatedEditMode
		if (get(action.payload, 'routeState.queryParams.isolatedEditMode', false)) {
			return;
		}
		const routeState = get(
			action,
			'payload.routeState',
			get(properties, 'routeState', {})
		);
		const { routeName } = properties;
		const applicationType = action.payload.applicationType || null;
		const appRoutes = action.payload.appRoutes || [];
		const configLoaded = !!(action.payload.configLoaded === true);
		const queryParams = get(action.payload, 'routeState.queryParams', {});

		// If the app config has been loaded and there are no appRoutes available,
		// redirect to the experience's splash page and show an alert if necessary.
		if (configLoaded && applicationType === 'app' && !appRoutes.length) {
			if (routeName !== screenNames.appHome) {
				const typeRequested =
					routeName === screenNames.appVariant ? t('variant') : t('page');
				routerAlertHandler(typeRequested, dispatch, updateProperties);
			}
			forceTransitionTo(screenNames.appHome, {
				appsSysId: routeState.appsSysId,
				queryParams
			});
		}
		if (routeState.show404) {
			updateProperties({
				isLoading: false,
				routeState: { ...routeState, show404: true }
			});
		}
		if (!routeName || !appRoutes.length) {
			return;
		}

		const firstPageSysId = get(appRoutes, '0.page.sysId', null);
		let firstVariantSysId, page;

		switch (routeName) {
			case screenNames.appHome:
				// Redirect to the first valid variant or splash page in the experience.
				firstVariantSysId = get(appRoutes, '0.page.variants[0].sysId', null);
				findRedirect({
					appsSysId: routeState.appsSysId,
					pageSysId: firstPageSysId,
					variantSysId: firstVariantSysId,
					queryParams
				});
				break;
			case screenNames.appPage:
				page = flattenAppRoutes(appRoutes).find(
					({ page }) => page.sysId === routeState.pageSysId
				);

				if (!page) {
					// If the pageSysId in the URL does not match an existing page in the current experience,
					// show an alert and redirect the user to the first valid route in the experience.
					updateProperties({
						routeState: { ...routeState, show404: true }
					});
					routerAlertHandler(t('page'), dispatch, updateProperties);

					firstVariantSysId = get(appRoutes, '0.page.variants[0].sysId', null);
					findRedirect({
						appsSysId: routeState.appsSysId,
						pageSysId: firstPageSysId,
						variantSysId: firstVariantSysId,
						queryParams
					});
					return;
				}

				// If the pageSysId in the URL does exist in the experience, try to redirect the user
				// to the page's first valid variant (if none, go to the page's splash page).
				firstVariantSysId = get(page, 'page.variants.0.sysId', null);
				findRedirect({
					appsSysId: routeState.appsSysId,
					pageSysId: get(page, 'page.sysId', firstPageSysId),
					variantSysId: firstVariantSysId,
					queryParams
				});
				break;
			case screenNames.appVariant:
				page = flattenAppRoutes(appRoutes).find(
					({ page }) => page.sysId === routeState.pageSysId
				);

				if (!page) {
					// If the pageSysId in the URL does not match an existing page in the current experience,
					// show an alert and redirect the user to the first valid route in the experience.
					updateProperties({
						routeState: { ...routeState, show404: true }
					});
					routerAlertHandler(t('variant'), dispatch, updateProperties);

					firstVariantSysId = get(appRoutes, '0.page.variants[0].sysId', null);
					findRedirect({
						appsSysId: routeState.appsSysId,
						pageSysId: firstPageSysId,
						variantSysId: firstVariantSysId,
						queryParams
					});
				} else {
					const {
						page: { variants }
					} = page;
					const variant = variants.find(
						({ sysId }) => sysId === routeState.variantSysId
					);
					if (
						!variant &&
						qpUtils.getQueryNoDomain(routeState.queryParams) !== 'true'
					) {
						// If the requested variant is not found, display an alert to the user.
						updateProperties({
							routeState: { ...routeState, show404: true, pageIsValid: true }
						});
						routerAlertHandler(t('variant'), dispatch, updateProperties);
					}
				}
				break;
		}
	},
	['UIB_APP#RESET_ROUTE_STATUS']: ({ updateProperties }) => {
		updateProperties({
			routeStatus: {
				isCreatingPage: false,
				isChangingPage: false,
				isDeletingPage: false
			},
			showPageInterstitial: false,
			pageInterstitialContent: '',
			isLoading: false
		});
	},
	'UIB_APP#PAGE_SELECTED': ({ updateProperties }) => {
		updateProperties({
			selectedElementId: null
		});
	},
	'UIB_APP#PAGE_REQUESTED': ({ action, updateProperties }) => {
		updateProperties({
			showPageInterstitial: true,
			pageInterstitialContent: t('Loading {0}...', action.payload.name)
		});
	},
	'UIB_APP#UPDATE_UNSAVED_CHANGES_CHECK_CALLBACK_EVENT': ({
		updateState,
		action
	}) => {
		const { unsavedChangesCheckCallbackEvent } = action.payload;

		updateState({
			unsavedChangesCheckCallbackEvent
		});
	},
	'SN_UIB_APP#SCREEN_TYPE_REQUESTED': ({
		action,
		properties,
		updateProperties,
		updateState
	}) => {
		const { applicationType, isDashboardBuilder } = properties;
		updateProperties({
			showPageInterstitial: true,
			pageInterstitialContent: t('Loading {0}...', action.payload.name)
		});
		updateState({ dataBrokerPanelExpanded: false });
		if (isDashboardBuilder) {
			forceTransitionTo('dashboards.dashboard', {
				...action.payload,
				dashboardId: action.payload.pageSysId
			});
			return;
		}

		forceTransitionTo(`${applicationType}.page`, action.payload);
	},
	['UIB_ROUTER#APP_PAGE_LOAD']: ({ updateProperties, properties }) => {
		const { routeState, appRoutes, isDashboardBuilder } = properties;
		let newRouteState;
		if (isDashboardBuilder) {
			newRouteState = appRoutes.find(({ id }) => id === routeState.dashboardId)
				? routeState
				: { ...routeState, show404: false };
		}
		updateProperties({
			selectedElementId: null,
			routeState: newRouteState || routeState
		});
	},
	[actionTypes.COMPONENT_ERROR_THROWN]: ({ state, action }) => {
		const {
			details: { actionType }
		} = action.payload;

		if (actionType === snUXFActions.PAGE_REQUESTED_STARTED) {
			const { appsSysId, applicationType } = state.properties;

			forceReplaceWith(`${applicationType}`, {
				show404: true,
				appsSysId
			});
		}
	},
	['SN_UXF#COMPONENT_LIST_REQUEST_STARTED']: ({ updateProperties }) => {
		updateProperties({ componentsRequestStatus: 'started' });
	},
	['SN_UXF#COMPONENT_LIST_REQUEST_SUCCEEDED']: ({ updateProperties }) => {
		updateProperties({ componentsRequestStatus: 'succeeded' });
	},
	['SN_UXF#COMPONENT_LIST_REQUEST_FAILED']: ({ updateProperties }) => {
		updateProperties({ componentsRequestStatus: 'failed' });
	},
	['SN_UXF#PAGE_LIST_REQUEST_FAILED']: ({ updateProperties }) => {
		updateProperties({ pagesRequestError: true });
	},
	['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: ({
		updateProperties,
		action
	}) => {
		const {
			isNecessaryTransactionInProgress,
			isPageDefReadonly
		} = action.payload;

		updateProperties({
			isNecessaryTransactionInProgress,
			isScreenDisabled: !isNecessaryTransactionInProgress,
			isMacroponentReadOnly:
				typeof isPageDefReadonly === 'boolean' ? isPageDefReadonly : false
		});
	}
};
