import { applicationsRequestActions } from '../../../behaviors/applicationsRequestBehavior';

export const applicationScopeHandlers = {
	'SN_UIB_PAGE_HEADER#APPLICATION_SELECTED': ({ action, dispatch }) => {
		const { id: app_id } = action.payload;
		if (app_id) {
			dispatch(
				applicationsRequestActions.APPLICATIONS_REQUESTED_BEHAVIOR_UPDATE_APPLICATION_REQUESTED,
				{
					data: {
						app_id
					}
				}
			);
		}
	}
};
