import { createCustomElement } from '@servicenow/ui-core';
import { updateAesTabState } from '../../utils/aesApi';

/**
 * See App Engine Studio docs for more info:
 * https://servicenow.sharepoint.com/:w:/s/AppEngineStudio/Eb70tfob3MtOupks7123MwsBsVRAj-Bg3UsYIm4c_1rkhQ?e=F9Qe1F
 */

export default createCustomElement('uib-aes-dirty-state-dispatcher', {
	properties: {
		isPageStateDirty: {
			default: false
		}
	},
	transformState: (state) => {
		const { isPageStateDirty } = state.properties;

		if (isPageStateDirty) {
			updateAesTabState({ hasUnsavedChanges: true });
		} else {
			updateAesTabState({ hasUnsavedChanges: false });
		}

		return state;
	}
});
