import { snUXFActions } from 'sn-uxf-builder-middleware';
import mapContextToPayload from './reducers/mapContextToPayload';
import { APPLICATIONS_REQUESTED_BEHAVIOR_LOADING } from '../../../behaviors/applicationsRequestBehavior/constants';
import { t } from 'sn-translate';

const initializeModal = (
	tagName,
	updateState,
	primaryFooterAction,
	headerLabel,
	state,
	pageRegistrySysId,
	properties
) => {
	updateState({
		context: {
			...state.context,
			pageRegistrySysId,
			selectedApplication: {
				id: properties.userScope.id
			}
		},
		page: tagName,
		headerLabel,
		footerActions: [
			primaryFooterAction,
			{
				clickActionType: 'UIB_CREATE_NEW_MODAL#CANCEL_CLICKED',
				variant: 'secondary',
				label: t('Cancel')
			}
		]
	});
};

const copyPrimaryFooterActions = {
	clickActionType: 'UIB_CREATE_NEW_MODAL#COPY_CLICKED',
	variant: 'primary',
	label: t('Create copy'),
	disabled: false
};

const editPrimaryFooterActions = (disabled) => {
	return {
		clickActionType: 'UIB_CREATE_NEW_MODAL#UPDATE_CLICKED',
		variant: 'primary',
		label: t('Update'),
		checkForDisabled: true,
		disabled
	};
};

export const modalHandlers = {
	['UIB_CREATE_NEW_MODAL#SUBMIT_CLICKED']: ({
		properties,
		state,
		dispatch
	}) => {
		const { context } = state;
		const { appsSysId } = properties;
		const payload = mapContextToPayload(context, appsSysId);
		dispatch(snUXFActions.PAGE_CREATE_REQUESTED, payload);
		dispatch('UIB_CREATE_NEW_MODAL#CANCEL_CLICKED');
	},
	['NOW_MODAL#OPENED_SET']: ({ dispatch }) => {
		dispatch('UIB_CREATE_NEW_MODAL#CANCEL_CLICKED');
	},
	['UIB_CREATE_NEW_MODAL#UPDATE_CLICKED']: ({
		properties,
		state,
		dispatch
	}) => {
		const { context } = state;
		const payload = mapContextToPayload(context);

		const pageToUpdate = properties.modalInfo ? properties.modalInfo : null;

		dispatch(snUXFActions.METADATA_UPDATE_REQUESTED, {
			...payload,
			pageToUpdate
		});
		dispatch('UIB_CREATE_NEW_MODAL#CANCEL_CLICKED');
	},
	['UIB_CREATE_NEW_MODAL#COPY_CLICKED']: ({ properties, state, dispatch }) => {
		const { context } = state;
		const { appsSysId, modalInfo, pageState } = properties;
		const payload = mapContextToPayload(context, appsSysId);

		// TODO: copying from a given id needs to still be supported in the backend
		// At the moment, the macroponent that is copied is the current page
		const copyFrom = modalInfo ? modalInfo.sysId : pageState.sysId;

		dispatch(snUXFActions.PAGE_COPY_REQUESTED, { ...payload, copyFrom });
		dispatch('UIB_CREATE_NEW_MODAL#CANCEL_CLICKED');
	},
	['UIB_CREATE_NEW_MODAL#CANCEL_CLICKED']: ({
		dispatch,
		updateProperties,
		updateState
	}) => {
		updateProperties({
			page: 'sn-uib-splash-page',
			footerActions: []
		});

		updateState({
			context: {}
		});

		dispatch('UIB_CREATE_NEW_MODAL#MODAL_CLOSED');
	},
	['UIB_CREATE_NEW_MODAL#INITIALIZE_EDIT_PAGE']: ({
		properties,
		state,
		updateState
	}) => {
		const disabled = properties.disabled;
		const { name, type } = properties.modalInfo
			? properties.modalInfo
			: properties.pageState;

		return initializeModal(
			'sn-uib-edit-page-info',
			updateState,
			editPrimaryFooterActions(disabled),
			t("OK. Let's update your {0} {1} page", name, type),
			state,
			properties.pageRegistrySysId,
			properties
		);
	},
	['UIB_CREATE_NEW_MODAL#INITIALIZE_COPY_PAGE']: ({
		updateState,
		state,
		properties
	}) => {
		return initializeModal(
			'sn-uib-copy-page-info',
			updateState,
			copyPrimaryFooterActions,
			t("OK. Let's get some general info for this page"),
			state,
			properties.pageRegistrySysId,
			properties
		);
	},
	[APPLICATIONS_REQUESTED_BEHAVIOR_LOADING]: ({ action, updateState }) => {
		const { loading } = action.payload;

		updateState({ loading });
	}
};
