import get from 'lodash/get';

export const findTriggeringElementsForModal = (
	elementId,
	eventHandlerElementMap
) => {
	const triggeringElements = [];
	Object.values(eventHandlerElementMap).forEach((arr) =>
		arr.find((element) => {
			return element.eventMappings.find((eventMapping) => {
				return eventMapping.targets.find((target) => {
					const payload = get(target, 'event.payload', []);
					return payload.find((prop) => {
						if (
							prop.columnName === 'modalId' &&
							prop.value === `[component-id$='${elementId}']`
						) {
							triggeringElements.push({ element });
						}
					});
				});
			});
		})
	);
	return triggeringElements;
};

export const findParentModalElements = (parentElements) =>
	Object.entries(parentElements)
		.filter(([_, val]) => val.category === 'modal')
		.map(([elid, val]) => ({ id: elid, instanceName: val.label }));
