import _camelCase from 'lodash/camelCase';
import { t } from 'sn-translate';

export const createDraggableFieldItem = (
	id,
	label = '',
	messages = [],
	order
) => {
	return {
		id,
		order,
		label,
		messages,
		type: 'field',
		actions: [
			{
				icon: 'pencil-outline',
				label: t('Rename'),
				id: 'edit-field'
			},
			{
				icon: 'trash-outline',
				label: t('Delete'),
				id: 'delete-field',
				alwaysVisible: true
			}
		]
	};
};

export const validateString = (value, input = '') => {
	const validationRegex = /(?:^sub$|^params$|[^\w-])/g;
	const propValidationRegex = /(?:[^\w-])/g;
	let matches;

	const camelCase = _camelCase(value);

	switch (input) {
		case 'property':
			matches = value.match(propValidationRegex) || value !== camelCase;
			break;
		case 'field':
		case 'parameter':
			matches = value.match(validationRegex) || value !== camelCase;
			break;
		default:
			matches = value.match(validationRegex);
			break;
	}

	return {
		invalid: Boolean(matches),
		keyword:
			Array.isArray(matches) &&
			matches.filter((match) => match === 'sub' || match === 'params')[0],
		suggestion: camelCase
	};
};

export const validationErrorMessage = (value, input, checkForMatches = {}) => {
	let errorMessage;
	const alreadyUsed =
		Object.values(checkForMatches).filter(
			({ name, label }) => name === value || label === value
		).length > 0;
	const { invalid, keyword, suggestion } = validateString(value, input);
	const inputLabel = {
		property: 'property name',
		field: 'required parameter name',
		parameter: 'optional parameter name',
		path: 'path'
	};
	if (alreadyUsed) {
		errorMessage = (
			<span>
				<strong> {value} </strong>
				{
					/* prettier-ignore */
					t('has already been used for another {0}. Please use a different name.', input === 'property' ? inputLabel[input] : `${inputLabel['field']}/${inputLabel['parameter']}`)
				}
			</span>
		);
	} else if (keyword) {
		errorMessage = (
			<span>
				{
					/* prettier-ignore */
					t('Invalid {0}. The following keyword is not allowed: ', inputLabel[input]	)
				}
				<strong> {keyword} </strong>
			</span>
		);
	} else {
		switch (input) {
			case 'parameter':
			case 'property':
			case 'field':
				errorMessage = (
					<span>
						{
							/* prettier-ignore */
							t('Invalid {0}. Name must use alphanumeric characters in camel case.',	inputLabel[input])
						}
						{suggestion ? (
							<span>
								{' '}
								{t('Suggestion: ')} <strong> {suggestion} </strong>
							</span>
						) : null}
					</span>
				);
				break;
			default:
				errorMessage = (
					<span>
						{
							/* prettier-ignore */
							t('Invalid {0}. Supported characters are: alphanumeric, dash and underscore.',	input)
						}
					</span>
				);
		}
	}

	return !(invalid || alreadyUsed) || !value
		? []
		: [
				{
					content: errorMessage,
					status: 'critical',
					icon: 'circle-exclamation-outline'
				}
		  ];
};
