import { snUXFActions } from 'sn-uxf-builder-middleware';
import debounce from 'lodash/debounce';
import get from 'lodash/get';

const sendNewPropertyValueBase = ({ action, dispatch, actionToDispatch }) => {
	dispatch(actionToDispatch, action.payload);
};
const makeDebouncedActionHandler = (ms, fn) => debounce(fn, ms);
const debounceSendNewPropertyValue = makeDebouncedActionHandler(
	500,
	sendNewPropertyValueBase
);

export const dataBrokerPropertyChanged = ({ action, dispatch }) => {
	action.stopPropagation();
	const actionToDispatch = snUXFActions.DATA_BROKER_INSTANCE_PROPERTY_UPDATED;
	if (action.payload.noDebounce) {
		sendNewPropertyValueBase({ action, dispatch, actionToDispatch });
	} else {
		debounceSendNewPropertyValue({ action, dispatch, actionToDispatch });
	}
};

export const resetAndSendNewPropertyValue = ({ action, dispatch }) => {
	action.stopPropagation();

	// TODO: dispatch a separate action up to UXF once they support storing metadata
	// about whether a property is statically or dynamically configured. For now, we
	// will just reset the property value when the property's binding is switched.
	const newPropertyValue = action.payload.value ? null : '@';
	const payload = {
		propertyId: action.payload.propertyId,
		dataBrokerId: action.payload.dataBrokerId,
		value: newPropertyValue
	};
	dispatch(snUXFActions.DATA_BROKER_INSTANCE_PROPERTY_UPDATED, payload);
};

const requestReferenceResults = (
	dispatch,
	payload = {},
	properties = [],
	propertyId
) => {
	const property = properties.find(({ id }) => id === propertyId) || {};
	dispatch('UIB_DATA_BROKER_CONFIGURATION_PANEL#REFERENCE_REQUESTED', {
		data: {
			...payload,
			referenceFilter: get(property, 'typeMeta.reference.referenceFilter', null)
		}
	});
};

export const dataBrokerPropertyReferenceSearched = ({
	action,
	dispatch,
	properties
}) => {
	const { id: propertyId } = action.payload;
	const { selectedDataBrokerId, dataBrokerInstances } = properties;

	const selectedDataBroker = dataBrokerInstances.find(
		(dataBroker) => dataBroker.id === selectedDataBrokerId
	);
	const currentProperties = selectedDataBroker
		? Object.values(selectedDataBroker.properties)
		: [];
	requestReferenceResults(
		dispatch,
		action.payload,
		currentProperties,
		propertyId
	);
};

export const dataBrokerEventMappingHandlerReferenceSearched = ({
	action,
	dispatch,
	properties
}) => {
	const { eventMappingId, handlerId, id: propertyId } = action.payload || {};
	const { eventMappingHandlers = {} } = properties;
	const handlers = eventMappingHandlers[eventMappingId] || [];
	const currentHandler =
		handlers.find((handler) => handler.handlerId === handlerId) || {};
	requestReferenceResults(
		dispatch,
		action.payload,
		currentHandler.values || [],
		propertyId
	);
};

export const requestReferenceDisplayValues = ({ action, dispatch }) => {
	const { references = [] } = action.payload || {};
	references.length &&
		dispatch('UIB_DATA_BROKER_CONFIGURATION_PANEL#DISPLAY_VALUES_REQUESTED', {
			references
		});
};

export const requestFieldValues = ({ action, dispatch }) => {
	const { table } = action.payload;

	dispatch('UIB_DATA_BROKER_CONFIGURATION_PANEL#FIELD_VALUES_REQUESTED', {
		table
	});
};

export const renderClientStateDataBroker = ({
	state,
	action,
	updateProperties
}) => {
	const {
		properties: { clientStateOutput }
	} = state;
	const dataBrokerClientStateElement = action.payload;
	updateProperties({
		clientStateOutput: {
			...clientStateOutput,
			...dataBrokerClientStateElement
		}
	});
};

