export const deleteHandlerForTriggeringElement = ({ action, dispatch }) => {
	const { element, handlerTypeId } = action.payload;
	element.eventMappings.forEach((eventMapping) => {
		eventMapping.targets.forEach((target) => {
			if (target.clientScript && target.clientScript.sysId === handlerTypeId) {
				dispatch(snUXFActions.ELEMENT_EVENT_MAPPING_HANDLER_DELETED, {
					elementId: element.id,
					eventMappingId: eventMapping.eventMappingId,
					handlerId: target.targetId
				});
			}
		});
	});
};

