export const requestDataBrokerMetadataChange = ({ action, dispatch }) => {
	action.payload.context === 'data broker' &&
		dispatch(
			snUXFActions.DATA_BROKER_INSTANCE_METADATA_UPDATE_REQUESTED,
			action.payload
		);
};

