export const dataBrokerEventMappingHandlerChanged = ({ action, dispatch }) => {
	const actionToDispatch =
		snUXFActions.DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_CHANGED;
	if (action.payload.immediate) {
		sendNewPropertyValueBase({ action, dispatch, actionToDispatch });
	} else {
		debounceSendNewPropertyValue({ action, dispatch, actionToDispatch });
	}
};

export const sendNewScriptedHandlerValue = ({ action, dispatch }) => {
	action.stopPropagation();
	const {
		payload: { script, compatibilityMode }
	} = action;
	dispatch(snUXFActions.DATA_BROKER_INSTANCE_EVENT_MAPPING_HANDLER_CHANGED, {
		...action.payload,
		scripted: true,
		value: script ? `javascript(${script})${!compatibilityMode ? '!' : ''}` : ''
	});
};
