import '@servicenow/now-modal';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import ScreenSettingsConfigurationForm from './partials/ScreenSettingsConfigurationForm'; // eslint-disable-line no-unused-vars
import ScreenConditionsConfigurationForm from './partials/ScreenConditionsConfigurationForm'; // eslint-disable-line no-unused-vars
import styles from './uib-screen-configuration-modal.scss';
import { createScreenPayloadFromObject } from './utils';
import { SHARED_PAGE_DEFINITION_SYS_ID } from './../../globals/constants';
import {
	screenRequestActions,
	screenRequestBehavior
} from '../../behaviors/screenRequestBehavior';
import isEqual from 'lodash/isEqual';
import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';
import {
	UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED,
	UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED,
	UIB_PAGE_DEFINITION_SELECTOR_SHOW_SELECTOR
} from '../uib-page-definition-selector';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
const CANCEL_BUTTON_CLICKED =
	'UIB_SCREEN_CONFIGURATON_MODAL#CANCEL_BUTTON_CLICKED';
const SAVE_BUTTON_CLICKED = 'UIB_SCREEN_CONFIGURATON_MODAL#SAVE_BUTTON_CLICKED';
const DELETE_BUTTON_CLICKED =
	'UIB_SCREEN_CONFIGURATION_MODAL#DELETE_BUTTON_CLICKED';
const CONFIRM_BUTTON_CLICKED =
	'UIB_SCREEN_CONFIGURATION_MODAL#CONFIRM_BUTTON_CLICKED';
const CANCEL_CONFIRM_BUTTON_CLICKED =
	'UIB_SCREEN_CONFIGURATION_MODAL#CANCEL_CONFIRM_BUTTON_CLICKED';
export const UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED =
	'UIB_SCREEN_CONFIGURATION_MODAL#MODAL_DISMISSED';
export const UIB_SCREEN_CONFIGURATION_MODAL_UNLOCK_TO_OVERRIDE_CLICKED =
	'UIB_SCREEN_CONFIGURATION_MODAL#UNLOCK_TO_OVERRIDE_CLICKED';
export const UIB_SCREEN_CONFIGURATION_MODAL_DELETE_CLICKED =
	'UIB_SCREEN_CONFIGURATION_MODAL#DELETE_CLICKED';

const integerRegEx = new RegExp(/^([+-]?[0-9]+)?$/g);
const confirmHeaderLabel = t('Are you sure? ');
/* prettier-ignore */
const confirmText = t('Changing your body will delete all configuration of this variant permanently.');
const confirmFooterActions = [
	{
		label: t('Yes, change'),
		variant: 'primary-negative',
		clickActionType: CONFIRM_BUTTON_CLICKED
	},
	{
		label: t('No'),
		variant: 'secondary',
		clickActionType: CANCEL_CONFIRM_BUTTON_CLICKED
	}
];

const footerActions = (
	disableSave,
	isNecessaryTransactionInProgress,
	isLoadingScreenDetails,
	screen,
	configType
) => {
	const deleteButton =
		configType === 'properties'
			? {
					label: t('Delete'),
					variant: 'primary-negative',
					clickActionType: DELETE_BUTTON_CLICKED
			  }
			: {};
	return isLoadingScreenDetails || !screen
		? null
		: !isNecessaryTransactionInProgress
		? [
				{
					label: t('Done'),
					variant: 'primary',
					clickActionType: CANCEL_BUTTON_CLICKED
				}
		  ]
		: [
				{
					label: t('Save'),
					variant: 'primary',
					clickActionType: SAVE_BUTTON_CLICKED,
					disabled: disableSave
				},
				{
					label: t('Cancel'),
					variant: 'tertiary',
					clickActionType: CANCEL_BUTTON_CLICKED
				},
				deleteButton
		  ].filter((button) => !isEmpty(button));
};

/**
 * Returns the content of the modal based on the configuration mode
 *
 * @param {string} type - configuration mode (properties|conditions)
 * @param {object} screen - screen object used to populate the fields
 * @param {boolean} isNecessaryTransactionInProgress - flag indicating whether the transaction is in scope
 * @param {boolean} noUnsavedChanges - Are there no unsaved changes?
 * @param {object} validationErrors - object containing the validation state
 * @param {boolean} showPageDefinitionSelector
 * @param {boolean} usingSharedPageDefinition
 * @param {object} pageDefinition
 * @param {screen} screenValidationErrorMessage
 * @param {boolean} supportDomains - Is Domain support enabled?
 * @param {boolean} isChangingPageDefinition - Are we changing the page definition?
 * @param {boolean} copyPageDefinition - Are we copying the page definition?
 * @returns {object} object containing modal content, modal header text, and state of the save button
 */
const getModalContent = (
	type,
	screen,
	isNecessaryTransactionInProgress,
	noUnsavedChanges,
	validationErrors,
	supportDomains,
	isChangingPageDefinition,
	copyPageDefinition,
	screenValidationErrorMessage
) => {
	if (type === 'properties') {
		return {
			disableSave:
				validationErrors.name.invalid ||
				(noUnsavedChanges && !isChangingPageDefinition && !copyPageDefinition),
			headerLabel: t('Edit settings for {0}', screen ? screen.name : 'variant'),
			modalContent: (
				<ScreenSettingsConfigurationForm
					screen={screen}
					disabled={!isNecessaryTransactionInProgress}
					supportDomains={supportDomains}
				/>
			)
		};
	}
	if (type === 'conditions') {
		return {
			disableSave:
				validationErrors.order.invalid ||
				!!screenValidationErrorMessage ||
				noUnsavedChanges,
			headerLabel: t('Edit conditions'),
			modalContent: (
				<ScreenConditionsConfigurationForm
					screen={screen}
					disabled={!isNecessaryTransactionInProgress}
					validationErrors={validationErrors}
					supportDomains={supportDomains}
					screenValidationErrorMessage={screenValidationErrorMessage}
				/>
			)
		};
	}
};

/**
 * Handler for the Save action
 *
 * @param {object} coeffects - seismic coeffects
 * @param {Function} coeffects.dispatch - Seismic dispatch function
 * @param {object} coeffects.properties - Component properties
 * @param {object} coeffects.state - Component state
 * @param {Function} coeffects.updateState - Seismic updateState function
 */
const saveHandler = ({ dispatch, properties, state, updateState }) => {
	const {
		screen,
		pristineScreen,
		isChangingPageDefinition,
		showConfirmModal,
		copyPageDefinition
	} = state;
	if (isChangingPageDefinition && !showConfirmModal) {
		updateState({
			showConfirmModal: true
		});
		return;
	}

	const { configType, viewportScreenInfo, returnToSuccessModal } = properties;
	const payload = createScreenPayloadFromObject(screen, pristineScreen, state);
	if (payload) {
		dispatch(
			snUXFActions.SCREEN_PROPERTIES_UPDATE_REQUESTED,
			payload,
			{
				configType,
				pageName: screen.name,
				viewportScreenInfo,
				returnToSuccessModal,
				changePageDefinition: copyPageDefinition || isChangingPageDefinition
			},
			{}
		);
	}
	dispatch(UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED);
};

const appendToTransactionMessagePayload = { form: 'screenConfig' };

const validateScreenCondition = (screen) => {
	if (!screen || !screen.screenCondition) {
		return null;
	}

	const { screenCondition } = screen;
	const conditions = screenCondition.split(/\^OR|\^/);
	const alphaNumRegex = /^[0-9a-zA-Z_.-]+$/;
	for (let i = 0; i < conditions.length; i++) {
		const pair = conditions[i].split(/!=|=|STARTSWITH/);
		if (!pair) {
			continue;
		}

		if (
			!pair[0].match(alphaNumRegex) ||
			(pair[1] && !pair[1].match(alphaNumRegex))
		) {
			return t('Invalid syntax in screen condition: {0}', conditions[i]);
		}
	}

	return null;
};

const view = (state) => {
	const {
		isLoadingScreenDetails,
		properties,
		pristineScreen,
		screen,
		isNecessaryTransactionInProgress,
		validationErrors,
		isChangingPageDefinition,
		copyPageDefinition,
		showConfirmModal
	} = state;
	const noUnsavedChanges = isEqual(screen, pristineScreen);
	const {
		userScope,
		userDomain,
		supportDomains,
		applications,
		configType,
		domains,
		supportScope,
		isDomainExpanded,
		overrideActionDisabled
	} = properties;

	const screenValidationErrorMessage =
		configType === 'conditions' ? validateScreenCondition(screen) : null;

	const modalProps = getModalContent(
		configType,
		screen,
		isNecessaryTransactionInProgress,
		noUnsavedChanges,
		validationErrors,
		supportDomains,
		isChangingPageDefinition,
		copyPageDefinition,
		screenValidationErrorMessage
	);

	const isLoading = isLoadingScreenDetails || !screen;

	return (
		<now-modal
			manage-opened={true}
			opened={true}
			size="lg"
			header-label={isLoading ? null : modalProps.headerLabel}
			footer-actions={footerActions(
				modalProps.disableSave,
				isNecessaryTransactionInProgress,
				isLoadingScreenDetails,
				screen,
				configType
			)}>
			<div className={`container ${isLoading ? 'loading' : ''}`}>
				{isLoading ? (
					<sn-uib-loader />
				) : (
					<div>
						<sn-uib-transaction-alert
							applications={applications}
							domains={domains}
							userScope={userScope}
							userDomain={userDomain}
							supportDomains={supportDomains}
							requestScopeId={screen.scopeId}
							requestScopeName={escapeScopeLabel(screen.scopeName)}
							requestDomainId={screen.domainId}
							requestDomainName={screen.domainName}
							recordProtectionPolicy={screen.sysPolicy}
							supportScope={supportScope}
							isDomainExpanded={isDomainExpanded}
							overrideActionDisabled={overrideActionDisabled}
							requestItemType="Variant"
						/>
						{modalProps.modalContent}
					</div>
				)}
			</div>
			<now-modal
				header-label={confirmHeaderLabel}
				append-to-payload={{ category: 'confirm-modal' }}
				content={confirmText}
				manage-opened
				opened={showConfirmModal}
				size="sm"
				footer-actions={confirmFooterActions}
			/>
		</now-modal>
	);
};

createCustomElement('uib-screen-configuration-modal', {
	view,
	styles,
	initialState: {
		/**
		 * Whether the screen information is being fetched
		 *
		 * @type {boolean}
		 */
		isLoadingScreenDetails: false,
		/**
		 * model of the screen being edited
		 *
		 * @type {object}
		 */
		screen: null,
		/**
		 * Whether the user is able to edit this record
		 *
		 * @type {boolean}
		 */
		isNecessaryTransactionInProgress: false,
		/**
		 * whether the current form is valid
		 *
		 * @type {object}
		 */
		validationErrors: {
			name: {
				invalid: false
			},
			order: {
				invalid: false,
				messages: []
			}
		},
		/**
		 * Whether the user selects to copy a page definition
		 *
		 * @type {boolean}
		 */
		copyPageDefinition: false,
		/**
		 * the model representing the page defintion
		 *
		 * @type {object}
		 */
		pageDefinition: null,
		/**
		 * Used to alert the user when changing page definitions
		 *
		 * @type {boolean}
		 */
		showConfirmModal: false,
		/**
		 * If a user is choosing to change the current page definition of the screen
		 *
		 * @type {boolean}
		 */
		isChangingPageDefinition: false
	},
	properties: {
		/**
		 * The view that should be rendered for the modal
		 *
		 * @type {'properties'|'conditions'}
		 */
		configType: {
			default: 'properties'
		},
		/**
		 * Sys Id of the screen to configure
		 *
		 * @type {string}
		 */
		screenSysId: {},
		/**
		 * viewport information of the screen to configure
		 *
		 * @type {object}
		 */
		viewportScreenInfo: {},
		/**
		 * Boolean determining whether application should navigate back to success modal
		 *
		 * @type {boolean}
		 */
		returnToSuccessModal: {
			default: false
		},
		/**
		 * id and label of the user's current scope
		 *
		 * @type {object}
		 */
		userScope: {
			default: null
		},
		/**
		 * id and label of the user's current domain
		 *
		 * @type {object}
		 */
		userDomain: {
			default: null
		},
		/**
		 * whether or not domain support is enabled
		 *
		 * @type {boolean}
		 */
		supportDomains: {
			default: null
		},
		/**
		 * a list of the available application scopes
		 *
		 * @type {object[]}
		 */
		applications: {
			default: null
		},
		/**
		 * a list of available domains
		 *
		 * @type {object[]}
		 */
		domains: {
			default: null
		},
		supportScope: {
			default: false
		},
		isDomainExpanded: {
			default: false
		},
		overrideActionDisabled: {
			default: false
		}
	},
	behaviors: [
		{
			behavior: screenRequestBehavior
		}
	],
	actionHandlers: {
		[CANCEL_BUTTON_CLICKED]: {
			effect: ({ dispatch, properties }) => {
				const { returnToSuccessModal } = properties;
				dispatch(UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED, {
					returnToSuccessModal
				});
			},
			stopPropagation: true
		},
		[DELETE_BUTTON_CLICKED]: {
			stopPropagation: true,
			effect: ({ dispatch, properties, state }) => {
				const { screenSysId: sysId } = properties;
				const {
					screen: { name }
				} = state;
				dispatch(UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED);
				dispatch(UIB_SCREEN_CONFIGURATION_MODAL_DELETE_CLICKED, {
					sysId,
					name
				});
			}
		},
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties }) => {
			dispatch(screenRequestActions.SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUESTED, {
				sysId: properties.screenSysId
			});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, dispatch, properties }) => {
			if (action.payload.name === 'screenSysId') {
				dispatch(
					screenRequestActions.SCREEN_REQUEST_BEHAVIOR_SCREEN_REQUESTED,
					{
						sysId: properties.screenSysId
					}
				);
			}
		},
		'NOW_CHECKBOX#CHECKED_SET': {
			effect: ({ action, updateState }) => {
				const {
					payload: { input, value }
				} = action;
				switch (input) {
					case 'active':
						updateState({
							operation: 'set',
							path: 'screen.active',
							value
						});
						break;
				}
			},
			stopPropagation: true
		},
		'NOW_INPUT#INPUT': {
			effect: ({ action, updateState }) => {
				const {
					payload: { input, fieldValue: value }
				} = action;
				switch (input) {
					case 'order':
						updateState([
							{
								operation: 'set',
								path: 'validationErrors.order.messages',
								value: value.match(integerRegEx)
									? []
									: [
											{
												status: 'warning',
												/* prettier-ignore */
												content: t('{0} is not an integer. The value will be saved as {1}', value, parseInt(value))
											}
									  ]
							},
							{
								operation: 'set',
								path: 'screen.order',
								value: parseInt(value)
							}
						]);
						break;
					case 'name':
						updateState({
							operation: 'set',
							path: 'screen.name',
							value
						});
						break;
				}
			},
			stopPropagation: true
		},
		'NOW_INPUT#INVALID_SET': {
			effect: ({ action, updateState }) => {
				const {
					payload: { input, value: invalid }
				} = action;
				if (input === 'name') {
					updateState({
						operation: 'set',
						path: 'validationErrors.name.invalid',
						value: invalid
					});
				} else if (input === 'order') {
					updateState({
						operation: 'set',
						path: 'validationErrors.order.invalid',
						value: invalid
					});
				}
			},
			stopPropagation: true
		},
		'NOW_MODAL#OPENED_SET': {
			effect: ({ dispatch, updateState, action }) => {
				const { category } = action.payload;
				if (category === 'confirm-modal') {
					updateState({
						showConfirmModal: false
					});
				} else {
					dispatch(UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED);
				}
			},
			stopPropagation: true
		},
		'NOW_TEXTAREA#INPUT': {
			effect: ({ action, updateState }) => {
				const {
					payload: { input, fieldValue: value }
				} = action;
				if (input === 'screenCondition') {
					updateState({
						operation: 'set',
						path: 'screen.screenCondition',
						value
					});
				}
			},
			stopPropagation: true
		},
		[SAVE_BUTTON_CLICKED]: {
			effect: saveHandler,
			stopPropagation: true
		},
		[CONFIRM_BUTTON_CLICKED]: {
			effect: saveHandler,
			stopPropagation: true
		},
		['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: {
			effect: ({ action, state, updateState }) => {
				const {
					payload: { isNecessaryTransactionInProgress }
				} = action;
				if (
					isNecessaryTransactionInProgress !==
					state.isNecessaryTransactionInProgress
				) {
					updateState({ isNecessaryTransactionInProgress });
				}
			},
			stopPropagation: true
		},
		['SN_UIB_TRANSACTION_ALERT#UNLOCK_TO_OVERRIDE_CLICKED']: ({
			state,
			dispatch
		}) => {
			dispatch(UIB_SCREEN_CONFIGURATION_MODAL_UNLOCK_TO_OVERRIDE_CLICKED, {
				...state.screen
			});
		},
		[UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED]: {
			stopPropagation: true,
			effect: ({ updateState, action, state }) => {
				const { value } = action.payload;
				const { screen, copyPageDefinition } = state;
				const macroponent =
					screen.macroponentInstance.macroponentDefinitions[
						screen.macroponentInstance.rootMacroponentId
					];
				const preBuilt = macroponent.associatedTypes.find(
					(id) => id === SHARED_PAGE_DEFINITION_SYS_ID
				);
				updateState({
					pageDefinition: value !== 'none' ? value : null,
					isChangingPageDefinition:
						(preBuilt && value !== macroponent.id) ||
						(!preBuilt && value !== 'none'),
					copyPageDefinition: value !== 'none' ? copyPageDefinition : false
				});
			}
		},
		[UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED]: {
			stopPropagation: true,
			effect: ({ updateState, action }) => {
				const { value: copyPageDefinition } = action.payload;
				updateState({
					copyPageDefinition
				});
			}
		},
		[UIB_PAGE_DEFINITION_SELECTOR_SHOW_SELECTOR]: {
			stopPropagation: true,
			effect: ({ updateState }) => {
				updateState({
					pageDefinition: null,
					isChangingPageDefinition: false,
					copyPageDefinition: false
				});
			}
		},
		[CANCEL_CONFIRM_BUTTON_CLICKED]: {
			stopPropagation: true,
			effect: ({ updateState }) => {
				updateState({
					showConfirmModal: false
				});
			}
		}
	}
});
