import { t } from 'sn-translate';

export const subHeaderContentWrapper = (
	successModalType,
	subHeaderContent,
	selectedPageIsTab
) => {
	if (subHeaderContent) {
		return (
			<div className="subheader">
				<p className="first">{subHeaderContent}</p>
			</div>
		);
	}
	switch (successModalType) {
		case 'page':
		default:
			return (
				<div className="subheader">
					<p className="first">
						{
							/* prettier-ignore */
							t('Your {0} has been created. We also created your first {0} variant for you. In the future, you can create multiple variants for a {0} to target content to specific audience, such as Agents or Managers. ',selectedPageIsTab ? 'tab' : 'page')
						}
					</p>
					<p>
						{
							/* prettier-ignore */
							t('Before you start adding content, you can apply these advanced settings to your {0} or variant.', selectedPageIsTab ? 'tab' : 'page')
						}
					</p>
				</div>
			);
		case 'variant':
			return (
				<div className="subheader">
					<p className="first">
						{
							/* prettier-ignore */
							t('Your variant has been created. Before you add content to this variant, you can apply these advanced settings, such as controlling who sees this variant. You can always edit these settings later.')
						}
					</p>
				</div>
			);
	}
};
