import get from 'lodash/get';

/** @private */
export const FORCED_TRANSITION_EVENT_TYPE =
	'ROUTE_BEHAVIOR_FORCED_TRANSITION_EVENT';

/**
 * @typedef ForcedTransitionDetail
 * @property {'ROUTER_BEHAVIOR#TRANSITION_TO'|'ROUTER_BEHAVIOR#REPLACE_WITH'} action
 * @property {{routeName: string, routeState: object}} payload
 */

/**
 * @private
 * @type {Set<CustomEvent<ForcedTransitionDetail>>}
 */
export const FORCED_TRANSITION_EVENT_QUEUE = new Set();

/**
 * @private
 * @type {function}
 */
export let FORCED_TRANSITION_EVENT_HANDLER;

/**
 * A helper function to be inappropriately used outside the scope of the host component.
 * Prone to race conditions... There be dragons.
 * @param {string} routeName The name of the desired route
 * @param {Promise<object>|object} routeState Arbitrary data to store in browser history
 */
export function forceTransitionTo(routeName, routeState) {
	/** @type {CustomEvent<ForcedTransitionDetail>} */
	const applicationType = get(
		routeName.match(/^(workspace|app|components|dashboard)/),
		'[1]',
		null
	);
	const transitionEvent = new CustomEvent(FORCED_TRANSITION_EVENT_TYPE, {
		detail: {
			action: 'ROUTER_BEHAVIOR#TRANSITION_TO',
			payload: { routeName, routeState, applicationType }
		}
	});

	if (typeof FORCED_TRANSITION_EVENT_HANDLER === 'function') {
		window.dispatchEvent(transitionEvent);
	} else {
		FORCED_TRANSITION_EVENT_QUEUE.add(transitionEvent);
	}
}

/**
 * A helper function to be inappropriately used outside the scope of the host component.
 * Prone to race conditions... There be dragons.
 * @param {string} routeName The name of the desired route
 * @param {Promise<object>|object} routeState Arbitrary data to store in browser history
 */
export function forceReplaceWith(routeName, routeState) {
	/** @type {CustomEvent<ForcedTransitionDetail>} */
	const transitionEvent = new CustomEvent(FORCED_TRANSITION_EVENT_TYPE, {
		detail: {
			action: 'ROUTER_BEHAVIOR#REPLACE_WITH',
			payload: { routeName, routeState }
		}
	});

	if (typeof FORCED_TRANSITION_EVENT_HANDLER === 'function') {
		window.dispatchEvent(transitionEvent);
	} else {
		FORCED_TRANSITION_EVENT_QUEUE.add(transitionEvent);
	}
}

/**
 * A helper function to be inappropriately used outside the scope of the host component.
 * Prone to race conditions... There be dragons.
 * @param {string} routeName The name of the desired route
 * @param {Promise<object>|object} routeState Arbitrary data to store in browser history
 */
export function forceLoad(routeName, routeState) {
	/** @type {CustomEvent<ForcedTransitionDetail>} */
	const transitionEvent = new CustomEvent(FORCED_TRANSITION_EVENT_TYPE, {
		detail: {
			action: 'ROUTER_BEHAVIOR#LOAD',
			payload: { routeName, routeState }
		}
	});

	if (typeof FORCED_TRANSITION_EVENT_HANDLER === 'function') {
		window.dispatchEvent(transitionEvent);
	} else {
		FORCED_TRANSITION_EVENT_QUEUE.add(transitionEvent);
	}
}

/**
 * To be called in COMPONENT_CONNECTED to scope global custom events
 * @param {function} dispatch Dispatch function bound to host component
 */
export function attachForcedHandlers(dispatch) {
	/**
	 * @param {CustomEvent<ForcedTransitionDetail>} transitionEvent
	 */
	function forcedTransitionHandler(transitionEvent) {
		const { action, payload } = transitionEvent.detail;
		dispatch(action, payload);
	}

	FORCED_TRANSITION_EVENT_HANDLER = forcedTransitionHandler;
	window.addEventListener(
		FORCED_TRANSITION_EVENT_TYPE,
		FORCED_TRANSITION_EVENT_HANDLER,
		false
	);

	for (const transitionEvent of FORCED_TRANSITION_EVENT_QUEUE.values()) {
		window.dispatchEvent(transitionEvent);
	}
	FORCED_TRANSITION_EVENT_QUEUE.clear();
}

/**
 * To be called in COMPONENT_DISCONNECTED to remove global custom events
 */
export function detachForcedHandlers() {
	if (typeof FORCED_TRANSITION_EVENT_HANDLER === 'function') {
		window.removeEventListener(
			FORCED_TRANSITION_EVENT_TYPE,
			FORCED_TRANSITION_EVENT_HANDLER
		);
		FORCED_TRANSITION_EVENT_HANDLER = undefined;
	}
}
