import get from 'lodash/get';
import toString from 'lodash/toString';
import { createCustomElement } from '@servicenow/ui-core';
import {
	snUIBPagePropertiesConfigurationPane,
	snUIBDynamicPropertyInput
} from 'sn-ui-builder-components';
import { snUXFActions, contextTypes } from 'sn-uxf-builder-middleware';
import { createDataBrokerInstanceNameMap } from '../uib-data-configuration-shelf/utils';
import {
	createElementInstanceNameMap,
	getCurrentSelectedEventMappingHandlers,
	processCurrentSelectedEventMappings,
	processEventHandlers,
	processEventMappingHandlers
} from '../../utils/properties';
import { findParentModalElements } from '../../utils/elements';
import styles from './uib-page-properties-configuration-panel.scss';
import processorOverrides from './processors';
import { getFlaggedPropsAndOutOfSync } from '../../utils/getFlaggedPropsAndOutOfSync';
import getHelpResourceUrl from '../uib-app/utils/getHelpResourceUrl';
const {
	PAGE_PROP_CREATED,
	PAGE_PROP_CHANGED,
	PAGE_PROP_DELETED
} = snUIBPagePropertiesConfigurationPane;

export const UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL_PROPERTIES_MODAL_OPENED =
	'UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL#PROPERTIES_MODAL_OPENED';

const generatePagePropertiesAndAlertInfo = (
	fieldsParamsEnabled,
	appConfig,
	selectedPageId,
	pageProperties,
	dispatch
) => {
	if (fieldsParamsEnabled) {
		const appRoutes = get(appConfig, 'appRoutes');
		const {
			restrictedPageProps,
			outOfSyncFields
		} = getFlaggedPropsAndOutOfSync(
			fieldsParamsEnabled,
			appRoutes,
			selectedPageId,
			pageProperties
		);

		// in the case that a user creates a field/param that matches an already existing page property
		// that property needs to be changed to string type.
		const propsNeedingUpdate = restrictedPageProps.filter(
			(prop) =>
				(prop.isField || prop.isParam) &&
				prop.fieldType &&
				prop.fieldType !== 'string'
		);
		propsNeedingUpdate.forEach((prop) =>
			dispatch(snUXFActions.PAGE_PROPERTIES_CHANGED, {
				...prop,
				pagePropertyId: prop.id,
				fieldType: 'string',
				value: toString(prop.value),
				testValue: toString(prop.testValue)
			})
		);
		return {
			outOfSyncFields,
			restrictedPageProps: restrictedPageProps || pageProperties
		};
	} else {
		return { outOfSyncFields: [], restrictedPageProps: pageProperties };
	}
};

const view = (state, { dispatch }) => {
	const {
		currentEventHandlers,
		currentEventMappingHandlers,
		currentScreenEventMappings,
		currentInternalEventMappings,
		properties
	} = state;

	const {
		pageProperties,
		pageConfigurationPanelExpanded,
		pageTemplates,
		dataBindingEnabled,
		bindings,
		dispatchedEvents,
		interfaceConfigurationComponent,
		tables,
		appConfig,
		disabled,
		parentElements,
		lastAppliedTemplateId,
		isMacroponentReadOnly,
		selectedPageId,
		fieldsParamsEnabled,
		isPageDefinitionEventsDisabled,
		isPageDefinitionLayoutDisabled,
		internalEventMappingsSourceEvents,
		handledEvents,
		usingSharedPageDefinition,
		isPageLayoutModified,
		pagePropertiesEnabled,
		eventMappingsEnabled,
		itemType,
		referenceDisplayValues,
		referenceResults,
		referenceSearching
	} = properties;

	const parentModalElements = findParentModalElements(parentElements);
	const appConfigModalElements = get(appConfig, 'modalElements', []);

	const {
		outOfSyncFields,
		restrictedPageProps
	} = generatePagePropertiesAndAlertInfo(
		fieldsParamsEnabled,
		appConfig,
		selectedPageId,
		pageProperties,
		dispatch
	);

	return (
		<sn-uib-page-configuration-panel
			component-id="sn-uib-page-configuration-panel"
			pageConfigurationPanelExpanded={pageConfigurationPanelExpanded}
			pageTemplates={pageTemplates}
			disabled={disabled}
			pageProperties={restrictedPageProps}
			lastAppliedTemplateId={lastAppliedTemplateId}
			isPageLayoutModified={isPageLayoutModified}
			dataBindingEnabled={dataBindingEnabled}
			bindings={bindings}
			currentTab={'@ui-state/pageConfigTab'}
			screen-event-mappings={currentScreenEventMappings}
			dispatched-events={dispatchedEvents}
			event-mapping-handlers={currentEventMappingHandlers}
			event-handlers={currentEventHandlers}
			interface-configuration-component={interfaceConfigurationComponent}
			tables={tables}
			app-config={{
				...appConfig,
				modalElements: [...appConfigModalElements, ...parentModalElements],
				bindings
			}}
			isMacroponentReadOnly={isMacroponentReadOnly}
			out-of-sync-fields={outOfSyncFields}
			is-page-definition-events-disabled={isPageDefinitionEventsDisabled}
			is-page-definition-layout-disabled={isPageDefinitionLayoutDisabled}
			internalEventMappings={currentInternalEventMappings}
			internalEventMappingsSourceEvents={internalEventMappingsSourceEvents}
			handledEvents={handledEvents}
			using-shared-page-definition={usingSharedPageDefinition}
			page-properties-enabled={pagePropertiesEnabled}
			event-mappings-enabled={eventMappingsEnabled}
			item-type={itemType}
			reference-display-values={referenceDisplayValues}
			reference-results={referenceResults}
			reference-searching={referenceSearching}
		/>
	);
};

createCustomElement('uib-page-properties-configuration-panel', {
	view,
	initialState: {
		currentEventHandlers: [],
		currentEventMappingHandlers: [],
		currentScreenEventMappings: [],
		currentInternalEventMappings: []
	},
	transformState: (state) => {
		const { properties } = state;
		const {
			eventHandlers = [],
			eventMappingHandlers,
			screenEventMappings,
			internalEventMappings
		} = properties;

		const filteredHandlers = eventHandlers.filter(
			(handler) => handler.relay === false
		);
		const currentEventHandlers = processEventHandlers(filteredHandlers);

		const currentScreenEventMappings = processCurrentSelectedEventMappings(
			screenEventMappings
		);
		const currentInternalEventMappings = processCurrentSelectedEventMappings(
			internalEventMappings
		);

		const selectedEventMappingHandlers = getCurrentSelectedEventMappingHandlers(
			[
				...(currentScreenEventMappings || []),
				...(currentInternalEventMappings || [])
			],
			eventMappingHandlers
		);
		const currentEventMappingHandlers = processEventMappingHandlers(
			selectedEventMappingHandlers,
			state,
			processorOverrides,
			'sn-uib-page-configuration-panel'
		);

		return {
			...state,
			currentEventHandlers,
			currentEventMappingHandlers,
			currentScreenEventMappings,
			currentInternalEventMappings
		};
	},
	properties: {
		pageProperties: {
			default: []
		},
		pageTemplates: {
			default: []
		},
		pageLayout: {
			default: {}
		},
		elementLayouts: {
			default: {}
		},
		selectedElementId: {
			default: null
		},
		isDefaultPageLayout: {
			default: false
		},
		disabled: {
			default: false
		},
		pageConfigurationPanelExpanded: {
			selectable: true,
			default: true
		},
		lastAppliedTemplateId: {
			computed({ properties: { pageLayout } }) {
				return get(pageLayout, 'default.templateId', null);
			}
		},
		isPageLayoutModified: {
			computed({ properties: { elementLayouts, selectedElementId } }) {
				return get(
					elementLayouts,
					`${selectedElementId}.isLayoutModified`,
					null
				);
			}
		},
		elementInstanceNameMap: {
			computed({ properties: { ancestorElements } }) {
				const ancestorInstanceNames =
					ancestorElements &&
					Object.entries(ancestorElements).reduce((acc, el) => {
						const [elementId, elementProps] = el;
						acc[elementId] = [
							{ id: '__instance_name__', value: elementProps.label }
						];
						return acc;
					}, {});
				return createElementInstanceNameMap(ancestorInstanceNames);
			}
		},
		ancestorDataBrokerInstances: {
			default: []
		},
		dataBrokerInstanceNameMap: {
			computed({ properties: { ancestorDataBrokerInstances } }) {
				return createDataBrokerInstanceNameMap(ancestorDataBrokerInstances);
			}
		},
		dataBindingEnabled: {
			default: true,
			selectable: true
		},
		/**
		 * @typedef bindingInterface
		 * @property {string} label
		 * @property {any} value
		 */

		/**
		 * A list of available context bindings
		 *
		 * @type {Array.<bindingInterface>}
		 */
		bindings: {
			default: []
		},
		/**
		 * The event mappings that live on screen
		 */
		screenEventMappings: {
			default: []
		},
		/**
		 * The list of root macroponent dispatched events
		 */
		dispatchedEvents: {
			default: []
		},
		/**
		 * The list of root macroponent handled events
		 */
		handledEvents: {
			default: []
		},
		/**
		 * All the event mapping handlers
		 */
		eventMappingHandlers: {
			default: {}
		},
		/**
		 * All the event handlers
		 */
		eventHandlers: {
			default: []
		},
		/**
		 * a map containing component interfaces
		 */
		interfaceConfigurationComponent: {
			default: {}
		},
		/**
		 * A list of tables for the table picker
		 */
		tables: {
			default: [],
			schema: { type: 'array' }
		},
		/**
		 * The consolidated app configuration data used in property configurations
		 */
		appConfig: {
			default: {},
			schema: { type: 'object' }
		},
		/**
		 * Elements in the compositions of the ancestor mcp hierarchy
		 */
		ancestorElements: {
			default: {}
		},
		parentElements: {
			default: {}
		},
		isMacroponentReadOnly: {
			default: false
		},
		selectedPageId: {
			default: null
		},
		fieldsParamsEnabled: {
			default: true
		},
		isPageStateDirty: {
			default: false
		},
		isPageDefinitionEventsDisabled: {
			default: false
		},
		isPageDefinitionLayoutDisabled: {
			default: false
		},
		internalEventMappings: {
			default: []
		},
		internalEventMappingsSourceEvents: {
			default: []
		},
		usingSharedPageDefinition: {
			default: false
		},
		pagePropertiesEnabled: {
			default: true
		},
		eventMappingsEnabled: {
			default: true
		},
		itemType: {
			default: 'page'
		},
		referenceDisplayValues: {
			default: {}
		},
		referenceResults: {
			default: {}
		},
		referenceSearching: {
			default: {}
		}
	},
	styles,
	actionHandlers: {
		['SN_UIB_PAGE_CONFIGURATION_PANEL#RESET_LAYOUT']: ({
			properties,
			dispatch
		}) => {
			const dispatchFn = () => {
				dispatch(snUXFActions.PAGE_TEMPLATE_SELECTED, {
					pageTemplateId: properties.lastAppliedTemplateId
				});
			};
			if (properties.isDefaultPageLayout) {
				dispatchFn();
			} else {
				dispatch('SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED', {
					type: 'page',
					context: 'reset',
					applyActionHandler: dispatchFn
				});
			}
		},
		['SN_UIB_PAGE_CONFIGURATION_PANEL#PAGE_TEMPLATE_SELECTED']: ({
			properties,
			action,
			dispatch
		}) => {
			const { isLayoutModified, pageTemplateId } = action.payload;
			const dispatchFn = () => {
				dispatch(snUXFActions.PAGE_TEMPLATE_SELECTED, {
					pageTemplateId
				});
			};
			if (properties.isDefaultPageLayout || !isLayoutModified) {
				dispatchFn();
			} else {
				dispatch('SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED', {
					type: 'page',
					applyActionHandler: dispatchFn
				});
			}
		},
		[PAGE_PROP_CREATED]: ({ action, dispatch }) => {
			dispatch(snUXFActions.PAGE_PROPERTIES_CREATED, action.payload);
		},
		[PAGE_PROP_CHANGED]: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(snUXFActions.PAGE_PROPERTIES_CHANGED, action.payload);
		},
		[PAGE_PROP_DELETED]: ({ action, dispatch }) => {
			dispatch(snUXFActions.PAGE_PROPERTIES_DELETED, action.payload);
		},
		['SN_UIB_PAGE_CONFIGURATION_PANEL#REFERENCE_DISPLAY_VALUES_REQUESTED']: ({
			action,
			dispatch
		}) => {
			const { references = [] } = action.payload || {};
			references.length &&
				dispatch(
					'UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL#DISPLAY_VALUES_REQUESTED',
					{ references }
				);
		},
		['SN_UIB_PAGE_PROPERTIES_CONFIGURATION_PANE#DOCUMENTATION_LINK_CLICKED']: ({
			action
		}) => {
			const { resourceId } = action.payload;

			window.open(getHelpResourceUrl(resourceId));
		},
		['PAGE_EVENT_CONFIG_PANE#HANDLER_VALUE_REFERENCE_PROPERTY_SEARCHED']: ({
			action,
			dispatch,
			properties
		}) => {
			const { eventMappingId, handlerId, id: propertyId } = action.payload;
			const { eventMappingHandlers = {} } = properties;
			const handlerToSearch =
				(eventMappingHandlers[eventMappingId] || []).find(
					(handler) => handler.handlerId === handlerId
				) || {};
			const valueToSearch =
				(handlerToSearch.values || []).find((val) => val.id === propertyId) ||
				{};

			dispatch('UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL#REFERENCE_REQUESTED', {
				data: {
					...action.payload,
					referenceFilter: get(
						valueToSearch,
						'typeMeta.reference.referenceFilter',
						null
					)
				}
			});
		},
		['PAGE_EVENT_CONFIG_PANE#EVENT_MAPPING_ADDED']: ({ action, dispatch }) => {
			if (action.payload.context === 'page') {
				dispatch(
					snUXFActions.PAGE_INTERNAL_EVENT_MAPPING_CREATED,
					action.payload
				);
			} else {
				dispatch(snUXFActions.SCREEN_EVENT_MAPPING_CREATED, action.payload);
			}
		},
		['PAGE_EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_ADDED']: ({
			action,
			dispatch
		}) => {
			if (action.payload.context === 'page') {
				dispatch(
					snUXFActions.PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CREATED,
					action.payload
				);
			} else {
				dispatch(
					snUXFActions.SCREEN_EVENT_MAPPING_HANDLER_ADDED,
					action.payload
				);
			}
		},
		['PAGE_EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_DELETED']: ({
			action,
			dispatch
		}) => {
			if (action.payload.context === 'page') {
				dispatch(
					snUXFActions.PAGE_INTERNAL_EVENT_MAPPING_HANDLER_DELETED,
					action.payload
				);
			} else {
				dispatch(
					snUXFActions.SCREEN_EVENT_MAPPING_HANDLER_DELETED,
					action.payload
				);
			}
		},
		['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_CHANGED']: ({
			action,
			dispatch
		}) => {
			if (action.payload.context === 'page') {
				dispatch(
					snUXFActions.PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CHANGED,
					action.payload
				);
			} else if (action.payload.context === 'screen') {
				dispatch(
					snUXFActions.SCREEN_EVENT_MAPPING_HANDLER_CHANGED,
					action.payload
				);
			}
		},
		['EVENT_CONFIG_PANE#EVENT_MAPPING_HANDLER_CHANGED_IMMEDIATE']: ({
			action,
			dispatch
		}) => {
			if (action.payload.context === 'page') {
				dispatch(
					snUXFActions.PAGE_INTERNAL_EVENT_MAPPING_HANDLER_CHANGED,
					action.payload
				);
			} else if (action.payload.context === 'screen') {
				dispatch(
					snUXFActions.SCREEN_EVENT_MAPPING_HANDLER_CHANGED,
					action.payload
				);
			}
		},
		['SN_UIB_PAGE_PROPERTIES_CONFIGURATION_PANE#EDIT_CLICKED']: ({
			action,
			dispatch
		}) => {
			action.stopPropagation();

			dispatch(UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL_PROPERTIES_MODAL_OPENED);
		},
		[snUIBDynamicPropertyInput.DYNAMIC_PROPERTY_INPUT_FOCUSED]: ({
			action,
			dispatch
		}) => {
			const { id: propertyId } = action.payload;
			dispatch(snUXFActions.SN_UXF_CONTEXT_BINDINGS_REQUESTED, {
				contextType: contextTypes.pageProperty,
				propertyId
			});
		}
	}
});
