import '@servicenow/now-text-link';
import { t } from 'sn-translate';
import PAGE_TITLE, { PAGE_TITLE_SUFFIX } from '../../../utils/pageTitle';
import { applicationTypes } from '../../../utils/routingConstants';

/**
 * @param routeName
 * @param routeState
 * @param applicationType
 * @param isLoading
 */
export function shouldShowRouteNotFound(
	routeName,
	routeState,
	applicationType,
	isLoading
) {
	return (
		!isLoading &&
		routeState.show404 &&
		(!routeName || routeName === applicationType)
	);
}

/**
 * @param _
 * @param children
 */
function RouteNotFoundLayout(_, children) {
	// eslint-disable-line no-unused-vars
	return (
		<div className="route-not-found">
			<div className="not-found">{children}</div>
		</div>
	);
}

/**
 * @param root0
 * @param root0.routeName
 * @param root0.routeState
 * @param root0.applicationType
 */
export default function RouteNotFound({
	routeName,
	routeState = {},
	applicationType
}) {
	const { appsSysId } = routeState;
	const URL =
		applicationType === applicationTypes.workspace
			? 'sys_aw_master_config_list.do'
			: 'sys_ux_app_config_list.do';
	const article = applicationType === applicationTypes.workspace ? 'a' : 'an';
	const title =
		applicationType === applicationTypes.workspace
			? 'Workspace'
			: 'Application';
	PAGE_TITLE.set(t('{0} Not Found', title), PAGE_TITLE_SUFFIX);

	if (applicationType === applicationTypes.dashboards) {
		return (
			<RouteNotFoundLayout>
				{t('Cannot find the requested dashboard')}
				<br />
				<now-button-bare
					label={t('Choose or create a new dashboard')}
					append-to-payload={{ name: 'routeNotFound' }}
				/>
			</RouteNotFoundLayout>
		);
	}

	if (routeName && routeName === applicationType) {
		return (
			<RouteNotFoundLayout>
				{t('Cannot find {0} {1}', applicationType, appsSysId)}
				<br />
				<now-text-link
					label={t('Choose {0} {1} here', article, applicationType)}
					href={`${window.location.origin}/${URL}`}
				/>
			</RouteNotFoundLayout>
		);
	}

	return (
		<RouteNotFoundLayout>
			{t('No {0} selected', applicationType || 'app')}
			<br />
			<now-text-link
				label={t('Choose {0} {1} here', article, applicationType || 'app')}
				href={`${window.location.origin}/${URL}`}
			/>
		</RouteNotFoundLayout>
	);
}
