const handleValidation = ({ action, state, updateState, properties }) => {
	action.stopPropagation();
	const { pageIsValid } = action.payload;
	const { footerActions } = state;
	const { disabled } = properties;

	updateState({
		...state,
		footerActions: updateFooterActions(footerActions, pageIsValid, disabled)
	});
};

const updateFooterActions = (footerActions, pageIsValid, disabled) => {
	return footerActions.map((footerItem) => {
		return footerItem.hasOwnProperty('disabled')
			? {
					...footerItem,
					disabled: (footerItem.checkForDisabled && disabled) || !pageIsValid
			  }
			: footerItem;
	});
};

export const dataHandlers = {
	['SN_UIB_CHILD#CHILD_DATA_SET']: ({ action, state, updateState }) => {
		const { key, value } = action.payload;

		updateState({
			...state,
			context: {
				...state.context,
				[key]: value
			}
		});
	},
	['VALIDATION_BEHAVIOR#VALIDATION_CHANGED']: handleValidation
};
