import '@servicenow/now-alert';
import '@servicenow/now-code-editor';
import '@servicenow/now-modal';
import { createCustomElement } from '@servicenow/ui-core';
import 'sn-devx-components';
import { t } from 'sn-translate';
import 'sn-ui-builder-components';
import { snUXFActions } from 'sn-uxf-builder-middleware';

const APPLY_ACTION = 'SN_UIB_ELEMENT_LAYOUT_MODAL_FOOTER_APPLY#CLICKED';
const CANCEL_ACTION = 'SN_UIB_ELEMENT_LAYOUT_MODAL_FOOTER_CANCEL#CLICKED';
const CLOSE_ACTION = 'SN_UIB_ELEMENT_LAYOUT_MODAL_CLOSED_BUTTON#CLICKED';
const CREATE_TEMPLATE_ACTION =
	'SN_UIB_ELEMENT_LAYOUT_MODAL_CREATE_TEMPLATE_BUTTON#CLICKED';

const layoutModalFooterActions = [
	{
		label: t('Apply'),
		variant: 'primary',
		clickActionType: APPLY_ACTION
	},
	{
		label: t('Cancel'),
		variant: 'secondary',
		clickActionType: CANCEL_ACTION
	}
];
const newTemplateFooterActions = (disabled) => [
	{
		label: t('Create template'),
		variant: 'primary',
		clickActionType: CREATE_TEMPLATE_ACTION,
		disabled
	},
	{
		label: t('Cancel'),
		variant: 'secondary',
		clickActionType: CANCEL_ACTION
	}
];

const getModalLabel = (templateType, isNewTemplate) => {
	if (isNewTemplate) {
		if (templateType === 'layout') {
			return t('Create your element template');
		} else if (templateType === 'page') {
			return t('Create your page template');
		}
	} else if (templateType === 'layout') {
		return t("Update your element's layout");
	} else if (templateType === 'page') {
		return t("Update your page's layout");
	}
};

const closeHandler = (dispatch, updateState) => {
	updateState({ layout: null, templateName: '' });
	dispatch(CLOSE_ACTION);
};
export default createCustomElement('uib-element-layout-modal', {
	view: ({
		properties: { showModal, isValid, templateType, isNewTemplate },
		selectedLayout,
		layout,
		templateName
	}) => {
		return showModal ? (
			<now-modal
				header-label={getModalLabel(templateType, isNewTemplate)}
				opened
				size="lg"
				footerActions={
					isNewTemplate
						? newTemplateFooterActions(!templateName.trim() || !layout)
						: layoutModalFooterActions
				}>
				{!isNewTemplate ? (
					<now-alert
						status="warning"
						/* prettier-ignore */
						content={t('Changes made here will overwrite the existing configuration.')}
					/>
				) : null}
				<div style={{ height: '20rem', marginBottom: '1.29rem' }}>
					<now-code-editor
						language="json"
						content={
							selectedLayout ? JSON.stringify(selectedLayout, null, 2) : ''
						}
						style={{ width: '100%', height: '100%' }}
					/>
				</div>
				{isNewTemplate ? (
					<div>
						<now-input label={t('Template Name')} />
					</div>
				) : null}
				{!isValid ? (
					<now-alert content={t('Your JSON is invalid')} status="critical" />
				) : null}
			</now-modal>
		) : null;
	},
	properties: {
		showModal: {
			default: false
		},
		layouts: {
			default: {}
		},
		selectedElementId: {
			default: null
		},
		isValid: {
			default: true
		},
		templateType: {
			default: null
		},
		isNewTemplate: {
			default: false
		},
		pageLayout: {
			default: {}
		}
	},
	initialState: {
		layout: null,
		templateName: ''
	},
	transformState: (state) => {
		const {
			layouts,
			selectedElementId,
			pageLayout,
			templateType,
			isNewTemplate
		} = state.properties;
		let selectedLayout;
		if (isNewTemplate) {
			selectedLayout = null;
			return {
				...state,
				selectedLayout
			};
		} else {
			if (templateType === 'page' && pageLayout) {
				selectedLayout = pageLayout;
			} else {
				selectedLayout = (layouts[selectedElementId] || {}).layout;
			}

			return {
				...state,
				selectedLayout
			};
		}
	},
	actionHandlers: {
		['NOW_MODAL#OPENED_SET']: ({ updateProperties, dispatch, updateState }) => {
			updateProperties({ isValid: true });
			closeHandler(dispatch, updateState);
		},
		['NOW_INPUT#INPUT']: ({ updateState, action }) => {
			updateState({
				templateName: action.payload.fieldValue
			});
		},
		[APPLY_ACTION]: ({
			updateProperties,
			dispatch,
			state,
			properties,
			updateState
		}) => {
			const { templateType } = properties;
			try {
				JSON.parse(state.layout);
				if (state.layout) {
					if (templateType === 'layout') {
						dispatch(snUXFActions.ELEMENT_LAYOUT_CHANGED, {
							elementId: properties.selectedElementId,
							layout: state.layout
						});
					} else if (templateType === 'page') {
						dispatch(snUXFActions.PAGE_LAYOUT_CHANGED, {
							layout: state.layout
						});
					}
				}
				updateProperties({ layout: null, isValid: true });
				closeHandler(dispatch, updateState);
			} catch (er) {
				updateProperties({ isValid: false });
			}
		},
		[CANCEL_ACTION]: ({ updateProperties, dispatch, updateState }) => {
			updateProperties({ isValid: true });
			closeHandler(dispatch, updateState);
		},
		[CREATE_TEMPLATE_ACTION]: ({
			updateProperties,
			dispatch,
			state,
			properties,
			updateState
		}) => {
			const { templateType } = properties;
			const { templateName } = state;
			try {
				JSON.parse(state.layout);
				if (state.layout) {
					const payload = {
						layout: state.layout,
						name: templateName.trim()
					};
					const action =
						templateType === 'layout'
							? snUXFActions.LAYOUT_TEMPLATE_CREATE_REQUESTED
							: snUXFActions.PAGE_TEMPLATE_CREATE_REQUESTED;
					dispatch(action, payload);
				}
				updateProperties({ layout: null, isValid: true });
				closeHandler(dispatch, updateState);
			} catch (er) {
				updateProperties({ isValid: false });
			}
		},
		'NOW_CODE_EDITOR#CONTENT_CHANGED': ({ action, updateState }) => {
			const script = action.payload.text;

			action.stopPropagation();
			updateState({ layout: script });
		}
	}
});
