import { createCustomElement } from '@servicenow/ui-core';

import '../uib-app';
import { USER_DATA_PROVIDER_ID } from '../uib-user-data-provider';

export function defineComponentAppContainer() {
	createCustomElement('sn-component-app-container', {
		view: () => {
			return (
				<uib-user-data-provider component-id={USER_DATA_PROVIDER_ID}>
					<uib-app component-id="uib-app" is-component-builder />
				</uib-user-data-provider>
			);
		}
	});
}
