import get from 'lodash/get';
import { t } from 'sn-translate';
import cuid from 'cuid';

import { NULLIFIED_C3_CONFIG } from './extendedPropertiesHandlers';
import {
	SN_UIB_APP_SCREEN_CONFIGURATION_MODAL_OPENED,
	SN_UIB_APP_UNLOCK_TO_CREATE_OVERRIDE_CLICKED
} from './uibAppActions';
import { UIB_ADVANCED_URL_CONFIGURATION_MODAL_DISMISSED } from '../../uib-advanced-url-configuration-modal';
import { UIB_NEW_CREATE_PAGE_MODAL_DISMISSED } from '../../uib-new-create-page';
import { UIB_PAGE_CONFIGURATION_MODAL_MODAL_DISMISSED } from '../../uib-page-configuration-modal';
import { UIB_VARIANT_MODAL_MODAL_DISMISSED } from '../../uib-new-variant-modal';
import {
	UIB_CREATE_SUCCESS_MODAL_DISMISSED,
	UIB_CREATE_SUCCESS_MODAL_ADD_AUDIENCES_CLICKED,
	UIB_CREATE_SUCCESS_MODAL_ADD_FIELDS_CLICKED,
	UIB_CREATE_SUCCESS_MODAL_ADD_PARAMS_CLICKED,
	UIB_CREATE_SUCCESS_MODAL_SET_CONDITIONS_CLICKED
} from '../../uib-create-success-modal';
import { UIB_AUDIENCES_MODAL_DISMISSED } from '../../uib-audiences-modal';
import {
	UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED,
	UIB_SCREEN_CONFIGURATION_MODAL_UNLOCK_TO_OVERRIDE_CLICKED
} from '../../uib-screen-configuration-modal';
import { UIB_FIELDS_PROPS_SYNC_DIALOG_DISMISS } from '../../uib-fields-props-sync-dialog';
import { UIB_PAGE_PROPERTIES_MODAL_DISMISSED } from '../../uib-page-properties-modal';
import { UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL_PROPERTIES_MODAL_OPENED } from '../../uib-page-properties-configuration-panel';
import { UIB_ELEMENT_DA_EVENT_MAPPING_MODAL_DISMISSED } from '../../uib-element-da-event-mapping-modal';

import {
	SHARE_CLICKED,
	SHARING_MODAL_CLOSED,
	DASHBOARD_SHARING_NOTIFICATION
} from '../../uib-page-header/actions';
import {
	flattenAppRoutes,
	flattenDashboardRoutes
} from '../../../utils/appRoutes';
import pageStateToTabModal from '../../../utils/selectors/page-state-to-tab-modal';
import { screenNames } from '../../../utils/routingConstants';
import '../../../constants/';
import { SHARED_PAGE_DEFINITION_SYS_ID } from '../../../globals/constants';
import { checkForUnsavedChanges } from '../utils/checkForUnsavedChanges';

export const modalHandlers = {
	['SN_UIB_APP#CREATE_PAGE_REQUESTED']: checkForUnsavedChanges(
		({ updateProperties, action, properties }) => {
			const type = get(action, 'payload.type', '');
			const createAsTab = get(action, 'payload.createAsTab', false);
			const elementId = get(action, 'payload.elementId', '');
			const elementProperties = get(action, 'payload.elementProperties', {});

			const {
				rootElementId,
				routeState,
				selectedPage,
				selectedPageVariants
			} = properties;

			if (createAsTab) {
				const createAsTabMetadata = pageStateToTabModal(
					rootElementId,
					elementId,
					elementProperties,
					selectedPage,
					selectedPageVariants,
					routeState
				);
				updateProperties({
					createAsTab,
					createAsTabMetadata,
					showNewPageModal: true,
					pageCreateType: 'createPage',
					isDynamicTabCreateClicked: type === 'dynamicTab'
				});
			} else {
				updateProperties({
					createAsTabMetadata: '@uxf/parentInfo',
					showNewPageModal: true,
					pageCreateType: 'createPage',
					isDynamicTabCreateClicked: type === 'dynamicTab'
				});
			}
		}
	),
	['SN_UIB_APP#CREATE_NEW_PAGE_REQUESTED']: ({ updateProperties }) => {
		updateProperties({
			showNewPageModal: true
		});
	},
	['SN_UIB_APP#NEW_PAGE_CLICKED']: ({ updateProperties }) => {
		updateProperties({
			showNewPageModal: true,
			pageCreateType: 'createPage'
		});
	},
	['SN_UIB_APP#NEW_PAGE_REQUESTED']: ({ updateProperties }) => {
		updateProperties({
			showNewPageModal: true,
			pageCreateType: null
		});
	},
	['SN_UIB_APP#ACTIVATION_DIALOG_DISMISSED']: ({ updateProperties }) => {
		updateProperties({
			showActivationConfirmation: false
		});
	},
	['SN_UIB_APP#CLONE_PAGE_REQUESTED']: checkForUnsavedChanges(
		({ action, updateProperties }) => {
			const { payload = {} } = action;
			const { page = null } = payload;
			updateProperties({
				showNewPageModal: true,
				pageCreateType: 'copyPage',
				newPageModalInfo: page
			});
		}
	),
	['UIB_EXPERIENCES_MODAL#CLOSE_MODAL']: ({ updateState }) => {
		updateState({
			operation: 'set',
			path: 'showExperienceModal',
			value: false
		});
	},
	'SN_EXPERIENCE_SETTINGS_CONNECTED#CANCEL_REQUESTED': ({ updateState }) => {
		updateState({
			operation: 'set',
			path: 'showExperienceModal',
			value: false
		});
	},
	'SN_EXPERIENCE_SETTINGS_CONNECTED#UPDATE_SUCCEEDED': ({ updateState }) => {
		updateState({
			operation: 'set',
			path: 'showExperienceModal',
			value: false
		});
	},
	['SN_UIB_APP#EDIT_PAGE_REQUESTED']: ({ action, updateProperties }) => {
		const { payload = {} } = action;
		const { page = null } = payload;

		updateProperties({
			showNewPageModal: true,
			pageCreateType: 'editPage',
			newPageModalInfo: page
		});
	},
	['UIB_CREATE_NEW_MODAL#MODAL_CLOSED']: ({ updateProperties }) => {
		updateProperties({
			createAsTab: false,
			createAsTabMetadata: '@uxf/parentInfo',
			showNewPageModal: false,
			pageCreateType: null,
			newPageModalInfo: null,
			isDynamicTabCreateClicked: false
		});
	},
	['SN_UIB_PAGE_HEADER#EDIT_PAGE_PROPERTIES_CLICKED']: ({
		updateProperties,
		properties,
		dispatch
	}) => {
		const { isWorkspaceApp } = properties;
		if (isWorkspaceApp) {
			updateProperties({
				showNewPageModal: true,
				pageCreateType: 'editPage'
			});
		} else {
			dispatch('SN_UIB_APP#PAGE_CONFIGURATION_MODAL_OPENED');
		}
	},
	['SN_UIB_APP#UNSAVED_DIALOG_DISMISSED']: ({ updateState }) => {
		updateState({
			unsavedChangesCheckCallbackEvent: null
		});
	},
	['SN_UIB_APP#CLOSE_COMPONENT_MODAL']: ({ updateProperties }) => {
		updateProperties({
			modalConfig: null
		});
	},
	['SN_UIB_LAYOUT_BUTTON_OPEN#CLICKED']: ({ updateProperties }) => {
		updateProperties({ showLayoutModal: true });
	},
	['UIB_LAYOUT_MODAL_CLOSED_BUTTON#CLICKED']: ({ updateProperties }) => {
		updateProperties({ showLayoutModal: false });
	},
	['UIB_LAYOUT_MODAL_LAYOUT#SELECTED']: ({ updateState, action }) => {
		updateState({ selectedLayoutId: action.payload.value });
	},
	['SN_UIB_LAYOUT_CONTEXT_MENU#EDIT_LAYOUT']: ({
		updateProperties,
		action
	}) => {
		updateProperties({
			showElementLayoutModal: true,
			templateType: action.payload.context
		});
	},
	['SN_UIB_ELEMENT_LAYOUT_BUTTON_OPEN#CLICKED']: ({
		updateProperties,
		action
	}) => {
		updateProperties({
			showElementLayoutModal: true,
			templateType: action.payload.templateType,
			isNewTemplate: action.payload.isNewTemplate
		});
	},
	['SN_UIB_ELEMENT_LAYOUT_MODAL_CLOSED_BUTTON#CLICKED']: ({
		updateProperties
	}) => {
		updateProperties({ showElementLayoutModal: false, templateType: null });
	},
	['SN_UIB_APP#C3_MODAL_CLOSED']: ({ updateProperties }) => {
		updateProperties({
			showC3Modal: false
		});
	},
	['SN_UIB_APP#C3_MODAL_OPENED']: ({ updateProperties }) => {
		updateProperties({
			showC3Modal: true
		});
	},
	['SN_UIB_C3_PROPERTY#CLEAR_C3_CONFIG']: ({
		updateState,
		updateProperties
	}) => {
		updateProperties({
			c3PropertyConfig: { ...NULLIFIED_C3_CONFIG }
		});
		updateState({
			c3ContentHeight: 'auto',
			c3ContentInitialized: false
		});
	},
	['SN_UIB_APP#CREATE_PAGE_OPENED']: ({ updateProperties }) => {
		updateProperties({ showNewPageModal: true });
	},
	['SN_UIB_SPLASH_PAGE#CREATE_PAGE_BUTTON_CLICKED']: ({ updateProperties }) => {
		updateProperties({ showNewPageModal: true });
	},
	[UIB_NEW_CREATE_PAGE_MODAL_DISMISSED]: ({
		updateProperties,
		properties,
		dispatch
	}) => {
		const { routeName } = properties;
		updateProperties({
			createAsTab: false,
			createAsTabMetadata: '@uxf/parentInfo',
			showNewPageModal: false,
			isDynamicTabCreateClicked: false
		});
		if (routeName === screenNames.dashboardsHome) {
			dispatch('SN_UIB_APP#UPDATE_DASHBOARDS_LIST');
		}
	},
	[UIB_CREATE_SUCCESS_MODAL_DISMISSED]: ({ updateProperties }) => {
		updateProperties({ successModalInfo: null });
	},
	[UIB_CREATE_SUCCESS_MODAL_ADD_AUDIENCES_CLICKED]: ({
		action,
		updateProperties,
		properties
	}) => {
		const { screen } = action.payload;
		const { successModalInfo } = properties;
		updateProperties({
			audiencesModalInfo: {
				screenId: screen.sysId,
				returnToSuccessModal: true
			},
			successModalInfo: {
				...successModalInfo,
				render: false
			}
		});
	},
	[UIB_CREATE_SUCCESS_MODAL_ADD_FIELDS_CLICKED]: ({
		action,
		updateProperties,
		properties
	}) => {
		const {
			page: { page, ...route }
		} = action.payload;
		const { successModalInfo } = properties;
		updateProperties({
			advancedUrlConfigurationModalInfo: {
				route: { ...route, ...page },
				configType: 'fields',
				returnToSuccessModal: true
			},
			successModalInfo: {
				...successModalInfo,
				render: false
			}
		});
	},
	[UIB_CREATE_SUCCESS_MODAL_ADD_PARAMS_CLICKED]: ({
		action,
		updateProperties,
		properties
	}) => {
		const {
			page: { page, ...route }
		} = action.payload;
		const { successModalInfo } = properties;
		updateProperties({
			advancedUrlConfigurationModalInfo: {
				route: { ...route, ...page },
				configType: 'parameters',
				returnToSuccessModal: true
			},
			successModalInfo: {
				...successModalInfo,
				render: false
			}
		});
	},
	[UIB_CREATE_SUCCESS_MODAL_SET_CONDITIONS_CLICKED]: ({
		action,
		updateProperties,
		properties
	}) => {
		const {
			screen: { sysId: screenSysId },
			id: configType
		} = action.payload;
		const { successModalInfo } = properties;
		updateProperties({
			screenConfigurationModalInfo: {
				screenSysId,
				configType,
				returnToSuccessModal: true
			},
			successModalInfo: {
				...successModalInfo,
				render: false
			}
		});
	},
	[UIB_AUDIENCES_MODAL_DISMISSED]: ({
		updateProperties,
		action,
		properties
	}) => {
		const { payload = {} } = action;
		const { returnToSuccessModal } = payload;
		const { successModalInfo } = properties;
		const newSuccessModalInfo = {
			...successModalInfo,
			currentTab: 'variant',
			subHeaderContent: t('Do you want to do anything else?')
		};
		updateProperties({
			audiencesModalInfo: null,
			successModalInfo: returnToSuccessModal
				? { ...newSuccessModalInfo, render: true }
				: { ...newSuccessModalInfo, render: false }
		});
	},
	[UIB_PAGE_PROPERTIES_MODAL_DISMISSED]: ({ action, updateProperties }) => {
		action.stopPropagation();
		updateProperties({
			showPagePropertiesModal: false
		});
	},
	['SN_UIB_APP#PAGE_CONFIGURATION_MODAL_OPENED']: ({
		updateProperties,
		properties,
		action
	}) => {
		let route;
		if (action.payload && action.payload.route) {
			route = flattenAppRoutes(properties.appRoutes).find(
				(route) => route.page.sysId === action.payload.route
			);
		} else {
			route = findRouteForCurrentPage(
				properties.appRoutes,
				properties.routeState.pageSysId
			);
		}
		if (route === undefined || route === null) {
			return;
		}
		const {
			name,
			scope,
			sysId,
			routeType,
			optionalParameters,
			fields,
			page,
			order,
			icon,
			sysPolicy
		} = route;

		updateProperties({
			pageConfigurationModalInfo: {
				route: {
					routeType,
					optionalParameters,
					fields,
					order,
					icon,
					sysPolicy,
					page: {
						name,
						scope,
						sysId: page.sysId
					},
					sysId
				}
			}
		});
	},
	[UIB_PAGE_CONFIGURATION_MODAL_MODAL_DISMISSED]: ({ updateProperties }) => {
		updateProperties({
			pageConfigurationModalInfo: null
		});
	},

	['SN_UIB_APP#NEW_VARIANT_MODAL_OPENED']: checkForUnsavedChanges(
		({ updateProperties, action, properties }) => {
			const { payload = {} } = action;
			const { copyFrom, variant = {}, pageSysId: payloadPageSysId } = payload;
			let isUsingSharedPage;
			const associatedTypes = get(
				variant,
				`macroponentInstance.macroponentDefinitions[${get(
					variant,
					'macroponentInstance.rootMacroponentId'
				)}].associatedTypes`
			);
			if (associatedTypes) {
				isUsingSharedPage =
					associatedTypes.indexOf(SHARED_PAGE_DEFINITION_SYS_ID) > -1;
			}
			let variantName = '',
				copyFromSysId = null,
				variantOrder;
			if (copyFrom) {
				const copyText = t('copy');
				if (copyFrom === 'current') {
					const {
						pageState: {
							name: currentPageName,
							uiTypeParameters: { order }
						},
						routeState: { variantSysId: currentPageSysId }
					} = properties;
					variantName = `${currentPageName} ${copyText}`;
					copyFromSysId = currentPageSysId;
					variantOrder = order;
				} else {
					const { name: variantPageName, sysId: variantSysId, order } = variant;
					variantName = `${variantPageName} ${copyText}`;
					copyFromSysId = variantSysId;
					variantOrder = order;
				}
			}

			updateProperties({
				newVariantModalInfo: {
					pageSysId: payloadPageSysId || properties.routeState.pageSysId,
					copyFrom: copyFromSysId,
					variantName,
					order: variantOrder,
					rootMacroponentId: isUsingSharedPage
						? get(variant, 'macroponentInstance.rootMacroponentId')
						: null
				}
			});
		}
	),
	[UIB_VARIANT_MODAL_MODAL_DISMISSED]: ({ updateProperties }) => {
		updateProperties({ newVariantModalInfo: null });
	},
	[UIB_FIELDS_PROPS_SYNC_DIALOG_DISMISS]: ({ updateProperties }) => {
		updateProperties({ syncDialogProps: null });
	},
	['EVENT_CONFIG_PANE#DISPATCHED_EVENTS_MODAL_OPENED']: ({
		action,
		updateProperties,
		properties
	}) => {
		openDispatchedEventsModal(action, updateProperties, properties);
	},
	['PAGE_EVENT_CONFIG_PANE#MACROPONENT_CONTRACT_EVENT_CREATED']: ({
		action,
		updateProperties,
		properties
	}) => {
		openDispatchedEventsModal(action, updateProperties, properties);
	},
	['PAGE_EVENT_CONFIG_PANE#MACROPONENT_CONTRACT_EVENT_EDITED']: ({
		action,
		updateProperties,
		properties
	}) => {
		openDispatchedEventsModal(action, updateProperties, properties);
	},
	[SN_UIB_APP_SCREEN_CONFIGURATION_MODAL_OPENED]: ({
		action,
		updateProperties
	}) => {
		const {
			payload: { configType, screenSysId, viewportScreenInfo }
		} = action;
		updateProperties({
			screenConfigurationModalInfo: {
				configType,
				screenSysId,
				viewportScreenInfo
			}
		});
	},
	['UIB_PAGE_MANAGEMENT_PANEL#ADVANCED_URL_CONFIGURATION_MODAL_REQUESTED']: checkForUnsavedChanges(
		({ action, updateProperties }) => {
			const {
				payload: { route, configType }
			} = action;
			updateProperties({
				advancedUrlConfigurationModalInfo: {
					route,
					configType
				}
			});
		}
	),
	[UIB_ADVANCED_URL_CONFIGURATION_MODAL_DISMISSED]: ({
		action,
		properties,
		updateProperties
	}) => {
		const { payload = {} } = action;
		const { returnToSuccessModal } = payload;
		const { successModalInfo } = properties;
		const newSuccessModalInfo = {
			...successModalInfo,
			currentTab: 'page',
			subHeaderContent: t('Do you want to do anything else?')
		};
		updateProperties({
			advancedUrlConfigurationModalInfo: null,
			successModalInfo: returnToSuccessModal
				? { ...newSuccessModalInfo, render: true }
				: { ...newSuccessModalInfo, render: false }
		});
	},
	[UIB_SCREEN_CONFIGURATION_MODAL_DISMISSED]: ({
		action,
		properties,
		updateProperties
	}) => {
		const { payload = {} } = action;
		const { returnToSuccessModal } = payload;
		const { successModalInfo } = properties;
		const newSuccessModalInfo = {
			...successModalInfo,
			currentTab: 'variant',
			subHeaderContent: t('Do you want to do anything else?')
		};
		updateProperties({
			screenConfigurationModalInfo: null,
			successModalInfo: returnToSuccessModal
				? { ...newSuccessModalInfo, render: true }
				: { ...newSuccessModalInfo, render: false }
		});
	},
	['UIB_PAGE_MANAGEMENT_PANEL#AUDIENCES_MODAL_REQUESTED']: ({
		action,
		updateProperties
	}) => {
		const {
			payload: { screenSysId: screenId }
		} = action;
		updateProperties({
			audiencesModalInfo: {
				screenId
			}
		});
	},
	['UIB_PAGE_MANAGEMENT_PANEL#EDIT_DASHBOARD_CLICKED']: ({
		action,
		properties,
		updateProperties
	}) => {
		const { appRoutes } = properties;
		const { dashboardId } = action.payload || {};

		if (!dashboardId) {
			return;
		}

		const dashboard = flattenDashboardRoutes(appRoutes).find(
			(route) => route.id === dashboardId
		);
		const { routeType, name, scope, id, icon, order, sysPolicy } = dashboard;

		updateProperties({
			pageConfigurationModalInfo: {
				route: {
					icon,
					order,
					routeType,
					page: {
						name,
						scope,
						sysId: id
					},
					sysId: id,
					sysPolicy
				}
			}
		});
	},
	[UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL_PROPERTIES_MODAL_OPENED]: ({
		action,
		updateProperties
	}) => {
		action.stopPropagation();
		updateProperties({
			showPagePropertiesModal: true
		});
	},
	[SHARE_CLICKED]: ({ action, updateProperties }) => {
		action.stopPropagation();
		updateProperties({
			showSharingDialog: true
		});
	},
	[SHARING_MODAL_CLOSED]: ({ updateProperties }) => {
		updateProperties({ showSharingDialog: false });
	},
	[UIB_SCREEN_CONFIGURATION_MODAL_UNLOCK_TO_OVERRIDE_CLICKED]: ({
		updateProperties,
		dispatch,
		action
	}) => {
		updateProperties({
			screenConfigurationModalInfo: null
		});

		dispatch(SN_UIB_APP_UNLOCK_TO_CREATE_OVERRIDE_CLICKED, {
			...action.payload
		});
	},
	[DASHBOARD_SHARING_NOTIFICATION]: ({ action, updateProperties }) => {
		const { show, status, icon, content } = action.payload;
		updateProperties({
			alertProps: {
				show,
				status,
				icon,
				content,
				action: {
					type: 'dismiss'
				}
			}
		});
	},
	['UIB_ELEMENT_CONFIGURATION_PANEL#DA_MAPPING_MODAL_REQUESTED']: ({
		action,
		updateProperties
	}) => {
		const { elementId } = action.payload;
		updateProperties({
			daEventMappingModalInfo: {
				elementId
			}
		});
	},
	[UIB_ELEMENT_DA_EVENT_MAPPING_MODAL_DISMISSED]: ({ updateProperties }) => {
		updateProperties({
			daEventMappingModalInfo: null
		});
	}
};

const openDispatchedEventsModal = (action, updateProperties, properties) => {
	const { headerLabel, footerActions, componentProps } = action.payload;
	const { payloadFields = [] } = componentProps;
	const { pageState } = properties;
	const updatedPayloadFields = payloadFields.map((property) => ({
		name: property.name,
		type: property.fieldType,
		label: property.label,
		interfaceApi: property.interfaceApiName,
		id: cuid()
	}));
	componentProps.payloadFields = updatedPayloadFields;

	updateProperties({
		modalConfig: {
			componentName: 'sn-uib-dispatched-events-modal',
			headerLabel,
			footerActions,
			componentProps,
			variantScope: pageState ? pageState.scope : undefined
		}
	});
};

const findRouteForCurrentPage = (appRoutes, pageSysId) =>
	appRoutes.find((appRoute) => {
		return appRoute.page.sysId === pageSysId;
	});
