export const UIB_CREATE_SUCCESS_MODAL_DISMISSED =
	'UIB_CREATE_SUCCESS_MODAL#MODAL_DISMISSED';
export const UIB_CREATE_SUCCESS_MODAL_ADD_FIELDS_CLICKED =
	'UIB_CREATE_SUCCESS_MODAL#ADD_FIELDS_CLICKED';
export const UIB_CREATE_SUCCESS_MODAL_ADD_PARAMS_CLICKED =
	'UIB_CREATE_SUCCESS_MODAL#ADD_PARAMS_CLICKED';
export const UIB_CREATE_SUCCESS_MODAL_ADD_AUDIENCES_CLICKED =
	'UIB_CREATE_SUCCESS_MODAL#ADD_AUDIENCES_CLICKED';
export const UIB_CREATE_SUCCESS_MODAL_SET_CONDITIONS_CLICKED =
	'UIB_CREATE_SUCCESS_MODAL#SET_CONDITIONS_CLICKED';
import { t } from 'sn-translate';

export const actions = {
	fields: UIB_CREATE_SUCCESS_MODAL_ADD_FIELDS_CLICKED,
	parameters: UIB_CREATE_SUCCESS_MODAL_ADD_PARAMS_CLICKED,
	audiences: UIB_CREATE_SUCCESS_MODAL_ADD_AUDIENCES_CLICKED,
	conditions: UIB_CREATE_SUCCESS_MODAL_SET_CONDITIONS_CLICKED
};

export const getTabs = (selectedPageIsTab) => [
	{
		id: 'page',
		label: selectedPageIsTab ? t('Tab') : t('Page')
	},
	{
		id: 'variant',
		label: t('Variant')
	}
];

export const getNavItems = (selectedPageIsTab) => {
	const pageDescription = selectedPageIsTab
		? t('Ordered pieces of data that are required by your tab to work.')
		: t('Ordered pieces of data that are required by your page to work.');

	const variantDescription = selectedPageIsTab
		? t('You can adjust when this tab will display.')
		: t('You can adjust when this page will display.');

	const message = selectedPageIsTab
		? t('Only data driven tabs have required parameters.')
		: t('Commonly used required parameters are: table, sysID, view.');
	return {
		page: {
			sectionLabel: t('Advanced URL settings'),
			items: [
				{
					label: t('Required parameters'),
					/* prettier-ignore */
					description: `${pageDescription} ${message}`,
					id: 'fields'
				},
				{
					label: t('Optional parameters'),
					description: t('These are optional.'),
					id: 'parameters'
				}
			]
		},
		variant: {
			sectionLabel: t('Visibility'),
			items: [
				{
					label: t('Audiences'),
					/* prettier-ignore */
					description: t('You can target specific audiences with specific content. Audiences are based on user criteria or roles.'),
					id: 'audiences'
				},
				{
					label: t('Conditions'),
					/* prettier-ignore */
					description: variantDescription,
					id: 'conditions'
				}
			]
		}
	};
};
