import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { lifecycleHandlers } from './actionHandlers/lifecycleHandlers';
import styles from './uib-page-info-data-manager.scss';
import {
	applicationsRequestBehavior,
	applicationsRequestActions,
	applicationsRequestHandlers
} from '../../behaviors/applicationsRequestBehavior';
import {
	protectionPolicyGraphQLBehavior,
	protectionPolicyGQLHandlers
} from '../../behaviors/protectionPolicyGraphQLBehavior';
import {
	validationBehavior,
	isRequired,
	isNonEmptyString,
	isRequiredTypeahead
} from '../../behaviors/validationBehavior';

createCustomElement('uib-page-info-data-manager', {
	view: (state) => {
		const {
			applications,
			isLoadingApplications,
			isLoadingProtectionChoices,
			properties,
			protectionChoices
		} = state;

		const {
			pageName,
			selectedApplication,
			selectedProtectionChoice,
			isEditMode,
			disabled,
			validationErrors
		} = properties;

		if (isLoadingApplications || isLoadingProtectionChoices) {
			return (
				<div className="loader-wrapper">
					<sn-uib-loader label={t('Loading ...')} size="md" />
				</div>
			);
		}

		return (
			<sn-uib-page-info
				available-applications={applications}
				available-protection-choices={protectionChoices}
				is-edit-mode={isEditMode}
				disabled={disabled && isEditMode}
				page-name={pageName}
				selected-application={selectedApplication}
				selected-protection-choice={selectedProtectionChoice}
				validationErrors={validationErrors}
			/>
		);
	},
	initialState: {
		pageTypes: []
	},
	properties: {
		pageName: {
			default: ''
		},
		selectedApplication: {
			default: {
				id: 'global',
				label: 'Global'
			}
		},
		selectedProtectionChoice: {
			default: {
				id: 'none',
				label: t('None (default)')
			}
		},
		isEditMode: {
			default: false
		},
		/**
		 * Whether the element belongs to a page that has a read-only state, eg.
		 * should not allow changing any values for that page
		 * @type {boolean}
		 */
		disabled: { default: false }
	},
	styles,
	behaviors: [
		{
			behavior: validationBehavior,
			options: {
				config: [
					{
						id: 'pageName',
						validators: [
							{
								validator: isRequired,
								errorMessage: t('Page name is required.')
							},
							{
								validator: isNonEmptyString,
								errorMessage: t('Page name is required.')
							}
						]
					},
					{
						id: 'selectedApplication',
						validators: [
							{
								validator: isRequiredTypeahead,
								options: {
									path: 'applications'
								},
								errorMessage: t('Application is required.')
							}
						]
					},
					{
						id: 'selectedProtectionChoice',
						validators: [
							{
								validator: isRequiredTypeahead,
								options: {
									path: 'protectionChoices'
								},
								errorMessage: t('Protection choice is required.')
							}
						]
					}
				]
			}
		},
		applicationsRequestBehavior,
		protectionPolicyGraphQLBehavior
	],
	actionHandlers: {
		['SN_UIB_PAGE_INFO#APPLICATION_SEARCHED']: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(applicationsRequestActions.APPLICATION_SEARCHED, action.payload);
		},
		...applicationsRequestHandlers,
		...lifecycleHandlers,
		...protectionPolicyGQLHandlers
	}
});
