import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import get from 'lodash/get';

import {
	BRANDING_UPDATE_REQUESTED,
	CANCEL_BTN,
	GENERAL_UPDATE_REQUESTED,
	NAVIGATION_UPDATE_REQUESTED,
	PAGE_BRANDING,
	PAGE_GENERAL,
	PAGE_NAVIGATION,
	PAGE_SEARCH,
	PAGE_TOOLBAR,
	PAGE_UTILITIES,
	SAVE_BTN,
	SEARCH_UPDATE_REQUESTED,
	SETTINGS_CANCEL_REQUESTED,
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_FETCH_SUCCEEDED,
	SETTINGS_UPDATE_REQUESTED,
	TOOLBAR_UPDATE_REQUESTED,
	UTILITIES_UPDATE_REQUESTED
} from '../constants';

const props = `
	  panelSysId
	  applicationType
`;

const query = `
query snExperienceSettingsConnected($sysId: ID!) {
  snExpSettings {
    settings {
      settings(sysId: $sysId) {
        ${props}
      }
    }
  }
}`;

export const settingsFetchRequestedEffect = createGraphQLEffect(query, {
	variableList: ['sysId'],
	successActionType: SETTINGS_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const settingsFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const { panelSysId, applicationType } = get(
		payload,
		`data.snExpSettings.settings.settings`,
		{}
	);
	updateState({ panelSysId, applicationType });
};

export const settingsUpdateRequestedEffect = ({
	properties: { sysId },
	state: { general, branding, navigation, search, toolbar, utilities },
	dispatch,
	updateState
}) => {
	if (general.updated) {
		const { title, description, path } = general;
		dispatch(GENERAL_UPDATE_REQUESTED, { sysId, title, description, path });
		updateState([
			{ path: `${PAGE_GENERAL}.updated`, value: false, operation: 'set' }
		]);
	}

	if (branding.updated) {
		const { primaryThemeId } = branding;
		dispatch(BRANDING_UPDATE_REQUESTED, { sysId, primaryThemeId });
		updateState([
			{ path: `${PAGE_BRANDING}.updated`, value: false, operation: 'set' }
		]);
	}

	if (navigation.updated) {
		const { menu, footerTop, footerBottom } = navigation;
		dispatch(NAVIGATION_UPDATE_REQUESTED, {
			sysId,
			menu,
			footerTop,
			footerBottom
		});
		updateState([
			{ path: `${PAGE_NAVIGATION}.updated`, value: false, operation: 'set' }
		]);
	}

	if (search.updated) {
		const { searchPublic, searchPrivate } = search;
		dispatch(SEARCH_UPDATE_REQUESTED, { sysId, searchPublic, searchPrivate });
		updateState([
			{ path: `${PAGE_SEARCH}.updated`, value: false, operation: 'set' }
		]);
	}

	if (toolbar.updated) {
		const { buttons } = toolbar;
		dispatch(TOOLBAR_UPDATE_REQUESTED, { sysId, buttons: buttons });
		updateState([
			{ path: `${PAGE_TOOLBAR}.updated`, value: false, operation: 'set' }
		]);
	}

	if (utilities.updated) {
		const { notifications, globalSearch, selectedSearchSource } = utilities;
		dispatch(UTILITIES_UPDATE_REQUESTED, {
			sysId,
			notifications,
			globalSearch,
			selectedSearchSource
		});
		updateState([
			{ path: `${PAGE_UTILITIES}.updated`, value: false, operation: 'set' }
		]);
	}
};

export const settingsButtonClickedEffect = ({
	action: {
		stopPropagation,
		payload: { name }
	},
	dispatch
}) => {
	stopPropagation();
	if (name === CANCEL_BTN) {
		dispatch(SETTINGS_CANCEL_REQUESTED);
	}

	if (name === SAVE_BTN) {
		dispatch(SETTINGS_UPDATE_REQUESTED);
	}
};

export const settingsUpdatedEffect = ({
	action: {
		payload: { page, name, value, id, key }
	},
	updateState
}) => {
	if (key) {
		updateState([
			{ path: `${page}.${key}[${id}].${name}`, value, operation: 'set' }
		]);
	} else {
		updateState([{ path: `${page}.${name}`, value, operation: 'set' }]);
	}
	updateState([{ path: `${page}.updated`, value: true, operation: 'set' }]);
};
