import { createHttpEffect } from '@servicenow/ui-effect-http';

// adding double ?? to the tableName parameter is odd, but createHttpeffect will remove the ? character after the parameter, so we need 2 of them.
const fieldsRequestedHttpEffect = createHttpEffect(
	'/api/now/ui/meta/:table??sysparm_operators=false&sysparm_keywords=false',
	{
		method: 'GET',
		batch: false,
		pathParams: ['table'],
		dataParam: ['table'],
		successActionType: 'UIB_APP#FIELD_VALUES_RESOLVED'
	}
);

export const fieldHandlers = {
	'UIB_ELEMENT_CONFIGURATION_PANEL#FIELD_VALUES_REQUESTED': fieldsRequestedHttpEffect,
	'UIB_DATA_BROKER_CONFIGURATION_PANEL#FIELD_VALUES_REQUESTED': fieldsRequestedHttpEffect,
	'UIB_APP#FIELD_VALUES_RESOLVED': ({
		properties,
		action,
		updateProperties
	}) => {
		const {
			payload: {
				result: { columns }
			},
			meta: {
				request: { data: table }
			}
		} = action;
		const values = Object.entries(columns).map(([, { name: id, label }]) => ({
			id,
			label
		}));
		updateProperties({
			tableFieldValues: {
				...properties.tableFieldValues,
				[table]: values
			}
		});
	}
};
