import '@servicenow/now-modal';
import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './uib-create-override-dialog.scss';

export const UIB_CREATE_OVERRIDE_DIALOG_CONFIRMED =
	'UIB_CREATE_OVERRIDE_DIALOG_CONFIRMED';
export const UIB_CREATE_OVERRIDE_DIALOG_INTERACTION_COMPLETE =
	'UIB_CREATE_OVERRIDE_DIALOG_INTERACTION_COMPLETE';
const SN_UIB_CREATE_OVERRIDE_DIALOG_ENABLED =
	'SN_UIB_CREATE_OVERRIDE_DIALOG_ENABLED';

const view = (state, { dispatch }) => {
	const { opened, builderType } = state.properties;
	const { overrideDisabled, counter } = state;
	const overrideDialogType =
		builderType === 'Dashboard' ? 'dashboard' : 'variant';
	return (
		<now-modal
			opened={opened}
			manage-opened={true}
			size="sm"
			header-label={t('Create an override?')}
			hook-insert={() => dispatch(SN_UIB_CREATE_OVERRIDE_DIALOG_ENABLED)}>
			<p className="uib-create-override-dialog-content">
				{
					/* prettier-ignore */
					t('Creating an override creates a copy of this {0} in your current domain. An override is a domain process that supersedes a previously created process that was inherited from a parent or global domain.',overrideDialogType)
				}
			</p>
			<div className="uib-create-override-dialog-footer">
				<now-button
					disabled={overrideDisabled}
					label={
						overrideDisabled
							? t('Create override {0}', `(${counter})`)
							: t('Create override')
					}
					on-click={() => dispatch(UIB_CREATE_OVERRIDE_DIALOG_CONFIRMED)}
					variant="primary"
					size="md"
				/>
			</div>
		</now-modal>
	);
};

createCustomElement('uib-create-override-dialog', {
	view,
	styles,
	initialState: {
		overrideDisabled: true,
		counter: 3
	},
	properties: {
		opened: {
			default: false
		},
		confirmAction: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					payload: { type: 'object' },
					actionType: { type: 'string' }
				}
			}
		},
		builderType: {
			default: null
		}
	},
	actionHandlers: {
		[UIB_CREATE_OVERRIDE_DIALOG_CONFIRMED]: ({
			action,
			properties,
			dispatch
		}) => {
			action.stopPropagation();
			const {
				confirmAction: { actionType, payload = {} }
			} = properties;
			actionType && dispatch(actionType, payload);
			dispatch(UIB_CREATE_OVERRIDE_DIALOG_INTERACTION_COMPLETE);
		},
		['NOW_MODAL#OPENED_SET']: ({ action, dispatch }) => {
			action.stopPropagation();
			dispatch(UIB_CREATE_OVERRIDE_DIALOG_INTERACTION_COMPLETE);
		},
		[SN_UIB_CREATE_OVERRIDE_DIALOG_ENABLED]: ({ action, updateState }) => {
			action.stopPropagation();
			const disabledInterval = setInterval(() => {
				updateState(({ state }) => {
					if (state.counter !== 1) {
						return {
							counter: state.counter - 1
						};
					} else {
						clearInterval(disabledInterval);
						return {
							overrideDisabled: false
						};
					}
				});
			}, 1000);
		}
	}
});
