import { actionTypes } from '@servicenow/ui-core';
import BrowserRouter from './BrowserRouter';

const { COMPONENT_CONNECTED, COMPONENT_DISCONNECTED } = actionTypes;

/**
 * @typedef RouteCoeffects
 * @property {Function} dispatch
 * @property {string} routeName
 * @property {object.<string, any>} routeState
 */

/** @type {BrowserRouter} */
export let ROUTER;

/**
 * @param {BrowserRouter} router
 * @param {RouteCoeffects} routeCoeffects
 */
export function handleTransition(router, routeCoeffects) {
	let { routeState } = routeCoeffects;
	const { routeName, dispatch } = routeCoeffects;
	const { onEnter } = router.getRouteOptions(routeName) || {};

	if (typeof onEnter === 'function') {
		const newState = onEnter({
			...routeCoeffects,
			transitionTo(routeName, routeState) {
				dispatch('ROUTER_BEHAVIOR#TRANSITION_TO', { routeName, routeState });
			},
			replaceWith(routeName, routeState) {
				dispatch('ROUTER_BEHAVIOR#REPLACE_WITH', { routeName, routeState });
			}
		});

		if (newState && newState !== routeState) {
			router.replaceRouteState(newState);
			routeState = newState;
		}
	}

	const payload = {
		routeName,
		routeState
	};

	dispatch('ROUTER_BEHAVIOR#UPDATE_ROUTE_PROPERTIES', payload);
	dispatch('ROUTER_BEHAVIOR#ROUTE_ENTERED', payload);
}

/**
 * @param dispatch
 */
export function createPopStateHandler(dispatch) {
	/**
	 * @param {PopStateEvent} event
	 * @param {BrowserRouter} router
	 */
	function popStateHandler(event, router) {
		const { state: routeState = {}, currentTarget } = event;
		const { pathname } = currentTarget.location;
		const routeName = router.getRouteNameByUrl(pathname);

		if (routeName) {
			const routeParams = router.extractRouteParams(routeName, pathname);
			const routeCoeffects = {
				dispatch,
				routeName,
				routeState: {
					...routeState,
					...routeParams
				}
			};
			handleTransition(router, routeCoeffects);
		}
	}

	return popStateHandler;
}

const unselectCurrentlySelectedElement = (dispatch) => {
	// when a modal is selected for viewing on the stage
	// it is rendered on top of the entire stage by the
	// renderer unlike the rest of the elements which are
	// rendered by the middleware. Whenever we navigate
	// we need to unselect the currently selected element
	// to remove the modal from the DOM.
	dispatch('UIB_ELEMENT#ELEMENT_SELECTED', {
		elementId: null,
		elementInstanceName: null
	});
};

const getNamespace = ({ isComponentBuilder, isDashboardBuilder }) => {
	/* eslint-disable no-undef */
	if (
		process.env.NODE_ENV === 'development' ||
		process.env.NODE_ENV === 'test'
	) {
		return '';
	} else if (isDashboardBuilder) {
		return '/now/build/dashboard';
	} else if (isComponentBuilder) {
		return '/now/build';
	}
	/* eslint-enable no-undef */
	return '/now/build/ui';
};

const routerBehavior = {
	name: 'router',

	properties: {
		routeName: {
			default: null,
			selectable: true
		},
		routeState: {
			default: {},
			selectable: true
		},
		isComponentBuilder: {
			default: false
		},
		isDashboardBuilder: {
			default: false
		}
	},

	actionHandlers: {
		[COMPONENT_CONNECTED]: ({
			action,
			dispatch,
			updateProperties,
			properties
		}) => {
			if (!ROUTER) {
				const { isComponentBuilder, isDashboardBuilder } = properties;

				const namespace = getNamespace({
					isComponentBuilder,
					isDashboardBuilder
				});
				const routes = action.payload.options.routesFn({
					isComponentBuilder,
					isDashboardBuilder
				});
				const options = { namespace, ...action.payload.options, routes };

				ROUTER = new BrowserRouter(options);
				ROUTER.onPopState(createPopStateHandler(dispatch));
				const {
					currentRouteName: routeName,
					currentHistoryState = {}
				} = ROUTER;

				if (!routeName) {
					const windowURL = window.location.href;
					const applicationType = windowURL.match(
						/(workspace|app|components|dashboards)/
					)[1];
					const payload = {
						routeName,
						routeState: { ...currentHistoryState, show404: true }
					};
					updateProperties(payload);
					dispatch('UIB_APP#APP_LOADED', { ...payload, applicationType });
					return;
				}

				const { currentRouteParams } = ROUTER;
				const queryParams = window.location.search
					? (window.location.search || '?')
							.slice(1)
							.split('&')
							.reduce((acc, param) => {
								const [key, value] = param.split('=');
								return {
									...acc,
									[key]: value
								};
							}, {})
					: {};
				dispatch('ROUTER_BEHAVIOR#REPLACE_WITH', {
					routeName,
					routeState:
						Object.keys(queryParams).length > 0
							? {
									...currentHistoryState,
									...currentRouteParams,
									queryParams
							  }
							: {
									...currentHistoryState,
									...currentRouteParams
							  }
				});
			}
		},

		['ROUTER_BEHAVIOR#UPDATE_ROUTE_PROPERTIES']: {
			effect: ({ action, updateProperties }) => {
				const { routeName, routeState = {} } = action.payload;
				updateProperties({ routeName, routeState });
			},
			stopPropagation: true
		},

		['ROUTER_BEHAVIOR#TRANSITION_TO']: {
			effect: async ({ action, dispatch }) => {
				if (!ROUTER) {
					return;
				}

				const { routeName } = action.payload;
				const routeState = (await action.payload.routeState) || {};

				if (!ROUTER.hasRoute(routeName)) {
					dispatch('ROUTER_BEHAVIOR#ROUTE_NOT_FOUND', {
						routeName,
						routeState
					});
					return;
				}

				/** @type {RouteCoeffects} */
				const routeCoeffects = {
					routeName,
					routeState,
					dispatch
				};

				unselectCurrentlySelectedElement(dispatch);
				ROUTER.pushRouteState(routeState, routeState, routeName);
				handleTransition(ROUTER, routeCoeffects);
			},
			stopPropagation: true
		},

		['ROUTER_BEHAVIOR#REPLACE_WITH']: {
			effect: async ({ action, dispatch }) => {
				if (!ROUTER) {
					return;
				}

				const { routeName } = action.payload;
				const routeState = (await action.payload.routeState) || {};

				if (!ROUTER.hasRoute(routeName)) {
					dispatch('ROUTER_BEHAVIOR#ROUTE_NOT_FOUND', {
						routeName,
						routeState
					});
					return;
				}

				/** @type {RouteCoeffects} */
				const routeCoeffects = {
					routeName,
					routeState,
					dispatch
				};

				unselectCurrentlySelectedElement(dispatch);
				ROUTER.replaceRouteState(routeState, routeState, routeName);
				handleTransition(ROUTER, routeCoeffects);
			},
			stopPropagation: true
		},

		['ROUTER_BEHAVIOR#LOAD']: {
			effect: async ({ action, dispatch }) => {
				if (!ROUTER) {
					return;
				}

				const { routeName } = action.payload;
				const routeState = (await action.payload.routeState) || {};

				if (!ROUTER.hasRoute(routeName)) {
					dispatch('ROUTER_BEHAVIOR#ROUTE_NOT_FOUND', {
						routeName,
						routeState
					});
					return;
				}
				ROUTER.load(routeName, routeState);
			},
			stopPropagation: true
		},
		[COMPONENT_DISCONNECTED]: () => {
			if (ROUTER) {
				ROUTER.destroy();
				ROUTER = undefined;
			}
		}
	}
};

export default routerBehavior;
