import { createCustomElement } from '@servicenow/ui-core';

createCustomElement('uib-data-broker-client-state-renderer', {
	view: (state, { dispatch }) => {
		const {
			properties: { selectableProps, currentSelectedProp }
		} = state;
		dispatch('CLIENT_DATA_BROKER#RENDERED', {
			[currentSelectedProp]: selectableProps
		});
	},
	properties: {
		selectableProps: {
			default: {}
		},
		currentSelectedProp: {
			default: ''
		}
	}
});
