import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

export const tablesHandlers = {
	['UIB_APP#LOAD_TABLES']: {
		effect: () => null,
		interceptors: [
			(context) => {
				const inputAction = context.coeffects.action;
				inputAction.payload = {
					['sysparm_allow_public']: true,
					['sysparm_allow_sys_table']: true
				};
				const url = '/api/now/doc/table/schema';
				const options = {
					method: 'GET',
					batch: false,
					queryParams: ['sysparm_allow_public', 'sysparm_allow_sys_table'],
					startActionType: 'UIB_APP#LOAD_TABLES_START',
					successActionType: 'UIB_APP#LOAD_TABLES_SUCCESS',
					errorActionType: 'UIB_APP#LOAD_TABLES_ERROR'
				};
				return {
					...context,
					effects: [
						{
							...createHttpEffect('/api/now/doc/table/schema', options),
							args: [url, options, context.coeffects]
						}
					]
				};
			}
		]
	},

	['UIB_APP#LOAD_TABLES_START']: ({ updateProperties }) => {
		updateProperties({
			isLoadingTables: true
		});
	},

	['UIB_APP#LOAD_TABLES_ERROR']: ({ updateProperties }) => {
		updateProperties({
			isLoadingTables: false,
			alertProps: {
				action: {
					type: 'dismiss'
				},
				content: t('There was an unknown error loading data'),
				status: 'critical'
			}
		});
	},

	['UIB_APP#LOAD_TABLES_SUCCESS']: ({
		action: {
			payload: { result = [] }
		},
		updateProperties
	}) => {
		// TODO: workaround for this: https://code.devsnc.com/dev/design-system/blob/master/components/typeahead/src/utils/typeahead.js#L126.
		// now-typeahead currently dispatches an extra SELECTED_ITEM_SET action when there are two items
		// with duplicate labels, and overwrites the item selected by the user.
		// To defend against that, we are iterating over the tables to find those with duplicate labels and updating the labels
		// on those tables to include the table IDs as well, so that typeahead can differentiate between them.
		const updatedTables = result.reduce((tables, table = {}, idx) => {
			const previousTable = tables[idx - 1] || {};
			if (previousTable.label === table.label) {
				tables.pop();
				tables.push(
					{
						id: previousTable.id,
						label: `${previousTable.label} (${previousTable.id})`
					},
					{ id: table.value, label: `${table.label} (${table.value})` }
				);
			} else if (
				previousTable.label === `${table.label} (${previousTable.id})`
			) {
				tables.push({
					id: table.value,
					label: `${table.label} (${table.value})`
				});
			} else {
				tables.push({ id: table.value, label: table.label });
			}
			return tables;
		}, []);

		updateProperties({
			isLoadingTables: false,
			tables: updatedTables
		});
	}
};
