import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { SN_UIB_APP_SCREEN_CONFIGURATION_MODAL_OPENED } from '../uib-app';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import 'sn-ui-builder-components';
import { EDIT_DASHBOARD_CLICKED } from './actions';
import {
	getComponentsLoading,
	transformPageList,
	getPMPLabelsAndActions
} from './utils';
import { flattenAppRoutes } from '../../utils/appRoutes';
import { forceLoad } from '../uib-router/forcedTransitions';
import { screenNames } from '../../utils/routingConstants';
import qpUtils from '../../utils/queryParams';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const { createFocusRef } = DevxKeyFocusBehavior;

const QUICK_LINK_PAGE_MANAGEMENT_REF = createFocusRef(
	'quick-link-page-management-panel'
);

const findVariantInSelectedPage = (selectedPage, variantSysId) =>
	selectedPage.variants.find((variant) => variant.sysId === variantSysId);

const findPageById = (pages, pageSysId) =>
	pages.find((page) => page.sysId === pageSysId);

createCustomElement('uib-page-management-panel', {
	view: (state) => {
		const { pagesList, properties } = state;
		const {
			isWorkspaceApp,
			pageManagementPanelExpanded,
			pageSysId,
			screenTypeSysId,
			appsSysId,
			disabled,
			navigationDisabled,
			variantActionsDisabled,
			elementTreeDisabled,
			components,
			elementTree,
			selectedElementId,
			activeId,
			isComponentsLoading,
			isPagesLoading,
			pagesRequestError,
			componentsRequestError,
			fieldsParamsEnabled,
			pageVariantsEnabled,
			componentTreeEnabled,
			selectedPageIsTab,
			selectedTab,
			isDashboardBuilder,
			isEmptyState,
			currentOverridesTree,
			isDomainExpanded,
			overridesFetching,
			parentInfo
		} = properties;

		const pmpExpanded = isEmptyState ? false : pageManagementPanelExpanded;

		if (isWorkspaceApp) {
			return (
				<sn-uib-page-management-panel
					ref={QUICK_LINK_PAGE_MANAGEMENT_REF}
					is-workspace-app={isWorkspaceApp}
					selectedPage={pageSysId || screenTypeSysId}
					pages={pagesList}
					components={components}
					elements={elementTree}
					selectedElementId={selectedElementId || activeId}
					isPagesLoading={isPagesLoading}
					pagesRequestError={pagesRequestError}
					isComponentsLoading={isComponentsLoading}
					componentsRequestError={componentsRequestError}
					pageVariantsEnabled={pageVariantsEnabled}
					append-to-payload={{ appsSysId }}
					selected-tab={selectedTab}
					pageManagementPanelExpanded={pmpExpanded}
					disabled={disabled}
					selected-page-is-tab={selectedPageIsTab}
				/>
			);
		}

		const dynamicTabData = get(
			parentInfo,
			'elementProperties.dynamicTabData',
			''
		);
		const {
			dropdownHeaderLabels,
			createDropdownActions,
			listHeaderLabel,
			listHeaderActions
		} = getPMPLabelsAndActions(
			fieldsParamsEnabled,
			selectedPageIsTab,
			dynamicTabData,
			isDashboardBuilder,
			variantActionsDisabled
		);
		return (
			<sn-uib-generic-page-management-panel
				ref={QUICK_LINK_PAGE_MANAGEMENT_REF}
				selectedPage={screenTypeSysId || pageSysId}
				selectedVariant={pageSysId}
				pages={pagesList}
				components={components}
				elements={elementTree}
				selectedElementId={selectedElementId || activeId}
				isPagesLoading={isPagesLoading}
				pagesRequestError={pagesRequestError}
				isComponentsLoading={isComponentsLoading}
				componentsRequestError={componentsRequestError}
				pageVariantsEnabled={pageVariantsEnabled}
				componentTreeEnabled={componentTreeEnabled}
				append-to-payload={{ appsSysId }}
				pageManagementPanelExpanded={pmpExpanded}
				disabled={disabled}
				navigation-disabled={navigationDisabled}
				element-tree-disabled={elementTreeDisabled}
				selected-page-is-tab={selectedPageIsTab}
				selected-tab={selectedTab}
				dropdownHeaderLabels={dropdownHeaderLabels}
				createDropdownActions={createDropdownActions}
				listHeaderLabel={listHeaderLabel}
				listHeaderActions={listHeaderActions}
				is-dashboard-builder={isDashboardBuilder}
				currentOverridesTree={currentOverridesTree}
				is-domain-expanded={isDomainExpanded}
				overrides-fetching={overridesFetching}
			/>
		);
	},
	transformState: (state) => {
		const {
			applicationType,
			routes,
			pages,
			pageVariantsEnabled,
			disabled,
			isPagesLoading,
			displayDomain
		} = state.properties;

		const pagesList = transformPageList(
			applicationType,
			routes,
			pages,
			pageVariantsEnabled,
			disabled,
			isPagesLoading,
			displayDomain
		);

		return {
			...state,
			pagesList
		};
	},
	properties: {
		isWorkspaceApp: {
			default: false
		},
		isDashboardBuilder: {
			default: false
		},
		pages: {
			default: []
		},
		elementTree: {
			default: {}
		},
		selectedElementId: {
			default: null
		},
		activeId: {
			default: null
		},
		components: {
			default: []
		},
		isComponentsLoading: {
			default: true
		},
		isPagesLoading: {
			default: false
		},
		pagesRequestError: {
			default: false
		},
		appsSysId: {
			default: null
		},
		screenTypeSysId: {
			default: null
		},
		applicationType: {
			default: null
		},
		pageSysId: {
			default: null
		},
		pageManagementPanelExpanded: {
			default: true
		},
		isPageStateDirty: {
			default: false
		},
		disabled: {
			default: false
		},
		componentsRequestStatus: {
			default: 'notStarted'
		},
		componentsRequestError: {
			default: false
		},
		routes: {
			default: []
		},
		fieldsParamsEnabled: {
			default: false
		},
		pageVariantsEnabled: {
			default: false
		},
		componentTreeEnabled: {
			default: false
		},
		selectedPageIsTab: {
			default: false
		},
		selectedTab: {
			default: 'pages'
		},
		selectedPage: {
			default: null
		},
		displayDomain: {
			default: false
		},
		isEmptyState: {
			default: false
		},
		currentOverridesTree: {
			default: {}
		},
		isDomainExpanded: {
			default: false
		},
		overridesFetching: {
			default: false
		},
		parentInfo: {
			default: '@uxf/parentInfo'
		},
		navigationDisabled: {
			default: false
		},
		variantActionsDisabled: {
			default: false
		},
		elementTreeDisabled: {
			default: false
		}
	},
	actionHandlers: {
		['SN_UIB_PAGE_MANAGEMENT_PANEL#PAGE_CREATE_CLICKED']: ({
			dispatch,
			action
		}) => {
			dispatch('SN_UIB_APP#CREATE_PAGE_REQUESTED', action.payload);
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#VARIANT_CREATE_CLICKED']: ({ dispatch }) => {
			dispatch('SN_UIB_APP#NEW_VARIANT_MODAL_OPENED');
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ properties, updateProperties }) => {
			const { componentsRequestStatus } = properties;

			const isComponentsLoading = getComponentsLoading(componentsRequestStatus);
			const componentsRequestError = componentsRequestStatus === 'failed';
			updateProperties({ isComponentsLoading, componentsRequestError });
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#PAGE_SELECTED']: ({
			properties,
			dispatch,
			action
		}) => {
			const { applicationType, isWorkspaceApp } = properties;
			const { sysId, pageSysId, appsSysId, name } = action.payload;

			if ((isWorkspaceApp && !sysId) || (!isWorkspaceApp && !pageSysId)) {
				return;
			}

			const payload = {
				sysId,
				appsSysId,
				pageSysId,
				applicationType,
				name
			};

			dispatch('SN_UIB_APP#PAGE_REQUESTED', payload);
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#SCREEN_TYPE_SELECTED']: ({
			action,
			dispatch
		}) => {
			dispatch('SN_UIB_APP#SCREEN_TYPE_REQUESTED', action.payload);
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#COPY_PAGE_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			const {
				pageVariantsEnabled,
				pageSysId,
				pages,
				selectedPage
			} = properties;
			const { id } = action.payload;
			const duplicatePreventedProps = {};
			if (pageVariantsEnabled) {
				const variant = findVariantInSelectedPage(selectedPage, id);
				if (!variant) {
					return;
				}

				if (variant.sysPolicy === 'protected') {
					duplicatePreventedProps.variantSysId = variant.sysId;
				}
				const rootMacroponent = get(
					variant,
					`macroponentInstance.macroponentDefinitions[${get(
						variant,
						'macroponentInstance.rootMacroponentId'
					)}]`
				);

				if (rootMacroponent && rootMacroponent.sysPolicy === 'protected') {
					duplicatePreventedProps.macroponentSysId = rootMacroponent.id;
				}

				if (!isEmpty(duplicatePreventedProps)) {
					dispatch('SN_UIB_APP#DUPLICATE_PREVENTED_OPENED', {
						...duplicatePreventedProps,
						name: variant.name
					});
					return;
				}

				dispatch('SN_UIB_APP#NEW_VARIANT_MODAL_OPENED', {
					variant,
					pageSysId: selectedPage.sysId,
					copyFrom: 'payload'
				});
			} else {
				const page = findPageById(pages, id);
				if (!page) {
					return;
				}

				if (pageSysId === id) {
					dispatch('SN_UIB_APP#CLONE_PAGE_REQUESTED', {
						page,
						isPageSysIdCurrentId: true
					});
				} else {
					dispatch('SN_UIB_APP#CLONE_PAGE_REQUESTED', { page });
				}
			}
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#EDIT_PAGE_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			const { pageVariantsEnabled, pages } = properties;
			if (pageVariantsEnabled) {
				dispatch(SN_UIB_APP_SCREEN_CONFIGURATION_MODAL_OPENED, {
					screenSysId: action.payload.id
				});
			} else {
				const page = findPageById(pages, action.payload.id);
				if (!page) {
					return;
				}
				dispatch('SN_UIB_APP#EDIT_PAGE_REQUESTED', { page });
			}
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#EDIT_AUDIENCES_CLICKED']: ({
			action,
			dispatch
		}) => {
			dispatch('UIB_PAGE_MANAGEMENT_PANEL#AUDIENCES_MODAL_REQUESTED', {
				screenSysId: action.payload.id
			});
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#EDIT_CONDITIONS_CLICKED']: ({
			action,
			dispatch
		}) => {
			dispatch(SN_UIB_APP_SCREEN_CONFIGURATION_MODAL_OPENED, {
				screenSysId: action.payload.id,
				configType: 'conditions'
			});
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#EDIT_ROUTE_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			const { id } = action.payload;

			if (properties.isDashboardBuilder) {
				dispatch(EDIT_DASHBOARD_CLICKED, { dashboardId: id });
			} else {
				dispatch('SN_UIB_APP#PAGE_CONFIGURATION_MODAL_OPENED', { route: id });
			}
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#EDIT_FIELDS_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			const { id } = action.payload;
			const route = flattenAppRoutes(properties.routes).find(
				(route) => route.page.sysId === id
			);

			const {
				name,
				routeType,
				fields,
				optionalParameters,
				page,
				scope,
				sysPolicy
			} = route;

			const payload = {
				route: {
					name,
					routeType,
					fields,
					optionalParameters,
					sysId: page.sysId,
					scope,
					sysPolicy
				},
				configType: 'fields'
			};

			dispatch(
				'UIB_PAGE_MANAGEMENT_PANEL#ADVANCED_URL_CONFIGURATION_MODAL_REQUESTED',
				payload
			);
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#EDIT_PARAMETERS_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			const { id } = action.payload;
			const route = flattenAppRoutes(properties.routes).find(
				(route) => route.page.sysId === id
			);

			const {
				name,
				routeType,
				fields,
				optionalParameters,
				page,
				scope
			} = route;

			const callbackPayload = {
				route: {
					name,
					routeType,
					fields,
					optionalParameters,
					sysId: page.sysId,
					scope
				},
				configType: 'parameters'
			};

			dispatch(
				'UIB_PAGE_MANAGEMENT_PANEL#ADVANCED_URL_CONFIGURATION_MODAL_REQUESTED',
				callbackPayload
			);
		},
		['SN_UIB_OVERRIDES_TREE#OVERRIDE_SELECTED']: ({ action, properties }) => {
			const { appsSysId, screenTypeSysId: pageSysId } = properties;
			const { variantId: variantSysId, dashboardId } = action.payload;
			const queryParams = qpUtils.setParam(
				{},
				{
					queryNoDomain: true,
					overrideVariantOpenedFrom: !dashboardId
						? properties.pageSysId
						: get(properties, 'selectedPage.screen.id', null)
				}
			);

			let routeParams = {};
			if (!dashboardId) {
				routeParams = {
					...routeParams,
					appsSysId,
					pageSysId,
					variantSysId,
					queryParams
				};
			} else {
				routeParams = {
					...routeParams,
					dashboardId,
					queryParams: {
						...queryParams,
						overrideId: variantSysId
					}
				};
			}

			forceLoad(
				dashboardId ? screenNames.dashboardsDashboard : screenNames.appVariant,
				routeParams
			);
		}
	}
});
