import '@servicenow/now-button';
import '@servicenow/now-input';
import '@servicenow/now-empty-state';
import { snUibUrlPreview } from 'sn-ui-builder-components';
import { t } from 'sn-translate';

export default ({ parameters, disabled, runtimeUrl, showUrlPreview }) => {
	return (
		<div>
			<span>
				{
					/* prettier-ignore */
					t('Optional parameters are optional pieces of data added to the URL. Unlike required parameters, optional parameters are always name value pairs that work no matter what order theyre provided.')
				}
			</span>
			<PageParametersForm
				parameters={parameters}
				disabled={disabled}
				runtimeUrl={runtimeUrl}
				showUrlPreview={showUrlPreview}
			/>
		</div>
	);
};

/**
 * @param root0
 * @param root0.parameters
 * @param root0.disabled
 * @param root0.runtimeUrl
 * @param root0.showUrlPreview
 */
export function PageParametersForm({
	parameters,
	disabled,
	runtimeUrl,
	showUrlPreview
}) {
	const appendToPayload = { button: 'add-parameter' };
	return (
		<div className="set-height">
			<form className="set-height">
				{showUrlPreview ? (
					<sn-uib-url-preview
						runtime-url={runtimeUrl}
						parameters={parameters}
					/>
				) : null}
				<h4 className="parameters-heading">
					<span>{t('Parameters')}</span>
					<now-button-bare
						variant="secondary"
						icon-start="plus-outline"
						label={t('Add')}
						disabled={disabled}
						append-to-payload={appendToPayload}
					/>
				</h4>
				{parameters && Object.keys(parameters).length ? (
					<div className="parameters">
						{Object.keys(parameters || {}).map((id, idx) => {
							const param = parameters[id];
							return param ? renderParam(param, id, idx + 1, disabled) : null;
						})}
					</div>
				) : (
					<now-empty-state
						header-text={t('No optional parameters, yet')}
						body-text={false}
					/>
				)}
			</form>
		</div>
	);
}

/**
 * @param param
 * @param id
 * @param index
 * @param disabled
 */
function renderParam(param, id, index, disabled) {
	const appendToButtonPayload = { id, button: 'delete-parameter' };
	const appendToInputPayload = { input: 'parameter', id };
	return (
		<div className="param-row">
			<div className="param-input">
				<now-input
					value={param.name || ''}
					/* prettier-ignore */
					placeholder={t('Enter a name for your optional parameter, like "selectedTabIndex"')}
					manage-value
					autofocus={param.id && !param.name}
					name="name"
					manage-invalid
					invalid={param.messages && Boolean(param.messages.length)}
					messages={param.messages}
					label={t('Optional parameter {0} name', index)}
					disabled={disabled}
					append-to-payload={appendToInputPayload}
				/>
			</div>
			<div className="center">
				<now-button-iconic
					icon="trash-fill"
					variant="tertiary"
					bare
					size="md"
					disabled={disabled}
					append-to-payload={appendToButtonPayload}
				/>
			</div>
		</div>
	);
}
