import { t } from 'sn-translate';
import get from 'lodash/get';
import { escapeScopeLabel } from '../../../utils/escapeScopeLabel';

// eslint-disable-next-line no-unused-vars
const OutOfScopeAlert = ({ scopeName }) => {
	return (
		<section>
			<div className="uib-experience-scope-alert--content">
				<div className="uib-experience-scope-alert--message">
					<now-icon icon="lock-outline" />
					<span className="uib-experience-scope-alert--text">
						<span>
							{
								/* prettier-ignore */
								t('Different application scope. This experience is in {0}.', scopeName)
							}
						</span>
					</span>
				</div>
			</div>
		</section>
	);
};

const ExperiencesModal = ({
	sysId,
	pageRegistryName,
	userScope,
	pageRegistryScope
}) => {
	const headerLabel = pageRegistryName
		? t('{0} settings', `${pageRegistryName}`)
		: t('Experience settings');

	const registryScopeName =
		get(pageRegistryScope, 'label', null) !== null
			? escapeScopeLabel(get(pageRegistryScope, 'label', null))
			: null;
	const registryScopeId = pageRegistryScope.id;
	const readonly = userScope.id !== registryScopeId;

	return (
		<uib-modal-wrapper
			modalProps={{
				opened: true,
				size: 'fullscreen',
				headerLabel
			}}
			closeActionName="UIB_EXPERIENCES_MODAL#CLOSE_MODAL">
			{readonly ? <OutOfScopeAlert scopeName={registryScopeName} /> : null}
			<uib-experience-settings-connected readonly={readonly} sys-id={sysId} />
		</uib-modal-wrapper>
	);
};

export default ExperiencesModal;
