import { t } from 'sn-translate';
import get from 'lodash/get';
import * as actionTypes from './actions';
import { applicationTypes } from '../../utils/routingConstants';
import { memoizeDeepN } from 'seismic-memo-utils';

export const getMenuItems = memoizeDeepN(
	({
		isWorkspaceApp,
		pageIsSelected,
		screenIsSelected,
		screenIsReadOnly,
		screenIsCopiable,
		selectedPage,
		screenIsTab,
		applicationType,
		isInvalidAbsoluteUrl,
		parentInfo
	}) => {
		const dynamicTabData = get(
			parentInfo,
			'elementProperties.dynamicTabData',
			{}
		);
		const workspacePageItemChildren = [
			{
				id: 'new-page-menu-item',
				headerAction: 'SN_UIB_PAGE_HEADER#NEW_WORKSPACE_PAGE_CLICKED',
				label: t('Create page')
			},
			{
				id: 'clone-page-menu-item',
				headerAction: 'SN_UIB_PAGE_HEADER#COPY_WORKSPACE_PAGE_CLICKED',
				label: t('Copy page'),
				disabled: !screenIsSelected
			},
			{
				id: 'edit-page-menu-item',
				headerAction: actionTypes.EDIT_WORKSPACE_PAGE_CLICKED,
				label: t('Edit page properties'),
				disabled: isWorkspaceApp
					? screenIsReadOnly || !screenIsSelected
					: selectedPage === null,
				appendToPayload: selectedPage && {
					route: selectedPage.sysId
				}
			},
			{
				id: 'delete-page-menu-item',
				headerAction: actionTypes.DELETE_WORKSPACE_PAGE_CLICKED,
				label: t('Delete page'),
				disabled: screenIsReadOnly || !screenIsSelected,
				appendToPayload: selectedPage && {
					route: selectedPage.sysId
				}
			}
		];

		const newMenuItemChildren = [
			{
				id: 'new-page-menu-item',
				headerAction: 'SN_UIB_PAGE_HEADER#NEW_APP_PAGE_CLICKED',
				label: screenIsTab ? t('Create tab') : t('Create page'),
				icon: 'document-plus-outline'
			},
			...(screenIsTab && dynamicTabData
				? [
						{
							id: 'new-dynamic-tab-menu-item',
							headerAction: 'SN_UIB_PAGE_HEADER#NEW_APP_DYNAMIC_TAB_CLICKED',
							label: t('Create data driven tab'),
							icon: 'document-plus-outline'
						}
				  ]
				: []),
			{
				id: 'new-variant-menu-item',
				headerAction: 'SN_UIB_PAGE_HEADER#NEW_APP_PAGE_VARIANT_CLICKED',
				label: screenIsTab ? t('Create tab variant') : t('Create variant'),
				icon: 'diamond-outline',
				disabled: pageIsSelected === false
			},
			{
				id: 'clone-variant-menu-item',
				headerAction: 'SN_UIB_PAGE_HEADER#COPY_APP_PAGE_VARIANT_CLICKED',
				label: screenIsTab
					? t('Duplicate tab variant')
					: t('Duplicate variant'),
				icon: 'clone-outline',
				disabled: !screenIsSelected || !screenIsCopiable
			}
		];

		const configureMenuItemChildren = [
			{
				id: 'edit-page-menu-item',
				headerAction: actionTypes.EDIT_APP_PAGE_CLICKED,
				label: screenIsTab ? t('Edit tab settings') : t('Edit page settings'),
				icon: 'gear-outline',
				disabled: isWorkspaceApp
					? screenIsReadOnly || !screenIsSelected
					: selectedPage === null,
				appendToPayload: selectedPage && {
					route: selectedPage.sysId
				}
			},
			{
				id: 'edit-variant-menu-item',
				headerAction: actionTypes.EDIT_APP_PAGE_VARIANT_CLICKED,
				label: screenIsTab
					? t('Edit tab variant settings')
					: t('Edit variant settings'),
				icon: 'gear-outline',
				disabled: !screenIsSelected
			},
			{
				id: 'edit-experience-menu-item',
				headerAction: actionTypes.EDIT_EXPERIENCE_CLICKED,
				label: t('Edit experience settings'),

				icon: 'gear-outline',
				disabled: isWorkspaceApp
			}
		];

		const appNavItemChildren = [
			{
				id: 'go-to-route-menu-item',
				icon: 'open-link-fill',
				headerAction: actionTypes.GO_TO_SCREEN_COLLECTION,
				label: t('Open variant collection'),
				disabled: !pageIsSelected
			}
		];
		if (applicationType !== 'components') {
			appNavItemChildren.unshift({
				id: 'go-to-page-menu-item',
				icon: 'open-link-fill',
				headerAction: actionTypes.GO_TO_PAGE,
				label: t('Open page'),
				disabled: !screenIsSelected || screenIsTab || isInvalidAbsoluteUrl
			});
		}

		const navItemChildren = [
			{
				id: 'go-to-screen-menu-item',
				icon: 'open-link-fill',
				headerAction: actionTypes.GO_TO_SCREEN,
				label:
					applicationType === 'dashboards'
						? t('Open screen')
						: t('Open variant'),
				disabled: !screenIsSelected
			},
			{
				id: 'go-to-macroponent-menu-item',
				icon: 'open-link-fill',
				headerAction: actionTypes.GO_TO_MACROPONENT,
				label: t('Open page definition'),
				disabled: !screenIsSelected
			},
			...(applicationType === applicationTypes.app
				? [
						{
							id: 'go-to-app-config-menu-item',
							icon: 'open-link-fill',
							headerAction: actionTypes.GO_TO_APP_CONFIG,
							label: t('Open app config')
						}
				  ]
				: [])
		];

		const dashboardItemsChildren = [
			{
				id: 'new-dashboard-menu-item',
				headerAction: 'SN_UIB_PAGE_HEADER#NEW_DASHBOARD_CLICKED',
				label: t('Create dashboard'),
				disabled: screenIsTab,
				icon: 'document-plus-outline'
			},
			{
				id: 'edit-dashboard-menu-item',
				headerAction: actionTypes.EDIT_DASHBOARD_CLICKED,
				label: t('Edit dashboard settings'),
				icon: 'gear-outline',
				disabled: selectedPage === null || screenIsTab,
				appendToPayload: selectedPage && {
					route: selectedPage.sysId
				}
			}
		];

		const workspacePageItems = {
			id: 'page-actions',
			children: workspacePageItemChildren
		};

		const newMenuItems = {
			id: 'new-actions',
			children: newMenuItemChildren
		};

		const configureMenuItems = {
			id: 'configure-actions',
			children: configureMenuItemChildren,
			sectionLabel: t('Configure')
		};
		const navItems = {
			id: 'nav-actions',
			children:
				applicationType === 'dashboards'
					? navItemChildren
					: [...appNavItemChildren, ...navItemChildren]
		};

		const devChildren = [
			{
				id: 'dev-actions',
				label: t('Developer'),
				icon: 'wrench-outline',
				children: [navItems]
			}
		];

		const developerItems = {
			id: 'dev-sub-menu',
			children: devChildren
		};

		const dashboardItems = {
			id: 'dashboard-actions',
			children: dashboardItemsChildren
		};

		const helpItemsChildren = [
			{
				id: 'intro-help-item',
				headerAction: actionTypes.INTRO_LINK_CLICKED,
				label: t('Intro to UI Builder')
			},
			{
				id: 'learn-about-help-item',
				headerAction: actionTypes.LEARN_ABOUT_LINK_CLICKED,
				label: t('Training and learning')
			},
			{
				id: 'component-documentation-help-item',
				headerAction: actionTypes.COMPONENT_DOCUMENTATION_LINK_CLICKED,
				label: t('How to use components')
			},
			{
				id: 'whats-new-help-item',
				headerAction: actionTypes.WHATS_NEW_LINK_CLICKED,
				label: t("What's new")
			},
			{
				id: 'building-custom-components-help-item',
				headerAction: actionTypes.BUILDING_CUSTOM_LINK_CLICKED,
				label: t('How to create a custom component')
			}
		];

		const dashboardHelpItemsChildren = [
			{
				id: 'learn-about-dashboard-builder',
				headerAction: actionTypes.LEARN_ABOUT_DASHBOARD_BUILDER_CLICKED,
				label: t('Learn about Dashboard Builder')
			}
		];

		const helpItems = {
			id: 'help-actions',
			children:
				applicationType === applicationTypes.dashboards
					? dashboardHelpItemsChildren
					: helpItemsChildren
		};

		// TODO: Ideally, this would come from a property so that this component needs
		//       to know nothing about applicationTypes.
		if (
			applicationType === applicationTypes.app ||
			applicationType === applicationTypes.components
		) {
			return [
				{
					[t('Menu')]: [newMenuItems, configureMenuItems, developerItems]
				},
				{ [t('Help')]: [helpItems] }
			];
		} else if (applicationType === 'workspace') {
			return [
				{ [t('Menu')]: [workspacePageItems] },
				{ [t('Help')]: [helpItems] }
			];
		} else if (applicationType === 'dashboards') {
			return [
				{ [t('Menu')]: [dashboardItems, developerItems] },
				{ [t('Help')]: [helpItems] }
			];
		}
	}
);

export const getHeaderActions = memoizeDeepN(
	({
		isWorkspaceApp,
		screenIsReadOnly,
		screenIsSelected,
		pageStatus,
		saveDisabled,
		isSavingPage,
		builderType,
		isLoading,
		dashboardId
	}) => {
		const saveButton = {
			className: 'sn-uib-page-header--save now-m-inline-end--sm',
			configAria: {
				'aria-label': t('Save')
			},
			label: t('Save'),
			variant: !saveDisabled ? 'primary' : 'secondary',
			disabled:
				screenIsReadOnly ||
				!screenIsSelected ||
				saveDisabled ||
				isSavingPage ||
				isLoading,
			appendToPayload: { headerAction: actionTypes.SAVE_CLICKED },
			'hook-update': (old, vnode) => {
				if (!old.data.props.disabled && vnode.data.props.disabled) {
					vnode.elm.blur();
				}
			}
		};
		const dashboardShareButton = {
			className: 'sn-uib-page-header--save now-m-inline-end--sm',
			icon: 'share-outline',
			configAria: {
				'aria-label': t('Share')
			},
			label: t('Share'),
			variant: 'secondary',
			disabled:
				screenIsReadOnly ||
				!screenIsSelected ||
				isSavingPage ||
				isLoading ||
				!dashboardId,
			appendToPayload: { headerAction: actionTypes.SHARE_CLICKED }
		};
		const actions =
			builderType === 'Dashboard'
				? [dashboardShareButton, saveButton]
				: [saveButton];

		if (isWorkspaceApp) {
			actions.push(
				pageStatus === 'active'
					? {
							className: 'sn-uib-page-header--activate',
							configAria: {
								'aria-label': t('Deactivate')
							},
							label: t('Deactivate'),
							variant: 'primary-negative',
							disabled:
								screenIsReadOnly ||
								!screenIsSelected ||
								isSavingPage ||
								isLoading,
							appendToPayload: {
								headerAction: actionTypes.DEACTIVATE_CLICKED
							}
					  }
					: {
							className: 'sn-uib-page-header--activate',
							icon: 'upload-outline',
							configAria: {
								'aria-label': t('Activate')
							},
							label: t('Activate'),
							variant: 'primary',
							disabled:
								screenIsReadOnly ||
								!screenIsSelected ||
								isSavingPage ||
								isLoading,
							appendToPayload: {
								headerAction: actionTypes.ACTIVATE_CLICKED
							}
					  }
			);
		}

		return actions;
	}
);
