import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import get from 'lodash/get';
import cuid from 'cuid';

export const referenceResultsQuery = `
	query($query: String) {
	  	glideRecord: GlideRecord_Query {
			{{table}}(pagination: {limit: 30}, queryConditions: $query) {
			  	result: _results {
					row: _row_data {
					  	id: uniqueValue,
					  	label: displayValue
					}
			  	}
			}
	  	}
	}
`;

const getReferenceSysIds = (references) => {
	return references.map(({ sysId }) => sysId);
};

export const getReferenceDisplayValuesQuery = (references) => {
	const fragments = references
		.map((reference) => {
			const { table, sysId } = reference;

			return `
				${table}_${cuid()}:${table}(sys_id: "${sysId}") {
					result: _results {
						row: _row_data {
							sysId: uniqueValue,
							label: displayValue
						}
					}
				}
			`;
		})
		.join('');

	return `
		query {
			glideRecord: GlideRecord_Query {
				${fragments}
			}
		}
	`;
};

const referenceRequestedUrlPath =
	'/api/sn_ui_builder/uib_builder_api/reference_search';

const referenceRequestedHttpEffect = createHttpEffect(
	referenceRequestedUrlPath,
	{
		method: 'POST',
		successActionType: 'UIB_APP#REFERENCE_RESULTS_QUERY_SUCCESS',
		startActionType: 'UIB_APP#REFERENCE_RESULTS_QUERY_STARTED',
		batch: false
	}
);

const referenceDisplayValueRequestedEffect = {
	effect: () => {},
	interceptors: [
		(context) => {
			const { references } = context.coeffects.action.payload;
			context.coeffects.action.payload.referenceSysIds = getReferenceSysIds(
				references
			);

			const gqlQuery = getReferenceDisplayValuesQuery(references);
			const gqlEffect = createGraphQLEffect(gqlQuery, {
				successActionType: 'UIB_APP#DISPLAY_VALUES_QUERY_SUCCESS',
				errorActionType: 'UIB_APP#DISPLAY_VALUES_QUERY_FAILED',
				variableList: ['referenceSysIds']
			});

			return {
				...context,
				effects: [
					{
						...gqlEffect,
						args: [...gqlEffect.args, context.coeffects]
					}
				]
			};
		}
	]
};

export const referenceHandlers = {
	['UIB_DATA_BROKER_CONFIGURATION_PANEL#REFERENCE_REQUESTED']: referenceRequestedHttpEffect,
	['UIB_ELEMENT_CONFIGURATION_PANEL#REFERENCE_REQUESTED']: referenceRequestedHttpEffect,
	['UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL#REFERENCE_REQUESTED']: referenceRequestedHttpEffect,
	['UIB_APP#REFERENCE_RESULTS_QUERY_STARTED']: ({
		action,
		properties,
		updateProperties
	}) => {
		const referenceId = get(action, 'meta.request.data.referenceId', null);
		if (!referenceId) {
			return;
		}
		updateProperties({
			referenceSearching: {
				...properties.referenceSearching,
				[referenceId]: true
			}
		});
	},
	['UIB_APP#REFERENCE_RESULTS_QUERY_SUCCESS']: ({
		properties,
		action,
		updateProperties
	}) => {
		const referenceId = get(action, 'meta.request.data.referenceId', null);
		const { records } = action.payload.result;
		if (!referenceId) {
			return;
		}
		updateProperties({
			referenceSearching: {
				...properties.referenceSearching,
				[referenceId]: false
			},
			referenceResults: {
				...properties.referenceResults,
				[referenceId]: records
			}
		});
	},
	['UIB_ELEMENT_CONFIGURATION_PANEL#DISPLAY_VALUES_REQUESTED']: referenceDisplayValueRequestedEffect,
	['UIB_DATA_BROKER_CONFIGURATION_PANEL#DISPLAY_VALUES_REQUESTED']: referenceDisplayValueRequestedEffect,
	['UIB_PAGE_PROPERTIES_CONFIGURATION_PANEL#DISPLAY_VALUES_REQUESTED']: referenceDisplayValueRequestedEffect,
	['UIB_APP#DISPLAY_VALUES_QUERY_SUCCESS']: ({
		properties,
		action,
		updateProperties
	}) => {
		const { glideRecord } = action.payload.data;
		const referenceSysIds = get(
			action,
			'meta.options.variables.referenceSysIds',
			null
		);
		const referenceDisplayValues = {};
		const referenceDisplayValuesErrors =
			properties.referenceDisplayValuesErrors;

		// eslint-disable-next-line no-unused-vars
		Object.entries(glideRecord).forEach(([key, value]) => {
			value.result.map(({ row }) => {
				referenceDisplayValues[row.sysId] = row.label;
			});
		});

		const returnReferenceDisplayValues = referenceSysIds.reduce(
			(acc, sysId) => {
				if (!referenceDisplayValues[sysId]) {
					acc[sysId] = '';
					/* prettier-ignore */
					referenceDisplayValuesErrors[sysId] = t('Referenced record not found');
				} else {
					acc[sysId] = referenceDisplayValues[sysId];
					delete referenceDisplayValuesErrors[sysId];
				}
				return acc;
			},
			{}
		);

		updateProperties({
			referenceDisplayValuesErrors,
			referenceDisplayValues: returnReferenceDisplayValues
		});
	}
};
