import { snUXFActions } from 'sn-uxf-builder-middleware';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	OVERRIDES_LIST_REQUESTED,
	OVERRIDES_LIST_HTTP_REQUESTED,
	DASHBOARD_OVERRIDES_LIST_HTTP_REQUESTED
} from '../../uib-page-management-panel/actions';
import { domainsBehaviorActions } from '../../../behaviors/domainsBehavior';
import treeifyOverrides from '../utils/treeifyOverrides';
import get from 'lodash/get';

import { forceLoad } from '../../uib-router/forcedTransitions';
import { screenNames } from '../../../utils/routingConstants';

const OVERRIDES_LIST_REQUEST_SUCCEEDED =
	'UIB_APP#OVERRIDES_LIST_REQUEST_SUCCEEDED';
const OVERRIDES_LIST_REQUEST_STARTED = 'UIB_APP#OVERRIDES_LIST_REQUEST_STARTED';
const OVERRIDES_LIST_REQUEST_FAILED = 'UIB_APP#OVERRIDES_LIST_REQUEST_FAILED';

export const domainHandlers = {
	'SN_UIB_PAGE_HEADER#DOMAIN_SELECTED': ({ action, dispatch }) => {
		const { id: value } = action.payload;
		if (value) {
			dispatch(domainsBehaviorActions.DOMAIN_BEHAVIOR_UPDATE_DOMAIN_REQUESTED, {
				data: {
					value
				}
			});
		}
	},
	[domainsBehaviorActions.DOMAIN_BEHAVIOR_DOMAIN_UPDATED]: ({ properties }) => {
		const { routeState = {}, applicationType, routeName } = properties;

		const { pageSysId } = routeState;

		forceLoad(
			applicationType === 'app'
				? typeof pageSysId === 'string'
					? screenNames.appPage
					: screenNames.appHome
				: routeName,
			{
				...routeState,
				queryParams: {}
			}
		);
	},
	[snUXFActions.SET_DOMAIN_EXPANDED]: ({ action, updateState, properties }) => {
		const { isDomainExpanded } = action.payload;
		const { routeState = {}, routeName } = properties;
		updateState({ isDomainExpanded });

		const queryParams = {
			...(routeState.queryParams || {}),
			domainExpanded: isDomainExpanded
		};
		forceLoad(routeName, {
			...routeState,
			queryParams
		});
	},
	[OVERRIDES_LIST_REQUESTED]: ({ action, dispatch, properties }) => {
		const { variant = {}, variants = [], dashboard = {} } = action.payload;

		if (variant.domainName === 'global' && variant.overrides === '') {
			dispatch(OVERRIDES_LIST_REQUEST_SUCCEEDED, {
				result: variants.filter((v) => v.pageSysId === variant.pageSysId),
				root: {
					id: variant.id,
					label: `${variant.domainName} (${variant.name})`,
					children: []
				}
			});
		} else if (variant.id) {
			dispatch(
				OVERRIDES_LIST_HTTP_REQUESTED,
				{ id: variant.id },
				{ item: variant }
			);
		} else if (dashboard.id && dashboard.overrides) {
			const targetDashboardRoute =
				properties.appRoutes.find((route) => route.id === dashboard.id) || {};
			dispatch(
				OVERRIDES_LIST_HTTP_REQUESTED,
				{
					id: targetDashboardRoute.screen.id
				},
				{ item: targetDashboardRoute.screen }
			);
		} else {
			const dashboardId = get(action.payload, 'dashboard.dashboardId', '');
			dispatch(DASHBOARD_OVERRIDES_LIST_HTTP_REQUESTED, { dashboardId });
		}
	},
	[OVERRIDES_LIST_HTTP_REQUESTED]: createHttpEffect(
		'/api/now/uxframework/screens/:id/overrides',
		{
			method: 'GET',
			batch: false,
			pathParams: ['id'],
			successActionType: OVERRIDES_LIST_REQUEST_SUCCEEDED,
			errorActionType: OVERRIDES_LIST_REQUEST_FAILED,
			startActionType: OVERRIDES_LIST_REQUEST_STARTED
		}
	),
	[DASHBOARD_OVERRIDES_LIST_HTTP_REQUESTED]: createHttpEffect(
		'/api/now/builder/dashboards/:dashboardId',
		{
			method: 'GET',
			batch: false,
			pathParams: ['dashboardId'],
			successActionType: OVERRIDES_LIST_REQUEST_SUCCEEDED,
			errorActionType: OVERRIDES_LIST_REQUEST_FAILED,
			startActionType: OVERRIDES_LIST_REQUEST_STARTED
		}
	),
	[OVERRIDES_LIST_REQUEST_SUCCEEDED]: ({ action, updateState }) => {
		const { result, root } = action.payload;
		const currentOverridesTree = treeifyOverrides(
			result,
			root || null,
			action.meta.item
		);
		updateState({ currentOverridesTree, overridesFetching: false });
	},
	[OVERRIDES_LIST_REQUEST_STARTED]: ({ updateState }) => {
		updateState({ overridesFetching: true });
	},
	[OVERRIDES_LIST_REQUEST_FAILED]: () => {
		throw new Error('Something went wrong fetching variant overrides');
	}
};
