export const elementContextMenuHandlers = {
	'UIB_ELEMENT_CONTEXT_MENU#SET_OPENED': ({ action, updateProperties }) => {
		const { value } = action.payload;
		if (value) {
			const update = () =>
				updateProperties({ elementContextMenuOpened: value });
			setTimeout(update, 100); // let the call stack settle before showing toolbox; DEF0111672
		} else {
			updateProperties({
				elementContextMenuOpened: value,
				elementContextMenuPositionTarget: null,
				elementContextMenuTarget: {}
			});
		}
	},
	'UIB_ELEMENT_CONTEXT_MENU#SET_PROPERTIES': ({ action, updateProperties }) => {
		const { actionTarget, item, positions, type, info } = action.payload;
		updateProperties({
			elementContextMenuPositionTarget: actionTarget,
			elementContextMenuTarget: item,
			elementContextMenuPositions: positions,
			elementContextMenuType: type,
			elementContextMenuInfo: info
		});
	}
};
