import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { onNextFrame } from 'sn-ui-builder-components';
import getHelpResourceUrl from '../utils/getHelpResourceUrl';
const { focusTargetId } = DevxKeyFocusBehavior;

export const elementHandlers = {
	['SN_UIB_ELEMENT_CONTROLS#TRASH_BUTTON_CLICKED']: ({
		action,
		updateProperties
	}) => {
		updateProperties({ elementConfirmingDelete: action.payload.elementId });
	},
	['SN_UIB_ELEMENT_CONTROLS#TRASH_CANCEL_BUTTON_CLICKED']: ({
		updateProperties
	}) => {
		updateProperties({ elementConfirmingDelete: null });
	},
	['UIB_ELEMENT#ELEMENT_SELECTED']: ({
		action,
		updateProperties,
		properties
	}) => {
		const { elementId, stageClick } = action.payload;
		if (!stageClick) {
			const showScreenConfig =
				properties.rootElementId && elementId === properties.rootElementId;

			updateProperties({ showScreenConfig });
		}
	},
	['UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED']: ({
		action,
		updateProperties,
		properties
	}) => {
		const { elementId } = action.payload;

		const showScreenConfig =
			properties.rootElementId && elementId === properties.rootElementId;

		switch (action.payload.type) {
			case 'element':
				updateProperties({ showScreenConfig });
				break;
			default:
		}
	},

	['SN_UIB_ELEMENT_CONTROLS#TRASH_CONFIRM_BUTTON_CLICKED']: ({
		updateProperties
	}) => {
		updateProperties({
			elementConfirmingDelete: null,
			selectedElementInstanceName: null
		});
	},
	['SN_UIB_ELEMENT_CONTROLS#CONFIG_BUTTON_CLICKED']: ({
		properties: { elementConfigurationPanelExpanded },
		updateProperties,
		dispatch
	}) => {
		updateProperties({
			focusConfigPanel: true
		});
		if (!elementConfigurationPanelExpanded) {
			dispatch('COLLAPSIBLE_PANEL#PANEL_TOGGLED', {
				panel: 'element-configuration',
				value: true
			});
		}
		onNextFrame(() => {
			focusTargetId(dispatch, 'quick-link-element-configuration-panel');
			updateProperties({ focusConfigPanel: false });
		});
	},
	['SN_UIB_PROPERTY_INPUT_SCRIPT_LINK#HELP_CLICKED']: () => {
		window.open(getHelpResourceUrl('now-code-editor'), '_blank');
	}
};
