import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import styles from './uib-new-variant-modal.scss';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';
import {
	UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED,
	UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED
} from '../uib-page-definition-selector';

const DISMISS = 'UIB_NEW_VARIANT_MODAL#CANCEL_CLICKED';
const CREATE = 'UIB_NEW_VARIANT_MODAL#CREATE_CLICKED';
export const UIB_VARIANT_MODAL_MODAL_DISMISSED =
	'UIB_NEW_VARIANT_MODAL#MODAL_DISMISSED';

const appendToNamePayload = { category: 'name' };

const footerActions = (variantName, pageDefinition) => {
	const disabled = !variantName || variantName.trim().length === 0;

	return [
		{
			label: t('Create'),
			variant: 'primary',
			clickActionType: CREATE,
			disabled
		},
		{ label: t('Cancel'), variant: 'secondary', clickActionType: DISMISS }
	];
};

/**
 * @param appRoutes
 * @param pageSysId
 */
function findVariants(appRoutes, pageSysId) {
	const currentPage = appRoutes.find((route) => route.page.sysId === pageSysId);
	return currentPage ? currentPage.page.variants : [];
}

const view = (state) => {
	const {
		variantName,
		pageDefinition,
		isCreating,
		properties: {
			userScope,
			currentDomain,
			supportDomains,
			selectedPageIsTab,
			modalInfo: { copyFrom }
		}
	} = state;

	const modalTitle = selectedPageIsTab
		? t('Create a tab variant')
		: t('Create a variant');

	return (
		<now-modal
			manage-opened={true}
			opened={true}
			size="lg"
			header-label={!isCreating ? modalTitle : null}
			footer-actions={
				isCreating ? null : footerActions(variantName, pageDefinition)
			}>
			<div
				aria-busy={true}
				className={`container ${isCreating ? 'creating' : ''}`}>
				{isCreating ? (
					<LoadingStage
						size={'sm'}
						loadingStageContent={
							isCreating ? t('Creating {0}...', variantName) : t('Loading...')
						}
					/>
				) : (
					<div className="content">
						<div className="subheader">
							{
								/* prettier-ignore */
								t("First, let's set up this variant. We recommend a unique name that is different from your {0} name.", selectedPageIsTab ? 'tab' : 'page')
							}
						</div>
						<form>
							<div>
								<now-input
									className="name"
									label={t('Variant name')}
									value={variantName}
									required
									append-to-payload={appendToNamePayload}
									/* prettier-ignore */
									helper-content={t('Enter a unique name to identify this variant from others. For example, for a user registration page that\'s intended only for external users, you may enter "External User Registration."')}
									placeholder={t('Enter a name for your variant')}
								/>
								{!copyFrom || (copyFrom && pageDefinition) ? (
									<div className="page-selector">
										<uib-page-definition-selector
											copyMode={!!copyFrom}
											macroponentId={pageDefinition}
										/>
									</div>
								) : null}
							</div>
							<div className="two-column-flex">
								<div className="column">
									<now-input
										label={t('Application scope')}
										value={
											userScope && userScope.label
												? escapeScopeLabel(userScope.label)
												: ''
										}
										disabled
										readonly
										/* prettier-ignore */
										helper-content={t('The variant is created in this default application scope. To switch scope, use the application scope picker.')}
									/>
								</div>
								{supportDomains ? (
									<div className="column">
										<now-input
											label={t('Domain')}
											value={
												currentDomain && currentDomain.label
													? currentDomain.label.split('/').pop()
													: ''
											}
											disabled
											readonly
											/* prettier-ignore */
											helper-content={t('The variant is created in this domain. To switch domain scope, use the domain scope picker.')}
										/>
									</div>
								) : null}
							</div>
						</form>
					</div>
				)}
			</div>
		</now-modal>
	);
};

createCustomElement('uib-new-variant-modal', {
	view,
	styles,
	initialState: {
		copyFrom: null,
		variants: [],
		order: 100,
		variantName: '',
		isCreating: false,
		pageDefinition: null,
		usingSharedPageDefinition: false,
		copyPageDefinition: false
	},
	properties: {
		appRoutes: {
			default: null
		},
		appsSysId: {
			default: null
		},
		pageSysId: {
			default: null
		},
		modalInfo: {
			default: null
		},
		activePageSysId: {
			default: null,
			computed({ properties: { modalInfo, pageSysId } }) {
				return (modalInfo && modalInfo.pageSysId) || pageSysId;
			}
		},
		isComponentBuilder: {
			default: false
		},
		userScope: {
			default: {}
		},
		currentDomain: {
			default: {}
		},
		supportDomains: {
			default: false
		},
		selectedPageIsTab: {
			default: false
		},
		parentInfo: {
			default: {}
		},
		selectedPageVariants: {
			default: []
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, updateState }) => {
			const {
				appRoutes,
				activePageSysId,
				modalInfo: { copyFrom, variantName, order, rootMacroponentId }
			} = properties;
			const variantArrayLength = findVariants(appRoutes, activePageSysId)
				.length;
			updateState({
				copyFrom,
				variantName,
				order: copyFrom ? order : variantArrayLength * 100,
				pageDefinition: rootMacroponentId,
				usingSharedPageDefinition: !!rootMacroponentId
			});
		},
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) => {
			dispatch(UIB_VARIANT_MODAL_MODAL_DISMISSED);
		},
		[DISMISS]: ({ dispatch }) => {
			dispatch(UIB_VARIANT_MODAL_MODAL_DISMISSED);
		},
		['NOW_INPUT#INPUT']: ({ updateState, action }) => {
			action.stopPropagation();
			const { fieldValue: variantName, category } = action.payload;
			if (category === 'name') {
				updateState({
					variantName
				});
			}
		},
		[CREATE]: ({ state, dispatch, properties, updateState }) => {
			const {
				variantName,
				order,
				copyFrom,
				pageDefinition,
				usingSharedPageDefinition,
				copyPageDefinition
			} = state;
			const {
				isComponentBuilder,
				appsSysId: appId,
				activePageSysId: pageSysId,
				userScope,
				selectedPageIsTab,
				parentInfo,
				selectedPageVariants
			} = properties;

			/*
				when creating a page, if we set the parentMacroponent and
				parentMacroponentCompositionElementId we are essentially creating
				a "child" route.
			*/
			let newTabData = {};
			if (selectedPageIsTab) {
				if (isEmpty(parentInfo)) {
					throw new Error('parentInfo is needed to create a new tab');
				}
				let order = 0;
				if (!isEmpty(selectedPageVariants)) {
					const [lastRoute] = selectedPageVariants
						.sort((a, b) => a.order - b.order)
						.slice(-1);
					order = lastRoute.order + 100;
				}
				newTabData = {
					order,
					parentMacroponentId: parentInfo.macroponentId
				};
			}

			const pageDefinitionData = usingSharedPageDefinition
				? !copyFrom
					? {
							rootMacroponentId: pageDefinition,
							copyRootMacroponent: copyPageDefinition
					  }
					: !copyPageDefinition
					? {
							rootMacroponentId: pageDefinition
					  }
					: {}
				: {};

			dispatch(
				snUXFActions.NEW_PAGE_VARIANT_CREATE_REQUEST,
				{
					builderType: isComponentBuilder ? 'components' : 'apps',
					appId,
					pageSysId,
					data: {
						name: variantName,
						order:
							usingSharedPageDefinition && !copyPageDefinition && copyFrom
								? order - 10
								: order,
						copyFrom,
						scope: userScope && userScope.id,
						...newTabData,
						...pageDefinitionData
					}
				},
				{ pageSysId }
			);
			updateState({
				isCreating: true
			});
		},
		[UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED]: {
			stopPropagation: true,
			effect: ({ updateState, action }) => {
				const { value: pageDefinition } = action.payload;
				updateState({
					pageDefinition,
					usingSharedPageDefinition: pageDefinition !== 'none'
				});
			}
		},
		[UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED]: {
			stopPropagation: true,
			effect: ({ updateState, action }) => {
				const { value: copyPageDefinition } = action.payload;
				updateState({
					copyPageDefinition
				});
			}
		}
	}
});
