import { actionTypes } from '@servicenow/ui-core';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import { transitionTo, replaceWith } from '../behaviors/router';
import {
	attachForcedHandlers,
	detachForcedHandlers,
	forceReplaceWith
} from '../forcedTransitions';
import PAGE_TITLE, { PAGE_TITLE_SUFFIX } from '../../../utils/pageTitle';
import { pageListSorter } from '../../../utils/pageListSort';
import { routerGraphQLHandlers } from './routerGraphQLHandlers';
import { applicationTypes, screenNames } from '../../../utils/routingConstants';
import { updateAesTabSession } from '../../../integrations/app-engine-studio/utils/aesApi';
import get from 'lodash/get';
import { flattenDashboardRoutes } from '../../../utils/appRoutes';
import qpUtils from '../../../utils/queryParams';
import { checkForUnsavedChanges } from '../../uib-app/utils/checkForUnsavedChanges';

/**
 * Lifecycle events for uib-route.  When a url is parsed, a workspace query is performed,
 * and a subsequent page query is performed (if a page sysId is provided).
 *
 * After the workspace table has been successfully fetched:
 *
 * 	Request the list of pages
 * 	Dispatch workspace loaded even
 *
 * If no results are found, set the routeNotFound property
 *
 * The compiled_page_registry field on sys_aw_master_config contains a registry record
 * (referencing sys_ux_page_registry).  This registry record is responsible for
 * linking the current workspace registry record and a landing page record (see the
 * applicable_page_registry field on the sys_ux_custom_content_root_elem table):
 *
 * sys_aw_master_config <-> sys_ux_page_registry <-> sys_ux_custom_content_root_elem
 *
 */

const getApplicationType = (routeName, isComponentBuilder) => {
	const base = routeName.match(/^(workspace|app|dashboards)/)[1];
	if (base === 'app' && isComponentBuilder) {
		return 'components';
	}
	return base;
};

export const lifecycleHandlers = {
	[actionTypes.COMPONENT_CONNECTED]: ({ dispatch }) => {
		attachForcedHandlers(dispatch);
	},

	[actionTypes.COMPONENT_DISCONNECTED]: () => {
		detachForcedHandlers();
	},

	[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
		action,
		properties,
		updateProperties
	}) => {
		if (
			['pageSysId', 'pages'].includes(action.payload.name) &&
			properties.pageSysId &&
			properties.pages &&
			properties.pages.length > 0
		) {
			const currentPage = properties.pages.find(
				(page) => page.sysId === properties.pageSysId
			);
			if (currentPage) {
				updateProperties({
					screenTypeSysId: currentPage.screenType
				});
			}
		}
	},

	/**
	 * - router looping when clicking page or variant titles fast
	 */

	['ROUTER_BEHAVIOR#ROUTE_ENTERED']: checkForUnsavedChanges(
		({
			properties,
			action,
			dispatch,
			updateProperties,
			state,
			updateState
		}) => {
			const {
				appsSysId,
				isUnsavedDialogActive,
				routeName: previousRouteName,
				isComponentBuilder,
				isDomainExpanded
			} = properties;

			const { routeName, routeState = {} } = action.payload;

			PAGE_TITLE.reset();

			let applicationType = null;

			applicationType = getApplicationType(routeName, isComponentBuilder);

			if (
				previousRouteName === null ||
				getApplicationType(previousRouteName, isComponentBuilder) !==
					applicationType
			) {
				dispatch('UIB_APP#APP_LOADED', {
					applicationType: applicationType,
					routeState
				});
			}

			if (
				routeName === screenNames.dashboardsDashboard &&
				routeState.dashboardId === 'new'
			) {
				forceReplaceWith(screenNames.dashboardsHome);
				updateState({
					operation: 'set',
					path: 'createNew',
					value: true
				});
				dispatch(snUXFActions.TEMPLATES_REQUESTED);

				return;
			}

			if (
				routeState.forceAppConfigRefresh || // coming from uib-homepage
				(applicationType &&
					applicationType !== applicationTypes.dashboards &&
					routeState.appsSysId !== appsSysId)
			) {
				const payload = { sysId: routeState.appsSysId };
				const metadata = { applicationType, routeName, routeState };
				/**
				 * We are likely dealing with a full page load/refresh, so load the
				 * workspace information and instantiate the uxf middleware by dispatching
				 * actions.
				 */
				if (applicationType === applicationTypes.workspace) {
					metadata.tableName = 'sys_aw_master_config';
					dispatch(
						'UIB_ROUTER#WORKSPACE_CONFIG_REQUESTED',
						{ ...payload },
						{ ...metadata }
					);
				} else if (applicationType === applicationTypes.components) {
					metadata.tableName = 'sys_ux_macroponent';
					dispatch(
						'UIB_ROUTER#APPLICATION_CONFIG_REQUESTED',
						{
							sysId: payload.sysId,
							sysparm_query_no_domain: isDomainExpanded
						},
						{ ...metadata }
					);
				} else if (payload.sysId) {
					metadata.tableName = 'sys_ux_app_config';
					dispatch(
						'UIB_ROUTER#APPLICATION_CONFIG_REQUESTED',
						{
							sysId: payload.sysId,
							sysparm_query_no_domain: isDomainExpanded
						},
						{ ...metadata }
					);
				}
				return;
			} else if (
				previousRouteName === null &&
				applicationType === applicationTypes.dashboards &&
				!state.clearNew
			) {
				dispatch(
					'UIB_ROUTER#DASHBOARDS_CONFIG_REQUESTED',
					{ dashboardId: routeState.dashboardId },
					{ routeName, routeState }
				);
			}

			// We need to allow the workspace a chance to load to determine if 'workspace.page' is in error state
			if (routeState.show404) {
				return;
			}
			// if (isUnsavedDialogActive) {
			// 	dispatch(snUXFActions.PAGE_STATE_CLEARED);
			// }

			switch (routeName) {
				case screenNames.workspaceHome:
				case screenNames.appHome:
				case screenNames.dashboardsHome:
					/**
					 * if we are loading the same workspace, don't refetch the workspace.
					 * Instead, see if we have any valid pages to redirect to, or reset
					 * the route status to default to the splash page.
					 */
					dispatch('UIB_ROUTER#APP_SHOW_SPLASH', action.payload);
					if (state.createNew) {
						dispatch(snUXFActions.DASHBOARDS_SCREENS_LOADED, { screens: [] });
						dispatch('UIB_PAGE_HEADER#NEW_DASHBOARD_CLICKED');
						return;
					}
					if (
						get(
							action.payload,
							'routeState.queryParams.isolatedEditMode',
							false
						)
					) {
						dispatch(snUXFActions.ISOLATED_EDIT_MODE_REQUESTED, {
							parentScreenId: get(
								action.payload,
								'routeState.queryParams.parentScreenId',
								''
							),
							parentElementId: get(
								action.payload,
								'routeState.queryParams.parentElementId',
								''
							)
						});
					}
					break;
				case screenNames.appPage:
					dispatch(snUXFActions.PAGE_STATE_CLEARED);
					updateProperties({
						screenTypeSysId: action.payload.routeState.pageSysId,
						pageSysId: null
					});
					dispatch('UIB_ROUTER#APP_SHOW_SPLASH', action.payload);
					// when we are loading a tab with no variants, we need to explicitly load
					// parent info
					if (
						get(action.payload, 'routeState.variantSysId', true) &&
						get(
							action.payload,
							'routeState.queryParams.parentScreenId',
							false
						) &&
						get(action.payload, 'routeState.queryParams.parentElementId', false)
					) {
						dispatch(snUXFActions.ISOLATED_EDIT_MODE_REQUESTED, {
							parentScreenId: get(
								action.payload,
								'routeState.queryParams.parentScreenId',
								''
							),
							parentElementId: get(
								action.payload,
								'routeState.queryParams.parentElementId',
								''
							)
						});
					}
					break;
				case screenNames.workspacePage:
				case screenNames.appVariant:
					dispatch('UIB_ROUTER#APP_PAGE_LOAD', action.payload);
					break;
				case screenNames.dashboardsDashboard:
					/* eslint-disable no-case-declarations */
					const overrideId = window.location.search
						.slice(1)
						.split('&')
						.reduce((acc, kv) => {
							const [key, value] = kv.split('=');
							return {
								...acc,
								[key]: value
							};
						}, {})['overrideId'];
					/* eslint-enable no-case-declarations */
					if (overrideId) {
						dispatch('UIB_ROUTER#APP_PAGE_LOAD', {
							...action.payload,
							routeState: {
								...routeState,
								overrideId
							}
						});
					} else {
						dispatch('UIB_ROUTER#APP_PAGE_LOAD', action.payload);
					}
					break;
			}

			/**
			 * Send new url to App Engine Studio for sessionStorage update.
			 *
			 * See App Engine Studio docs for more info:
			 * https://servicenow.sharepoint.com/:w:/s/AppEngineStudio/Eb70tfob3MtOupks7123MwsBsVRAj-Bg3UsYIm4c_1rkhQ?e=F9Qe1F
			 */
			updateAesTabSession({ url: window.location.href });
		}
	),

	['UIB_ROUTER#APP_SHOW_SPLASH']: ({ dispatch, updateProperties, action }) => {
		PAGE_TITLE.reset();

		// uib-stage shows the splash page and is selecting this property
		const {
			routeState: { pageSysId: screenTypeSysId }
		} = action.payload;

		updateProperties({ pageSysId: null, screenTypeSysId });
		dispatch('UIB_APP#RESET_ROUTE_STATUS');
	},

	['UIB_ROUTER#APP_SHOW_PAGE']: ({ properties, action, dispatch }) => {
		const { appsSysId, pages } = properties;
		const { routeState = {}, applicationType } = action.payload;
		const sortedPages = pages.sort(pageListSorter);
		// `deleteSysId` is included in `snUXFActions.PAGE_DELETE_SUCCEEDED`
		const nextPage = routeState.deleteSysId
			? sortedPages.find((page) => page.sysId !== routeState.deleteSysId)
			: sortedPages[0];

		// Yes, this is a possible scenario due to race conditions
		if (!nextPage) {
			dispatch('UIB_ROUTER#APP_SHOW_SPLASH', action.payload);
			return;
		}

		if (applicationType === applicationTypes.workspace) {
			replaceWith(dispatch, screenNames.workspacePage, {
				appsSysId,
				pageSysId: nextPage.sysId
			});
		} else {
			replaceWith(dispatch, screenNames.appVariant, {
				appsSysId,
				pageSysId: nextPage.screenType,
				variantSysId: nextPage.sysId
			});
		}
	},

	['UIB_ROUTER#APP_PAGE_LOAD']: ({
		properties,
		action,
		dispatch,
		updateProperties
	}) => {
		const {
			routeStatus,
			pages = [],
			routeName,
			appRoutes = [],
			isComponentBuilder
		} = properties;
		const { routeState } = action.payload;
		const {
			pageSysId,
			variantSysId,
			dashboardId,
			overrideId,
			isNewlyCreatedTab,
			screenId
		} = routeState;
		const applicationType = getApplicationType(routeName, isComponentBuilder);
		let page = pages.find((page) => {
			switch (applicationType) {
				case applicationTypes.workspace:
					return page.sysId === pageSysId;
				case applicationTypes.app:
				case applicationTypes.components:
					return page.sysId === variantSysId;
			}
		});

		if (applicationType === applicationTypes.dashboards) {
			const dashboards = flattenDashboardRoutes(appRoutes);
			page = dashboards.find((dash) => dash.sysId === dashboardId);
		}

		if (page) {
			PAGE_TITLE.set(page.name, PAGE_TITLE_SUFFIX);
		}

		/**
		 * if the workspace is the same, but we are loading a page, evaluate the
		 * application state.  if the application is requesting a page change,
		 * deleting a page, or manually setting the hash, request a new page
		 *
		 * if the application is creating a page, we already have the page response,
		 * the list needs to be updated.
		 *
		 * update the properties
		 *
		 * reset the route status
		 */

		const pageRequestPayload = {};
		const noDomain = !!qpUtils.getQueryNoDomain(routeState.queryParams);
		noDomain && (pageRequestPayload.noDomain = true);

		switch (applicationType) {
			case applicationTypes.workspace:
				dispatch(snUXFActions.PAGE_REQUESTED, {
					sysId: routeState.pageSysId
				});
				break;
			case applicationTypes.app:
			case applicationTypes.components:
				dispatch('UIB_ROUTER#PAGE_REQUEST_SEQUENCE_INIT', {
					screenId: routeState.variantSysId,
					isCreatingPage: routeStatus.isCreatingPage,
					noDomain: noDomain
				});
				break;
			case applicationTypes.dashboards:
				if (isNewlyCreatedTab) {
					// the only way to pass data into forceTransitionTo is by
					// addition properties into routeState. Removing the two properties
					// which arent really properties on uib-app :facepalm:
					delete routeState['isNewlyCreatedTab'];
					delete routeState['screenId'];
					dispatch(snUXFActions.PAGE_REQUESTED, {
						...pageRequestPayload,
						sysId: screenId
					});
				} else if (overrideId) {
					dispatch(snUXFActions.PAGE_REQUESTED, {
						...pageRequestPayload,
						sysId: overrideId
					});
				} else if (page) {
					dispatch(snUXFActions.PAGE_REQUESTED, {
						...pageRequestPayload,
						sysId:
							page &&
							(applicationType === applicationTypes.dashboards
								? get(page, 'screen.id', '')
								: page.id)
					});
				} else {
					const routePage = searchAppRoutesForDashboard(dashboardId, appRoutes);
					if (routePage) {
						dispatch(snUXFActions.PAGE_REQUESTED, {
							...pageRequestPayload,
							sysId: routePage && routePage.screen && routePage.screen.id
						});
					} else {
						transitionTo(dispatch, screenNames.dashboardsHome, {
							show404: true
						});

						return;
					}
				}
		}

		updateProperties({
			routeStatus: {
				...routeStatus,
				isChangingPage: true
			}
		});

		dispatch(snUXFActions.PAGE_STATE_CLEARED);
	},
	['UIB_ROUTER#PAGE_REQUEST_SEQUENCE_INIT']: ({ action, dispatch }) => {
		const { screenId, isCreatingPage, noDomain } = action.payload;
		dispatch(snUXFActions.PAGE_REQUESTED, {
			sysId: screenId,
			noDomain,
			isCreatingPage
		});
	},
	['SN_UIB_APP#PAGE_REQUESTED']: checkForUnsavedChanges(
		({ action, properties, dispatch, updateProperties }) => {
			dispatch('UIB_APP#PAGE_REQUESTED', action.payload);

			PAGE_TITLE.reset();
			const {
				sysId,
				pageSysId,
				appsSysId,
				applicationType,
				queryParams,
				dashboardId,
				screenSysId
			} = action.payload;
			const { routeStatus } = properties;

			updateProperties({
				routeStatus: {
					...routeStatus,
					showPageInterstitial: true
				}
			});

			switch (applicationType) {
				case applicationTypes.workspace:
					transitionTo(dispatch, screenNames.workspacePage, {
						appsSysId,
						pageSysId: sysId
					});
					break;
				case applicationTypes.app:
				case applicationTypes.components:
					if (get(queryParams, 'isolatedEditMode', false)) {
						transitionTo(dispatch, screenNames.appHome, {
							appsSysId,
							queryParams
						});
					} else {
						if (sysId) {
							transitionTo(dispatch, screenNames.appVariant, {
								appsSysId,
								pageSysId,
								variantSysId: sysId,
								queryParams
							});
						} else {
							transitionTo(dispatch, screenNames.appPage, {
								appsSysId,
								pageSysId,
								queryParams
							});
						}
					}
					break;
				case applicationTypes.dashboards:
					if (get(queryParams, 'isolatedEditMode', false)) {
						transitionTo(dispatch, screenNames.dashboardsHome, {
							queryParams
						});
					} else if (!dashboardId && !sysId && screenSysId) {
						const dashboardPage =
							applicationType === applicationTypes.dashboards
								? flattenDashboardRoutes(properties.appRoutes).find(
										(route) => route.screen && route.screen.id === screenSysId
								  )
								: null;

						if (!dashboardPage) {
							transitionTo(dispatch, screenNames.dashboardsHome);
						}
						transitionTo(dispatch, screenNames.dashboardsDashboard, {
							dashboardId: dashboardPage && dashboardPage.sysId
						});
					} else {
						transitionTo(dispatch, screenNames.dashboardsDashboard, {
							dashboardId: dashboardId || sysId
						});
					}
					break;
			}
		}
	),
	...routerGraphQLHandlers
};

/**
 * @param dashboardId
 * @param appRoutes
 */
function searchAppRoutesForDashboard(dashboardId, appRoutes) {
	return findDashboard(appRoutes, (route) => route.id === dashboardId);
}

/**
 * @param appRoutes
 * @param pred
 */
function findDashboard(appRoutes, pred) {
	const routePage = appRoutes.find((route) => pred(route));
	if (!routePage) {
		const children = appRoutes.flatMap(({ children }) => children);
		return children && children.length > 0
			? findDashboard(children, pred)
			: null;
	} else {
		return routePage;
	}
}
