import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { t } from 'sn-translate';
import {
	PROTECTION_CHOICES_GRAPHQL_REQUEST_FAILED,
	PROTECTION_CHOICES_GRAPHQL_REQUEST_SENT,
	PROTECTION_CHOICES_REQUESTED,
	PROTECTION_CHOICES_REQUEST_SUCCEEDED,
	PROTECTION_CHOICES_REQUEST_FAILED
} from './constants';
import { effectProxy } from 'sn-uxf-builder-middleware';

export const protectionChoicesQuery = `
	query {
		glideRecord: GlideChoiceColumn_Query {
			protectionChoices: getChoiceFields(table:"sys_ux_macroponent", column:"sys_policy") {
				displayValue,
				rawValue
			}
		}
	}
`;

export const fetchProtectionChoicesEffect = createGraphQLEffect(
	protectionChoicesQuery,
	{
		successActionType: PROTECTION_CHOICES_REQUEST_SUCCEEDED,
		errorActionType: PROTECTION_CHOICES_GRAPHQL_REQUEST_FAILED
	}
);

const protectionChoicesRequested = ({ dispatch, updateState }) => {
	updateState({
		isLoadingProtectionChoices: true
	});

	dispatch(PROTECTION_CHOICES_GRAPHQL_REQUEST_SENT);
};

const protectionChoicesRequestedSucceeded = ({ action, updateState }) => {
	const { data } = action.payload;
	const protectionChoices = data.glideRecord.protectionChoices.map(
		(protectionChoice) => {
			// Map the "(empty)" choice to match our design and API contract
			// Set sysPolicy id to 'none' for UI, and parse it back to an empty string in 'mapContextToPayload'
			// to ensure selects, typeaheads, dropdowns, etc don't break
			if ('null' === protectionChoice.rawValue) {
				return {
					id: 'none',
					label: t('None (default)')
				};
			}

			return {
				id: protectionChoice.rawValue,
				label: protectionChoice.displayValue
			};
		}
	);

	updateState({
		protectionChoices,
		isLoadingProtectionChoices: false
	});
};

const protectionChoicesRequestedFailed = ({
	action,
	dispatch,
	updateState
}) => {
	updateState({
		isLoadingProtectionChoices: false
	});

	dispatch(PROTECTION_CHOICES_REQUEST_FAILED, action.payload);
};

export default {
	[PROTECTION_CHOICES_REQUESTED]: protectionChoicesRequested,
	[PROTECTION_CHOICES_GRAPHQL_REQUEST_SENT]: effectProxy(
		fetchProtectionChoicesEffect
	),
	[PROTECTION_CHOICES_REQUEST_SUCCEEDED]: protectionChoicesRequestedSucceeded,
	[PROTECTION_CHOICES_GRAPHQL_REQUEST_FAILED]: protectionChoicesRequestedFailed
};
