import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import get from 'lodash/get';

import {
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_UPDATE_ERROR_OCCURRED,
	UTILITIES_FETCH_SUCCEEDED,
	UTILITIES_UPDATE_SUCCEEDED
} from '../constants';

const query = `
query snExperienceSettingsConnected($sysId: ID!) {
    snExpSettings {
      settings {
        utilities(sysId: $sysId) {
		  headerSysId
		  searchSysId
          notifications
          globalSearch
          selectedSearchSource
          searchSources {
             id
             label
          }
        }
      }
    }
  }`;

export const utilitiesFetchRequestedEffect = createGraphQLEffect(query, {
	variableList: ['sysId'],
	successActionType: UTILITIES_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const utilitiesFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const {
		headerSysId,
		searchSysId,
		notifications,
		globalSearch,
		searchSources,
		selectedSearchSource
	} = get(payload, `data.snExpSettings.settings.utilities`, {});

	updateState({
		utilities: {
			headerSysId,
			searchSysId,
			notifications,
			globalSearch,
			searchSources,
			selectedSearchSource,
			updated: false
		}
	});
};

const mutation = `
mutation snExperienceSettingsConnected($sysId:ID! $notifications:Boolean! $globalSearch:Boolean! $selectedSearchSource:ID! ) {
  snExpSettings {
    settings {
		utilitiesUpdate(sysId:$sysId notifications:$notifications globalSearch:$globalSearch selectedSearchSource:$selectedSearchSource) {
		headerSysId
		searchSysId
		notifications
		globalSearch
		selectedSearchSource
		searchSources {
			id
			label
		}
      }
    }
  }
}`;

export const utilitiesUpdateRequestedEffect = createGraphQLEffect(mutation, {
	variableList: [
		'sysId',
		'notifications',
		'globalSearch',
		'selectedSearchSource'
	],
	successActionType: UTILITIES_UPDATE_SUCCEEDED,
	errorActionType: SETTINGS_UPDATE_ERROR_OCCURRED
});

export const utilitiesUpdateSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const {
		headerSysId,
		searchSysId,
		notifications,
		globalSearch,
		selectedSearchSource,
		searchSources
	} = get(payload, `data.snExpSettings.settings.utilitiesUpdate`, {});
	updateState({
		utilities: {
			headerSysId,
			searchSysId,
			notifications,
			globalSearch,
			selectedSearchSource,
			searchSources,
			updated: false
		}
	});
};
