import defineUIBAppContainer from './components/uib-app-container';
import defineDashboardAppContainer from './components/sn-dashboard-app-container';
import defineComponentAppContainer from './components/sn-component-app-container';
import isBrowserSupported, {
	showUnsupportedBrowserMessage
} from './utils/browserSupport';

if (isBrowserSupported()) {
	defineUIBAppContainer();
	defineDashboardAppContainer();
	defineComponentAppContainer();
} else {
	showUnsupportedBrowserMessage();
}
