import isEmpty from 'lodash/isEmpty';

/**
 * Converts the screen object to the payload for the page update action
 * @param {object} currentScreen - screen object containing the user changes
 * @param {object} pristineScreen - screen object without changes
 * @returns {object} payload - the payload for the screen update action
 */
export const createScreenPayloadFromObject = (
	currentScreen,
	pristineScreen,
	state
) => {
	const {
		pageDefinition,
		copyPageDefinition,
		isChangingPageDefinition
	} = state;
	const data = {};
	data.copyRootMacroponent = copyPageDefinition;
	if (currentScreen.name !== pristineScreen.name) {
		data.name = currentScreen.name;
	}
	if (currentScreen.active !== pristineScreen.active) {
		data.isActive = currentScreen.active;
	}
	if (currentScreen.order !== pristineScreen.order) {
		data.order = parseInt(currentScreen.order);
	}
	if (currentScreen.screenCondition !== pristineScreen.screenCondition) {
		data.screenCondition = currentScreen.screenCondition;
	}
	if (isChangingPageDefinition) {
		data.changeMacroponent = true;
		if (pageDefinition) {
			data.rootMacroponentId = pageDefinition;
		}
	}

	if (isEmpty(data)) {
		return null;
	}

	return {
		sysId: currentScreen.sysId,
		sysparm_transaction_scope: currentScreen.scopeId,
		data
	};
};
