import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { t } from 'sn-translate';
import { pageListSorter } from '../../utils/pageListSort';
import {
	EDIT_DASHBOARD_CLICKED,
	EDIT_ROUTE_CLICKED,
	EDIT_FIELDS_CLICKED,
	EDIT_PARAMETERS_CLICKED,
	CREATE_PAGE_CLICKED,
	OPEN_PAGE_CLICKED,
	EDIT_PAGE_CLICKED,
	EDIT_CONDITIONS_CLICKED,
	EDIT_AUDIENCES_CLICKED,
	COPY_PAGE_CLICKED
} from './actions';
import { applicationTypes } from '../../utils/routingConstants';
import { memoizeDeepN } from 'seismic-memo-utils';

const dashboardConfigurationActions = [
	{
		label: t('Edit'),
		icon: 'pencil-page-fill',
		type: EDIT_DASHBOARD_CLICKED
	}
];

export const pageTreeFromDashboards = (
	dashboards,
	parentId = '',
	child = false
) => {
	return dashboards.sort(pageListSorter).map((dashboard) => {
		return {
			id: dashboard.id,
			dashboardId: dashboard.id,
			parentId,
			type: child ? 'dashboard.child' : 'dashboard.parent',
			icon: 'document-outline',
			order: dashboard.order || 0,
			actionable: true,
			disabled: false,
			screenSysId: dashboard.screen.id,
			overrides: dashboard.screen.sysOverride,
			parentElementId: get(
				dashboard,
				'screen.contextState.parentInfo.compositionElementId',
				''
			),
			actions: [{ icon: 'ellipsis-v-fill', label: t('Configure Dashboard') }],
			configurationActions: child ? [] : dashboardConfigurationActions,
			label: dashboard.name,
			routeType: dashboard.routeType,
			pageSysId: dashboard.screen.id,
			children:
				Array.isArray(dashboard.children) && dashboard.children.length > 0
					? pageTreeFromDashboards(dashboard.children, dashboard.id, true)
					: []
		};
	});
};

export const pageTreeFromRoutes = (
	routes,
	applicationType,
	disabled,
	displayDomain,
	parentId = ''
) => {
	return (
		routes
			// sorting is done alphabetically by the backend ux-builder plugin
			.map((route) => {
				return {
					id: route.page.sysId,
					type: 'route',
					icon: 'document-outline',
					parentId,
					order: route.order || 0,
					disabled,
					actions: [],
					label: route.page.name,
					routeType: route.routeType,
					pageSysId: route.page.sysId,
					parentElementId: route.parentElementId,
					children: pagesFromPageVariants(
						route.page.variants.map((v) => ({
							...v,
							pageStatus: v.active ? 'active' : 'inactive'
						})),
						applicationType,
						disabled,
						displayDomain
					)
				};
			})
	);
};

export const pagesFromPageVariants = (
	pages,
	applicationType,
	disabled,
	displayDomain
) => {
	return pages.sort(pageListSorter).map((page) => {
		return {
			id: page.sysId,
			label: page.name,
			name: page.name,
			secondaryLabel: displayDomain ? page.domainName : undefined,
			disabled,
			actions: [],
			type: 'page',
			icon: 'minus-fill',
			order: page.order || 0,
			statuses:
				page.pageStatus === 'active'
					? [{ type: 'icon', label: 'Active', icon: 'presence-fill' }]
					: [
							{ type: 'icon', label: 'Inactive', icon: 'presence-offline-fill' }
					  ],
			pageStatus: page.pageStatus,
			children: !isEmpty(page.viewports)
				? pageTreeFromRoutes(
						page.viewports,
						applicationType,
						disabled,
						displayDomain,
						page.sysId
				  )
				: [],
			pageSysId: page.pageSysId,
			overrides: page.overrides,
			domainId: page.domainId,
			domainName: page.domainName
		};
	});
};

export const getLandingPagesList = (pages, applicationType, disabled) => {
	return pages.sort(pageListSorter).map((page) => {
		const viewports = (page.viewports || []).map((viewport, idx) => {
			const tabs = (viewport.tabs || []).map((tab) => ({
				id: tab.id,
				label: tab.name,
				icon: 'minus-fill',
				actionable: !disabled,
				disabled,
				pageSysId: tab.pageSysId || (tab.screen && tab.screen.id)
			}));

			return {
				id: idx,
				label: viewport.tempName || viewport.name,
				actionable: false,
				icon: 'window-outline',
				children: tabs.length > 0 ? tabs : undefined
			};
		});

		return {
			id: page.sysId,
			label: page.name,
			actionable: !disabled,
			disabled,
			type: 'page',
			icon: 'minus-fill',
			statuses:
				page.pageStatus === 'active'
					? [{ type: 'icon', label: 'Active', icon: 'presence-fill' }]
					: [
							{ type: 'icon', label: 'Inactive', icon: 'presence-offline-fill' }
					  ],
			pageStatus: page.pageStatus,
			children: viewports.length > 0 ? viewports : undefined,
			pageSysId: page.pageSysId
		};
	});
};

export const getTableLabel = (tables, table) => {
	if (!tables) {
		return table;
	}

	const tableOption = tables.find(({ id }) => id === table);

	if (!tableOption || !tableOption.label) {
		return table;
	}

	return tableOption.label.replace(`[${table}]`, '').trim();
};

export const getComponentsLoading = (status) => {
	switch (status) {
		case 'notStarted': {
			return true;
		}
		case 'started': {
			return true;
		}
		case 'succeeded': {
			return false;
		}
		case 'failed': {
			return false;
		}
		default: {
			return false;
		}
	}
};

export const transformPageList = memoizeDeepN(function (
	applicationType,
	routes,
	pages,
	pageVariantsEnabled,
	disabled,
	isPagesLoading,
	displayDomain
) {
	if (isPagesLoading) {
		return;
	}

	let pageList;

	if (applicationType === applicationTypes.dashboards) {
		pageList = isEmpty(routes) ? [] : pageTreeFromDashboards(routes);
	} else if (pageVariantsEnabled) {
		pageList = isEmpty(routes)
			? []
			: pageTreeFromRoutes(routes, applicationType, disabled, displayDomain);
	} else {
		pageList = isEmpty(pages)
			? []
			: [
					{
						id: 'pages',
						icon: 'home-outline',
						label: t('Landing Pages'),
						children: getLandingPagesList(pages, applicationType, false)
					}
			  ];
	}

	return pageList;
});

export const getPMPLabelsAndActions = memoizeDeepN(function (
	fieldsParamsEnabled,
	selectedPageIsTab,
	dynamicTabData,
	isDashboardBuilder
) {
	return {
		dropdownHeaderLabels: {
			TAB: t('Tab'),
			PAGE: t('Page'),
			DASHBOARD: t('Dashboard')
		},
		listHeaderLabel: t('Variants'),
		createDropdownActions: (item, label) => [
			{
				label: isDashboardBuilder
					? t('Edit settings')
					: selectedPageIsTab
					? t('Edit tab settings')
					: t('Edit page settings'),
				icon: 'gear-outline',
				type: EDIT_ROUTE_CLICKED,
				payload: { item }
			},
			...(fieldsParamsEnabled
				? [
						{
							label: t('Edit required parameters'),
							icon: 'database-outline',
							type: EDIT_FIELDS_CLICKED,
							payload: { item }
						},
						{
							label: t('Edit optional parameters'),
							icon: 'database-outline',
							type: EDIT_PARAMETERS_CLICKED,
							payload: { item }
						}
				  ]
				: []),
			...(selectedPageIsTab || isDashboardBuilder
				? []
				: [
						{
							label: t('Open'),
							icon: 'open-link-outline',
							type: OPEN_PAGE_CLICKED
						}
				  ]),
			{
				separator: true
			},
			{
				label: t('Create {0}', label.toLowerCase()),
				icon: 'plus-outline',
				type: CREATE_PAGE_CLICKED
			},
			...(selectedPageIsTab && dynamicTabData
				? [
						{
							label: t('Create data driven tab'),
							icon: 'plus-outline',
							type: CREATE_PAGE_CLICKED,
							payload: { isCreateDynamicTab: true }
						}
				  ]
				: [])
		],
		listHeaderActions: [
			{
				label: selectedPageIsTab
					? t('Edit tab variant settings')
					: t('Edit variant settings'),
				icon: 'gear-outline',
				type: EDIT_PAGE_CLICKED
			},
			{
				label: t('Edit conditions'),
				icon: 'filter-outline',
				type: EDIT_CONDITIONS_CLICKED
			},
			{
				label: t('Edit audiences'),
				icon: 'user-group-outline',
				type: EDIT_AUDIENCES_CLICKED
			},
			{
				label: t('Duplicate'),
				icon: 'clone-outline',
				type: COPY_PAGE_CLICKED
			}
		]
	};
});
