import { createCustomElement } from '@servicenow/ui-core';
import slotHandlers from './slotHandlers';
import toolboxHandlers from './toolboxHandlers';
import elementHandlers from './elementHandlers';
import lifecycleHandlers from './lifecycleHandlers';

const view = () => {
	return (
		<section>
			<slot />
		</section>
	);
};

createCustomElement('uib-ui-state-provider', {
	view,
	properties: {
		pageState: {
			default: null
		},
		selectedElementId: {
			default: null,
			selectable: true
		},
		selectedSlotName: {
			default: null,
			selectable: true
		},
		selectedSlotParent: {
			default: null,
			selectable: true
		},
		selectedSlotParentComponent: {
			default: null,
			selectable: true
		},
		selectedComponent: {
			default: null,
			selectable: true
		},
		selectedViewportElement: {
			default: null
		},
		onComponentSelect: {
			default: null,
			selectable: true
		},
		onToolboxClosed: {
			default: null,
			selectable: true
		},
		pageConfigTab: {
			default: 'style',
			selectable: true
		},
		rootElementId: {
			default: null
		},
		toolboxAnchor: {
			default: null,
			selectable: true
		},
		toolboxPositionTarget: {
			default: null,
			selectable: true
		},
		toolboxOpened: {
			default: null,
			selectable: true
		},
		hoveredSlotName: {
			default: null,
			selectable: true
		},
		hoveredSlotParent: {
			default: null,
			selectable: true
		},
		directionToAdd: {
			default: null,
			selectable: true
		},
		addNextToElementId: {
			default: null,
			selectable: true
		},
		hoveredElementId: {
			default: null,
			selectable: true
		},
		unregisteredElements: {
			default: {}
		},
		elementConfirmingDelete: {
			default: null
		},
		elementHasError: {
			default: false,
			selectable: true
		},
		isLegacy: {
			default: false
		}
	},
	actionHandlers: {
		...slotHandlers,
		...toolboxHandlers,
		...lifecycleHandlers,
		...elementHandlers
	}
});
