import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import '@servicenow/now-input';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from './uib-element-da-event-mapping-form.scss';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import {
	escapeScopeLabel,
	scopesAreNotEqual
} from '../../utils/escapeScopeLabel';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

export const UIB_ELEMENT_DA_EVENT_MAPPING_FORM_DISMISSED =
	'UIB_ELEMENT_DA_EVENT_MAPPING_FORM#FORM_DISMISSED';

const initializeStateFromProperties = ({ properties, updateState }) => {
	const {
		eventHandlers,
		eventMapping,
		userDomain,
		userScope,
		variantScope
	} = properties;
	const scopesAreUnequal = scopesAreNotEqual(userScope, variantScope);

	let eventMappingName,
		description,
		selectedHandler,
		handlerValues,
		applicationScope,
		currentTransactionScope,
		domain;

	if (eventMapping) {
		// initialize the state with all the form data
		selectedHandler = eventHandlers.find(
			(handler) => eventMapping.handlerId === handler.id
		);
		eventMappingName = eventMapping.name;
		description = eventMapping.description;
		handlerValues = getHandlerValuesFromPayloadFields(
			eventMapping.handlerValues
		);
		applicationScope = eventMapping.scope || {};
		currentTransactionScope = eventMapping.scope || {};
		domain = eventMapping.domain || {};
	} else {
		selectedHandler = eventHandlers[0];
		eventMappingName = 'New Event Mapping';
		description = '';
		handlerValues = getHandlerValuesFromPayloadFields(
			selectedHandler.payloadFields
		);
		applicationScope = userScope;
		currentTransactionScope = scopesAreUnequal ? variantScope : userScope;
		domain = userDomain;
	}

	updateState([
		{
			operation: 'set',
			path: 'eventMappingName',
			value: eventMappingName
		},
		{
			operation: 'set',
			path: 'description',
			value: description
		},
		{
			operation: 'set',
			path: 'selectedHandler',
			value: selectedHandler
		},
		{
			operation: 'set',
			path: 'handlerValues',
			value: handlerValues
		},
		{
			operation: 'set',
			path: 'applicationScope',
			value: applicationScope
		},
		{
			operation: 'set',
			path: 'domain',
			value: domain
		},
		{
			operation: 'set',
			path: 'loadingMessage',
			value: ''
		},
		{
			operation: 'set',
			path: 'currentTransactionScope',
			value: currentTransactionScope
		}
	]);
};

const getHandlerValuesFromPayloadFields = (fields) => {
	return fields.map((field) => {
		return {
			...field,
			id: field.name
		};
	});
};

const view = (state, { updateState }) => {
	const {
		clientTransformScript, // don't need this if scripting is not supported
		container,
		currentTransactionScope = {},
		disabled,
		domain,
		eventMappingName,
		description,
		handlerValues = [], // from eventMappingHandler
		loading,
		properties,
		selectedHandler = {},
		selectedMode = 'form',
		loadingMessage
	} = state;

	const {
		appConfig,
		applications,
		bindings = [],
		domains,
		elementId,
		eventHandlers,
		eventMapping,
		enableScripting,
		interfaceConfigurationComponent = {},
		scriptTemplate,
		supportDomains,
		userDomain,
		userScope,
		supportScope,
		isDomainExpanded,
		overrideActionDisabled
	} = properties;

	const escapedTransactionScopeLabel = escapeScopeLabel(
		currentTransactionScope.label
	);
	const escapedDomainLabel = escapeScopeLabel(domain.label);
	const disableSave = disabled || !eventMappingName;

	// HACK: Frustratingly, the Table API does not support editing a record in a domain other than
	// the users domain, unless the user is in a global domain. So, we're doing a hack to make the
	// transaction message imply that the record is readonly by simulating that the record is in
	// a private domain that the user does not have access to (even if that isnt true)
	// This disallows the user from editing the record directly across domains, but the user can still
	// override it into their domain.
	const transactionDomains =
		supportDomains &&
		eventMapping &&
		userDomain.id !== 'global' &&
		userDomain.id !== eventMapping.domain.id
			? domains.filter((domain) => domain.id !== eventMapping.domain.id)
			: domains;

	const recordProtectionPolicy = eventMapping && eventMapping.sysPolicy;
	const scopesAreUnequal = scopesAreNotEqual(
		userScope,
		currentTransactionScope
	);

	return (
		<div className="container">
			{loadingMessage || loading ? (
				<LoadingStage
					size={'sm'}
					loadingStageContent={loadingMessage || t('Loading mapping record...')}
				/>
			) : (
				<Fragment>
					<div
						className="sn-uib-event-mapping-configuration-handler-details-wrapper"
						hook-insert={({ elm }) => updateState({ container: elm })}>
						{/* Only display the transaction alert if editing an existing record
						and scope is defined (which it should always be)
						OR if creating a new record with user scope and variant scope unequal */}
						{(eventMapping && eventMapping.scope) || scopesAreUnequal ? (
							<sn-uib-transaction-alert
								size="sm"
								applications={applications}
								domains={domains}
								userScope={userScope}
								userDomain={userDomain}
								supportDomains={supportDomains}
								requestScopeId={currentTransactionScope.id}
								requestScopeName={escapedTransactionScopeLabel}
								requestDomainId={domain.id}
								requestDomainName={escapedDomainLabel}
								recordProtectionPolicy={recordProtectionPolicy}
								supportScope={supportScope}
								requestItemType={'Declarative action event mapping'}
								isDomainExpanded={isDomainExpanded}
								recordIsOverridenInCurrentDomain={overrideActionDisabled}
							/>
						) : null}
						<now-input
							label={t('Name')}
							value={eventMappingName}
							manage-value
							required
							disabled={disabled}
							append-to-payload={{ fieldName: 'eventMappingName' }}
						/>
						<now-input
							label={t('Description')}
							value={description}
							manage-value
							disabled={disabled}
							append-to-payload={{ fieldName: 'description' }}
						/>
						<div className="two-column-flex">
							<div className="column">
								<now-input
									label={t('Application scope')}
									value={escapedTransactionScopeLabel}
									readonly
									disabled
								/>
							</div>
							{supportDomains ? (
								<div className="column">
									<now-input
										label={t('Domain')}
										value={escapedDomainLabel}
										readonly
										disabled
									/>
								</div>
							) : null}
						</div>
						<div className="event-mapping-handler-header-section">
							<now-dropdown
								items={eventHandlers}
								selectedItems={[selectedHandler.id || eventHandlers[0].id]}
								manage-selected-items
								size="md"
								variant="secondary"
								select="single"
								panelFitProps={{}}
								configAria={{}}
								disabled={disabled}
								append-to-payload={{ type: 'eventHandler' }}></now-dropdown>
							{handlerValues.length > 0 && enableScripting ? (
								<section className="end">
									<label>{t('Mode')}</label>
									<now-dropdown
										items={[
											{ label: t('Form'), id: 'form' },
											{ label: t('Script'), id: 'script' }
										]}
										selectedItems={[selectedMode]}
										variant="secondary"
										select="single"
										disabled={disabled}
										append-to-payload={{ type: 'mode' }}></now-dropdown>
								</section>
							) : (
								''
							)}
						</div>
						<div className="body">
							{handlerValues.length > 0 ? (
								<sn-uib-event-mapping-handler-values-config
									key={selectedHandler.id} // sysId of the eventHandler
									app-config={appConfig}
									bindings={bindings}
									container={container}
									elementId={elementId}
									handler-values={handlerValues.map((handlerValue = {}) => ({
										...handlerValue,
										disabled,
										isStatic: !(
											typeof handlerValue.value === 'string' &&
											handlerValue.value.startsWith('@')
										)
									}))}
									interface-configuration-component={
										interfaceConfigurationComponent
									}
									enable-scripting={
										enableScripting && selectedMode === 'script'
									}
									client-transform-script={
										clientTransformScript || scriptTemplate
									}
									disabled={disabled}
								/>
							) : null}
						</div>
					</div>
					<div className={`footer${eventMapping ? '' : ' --flex-end'}`}>
						{eventMapping ? (
							<now-button
								className="delete"
								label={t('Delete')}
								variant="primary-negative"
								disabled={disabled}
								append-to-payload={{ type: 'delete' }}
							/>
						) : null}
						<div className="end-buttons">
							<now-button
								className="cancel"
								label={t('Cancel')}
								append-to-payload={{ type: 'cancel' }}
							/>
							<now-button
								className="add-edit"
								label={t('Save')}
								variant="primary"
								disabled={disableSave}
								append-to-payload={{ type: eventMapping ? 'edit' : 'add' }}
							/>
						</div>
					</div>
				</Fragment>
			)}
		</div>
	);
};

export default createCustomElement('uib-element-da-event-mapping-form', {
	view,
	styles,
	initialState: {
		applicationScope: {
			id: '',
			label: ''
		},
		container: null,
		currentScript: null,
		currentTransactionScope: {
			id: '',
			label: ''
		},
		domain: {
			id: '',
			label: ''
		},
		handlerValues: [],
		loadingMessage: '',
		selectedMode: 'form'
	},
	properties: {
		appConfig: {
			default: {}
		},
		applications: {
			default: []
		},
		bindings: {
			default: []
		},
		domains: {
			default: []
		},
		elementId: {
			default: ''
		},
		enableScripting: {
			default: true
		},
		event: {
			default: {}
		},
		eventHandlers: {
			default: []
		},
		eventMapping: {
			default: null
		},
		interfaceConfigurationComponent: {
			default: {}
		},
		scriptTemplate: {
			default: ''
		},
		supportDomains: {
			default: false
		},
		userScope: {
			default: null
		},
		userDomain: {
			default: null
		},
		supportScope: {
			default: false
		},
		isDomainExpanded: {
			default: false
		},
		overrideActionDisabled: {
			default: false
		},
		variantScope: {
			default: null
		}
	},
	dispatches: {},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: initializeStateFromProperties,
		[COMPONENT_PROPERTY_CHANGED]: initializeStateFromProperties,
		['EVENT_MAPPING_HANDLER_VALUE_CONFIG#HANDLER_VALUE_CHANGED']: {
			effect: ({ action, state, updateState }) => {
				const { handlerValues = [] } = state;
				const {
					handlerValueId: updatedHandlerValueId,
					value: updatedValue
				} = action.payload;

				const newHandlerValues = handlerValues.map((handlerValue) => {
					if (handlerValue.id !== updatedHandlerValueId) {
						return handlerValue;
					}

					handlerValue.value = updatedValue;
					return handlerValue;
				});

				// Update the handlerValues in the payloadMapping
				// Based on whatever the payload shape is
				updateState({
					operation: 'set',
					path: 'handlerValues',
					value: newHandlerValues
				});
			},
			stopPropagation: true
		},
		['EVENT_MAPPING_HANDLER_VALUE_CONFIG#HANDLER_VALUE_SCRIPT_CHANGED']: {
			effect: ({ action, updateState }) => {
				// Update the script in the payload mapping
				// if it's supported
			},
			stopPropagation: true
		},
		['NOW_BUTTON#CLICKED']: {
			effect: ({ action, dispatch, state, properties, updateState }) => {
				const { type } = action.payload || {};
				const {
					currentTransactionScope,
					selectedHandler,
					handlerValues,
					eventMappingName,
					description
				} = state;

				const { eventMapping, event } = properties;
				let loadingMessage;

				switch (type) {
					case 'add':
						loadingMessage = t('Creating mapping record...');
						dispatch('CREATE_EVENT_MAPPING_REQUESTED', {
							selectedHandler,
							handlerValues,
							eventMappingName,
							description,
							applicationScope: currentTransactionScope,
							event,
							overrides: null
						});
						break;
					case 'delete':
						loadingMessage = t('Deleting mapping record...');
						dispatch('DELETE_EVENT_MAPPING_REQUESTED', {
							recordId: eventMapping.sysId,
							sysparm_transaction_scope: eventMapping.scope.id,
							sysparm_query_no_domain: true
						});
						break;
					case 'edit':
						loadingMessage = t('Updating mapping record...');

						dispatch('UPDATE_EVENT_MAPPING_REQUESTED', {
							selectedHandler,
							handlerValues,
							eventMappingName,
							description,
							eventMapping
						});
						break;
					case 'cancel':
						dispatch(UIB_ELEMENT_DA_EVENT_MAPPING_FORM_DISMISSED);
						break;
				}
				updateState({
					operation: 'set',
					path: 'loadingMessage',
					value: loadingMessage
				});
			},
			stopPropagation: true
		},
		['NOW_DROPDOWN#ITEM_CLICKED']: {
			effect: ({ action, properties, updateState }) => {
				if (action.payload.type === 'mode') {
					const {
						item: { id }
					} = action.payload || {};
					updateState({
						operation: 'set',
						path: 'selectedMode',
						value: id
					});
				}

				if (action.payload.type === 'eventHandler') {
					const {
						item: { id: value }
					} = action.payload || {};
					const { eventHandlers = [] } = properties;
					if (value) {
						const selectedHandler =
							eventHandlers.find((handler) => handler.id === value) || {};
						console.log('SELECTED', selectedHandler);
						updateState([
							{
								operation: 'set',
								path: 'selectedHandler',
								value: selectedHandler
							},
							{
								operation: 'set',
								path: 'handlerValues',
								value: getHandlerValuesFromPayloadFields(
									selectedHandler.payloadFields
								)
							}
						]);
					} else {
						updateState([
							{
								operation: 'set',
								path: 'selectedHandler',
								value: {}
							},
							{
								operation: 'set',
								path: 'handlerValues',
								value: []
							}
						]);
					}
				}
			},
			stopPropagation: true
		},
		['NOW_INPUT#INPUT']: {
			effect: ({ action, updateState }) => {
				const { fieldName, fieldValue } = action.payload || {};
				if (fieldName === 'eventMappingName') {
					updateState({
						operation: 'set',
						path: 'eventMappingName',
						value: fieldValue
					});
				}

				if (fieldName === 'description') {
					updateState({
						operation: 'set',
						path: 'description',
						value: fieldValue
					});
				}
			},
			stopPropagation: true
		},
		['NOW_TYPEAHEAD#SELECTED_ITEM_SET']: {
			effect: ({ action, properties, updateState }) => {
				const { value } = action.payload || {};
				const { eventHandlers = [] } = properties;
				if (value) {
					const selectedHandler =
						eventHandlers.find((handler) => handler.id === value) || {};
					console.log('SELECTED', selectedHandler);
					updateState([
						{
							operation: 'set',
							path: 'selectedHandler',
							value: selectedHandler
						},
						{
							operation: 'set',
							path: 'handlerValues',
							value: getHandlerValuesFromPayloadFields(
								selectedHandler.payloadFields
							)
						}
					]);
				} else {
					updateState([
						{
							operation: 'set',
							path: 'selectedHandler',
							value: {}
						},
						{
							operation: 'set',
							path: 'handlerValues',
							value: []
						}
					]);
				}
			},
			stopPropagation: true
		},
		['SN_UIB_TRANSACTION_ALERT#REQUEST_STARTED']: {
			effect: ({ updateState }) => {
				updateState({
					operation: 'set',
					path: 'loading',
					value: true
				});
			},
			stopPropagation: true
		},
		['SN_UIB_TRANSACTION_ALERT#TRANSACTION_IN_SCOPE']: {
			effect: ({ action, updateState }) => {
				const { isNecessaryTransactionInProgress } = action.payload || {};
				updateState([
					{
						operation: 'set',
						path: 'loading',
						value: false
					},
					{
						operation: 'set',
						path: 'disabled',
						value: !isNecessaryTransactionInProgress
					}
				]);
			},
			stopPropagation: true
		},
		['SN_UIB_TRANSACTION_ALERT#UNLOCK_TO_OVERRIDE_CLICKED']: {
			effect: ({ dispatch }) => {
				dispatch('CREATE_OVERRIDE_REQUESTED');
			},
			stopPropagation: true
		}
	}
});
