import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	USER_DATA_PROPERTY,
	REQUEST_USER_FETCH,
	USER_FETCH_STARTED,
	USER_FETCH_SUCCEEDED,
	USER_FETCH_FAILED,
	USER_FETCH_ERROR
} from './constants';
import analyticsHelper from './analytics';
import { effectProxy } from 'sn-uxf-builder-middleware';

const fetchUserDataQuery = `
query {
	GlideDomain_Query {
	  user {
		sys_id
		firstName
		lastName
		fullName
		roles
	  }
	}
  }
`;

export const fetchUserEffect = createGraphQLEffect(fetchUserDataQuery, {
	startActionType: USER_FETCH_STARTED,
	successActionType: USER_FETCH_SUCCEEDED,
	errorActionType: USER_FETCH_FAILED
});

export default {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
		dispatch(REQUEST_USER_FETCH);
	},
	[REQUEST_USER_FETCH]: effectProxy(fetchUserEffect),
	[USER_FETCH_SUCCEEDED]: ({
		updateProperties,
		dispatch,
		action: { payload }
	}) => {
		const { errors = [] } = payload;

		if (errors.length) {
			console.error('GlideDomain_Query.user has returned errors: ', errors);
			dispatch(USER_FETCH_ERROR, errors);
			return;
		}

		const { user } = payload.data.GlideDomain_Query;
		updateProperties({ [USER_DATA_PROPERTY]: user });
		analyticsHelper.sendUserEnterAnalytic(user); // should take care in stubbing this out in tests
	},
	[USER_FETCH_FAILED]: () => {}, // FIXME(eguo): link up to error handling??
	[USER_FETCH_ERROR]: () => {}
};
