import isEmpty from 'lodash/isEmpty';
import { flattenAppRoutes } from './appRoutes';

export const getFlaggedPropsAndOutOfSync = (
	fieldsParamsEnabled,
	appRoutes,
	selectedPageId,
	pageProperties
) => {
	if (fieldsParamsEnabled) {
		let outOfSyncFields, outOfSyncParams, restrictedPageProps;
		const page =
			!isEmpty(appRoutes) &&
			flattenAppRoutes(appRoutes).find(
				({ page }) => page.sysId === selectedPageId
			);

		if (page && pageProperties) {
			let pageFields, pageParameters;
			if (page.fields) {
				pageFields = Object.values(page.fields).map(({ name }) => name);
				const fields = pageFields.filter(
					(name) =>
						!pageProperties.find(({ name: propName }) => propName === name)
				);
				outOfSyncFields = !isEmpty(fields) ? fields : null;
			}
			if (page.optionalParameters) {
				pageParameters = Object.values(page.optionalParameters).map(
					({ name }) => name
				);
				const params = pageParameters.filter(
					(name) =>
						!pageProperties.find(({ name: propName }) => propName === name)
				);
				outOfSyncParams = !isEmpty(params) ? params : null;
			}
			restrictedPageProps = pageProperties.map((prop) => ({
				...prop,
				isField: !!pageFields.find((name) => name === prop.name),
				isParam: !!pageParameters.find((name) => name === prop.name)
			}));
			restrictedPageProps.sort((a, b) => {
				if (a.isField && b.isField) {
					return (
						pageFields.findIndex((name) => name === a.name) -
						pageFields.findIndex((name) => name === b.name)
					);
				} else if (a.isField && !b.isField) {
					return 1;
				} else if (!a.isField && b.isField) {
					return -1;
				} else {
					return 0;
				}
			});
			return { restrictedPageProps, outOfSyncFields, outOfSyncParams };
		} else {
			return {
				restrictedPageProps: pageProperties,
				outOfSyncFields: [],
				outOfSyncParams: []
			};
		}
	} else {
		return {
			restrictedPageProps: pageProperties,
			outOfSyncFields: [],
			outOfSyncParams: []
		};
	}
};
