import find from 'lodash/find';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
const filter = (value) => {
	return value.replace(/[\u00A0-\u9999<>&]/gim, () => '');
};

import {
	ADD_BTN,
	BUTTON_BARE_CLICKED,
	DELETE_BTN,
	INPUT_CHANGED,
	ICON_SELECTED,
	ITEM_SELECTED,
	MAX_NUMBER_BUTTONS,
	PAGE_TOOLBAR,
	SETTINGS_UPDATED,
	TABLE_CONFIG,
	TOOLBAR_ADD_BUTTON_REQUESTED,
	TOOLBAR_COMPONENT_BOOTSTRAPPED,
	URLDOC
} from '../../constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

// Internal components
import '../sn-experience-settings-link';
import 'sn-ui-builder-components';

// NDS
import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-input';
import '@servicenow/now-select';
import '@servicenow/now-text-link';

import styles from '../../styles/forms.scss';

const groups = [
	{ id: 'top', label: t('Top') },
	{ id: 'bottom', label: t('Bottom') }
];

const view = ({ properties: { sysId, buttons, routes, readonly } }) => {
	let count = 0;
	return (
		<div>
			<now-heading
				label={t('Side navigation')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{t('Choose up to 7 pages to add to your side navigation.')}{' '}
				{t('Each page can have an icon and reflect a certain order.')}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/UIB_Workspace_Side_Navigation_Settings`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="flex-row">
				<div className="flex-end">
					{!readonly ? (
						<now-button-bare
							label={t('Add a page')}
							icon-start="plus-outline"
							variant="secondary"
							append-to-payload={{ name: ADD_BTN }}></now-button-bare>
					) : null}
				</div>
			</div>
			<div className="fieldset-w100">
				{buttons.map(
					({ label, icon, route, viewportRoute, group, order, deleted }) => {
						const selectedRoute = find(routes, (o) => o.id === route);
						const result = (
							<div className="flex-row">
								{!viewportRoute && !readonly ? (
									<now-select
										className="field-16"
										label={t('Page route')}
										items={routes}
										selectedItem={route}
										append-to-payload={{ name: 'route', id: count }}
									/>
								) : (
									''
								)}

								{!viewportRoute && readonly ? (
									<now-input
										className="field-16"
										label={t('Page route')}
										value={selectedRoute ? selectedRoute.label : ''}
										readonly={readonly}
									/>
								) : (
									''
								)}

								{viewportRoute ? (
									<now-input
										className="field-16"
										name="viewportRoute"
										label={t('Viewport route')}
										value={viewportRoute}
										append-to-payload={{ name: 'viewportRoute', id: count }}
										readonly={readonly}
									/>
								) : (
									''
								)}

								{!readonly ? (
									<sn-uib-icon-property-input
										className="field-32"
										placeholder={t('Icon')}
										label={t('Icon')}
										icons="@uib-app/icons"
										value={icon}
										append-to-payload={{ id: count }}
										isStatic={true}
										disabled={readonly}
										dataBindingEnabled={false}
										scriptingEnabled={false}
										helper-content={t('Choose icon')}
									/>
								) : (
									<now-input
										className="field-32"
										name="icon"
										label={t('Icon')}
										value={icon}
										readonly={readonly}
									/>
								)}

								<now-input
									className="field-16"
									name="label"
									label={t('Label')}
									value={label}
									append-to-payload={{ id: count }}
									readonly={readonly}
								/>

								{!readonly ? (
									<now-select
										className="field-16"
										label={t('Group')}
										items={groups}
										selectedItem={group}
										append-to-payload={{ name: 'group', id: count }}
									/>
								) : (
									<now-input
										className="field-16"
										name="group"
										label={t('Group')}
										value={group}
										readonly={readonly}
									/>
								)}

								<now-input
									className="field-16"
									label={t('Order')}
									value={order}
									append-to-payload={{ name: 'order', id: count }}
									pattern="^[0-9]+$"
									readonly={readonly}
								/>
								<now-button-bare
									className="icon-btn field-16"
									icon-start="trash-outline"
									append-to-payload={{
										name: DELETE_BTN,
										id: count
									}}
									disabled={readonly}></now-button-bare>
							</div>
						);
						count++;
						return !deleted ? result : '';
					}
				)}
			</div>
			{sysId ? (
				<sn-experience-settings-link
					sys-id={sysId}
					table={TABLE_CONFIG}
					/* prettier-ignore */
					label={t('Advanced side navigation settings')}></sn-experience-settings-link>
			) : (
				''
			)}
		</div>
	);
};

const iconSelectedEffect = ({
	action: {
		payload: { value, id }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, {
		page: PAGE_TOOLBAR,
		name: 'icon',
		value,
		id,
		key: 'buttons'
	});
};

const inputChangedEffect = ({
	action: {
		payload: { name, fieldValue, id }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, {
		page: PAGE_TOOLBAR,
		name,
		value: filter(fieldValue),
		id,
		key: 'buttons'
	});
};

const itemSelectedEffect = ({
	action: {
		payload: { name, value, id }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, {
		page: PAGE_TOOLBAR,
		name,
		value,
		id,
		key: 'buttons'
	});
};

const buttonClickedEffect = ({
	action: {
		payload: { name, id }
	},
	properties: { buttons },
	dispatch
}) => {
	if (name === DELETE_BTN) {
		dispatch(SETTINGS_UPDATED, {
			page: PAGE_TOOLBAR,
			name: 'deleted',
			value: true,
			id,
			key: 'buttons'
		});
	}

	if (name === ADD_BTN) {
		dispatch(TOOLBAR_ADD_BUTTON_REQUESTED);
	}
};

createCustomElement('sn-experience-settings-toolbar', {
	view,
	properties: {
		sysId: {
			required: true,
			schema: { type: 'string' }
		},
		buttons: {
			default: []
		},
		routes: {
			default: []
		},
		readonly: {
			schema: { type: 'boolean' }
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(TOOLBAR_COMPONENT_BOOTSTRAPPED);
		},
		[INPUT_CHANGED]: {
			stopPropagation: true,
			effect: inputChangedEffect
		},
		[ICON_SELECTED]: {
			stopPropagation: true,
			effect: iconSelectedEffect
		},
		[ITEM_SELECTED]: {
			stopPropagation: true,
			effect: itemSelectedEffect
		},
		[BUTTON_BARE_CLICKED]: {
			stopPropagation: true,
			effect: buttonClickedEffect
		}
	},
	styles
});
