import { createCustomElement } from '@servicenow/ui-core';
import { droppableBehavior } from 'sn-ui-builder-components';
import { keyboardNavBehavior } from 'sn-ui-builder-components/src/behaviors/droppable/utils';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
const {
	DevxKeyFocusBehavior: keyFocusBehavior,
	createFocusRef
} = DevxKeyFocusBehavior;

import styles from './_uib-placeholder.scss';

createCustomElement('uib-placeholder', {
	view: (state, { dispatch }) => {
		const { active } = state.behaviors.droppable;
		const {
			pageType,
			elementId,
			isDragging,
			draggedElement
		} = state.properties;
		const listeners = isDragging
			? keyboardNavBehavior({
					id: elementId,
					dispatch,
					hoverAction: 'UIB_DND_DROPZONE#DRAG_ENTER',
					blurAction: 'UIB_DND_DROPZONE#CLEAR_REGIONS'
			  })
			: {};

		const focusId = `${elementId}_PLACEHOLDER`;
		return (
			<div
				className="dropzone"
				ref={createFocusRef(focusId, {
					Escape: `TOOLBOX_COMPONENT_${draggedElement}`
				})}
				hook-insert={(node) => {
					dispatch('UIB_DND_DROPZONE#MOUNTED', {
						el: node.elm
					});
					dispatch('UIB_STAGE#DND_ENTRYPOINT_CREATED', { id: focusId });
				}}
				{...listeners}>
				<sn-uib-placeholder page-type={pageType} drop-zone-active={active} />
			</div>
		);
	},
	actionHandlers: {
		'UIB_DND_DROPZONE#DROPPED': {
			effect: ({
				action,
				dispatch,
				state: {
					properties: { elementId, slotName }
				}
			}) => {
				const {
					payload: { dataTransfer }
				} = action;
				dispatch('EMPTY_SLOT#DROPPED', {
					dataTransfer,
					parentId: elementId,
					slotName
				});
			}
		}
	},
	actions: {
		'UIB_DND_DROPZONE#DROPPED': {
			stopPropagation: true,
			private: true
		}
	},
	behaviors: [droppableBehavior, keyFocusBehavior],
	properties: {
		elementId: {
			default: null
		},
		slotName: {
			default: null
		},
		pageType: {
			default: null
		},
		isDragging: {
			default: false
		},
		draggedElement: {
			default: null
		}
	},
	styles
});
