import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import throttle from 'lodash/throttle';
import isEmpty from 'lodash/isEmpty';
import cuid from 'cuid';
import { elementRefMap, onNextFrame } from 'sn-ui-builder-components';

const ELEMENT_CONTROLS_HEIGHT = 33;
const THROTTLE_DELAY = 50;

const getVectors = (ref) => {
	if (ref && ref.current) {
		return ref.current.getBoundingClientRect();
	}
	return {
		top: 0,
		right: 0,
		bottom: 0,
		left: 0,
		x: 0,
		y: 0,
		width: 0,
		height: 0
	};
};
const updatePosition = (
	vnode,
	elementVector,
	container,
	resizeInfo = { left: null, right: null }
) => {
	onNextFrame(() => {
		const containerVector = container.getBoundingClientRect();
		const containerScrollTop = container.scrollTop;
		const controls = vnode.elm.shadowRoot.getElementById(
			'uib-element-controls'
		);
		let controlsWidth = elementVector.width;
		if (controls) {
			controlsWidth = controls.getBoundingClientRect().width;
		}
		let leftPosition;
		if (
			resizeInfo.left &&
			!resizeInfo.right &&
			controlsWidth > elementVector.width
		) {
			leftPosition =
				elementVector.x -
				containerVector.x +
				(elementVector.width - controlsWidth);
		} else {
			leftPosition = elementVector.x - containerVector.x;
		}
		const topPosition =
			elementVector.y -
			containerVector.y +
			containerScrollTop -
			ELEMENT_CONTROLS_HEIGHT;
		return (
			(vnode.elm.style.position = 'absolute') &&
			(vnode.elm.style.top = `${topPosition}px`) &&
			(vnode.elm.style.left = `${leftPosition}px`)
		);
	});
};

const requestRerender = (elementId, dispatch, nullifyRequest = false) => {
	onNextFrame(() => {
		dispatch(
			'UIB_ELEMENT_CONTROLS_PROVIDER#RERENDER_REQUESTED',
			!nullifyRequest ? elementId : null
		);
	});
	return null;
};

const renderSelectedElementControls = (
	element,
	idToRerender,
	container,
	elementConfirmingDelete,
	dispatch,
	disabled,
	isModalDescendant,
	parentModalId
) => {
	if (isEmpty(element) || !elementRefMap.get(element.id)) {
		return null;
	} else if (!elementRefMap.get(element.id) && idToRerender !== element.id) {
		return requestRerender(element.id, dispatch);
	}
	const { ref: elementRef, resizeInfo } = elementRefMap.get(element.id);

	const selectedElementVector = getVectors(elementRef);
	if (selectedElementVector.x === 0 && idToRerender === element.id) {
		return requestRerender(element.id, dispatch, true);
	}

	return (
		<sn-uib-workspace-element-controls
			hook-insert={(vnode) => {
				updatePosition(vnode, selectedElementVector, container, resizeInfo);
			}}
			hook-update={(vnode) => {
				updatePosition(vnode, selectedElementVector, container, resizeInfo);
			}}
			element={{
				id: element.id,
				icon: element.component.icon,
				displayName: element.instanceName
			}}
			append-to-payload={{
				isModalDescendant,
				parentModalId
			}}
			focused={true}
			keymaps={`@${element.id}_keybindings/element`}
			hovered={false}
			disabled={disabled}
			confirm={elementConfirmingDelete === element.id}
			resize-info={resizeInfo}
		/>
	);
};

const renderHoveredElementControls = (
	element,
	container,
	elementConfirmingDelete,
	disabled
) => {
	if (!elementRefMap.get(element.id)) {
		return null;
	}

	const { ref: elementRef, resizeInfo } = elementRefMap.get(element.id);

	const hoveredElementVector = elementRef.current
		? elementRef.current.getBoundingClientRect()
		: null;
	return (
		<sn-uib-workspace-element-controls
			hook-insert={(vnode) => {
				hoveredElementVector &&
					updatePosition(vnode, hoveredElementVector, container, resizeInfo);
			}}
			hook-update={(vnode) => {
				hoveredElementVector &&
					updatePosition(vnode, hoveredElementVector, container, resizeInfo);
			}}
			element={{
				id: element.id,
				icon: element.component.icon,
				displayName: element.instanceName
			}}
			focused={false}
			keymaps={`@${element.id}_keybindings/element`}
			hovered={true}
			disabled={disabled}
			confirm={elementConfirmingDelete === element.id}
		/>
	);
};

const view = (
	{
		properties: {
			selectedElementId,
			hoveredElementId,
			scrollContainer,
			elementConfirmingDelete,
			isResizing,
			isDragging,
			flatPage,
			isPanelTransitioning,
			disabled,
			isWorkspaceApp
		},
		idToRerender
	},
	{ dispatch }
) => {
	const container = scrollContainer.current;
	const selectedElement = flatPage[selectedElementId] || {};

	const hoveredElement = flatPage[hoveredElementId] || {};
	const { isModalDescendant, parentModalId } = selectedElement;
	return (
		<div
			className="uib-element-controls-field"
			style={{ position: 'relative' }}
			on-click={(event) => event.stopPropagation()}
			hidden={
				isResizing || isDragging || isPanelTransitioning ? 'true' : false
			}>
			{[
				renderSelectedElementControls(
					selectedElement,
					idToRerender,
					container,
					elementConfirmingDelete,
					dispatch,
					disabled,
					isModalDescendant,
					parentModalId
				),
				renderHoveredElementControls(
					hoveredElement,
					container,
					elementConfirmingDelete,
					disabled
				)
			]}
		</div>
	);
};

export default createCustomElement('uib-workspace-element-controls-provider', {
	view,
	properties: {
		selectedElementId: { default: null },
		hoveredElementId: { default: null },
		scrollContainer: { default: null },
		elementConfirmingDelete: { default: null },
		isDragging: { default: false },
		isResizing: { default: false },
		lastDroppedElementId: { default: null },
		flatPage: { default: {} },
		isPanelTransitioning: { default: false },
		disabled: { default: false }
	},
	initialState: {
		idToRerender: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			window.addEventListener(
				'resize',
				throttle(() => {
					dispatch('UIB_ELEMENT_CONTROLS_PROVIDER#RERENDER_REQUESTED', cuid());
				}, THROTTLE_DELAY)
			);
		},
		['UIB_ELEMENT_CONTROLS_PROVIDER#RERENDER_REQUESTED']: ({
			action,
			state,
			updateState
		}) => {
			if (state.idToRerender !== action.payload) {
				updateState({ idToRerender: action.payload });
			}
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name }
			},
			dispatch
		}) => {
			if (name === 'lastDroppedElementId') {
				requestRerender(cuid(), dispatch);
			}
		}
	}
});
