export default {
	'UIB_TOOLBOX#SET_PROPERTIES': ({ action, updateProperties }) => {
		const {
			toolboxAnchor,
			selectedSlotParent,
			selectedSlotName,
			onSelect,
			onClose
		} = action.payload;
		updateProperties({
			toolboxAnchor
		});
		updateProperties({
			selectedSlotParent,
			selectedSlotName,
			onComponentSelect: onSelect,
			onToolboxClosed: onClose
		});
	},
	'UIB_TOOLBOX#SET_POSITION': ({ action, updateProperties }) => {
		const { toolboxPositionTarget } = action.payload;
		updateProperties({ toolboxPositionTarget });
	},
	'UIB_TOOLBOX#SET_OPENED': ({
		dispatch,
		action,
		updateProperties,
		properties: { onToolboxClosed }
	}) => {
		const { value: toolboxOpened } = action.payload;

		const update = () => updateProperties({ toolboxOpened });
		if (toolboxOpened) {
			setTimeout(update, 100); // let the call stack settle before showing toolbox; DEF0111672
		} else {
			update();
			onToolboxClosed && onToolboxClosed({ dispatch });
			updateProperties({ onToolboxClosed: null });
		}
	}
};
