import get from 'lodash/get';

/**
 * Processor that takes in the currently configured property
 * and configures it for reference type
 *
 * @param {object} property - The current UXF element property being configured
 * @param {object[]} properties - The current set of UXF properties for the element being configured
 * @param {object} state - The current element configuration panel Seismic state
 * @returns {object} - The configured property object
 */
export default function (property, properties, state) {
	const {
		properties: { selectedElementId, referenceDisplayValuesErrors }
	} = state;

	const referenceResults =
		'@sn-uib-element-configuration-panel/referenceResults';
	const referenceDisplayValues =
		'@sn-uib-element-configuration-panel/referenceDisplayValues';
	const referenceSearching =
		'@sn-uib-element-configuration-panel/referenceSearching';
	const referenceId = `${selectedElementId}${property.id}`;

	const table = get(
		property,
		'typeMeta.reference.table',
		get(property, 'typeMeta.reference', null)
	);
	let disabled = false;
	if (!table) {
		disabled = true;
	}

	let errorMsg;
	if (referenceDisplayValuesErrors[property.value]) {
		errorMsg = referenceDisplayValuesErrors[property.value];
	}
	return {
		...property,
		disabled,
		referenceResults,
		referenceDisplayValues,
		referenceSearching,
		referenceId,
		table,
		errorMsg
	};
}
