import { t } from 'sn-translate';

/**
 * Processor that takes in the currently configured property and configures it for string types
 *
 * @param {object} property - The current UXF data broker instance property being configured
 * @param {object[]} properties - The current set of UXF properties for the data broker instance being configured
 * @param {object} state - The current data configuration shelf Seismic state
 * @returns {object} - The configured property object
 */
export default function (property) {
	return {
		...property,
		validation: {
			pattern: '^((.*)?@(?!elements)[^\\/]*|[^@]*|({.*}|\\[.*\\]))$',
			/* prettier-ignore */
			message: t('Binding the input of a data resource to an element property is not supported')
		}
	};
}
