import { createCustomElement } from '@servicenow/ui-core';
import 'sn-ui-builder-components';
import styles from './uib-toolbox.scss';
const CONTAINER_COMPONENT_ID = 'd356d14b6e293a3020a244b63d278d8f';

createCustomElement('uib-toolbox-anchor', {
	view: (state, dispatch) => {
		const {
			properties: { position }
		} = state;
		const isPlaced = position && position.top && position.left;
		return isPlaced ? (
			<span
				className="uib-toolbox-anchor"
				hook-insert={(n) => {
					dispatch('UIB_TOOLBOX#SET_POSITION', {
						toolboxPositionTarget: n.elm
					});
				}}
				hook-update={(o, n) => {
					dispatch('UIB_TOOLBOX#SET_POSITION', {
						toolboxPositionTarget: n.elm
					});
				}}
				style={{
					top: `${position.top}px`,
					left: `${position.left}px`
				}}
			/>
		) : null;
	},
	properties: {
		position: {
			default: { top: 0, left: 0 }
		}
	},
	styles
});

createCustomElement('uib-toolbox', {
	view: (state) => {
		const {
			properties: { components, opened, positionTarget }
		} = state;

		return (
			<sn-uib-toolbox
				opened={opened}
				prioritizedComponents={components.filter(({ id }) => {
					return id === CONTAINER_COMPONENT_ID;
				})}
				components={components}
				positionTarget={positionTarget}
			/>
		);
	},
	properties: {
		components: {
			default: []
		},
		opened: {
			default: false
		},
		positionTarget: {
			default: null
		},
		selectedSlotName: {
			default: null
		},
		selectedSlotParent: {
			default: null
		},
		onSelect: {
			default: null
		}
	},
	actionHandlers: {
		'SN_UIB_TOOLBOX#COMPONENT_SELECTED': ({ properties, action, dispatch }) => {
			const { component } = action.payload;
			const { selectedSlotName, selectedSlotParent, onSelect } = properties;
			if (onSelect) {
				onSelect({ dispatch, component });
				dispatch('SN_UIB_TOOLBOX#DISMISSED');
			} else if (selectedSlotName && selectedSlotParent) {
				dispatch('EMPTY_SLOT#DROPPED', {
					slotName: selectedSlotName,
					parentId: selectedSlotParent,
					dataTransfer: component
				});
				dispatch('SN_UIB_TOOLBOX#DISMISSED');
			}
		}
	}
});
