import { createHttpEffect } from '@servicenow/ui-effect-http';
import _cloneDeep from 'lodash/cloneDeep';
import _set from 'lodash/set';
import _get from 'lodash/get';

const injectBuilderType = (context) => {
	const clone = _cloneDeep(context);
	const builderType = _get(context, 'coeffects.properties.isComponentBuilder')
		? 'components'
		: 'apps';
	_set(clone, 'coeffects.action.payload.builderType', builderType);
	return clone;
};

export const fetchNewAppConfigEffect = (success, error) => ({
	...createHttpEffect(`/api/now/builder/:builderType/:sysId/info`, {
		method: 'GET',
		pathParams: ['builderType', 'sysId'],
		queryParams: ['sysparm_query_no_domain'],
		successActionType: success,
		errorActionType: error,
		batch: false
	}),
	interceptors: [injectBuilderType]
});

export const flattenQueryResult = (result, isMacroponent = false) => {
	return {
		[isMacroponent ? 'macroponent' : 'appConfig']: {
			appURL: _get(result, 'appURL', ''),
			name: _get(result, 'name', ''),
			pageRegistryId: _get(result, 'pageRegistryId', ''),
			pageRegistryName: _get(result, 'pageRegistryName', ''),
			pageRegistryScopeName: _get(result, 'pageRegistryScopeName', ''),
			pageRegistryScopeId: _get(result, 'pageRegistryScopeId', ''),
			sysId: _get(result, 'sysId', ''),
			scopeId: _get(result, 'scopeId', ''),
			scopeName: _get(result, 'scopeName', ''),
			routes: _get(result, 'routes', [])
		}
	};
};
