import authToken from '../../utils/getAuthToken';

/**
 * FIXME(eguo): Probably incurring some tech debt by linking analytics events with the data
 * provider but for the sake of expendiency.. This should also eventually be split out between
 * Piwik and analyticsNow (aka "custom metrics")
 */

// Sending that piwik information for tracking session info
const sendSessionAnalytic = (userId) => {
	if (window.GlideWebAnalytics) {
		window.GlideWebAnalytics.trackPage({ userId });
	}
};

/**
 * Sending that analyticsNow information to track whether the user has `admin` or `ui_builder_admin`
 * roles
 */

// Constants for analyticsNow framework call

// Stream id
export const STREAM_ID = 'sn_ui_builder';
// Payload keys
export const EVENT_TYPE_KEY = 'event.type';
export const USER_ID_KEY = 'user.id';
export const ADMIN_KEY = 'admin';
export const UIB_ADMIN_KEY = 'uib.admin';
// Event types
export const UIB_ENTER = 'uib.enter';

// Roles we care about
export const ADMIN_ROLE = 'admin';
export const UIB_ADMIN_ROLE = 'ui_builder_admin';

// Meant to be private
const sendAnalyticsNowEvent = (payload) => {
	const { [EVENT_TYPE_KEY]: eventType } = payload;

	if (!eventType) {
		throw new Error(
			'Missing required `event.type` key for call to analyticsNow framework'
		);
	}

	fetch(`/api/now/ua/analytics/sendAnalytic?sysparm_streamid=${STREAM_ID}`, {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json',
			...authToken
		},
		body: JSON.stringify(payload)
	});
};

// For excluding users who have both admin and ui_builder_admin roles
export const isUibAdmin = (roles) => {
	return !roles.includes(ADMIN_ROLE) && roles.includes(UIB_ADMIN_ROLE);
};

// This isn't being used right now because we're pending approval!
// eslint-disable-next-line
const sendUserRolesAnalytic = (userId, userRoles) => {
	const payload = {
		[EVENT_TYPE_KEY]: UIB_ENTER,
		[USER_ID_KEY]: userId,
		[ADMIN_KEY]: userRoles.includes(ADMIN_ROLE),
		[UIB_ADMIN_KEY]: isUibAdmin(userRoles)
	};

	return sendAnalyticsNowEvent(payload);
};

// Combining both events - Piwik and analyticsNow - into one function
const sendUserEnterAnalytic = (user) => {
	const { sys_id, roles } = user;
	if (!sys_id || !roles) {
		throw new TypeError('sendUserEnterAnalytic requires `sys_id` and `roles`');
	}
	sendSessionAnalytic(sys_id);
	sendUserRolesAnalytic(sys_id, roles);
	/**
	 * _sigh_ Need to leave this commented out for now since the request for custom
	 * metrics hasn't been approved yet. Until it is, this will throw errors.
	 */
};

/**
 * Exporting is as an object so that they can be stubbed out while testing
 */
export default {
	sendUserEnterAnalytic
};
