import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import styles from './uib-duplicate-prevented.scss';

import '@servicenow/now-modal';
import '@servicenow/now-text-link';
import 'sn-ui-builder-components';

export const UIB_DUPLICATE_PREVENTED_OK_CLICKED =
	'UIB_DUPLICATE_PREVENTED#OK_CLICKED';

createCustomElement('uib-duplicate-prevented', {
	view: ({ properties: { name, variantSysId, macroponentSysId } }) => {
		return (
			<now-modal
				manageOpened={true}
				opened={true}
				size="sm"
				header-label={t("Can't duplicate {0}", name)}
				footer-actions={[
					{
						clickActionType: UIB_DUPLICATE_PREVENTED_OK_CLICKED,
						variant: 'primary',
						label: t('OK')
					}
				]}>
				<div className="container">
					<span className="text">
						{
							/* prettier-ignore */
							t("This variant can't be duplicated because it has a protection policy.")
						}
					</span>
					{variantSysId ? (
						<now-text-link
							label={t('Open variant in platform')}
							href={`/nav_to.do?uri=%2Fsys_ux_screen.do%3Fsys_id%3D${variantSysId}`}
							opensWindow={true}
						/>
					) : null}
					{macroponentSysId ? (
						<now-text-link
							label={t('Open page definition in platform')}
							href={`/nav_to.do?uri=%2Fsys_ux_macroponent.do%3Fsys_id%3D${macroponentSysId}`}
							opensWindow={true}
						/>
					) : null}
				</div>
			</now-modal>
		);
	},
	styles,
	properties: {
		/**
		 * Name of variant to duplicate
		 *
		 * @type {string}
		 */
		name: {
			default: false
		},
		/**
		 * sysId of variant
		 *
		 * @type {string}
		 */
		variantSysId: {
			default: null
		},
		/**
		 * sysId of macroponent
		 *
		 * @type {string}
		 */
		macroponentSysId: {
			default: null
		}
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': ({ dispatch }) => {
			dispatch(UIB_DUPLICATE_PREVENTED_OK_CLICKED);
		}
	}
});
