import { createCustomElement } from '@servicenow/ui-core';
import styles from './uib-homepage.scss';
import '@servicenow/now-button';
import '@servicenow/now-record-list-connected';
import { screenNames } from '../../utils/routingConstants';
import { forceTransitionTo } from '../uib-router';
import getHelpResourceUrl from '../uib-app/utils/getHelpResourceUrl';
import { t } from 'sn-translate';
import uiBuilderLogoSVG from './assets/ui-builder-logo.svg';
import quickStartSVG from './assets/quick-start.svg';
import uiBuilderSVG from './assets/ui-builder.svg';
import componentDocSVG from './assets/component-doc.svg';
import communicationSVG from './assets/communication.svg';
import customSVG from './assets/custom.svg';

/*
 * TODO: do better than hard-coding these query criteria
 */
const SYS_UX_PAGE_REGISTRY_QUERY = [
	'admin_panel_table=sys_ux_app_config', // excludes UIB, Dashboard B, Component B, & any others w/o valid app_config
	'root_macroponentISNOTEMPTY', // root mcp is required for UIB to work
	'sys_id!=3bfb334573021010e12d1e3014f6a7a9', // exclude Service Catalog Builder
	'sys_id!=8f30c79577af00108a370870a810613a' // exclude AES Builder
].join('^');

createCustomElement('uib-homepage', {
	view: () => {
		return (
			<div className="uib-homepage">
				<div className="uib-homepage-header">
					<h1>
						<img src={uiBuilderLogoSVG} alt={t('UI Builder')} />
						{t('UI Builder')}
					</h1>
				</div>
				<div className="uib-homepage-content">
					<div className="uib-homepage-table">
						<div className="uib-homepage-table-header">
							<h2>{t('My experiences')}</h2>
							<a
								href="/nav_to.do?uri=%2Fsys_ux_page_registry_list.do"
								target="_blank">
								<now-button
									size="md"
									variant="primary"
									icon="plus-outline"
									label={t('Create experience in Platform')}
								/>
							</a>
						</div>
						<div className="table-wrap">
							<now-record-list-connected
								columns="sys_name,sys_created_on,path,root_macroponent,sys_scope,admin_panel"
								hideCellFilter
								hideColumnFiltering
								hideColumnResizing
								hideDeclarativeActions
								hideFilterPanel
								hideLastRefreshedText
								hideMultiEdit
								hidePanelAdvanced
								hideQuickEdit
								hideRefreshButton
								hideRowSelector
								hideTitleRowCount
								hideUnnecessaryRowSelectors
								hideTitle
								hideHeader
								query={SYS_UX_PAGE_REGISTRY_QUERY}
								table="sys_ux_page_registry"
							/>
						</div>
					</div>
					<div className="uib-homepage-help">
						<h2>{t('Help')}</h2>
						<div className="uib-homepage-help-items">
							<a
								href={getHelpResourceUrl('uib-quickstart')}
								target="_blank"
								className="uib-homepage-help-item">
								<img src={quickStartSVG} alt={t('Getting started')} />
								<div className="uib-homepage-help-item-text">
									<h4>{t('Getting started')}</h4>
									<div>
										{t('Welcome! Get started with the basics on UI Builder')}
									</div>
								</div>
							</a>
							<a
								href={getHelpResourceUrl('uib-overview')}
								target="_blank"
								className="uib-homepage-help-item">
								<img src={uiBuilderSVG} alt={t('Using UI Builder')} />
								<div className="uib-homepage-help-item-text">
									<h4>{t('Using UI Builder')}</h4>
									<div>
										{t('Learn how to start configuring your experiences')}
									</div>
								</div>
							</a>
							<a
								href="https://developer.servicenow.com/dev.do#!/reference/now-experience/quebec/components"
								target="_blank"
								className="uib-homepage-help-item">
								<img
									src={componentDocSVG}
									alt={t('Building with components')}
								/>
								<div className="uib-homepage-help-item-text">
									<h4>{t('Building with components')}</h4>
									<div>
										{t('Find guidance on where and when to use a component')}
									</div>
								</div>
							</a>
							<a
								href={getHelpResourceUrl('uib-release-notes')}
								target="_blank"
								className="uib-homepage-help-item">
								<img src={communicationSVG} alt={t('Staying up-to-date')} />
								<div className="uib-homepage-help-item-text">
									<h4>{t('Staying up-to-date')}</h4>
									<div>
										{t('Get the latest on updates, events, and releases')}
									</div>
								</div>
							</a>
							<a
								href={getHelpResourceUrl('custom-components')}
								target="_blank"
								className="uib-homepage-help-item">
								<img src={customSVG} alt={t('Creating custom components')} />
								<div className="uib-homepage-help-item-text">
									<h4>{t('Creating custom components')}</h4>
									<div>
										{t('Get training on how to build your own components')}
									</div>
								</div>
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	},
	properties: {
		goBack: {
			default: null
		}
	},
	actionHandlers: {
		['NOW_RECORD_LIST_CONNECTED#ROW_CLICKED']: ({ action }) => {
			action.stopPropagation();
			const appsSysId = action.payload.row.rowData.value.get('admin_panel')
				.columnData.value;
			forceTransitionTo(screenNames.appHome, {
				appsSysId,
				forceAppConfigRefresh: true
			});
		}
	},
	styles
});
