import utils from './utils';
import get from 'lodash/get';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
const { createFocusRef } = DevxKeyFocusBehavior;
const QUICK_LINK_ELEMENT_CONFIGURATION_PANEL_REF = createFocusRef(
	'quick-link-element-configuration-panel'
);

const { findParentModalElements, annotateAppConfig } = utils;

export default (state) => {
	const {
		currentSelectedProperties,
		currentSelectedEvents,
		currentSelectedEventMappings,
		currentElement,
		currentElementLayout,
		currentEventHandlers,
		currentStyles,
		currentSlotStyles,
		properties,
		externalConfigurationButtons,
		currentEventMappingHandlers,
		currentAncestorEventHandlers,
		currentTriggeringElements
	} = state;
	const {
		tables,
		icons,
		referenceDisplayValues,
		referenceResults,
		referenceSearching,
		tableFieldValues,
		disabled,
		c3PropertyConfig,
		elementConfigurationPanelExpanded,
		error,
		referenceDisplayValuesErrors,
		dataBindingEnabled,
		scriptedPropertiesEnabled,
		stylingPropertiesEnabled,
		eventMappingEnabled,
		interfaceConfigurationComponent,
		appConfig,
		layoutTemplates,
		bindings,
		dataBrokerInstanceNameMap,
		selectedSlotName,
		selectedSlotParent,
		selectedElementId,
		elementInstanceNameMap,
		editInstanceNameInHeader,
		dataBrokersEnabled,
		clientTransformScriptTemplates,
		parentElements,
		component,
		viewportVariantsEnabled,
		dynamicTabsEnabled,
		isEmptyState,
		isPageDefinitionEventsDisabled,
		isPageDefinitionLayoutDisabled,
		hideComponentPropertyBindingsEnabled,
		isMacroponentReadOnly
	} = properties;

	const parentModalElements = findParentModalElements(parentElements);
	const appConfigModalElements = get(appConfig, 'modalElements', []);
	const componentDescription = component ? component.description : null;
	const selectedComponentTag = component ? component.tag : null;
	const isExpanded = isEmptyState ? false : elementConfigurationPanelExpanded;
	const annotatedAppConfig = annotateAppConfig(
		appConfig,
		appConfigModalElements,
		parentModalElements,
		dataBrokerInstanceNameMap,
		bindings,
		selectedElementId,
		isMacroponentReadOnly
	);

	return (
		<sn-uib-element-configuration-panel
			component-id="sn-uib-element-configuration-panel"
			ref={QUICK_LINK_ELEMENT_CONFIGURATION_PANEL_REF}
			properties={currentSelectedProperties}
			element={currentElement}
			element-layout={currentElementLayout}
			element-styles={currentStyles}
			selected-slot-name={selectedSlotName}
			selected-slot-parent={selectedSlotParent}
			selected-slot-styles={currentSlotStyles}
			layout-templates={layoutTemplates}
			tables={tables}
			icons={icons}
			error={error}
			referenceDisplayValues={referenceDisplayValues}
			referenceResults={referenceResults}
			referenceSearching={referenceSearching}
			tableFieldValues={tableFieldValues}
			c3PropertyConfig={c3PropertyConfig}
			elementConfigurationPanelExpanded={isExpanded}
			disabled={disabled}
			reference-display-values-errors={referenceDisplayValuesErrors}
			externalConfigurationButtons={externalConfigurationButtons}
			events={currentSelectedEvents}
			event-mappings={currentSelectedEventMappings}
			event-handlers={currentEventHandlers}
			event-mapping-handlers={currentEventMappingHandlers}
			dataBindingEnabled={dataBindingEnabled}
			dynamic-tabs-enabled={dynamicTabsEnabled}
			scriptedPropertiesEnabled={scriptedPropertiesEnabled}
			stylingPropertiesEnabled={stylingPropertiesEnabled}
			eventMappingEnabled={eventMappingEnabled}
			interfaceConfigurationComponent={interfaceConfigurationComponent}
			appConfig={annotatedAppConfig}
			ancestor-event-handlers={currentAncestorEventHandlers}
			current-triggering-elements={currentTriggeringElements}
			currentTab={'@ui-state/pageConfigTab'}
			bindings={bindings}
			element-instance-name-map={elementInstanceNameMap}
			edit-instance-name-in-header={editInstanceNameInHeader}
			dataBrokersEnabled={dataBrokersEnabled}
			clientTransformScriptTemplates={clientTransformScriptTemplates}
			selectedComponentTag={selectedComponentTag}
			viewportVariantsEnabled={viewportVariantsEnabled}
			componentDescription={componentDescription}
			is-page-definition-events-disabled={isPageDefinitionEventsDisabled}
			is-page-definition-layout-disabled={isPageDefinitionLayoutDisabled}
			hide-component-property-bindings-enabled={
				hideComponentPropertyBindingsEnabled
			}
			is-macroponent-read-only={isMacroponentReadOnly}
		/>
	);
};
