import { createHttpEffect } from '@servicenow/ui-effect-http';
import { effectProxy } from 'sn-uxf-builder-middleware';
import {
	SN_UIB_APP_RETRIEVE_UIB_CONFIG_REQUESTED,
	SN_UIB_APP_RETRIEVE_UIB_CONFIG_SUCCEEDED,
	SN_UIB_APP_RETRIEVE_UIB_CONFIG_FAILED
} from './uibAppActions';

export const fetchUIBConfigEffect = createHttpEffect(
	'/api/sn_ui_builder/uib_builder_api/uib_config',
	{
		successActionType: SN_UIB_APP_RETRIEVE_UIB_CONFIG_SUCCEEDED,
		errorActionType: SN_UIB_APP_RETRIEVE_UIB_CONFIG_FAILED,
		method: 'GET',
		batch: false
	}
);

export const uibConfigHandlers = {
	[SN_UIB_APP_RETRIEVE_UIB_CONFIG_REQUESTED]: effectProxy(fetchUIBConfigEffect),
	[SN_UIB_APP_RETRIEVE_UIB_CONFIG_SUCCEEDED]: ({ action, updateState }) =>
		updateState({
			uibConfigResult: action.payload.result
		})
};
