import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import '@servicenow/now-modal';
import 'sn-ui-builder-components';

createCustomElement('uib-delete-confirmation', {
	view: (state) => {
		const { properties, name } = state;
		const { opened, content, headerLabel, additionalMessage } = properties;

		return (
			<now-modal
				hook-update={(_, vnode) => {
					// needs to show over uib-page-properties-modal
					if (vnode.data.props.opened) {
						vnode.elm.shadowRoot.querySelector(
							'.now-modal-overlay'
						).style.zIndex = 4001; // now-modal default is 4000
					}
				}}
				manageOpened={true}
				opened={opened}
				size={content ? 'lg' : 'sm'}
				header-label={headerLabel}
				/* prettier-ignore */
				content={t('Are you sure you want to delete {0}? {1}', name, additionalMessage)}
				footer-actions={[
					{
						clickActionType: 'UIB_DELETE_CONFIRMATION#SAVE_CLICKED',
						variant: 'primary-negative',
						label: t('Delete')
					},
					{
						clickActionType: 'UIB_DELETE_CONFIRMATION#CANCEL_CLICKED',
						variant: 'secondary',
						label: t('Cancel')
					}
				]}>
				{content}
			</now-modal>
		);
	},
	transformState: (state) => {
		const { objectToDelete } = state.properties;

		return {
			...state,
			name: objectToDelete ? objectToDelete.name : null
		};
	},
	properties: {
		/**
		 * Whether the delete confirmation modal is currently open
		 *
		 * @type {boolean}
		 */
		opened: {
			default: false
		},
		/**
		 * The label to dislpay in the modal header
		 *
		 * @type {string}
		 */
		headerLabel: {
			default: t('Delete')
		},
		/**
		 * fills default slot if defined
		 *
		 * @type {string}
		 */
		content: {
			default: null
		},
		/**
		 * Any additional information to convey about deleting the object.
		 * Should be localized through `sn-translate.t`.
		 *
		 * @type {string}
		 */
		additionalMessage: {
			default: t('This action cannot be undone.')
		},
		/**
		 * The object that the user has selected to delete (page, data broker instance, etc.)
		 *
		 * @type {object}
		 */
		objectToDelete: {
			default: null
		},
		/**
		 * The action handler to use if the delete button is clicked in the modal
		 *
		 * @type {Function}
		 */
		deleteActionHandler: {
			default: () => {}
		}
	},
	actionHandlers: {
		'NOW_MODAL#OPENED_SET': ({ dispatch }) => {
			dispatch('UIB_DELETE_CONFIRMATION#CLOSED');
		},
		'UIB_DELETE_CONFIRMATION#CANCEL_CLICKED': ({ dispatch }) => {
			dispatch('UIB_DELETE_CONFIRMATION#CLOSED');
		},
		'UIB_DELETE_CONFIRMATION#SAVE_CLICKED': ({ dispatch, properties }) => {
			const { objectToDelete, deleteActionHandler } = properties;
			deleteActionHandler(dispatch, objectToDelete);
		}
	}
});
