import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';

export const inspectEveryNode = (tree, cb) => {
	cb(tree);
	if (!tree.children || !tree.children.length) {
		return;
	}
	tree.children.forEach((node) => inspectEveryNode(node, cb));
};

export const getElementByProperty = (element, propertyValue, path) => {
	const nodes = [element];
	let parent = null;
	while (nodes.length && !parent) {
		const node = nodes.shift();
		if (get(node, path, null) === propertyValue) {
			parent = node;
		} else {
			if (!isEmpty(node.children)) {
				nodes.push(...node.children);
			}
		}
	}
	return parent;
};
