import { getElementByProperty } from '../../utils/tree.js';
import get from 'lodash/get';

export const getElementKey = (selectedElementId) => {
	return selectedElementId || '-';
};
const tabSelectionManager = new Map();

export const ElementTabSelectionManager = {
	set: (key, value) => tabSelectionManager.set(key, value),
	get: (key) => tabSelectionManager.get(key),
	reset: () => tabSelectionManager.clear(),
	has: (key) => tabSelectionManager.has(key)
};

export const evaluateConfigTabSelection = ({
	selectedElementId,
	properties
}) => {
	if (properties.isLegacy) {
		return 'properties';
	}
	if (!selectedElementId) {
		return 'style';
	}
	const elmKey = getElementKey(selectedElementId);

	const { pageState } = properties;
	const element = getElementByProperty(
		pageState.rootElement,
		selectedElementId,
		'id'
	);
	const component = get(element, 'component');
	if (!ElementTabSelectionManager.has(elmKey)) {
		const compTag = get(component, 'tag');
		if (
			!(
				compTag === 'now-uxf-page-container' ||
				compTag === 'now-uxf-page-simple-container'
			) &&
			selectedElementId !== pageState.rootElement.id
		) {
			ElementTabSelectionManager.set(elmKey, 'properties');
		}
	}
	return ElementTabSelectionManager.get(elmKey) || 'style';
};
