import {
	parseFilterCountFromConditionString,
	getDependentPropertyId
} from '../../../../utils/properties';
import c3Processor from '../c3';

/**
 * Processor that takes in the currently configured property
 * and configures it for condition_string type
 *
 * @param {object} property - The current UXF element property being configured
 * @param {object[]} properties - The current set of UXF properties for the element being configured
 * @param {object} state - The current element configuration panel Seismic state
 * @returns {object} - The configured property object
 */
export default function (property, properties, state) {
	const { typeMeta } = property;

	// condition_string is a C3 property
	const newProperty = c3Processor(property, properties, state);

	newProperty.disabled = false;
	if (!typeMeta.table || getDependentPropertyId(typeMeta.table) !== null) {
		newProperty.disabled = true;
	}
	newProperty.badge = property.value
		? parseFilterCountFromConditionString(newProperty.value)
		: '';

	return newProperty;
}
