import { memoizeDeepN } from 'seismic-memo-utils';

function appConfig(appRoutes, modalElements) {
	return {
		appRoutes,
		modalElements
	};
}

function pagePanelClassNames(pageManagementPanelExpanded, isEmptyState) {
	return {
		'pmp-panel--collapsed': !pageManagementPanelExpanded || isEmptyState,
		'panel--expanded': pageManagementPanelExpanded && !isEmptyState
	};
}

function configurationPanelClassNames(
	elementConfigurationPanelExpanded,
	isEmptyState
) {
	return {
		'cp-panel--collapsed': !elementConfigurationPanelExpanded || isEmptyState,
		'panel--expanded': elementConfigurationPanelExpanded && !isEmptyState,
		'config-panel-empty-state': isEmptyState
	};
}

export default {
	appConfig: memoizeDeepN(appConfig),
	pagePanelClassNames: memoizeDeepN(pagePanelClassNames),
	configurationPanelClassNames: memoizeDeepN(configurationPanelClassNames)
};
