export const groupPropsByCategory = (prop) => {
	if (prop.isField) {
		return 'isField';
	} else if (prop.isParam) {
		return 'isParam';
	} else if (prop.inherited) {
		return 'inherited';
	} else {
		return 'regular';
	}
};

export const transformPropertiesToContentTree = (
	pageProperties,
	appRoutes,
	selectedPageId,
	fieldsParamsEnabled,
	isReadOnly
) => {
	const {
		outOfSyncFields: unmappedPageFields,
		outOfSyncParams: unmappedPageParameters,
		restrictedPageProps: pagePropsWithFlags
	} = getFlaggedPropsAndOutOfSync(
		fieldsParamsEnabled,
		appRoutes,
		selectedPageId,
		pageProperties
	);

	const propGroups = groupBy(pagePropsWithFlags, groupPropsByCategory);

	const fieldProps = propGroups.isField
		? {
				id: 'fields',
				label: t('Required parameters'),
				statuses: isReadOnly
					? []
					: [
							{
								type: 'icon',
								/* prettier-ignore */
								label: t('Page properties mapped to required parameters cannot be deleted or modified'),
								icon: 'lock-fill'
							}
					  ],
				children: propGroups.isField.map(({ id, name: label }) => ({
					id,
					label,
					statuses: isReadOnly
						? []
						: [
								{
									type: 'icon',
									/* prettier-ignore */
									label: t('Page property mapped to required parameters cannot be deleted or modified'	),
									icon: 'lock-fill'
								}
						  ]
				}))
		  }
		: {};

	const paramProps = propGroups.isParam
		? {
				id: 'params',
				label: t('Optional parameters'),
				actions: isReadOnly
					? []
					: [
							{
								/* prettier-ignore */
								label: t('Deletes all page properties mapped to optional parameters'),
								icon: 'trash-outline',
								clickActionType: UIB_PAGE_PROPERTIES_MODAL_DELETE_ALL_PARAMS_CLICKED
							}
					  ],
				children: propGroups.isParam.map(({ id, name: label }) => ({
					id,
					label: kebabCase(label),
					actions: isReadOnly
						? []
						: [
								{
									label: t('Delete page property {0}', label),
									icon: 'trash-outline',
									clickActionType: UIB_PAGE_PROPERTIES_MODAL_DELETE_PROP_CLICKED
								}
						  ]
				}))
		  }
		: {};

	const inheritedProps = propGroups.inherited
		? propGroups.inherited.map(({ id, name: label }) => ({
				id,
				label,
				statuses: isReadOnly
					? []
					: [
							{
								type: 'icon',
								/* prettier-ignore */
								label: t('Inherited page properties cannot be deleted or modified'),
								icon: 'lock-fill'
							}
					  ]
		  }))
		: [];

	const remainingTreeItems = propGroups.regular
		? propGroups.regular.map(({ id, name: label }) => ({
				id,
				label,
				actions: isReadOnly
					? []
					: [
							{
								label: t('Delete page property {0}', label),
								icon: 'trash-outline',
								clickActionType: UIB_PAGE_PROPERTIES_MODAL_DELETE_PROP_CLICKED
							}
					  ]
		  }))
		: [];

	return {
		contentTreeItems: [
			...remainingTreeItems,
			...inheritedProps,
			paramProps,
			fieldProps
		].filter((prop) => !isEmpty(prop)),
		pagePropsWithFlags,
		unmappedPageParameters,
		unmappedPageFields
	};
};

export const updatePagePropsAndContentTree = (
	{ updateState, properties, state },
	payload = {}
) => {
	const {
		pageState,
		appRoutes,
		selectedPageId,
		fieldsParamsEnabled
	} = properties;
	const { selectedProperty, isReadOnly } = state;
	const {
		contentTreeItems,
		pagePropsWithFlags,
		unmappedPageParameters,
		unmappedPageFields
	} = transformPropertiesToContentTree(
		get(pageState, 'rootElement.macroponentProperties'),
		appRoutes,
		selectedPageId,
		fieldsParamsEnabled,
		payload.switchingReadOnly ? payload.isReadOnly : isReadOnly
	);

	const individualProps = pagePropsWithFlags.filter(
		({ isField, isParam }) => !isField && !isParam
	);
	const selectedPageState =
		!isEmpty(pagePropsWithFlags) && !selectedProperty
			? {
					selectedProperty: individualProps[0],
					propName: individualProps[0].name,
					propType: individualProps[0].fieldType || 'string',
					propTranslatable: get(
						individualProps[0],
						'typeMetadata.translatable'
					),
					propTooltip: individualProps[0].description
			  }
			: {};
	updateState({
		contentTreeItems,
		pagePropsWithFlags,
		unmappedPageParameters,
		unmappedPageFields,
		...selectedPageState
	});
};

export const generateFieldTypeOptions = (fieldType) => {
	const additionalFieldTypeOptions = [
		{ id: 'choice', label: 'Choice' },
		{ id: 'table_name', label: 'Table name' },
		{ id: 'reference', label: 'Reference' },
		{ id: 'condition_string', label: 'Condition string' }
	];

	const fieldTypeOptions = [
		{ id: 'string', label: 'String' },
		{ id: 'boolean', label: 'Boolean' },
		{ id: 'number', label: 'Number' },
		{ id: 'json', label: 'JSON' }
	];
	const unsupportedFieldType = additionalFieldTypeOptions.find(
		(option) => option.id === fieldType
	);

	if (unsupportedFieldType) {
		fieldTypeOptions.push(unsupportedFieldType);
	}

	return fieldTypeOptions;
};

export const disableFieldAndDelete = (selectedProperty, isReadOnly, field) => {
	switch (field) {
		case 'name':
		case 'type':
			return (
				(selectedProperty &&
					(selectedProperty.inherited ||
						selectedProperty.isField ||
						selectedProperty.isParam)) ||
				isReadOnly
			);
		case 'tooltip':
		case 'translatable':
			return (selectedProperty && selectedProperty.inherited) || isReadOnly;

		case 'delete-button':
			return (
				!selectedProperty ||
				(selectedProperty &&
					(selectedProperty.inherited || selectedProperty.isField)) ||
				isReadOnly
			);
	}
};

export const disableButtons = (
	selectedPageProperty,
	propName,
	propType,
	propTooltip,
	propTranslatable,
	isDirty,
	propNameMessages,
	button
) => {
	const formChanged =
		isDirty &&
		(selectedPageProperty
			? selectedPageProperty.label !== propName ||
			  get(selectedPageProperty, 'typeMetadata.translatable') !==
					propTranslatable ||
			  selectedPageProperty.fieldType !== propType ||
			  selectedPageProperty.description !== propTooltip
			: !!propName || !!propTooltip || propType !== 'string');

	switch (button) {
		case 'save-button':
			return (
				!formChanged ||
				!propName ||
				(propNameMessages && !!propNameMessages.length)
			);
		case 'reset-button':
			return !formChanged;
	}
};

export const getSelectedItem = (selectedProperty, newForm, pageProperties) => {
	if (newForm) {
		return [[]];
	} else if (!selectedProperty && pageProperties) {
		return [[pageProperties[0].id]];
	} else if (selectedProperty.isField) {
		return [['fields', selectedProperty.id]];
	} else if (selectedProperty.isParam) {
		return [['params', selectedProperty.id]];
	} else {
		return [[selectedProperty.id]];
	}
};

export const getPopoverItems = (unmappedPageFields, unmappedPageParameters) => {
	return [
		{
			children: [
				{
					label: popoverCreateLabel,
					icon: 'plus-outline',
					headerAction: UIB_PAGE_PROPERTIES_MODAL_POPOVER_CREATE_CLICKED,
					id: 'popover-create-item'
				}
			]
		},
		!isEmpty(unmappedPageFields)
			? {
					sectionLabel: popoverFieldsLabel,
					children: unmappedPageFields.map((name) => ({
						label: name,
						id: name,
						headerAction: UIB_PAGE_PROPERTIES_MODAL_POPOVER_URL_ADD_CLICKED
					}))
			  }
			: {},
		!isEmpty(unmappedPageParameters)
			? {
					sectionLabel: popoverParametersLabel,
					children: unmappedPageParameters.map((name) => ({
						label: name,
						id: name,
						headerAction: UIB_PAGE_PROPERTIES_MODAL_POPOVER_URL_ADD_CLICKED
					}))
			  }
			: {}
	].filter((item) => !isEmpty(item));
};
