function encodeLabel(variantObj, originalItem = {}) {
	return `${variantObj.domainName}${
		originalItem.name !== variantObj.name ? ` (${variantObj.name})` : ''
	}`;
}

export default function treeifyOverrides(variants, root, originalItem) {
	let parent = root;
	if (!parent) {
		const foundRoot =
			variants.find(
				(variant) => variant.sysOverride === '' || variant.overrides === ''
			) || {};
		parent = {
			id: foundRoot.id,
			label: encodeLabel(foundRoot, originalItem),
			children: []
		};
	}

	let currentParents = [parent.id];
	let out = [parent];
	while (currentParents.length) {
		let found = [];
		currentParents.forEach((parentId) => {
			found = [
				...found,
				...variants.filter(
					(child) =>
						child.overrides === parentId || child.sysOverride === parentId
				)
			];
		});
		if (!found.length) {
			break;
		}

		out = [
			...out,
			...found.map((child) => ({
				id: child.id,
				label: encodeLabel(child, originalItem),
				children: []
			}))
		];
		currentParents = found.map((child) => child.id);
	}
	return out;
}
