import { memoizeDeepN, memoizeDeep } from 'seismic-memo-utils';
import {
	getCurrentSelectedEventMappingHandlers,
	getExternalConfigurationButtons,
	processCurrentSelectedEventMappings,
	processCurrentSelectedEvents,
	processCurrentSelectedProperties,
	processEventHandlers,
	processEventMappingHandlers,
	createElementInstanceNameMap
} from '../../utils/properties';
import {
	findTriggeringElementsForModal,
	findParentModalElements
} from '../../utils/elements';
import { createDataBrokerInstanceNameMap } from '../uib-data-configuration-shelf/utils';

const annotateAppConfig = (
	appConfig,
	appConfigModalElements,
	parentModalElements,
	dataBrokerInstanceNameMap,
	bindings,
	selectedElementId,
	disabled
) => ({
	...appConfig,
	modalElements: [...appConfigModalElements, ...parentModalElements],
	dataBrokerInstanceNameMap,
	bindings,
	elementId: selectedElementId,
	disabled
});

export default {
	getCurrentSelectedEventMappingHandlers: memoizeDeepN(
		getCurrentSelectedEventMappingHandlers
	),
	getExternalConfigurationButtons: memoizeDeepN(
		getExternalConfigurationButtons
	),
	processCurrentSelectedEventMappings: memoizeDeep(
		processCurrentSelectedEventMappings
	),
	processCurrentSelectedEvents: memoizeDeep(processCurrentSelectedEvents),
	processCurrentSelectedProperties: memoizeDeepN(
		processCurrentSelectedProperties
	),
	processEventHandlers: memoizeDeep(processEventHandlers),
	processEventMappingHandlers: memoizeDeepN(processEventMappingHandlers),
	createElementInstanceNameMap: memoizeDeep(createElementInstanceNameMap),
	findTriggeringElementsForModal: memoizeDeepN(findTriggeringElementsForModal),
	findParentModalElements: memoizeDeep(findParentModalElements),
	createDataBrokerInstanceNameMap: memoizeDeep(createDataBrokerInstanceNameMap),
	annotateAppConfig: memoizeDeepN(annotateAppConfig)
};
