import { forceTransitionTo } from '../../uib-router';
import { screenNames } from '../../../utils/routingConstants';

export default {
	['NOW_BUTTON_BARE#CLICKED']: ({ action, dispatch }) => {
		const { name } = action.payload;

		if (name === 'routeNotFound') {
			forceTransitionTo(screenNames.dashboardsHome, {});
			dispatch('SN_UIB_APP#UPDATE_DASHBOARDS_LIST');
		}
	}
};
