import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-select';
import '@servicenow/now-loader';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import styles from './uib-page-definition-selector.scss';
import {
	sharedPageDefinitionBehavior,
	SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUESTED
} from '../../behaviors/sharedPageDefinitionBehavior';

import { SHARED_PAGE_DEFINITION_SYS_ID } from '../../globals/constants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const radioGroupLabel = t('How would you like to add content to this page?');
const referenceLabel = t('Reference the page template and its data (default)');
/* prettier-ignore */
const copyLabel = t('Only copy the contents of the template (not upgrade-safe)');
const pageTemplateLabel = t('Page template');
const copyPageTemplateLabel = t('Current page template');
const changeButtonLabel = t('Change');
const cancelButtonLabel = t('Cancel');
const appendToChangeButton = { category: 'change' };
const appendToCancelButton = { category: 'cancel' };

const noSelectionInfoHeader = t('What are page templates?');
/* prettier-ignore */
const noSelectionInfoText = t('Page templates are pre-defined pages that can be customized to fit your needs. You can choose to reference or copy a page template.');
/* prettier-ignore */
const selectionInfoHeader = t('Should I choose to reference or copy a page template?');
/* prettier-ignore */
const selectionInfoText = t("When you choose to reference a page template, your page will automatically update when you upgrade to a new release. When you choose to copy a page template, the page will remain as-is and won't be affected by the upgrade.");

export const UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED =
	'UIB_PAGE_DEFINITION_SELECTOR#PAGE_DEFINITION';
export const UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED =
	'UIB_PAGE_DEFINITION_SELECTOR#COPY_PAGE_DEFINITION_CHANGED';
export const UIB_PAGE_DEFINITION_SELECTOR_SHOW_SELECTOR =
	'UIB_PAGE_DEFINITION_SELECTOR#SHOW_SELECTOR';

const view = ({
	checkedButton,
	pageDefinitions = [],
	selectedPageDefinition,
	isLoadingPageDefinitions = true,
	showPicker,
	initialLoad,
	properties: { disabled, macroponentName, editMode, copyMode }
}) => {
	if (initialLoad) {
		return null;
	}

	const pageDefinitionIsSelected =
		selectedPageDefinition && selectedPageDefinition !== 'none';
	const selectedPageDefinitionItem = pageDefinitions.find(
		({ id }) => selectedPageDefinition === id
	);
	return (
		<div>
			{showPicker ? (
				<div className="container">
					<div className="left-panel">
						{isLoadingPageDefinitions ? (
							<now-loader />
						) : (
							<div>
								<now-select
									search="contains"
									label={copyMode ? copyPageTemplateLabel : pageTemplateLabel}
									items={pageDefinitions}
									selected-item={selectedPageDefinition || 'none'}
									disabled={disabled || copyMode}
								/>
								{pageDefinitionIsSelected ? (
									<now-radio-buttons
										label={radioGroupLabel}
										disabled={disabled}
										layout="vertical"
										options={[
											{
												id: 'reference',
												label: referenceLabel,
												checked: checkedButton === 'reference'
											},
											{
												id: 'copy',
												label: copyLabel,
												checked: checkedButton === 'copy',
												disabled:
													selectedPageDefinitionItem &&
													selectedPageDefinitionItem.sysPolicy === 'protected'
											}
										]}
									/>
								) : null}
							</div>
						)}
					</div>
					<div className="right-panel">
						<div>
							<div className="info-header">
								<now-icon size="sm" icon="circle-info-outline" />
								{pageDefinitionIsSelected
									? selectionInfoHeader
									: noSelectionInfoHeader}
							</div>
							<div className="info-text">
								{pageDefinitionIsSelected
									? selectionInfoText
									: noSelectionInfoText}
							</div>
						</div>
						{editMode ? (
							<now-button
								size="sm"
								label={cancelButtonLabel}
								append-to-payload={appendToCancelButton}
							/>
						) : null}
					</div>
				</div>
			) : (
				<div className="edit-mode">
					<div>
						<div className="field-label">{pageTemplateLabel}</div>
						<div className="field-content">
							<span className="page-definition-name">
								{' '}
								{macroponentName || t('None')}
							</span>
						</div>
					</div>
					<now-button
						label={changeButtonLabel}
						variant="secondary"
						disabled={disabled}
						append-to-payload={appendToChangeButton}
					/>
				</div>
			)}
		</div>
	);
};

export default createCustomElement('uib-page-definition-selector', {
	view,
	styles,
	behaviors: [{ behavior: sharedPageDefinitionBehavior }],
	initialState: {
		checkedButton: 'reference',
		selectedPageDefinition: 'none',
		showPicker: true,
		initialLoad: true
	},
	properties: {
		editMode: {
			default: false
		},
		disabled: {
			default: false
		},
		macroponentName: {
			default: 'None'
		},
		macroponentId: {
			default: 'none'
		},
		copyMode: {
			default: false
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties, updateState }) => {
			const { editMode, macroponentId } = properties;
			dispatch(SHARED_PAGE_DEFINITION_BEHAVIOR_DEFINITIONS_REQUESTED, {
				sysparm_query: `associated_types=${SHARED_PAGE_DEFINITION_SYS_ID}`
			});
			updateState({
				showPicker: !editMode,
				selectedPageDefinition: macroponentId || 'none',
				initialLoad: false
			});
		},
		['NOW_RADIO_BUTTONS#VALUE_SET']: {
			stopPropagation: true,
			effect: ({ action, updateState, dispatch }) => {
				const { value: checkedButton } = action.payload;

				updateState({
					checkedButton
				});
				dispatch(UIB_PAGE_DEFINITION_SELECTOR_COPY_PAGE_DEFINITION_CHANGED, {
					value: checkedButton === 'copy'
				});
			}
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: {
			stopPropagation: true,
			effect: ({ action, dispatch, updateState, state }) => {
				const { value } = action.payload;
				const { checkedButton } = state;
				dispatch(UIB_PAGE_DEFINITION_SELECTOR_PAGE_DEFINITION_SELECTED, {
					value
				});
				updateState({
					selectedPageDefinition: value,
					checkedButton: value === 'none' ? 'reference' : checkedButton
				});
			}
		},
		['NOW_BUTTON#CLICKED']: {
			stopPropagation: true,
			effect: ({ action, dispatch, updateState, properties }) => {
				const { macroponentId } = properties;
				const { category } = action.payload;
				const showPicker = category === 'change';
				updateState({
					showPicker,
					selectedPageDefinition: macroponentId,
					checkedButton: 'reference'
				});
				dispatch(UIB_PAGE_DEFINITION_SELECTOR_SHOW_SELECTOR, {
					value: showPicker
				});
			}
		}
	}
});
