import '@servicenow/now-input';
import '@servicenow/now-text-link';
import '@servicenow/now-checkbox';
import { t } from 'sn-translate';
import { escapeScopeLabel } from '../../../utils/escapeScopeLabel';
import '../../uib-page-definition-selector';
import { SHARED_PAGE_DEFINITION_SYS_ID } from '../../../globals/constants';

export default ({ screen, disabled, supportDomains }) => {
	if (!screen) {
		return null;
	}

	const macroponent =
		screen.macroponentInstance.macroponentDefinitions[
			screen.macroponentInstance.rootMacroponentId
		];
	const preBuilt = macroponent.associatedTypes.find(
		(id) => id === SHARED_PAGE_DEFINITION_SYS_ID
	);
	const definitionSelectorProps = preBuilt
		? {
				macroponentName: macroponent.name,
				macroponentId: macroponent.id,
				editMode: true,
				disabled
		  }
		: { editMode: true, disabled };

	return (
		<div>
			<form className="property-form-view set-height">
				<div className="property-form-view">
					<now-input
						label={t('Variant name')}
						required
						helper-content={
							/* prettier-ignore */
							t('Enter a unique name to identify this variant from others. For example, for a user registration page that\'s intended only for external users, you may enter "External User Registration."')
						}
						value={screen.name}
						disabled={disabled}
						manage-value
						append-to-payload={{ input: 'name' }}
					/>
					<div className="page-selector">
						<uib-page-definition-selector {...definitionSelectorProps} />
					</div>
					<div className="half checkbox">
						{/* Since NDS doesn't support a form label for checkbox */}
						<now-checkbox
							label={t('Active')}
							checked={screen.active}
							disabled={disabled}
							manage-checked
							append-to-payload={{ input: 'active' }}
						/>
					</div>
				</div>
				<div className="two-column-flex">
					<div className="column">
						<now-input
							label={t('Application scope')}
							value={escapeScopeLabel(screen.scopeName)}
							disabled
							readonly
							/* prettier-ignore */
							helper-content={t('The variant is created in this default application scope. To switch scope, use the application scope picker.')}
						/>
					</div>
					{supportDomains ? (
						<div className="column">
							<now-input
								label={t('Domain')}
								value={
									screen.domainName ? screen.domainName.split('/').pop() : ''
								}
								disabled
								readonly
								/* prettier-ignore */
								helper-content={t('The variant is created in this domain. To switch domain scope, use the domain scope picker.')}
							/>
						</div>
					) : null}
				</div>
			</form>
		</div>
	);
};
