import { t } from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';
import lookupActionByPageType from './reducers/lookupActionByPageType';

import get from 'lodash/get';
import has from 'lodash/has';

export const lifecycleHandlers = {
	[actionTypes.COMPONENT_DOM_READY]: ({
		state,
		dispatch,
		updateState,
		properties
	}) => {
		const { pageCreateType, pageState, modalInfo } = properties;

		const initialContext = {
			...state.context
		};

		const newContext = getNewContextObject(modalInfo, pageState);

		if (!initialContext.pageStatus && has(newContext, 'pageStatus')) {
			initialContext.pageStatus = newContext.pageStatus;
		}

		if (!initialContext.pageName && has(newContext, 'name')) {
			initialContext.pageName =
				pageCreateType === 'copyPage'
					? t('Copy of {0}', newContext.name)
					: newContext.name;
		}

		if (!initialContext.scope && has(newContext, 'scope')) {
			initialContext.selectedApplication = {
				id: get(newContext, 'scope', null)
			};
		}

		if (
			!initialContext.selectedProtectionChoice &&
			has(newContext, 'sysPolicy')
		) {
			const sysPolicyId = get(newContext, 'sysPolicy', null);
			// Set sysPolicy id to 'none' for UI, and parse it back to an empty string in 'mapContextToPayload'
			// to ensure selects, typeaheads, dropdowns, etc don't break
			initialContext.selectedProtectionChoice = {
				id: sysPolicyId === '' ? 'none' : sysPolicyId
			};
		}

		updateState({
			operation: 'set',
			value: initialContext,
			path: 'context'
		});

		const action = lookupActionByPageType(pageCreateType);
		dispatch(action);
	}
};

const getNewContextObject = (modalInfo, pageState = {}) => {
	// some times a page object has uiTypeParameters and some times it doesn't
	return modalInfo
		? {
				...modalInfo,
				...modalInfo.uiTypeParameters
		  }
		: pageState
		? {
				...pageState,
				...get(pageState, 'uiTypeParameters')
		  }
		: {};
};
