import { t } from 'sn-translate';
import { forceTransitionTo } from '../../uib-router';
import { screenNames } from '../../../utils/routingConstants';

const getRouterAlertProps = (routeTypeRequested) => ({
	action: { type: 'dismiss' },
	content: t('The requested {0} could not be found', routeTypeRequested),
	status: 'warning'
});

let alertTimeout = null;
export const routerAlertHandler = (type, dispatch, updateProperties) => {
	if (alertTimeout) {
		clearTimeout(alertTimeout);
	}

	alertTimeout = setTimeout(() => {
		dispatch('SN_UIB_APP#ALERT_CLEARED_BY_TIMEOUT');
	}, 5000);

	updateProperties({
		alertProps: getRouterAlertProps(type)
	});
};

export const findRedirect = ({
	appsSysId,
	pageSysId,
	variantSysId,
	queryParams
}) => {
	if (appsSysId && pageSysId && variantSysId) {
		forceTransitionTo(screenNames.appVariant, {
			appsSysId,
			pageSysId,
			variantSysId,
			queryParams
		});
		return;
	} else if (appsSysId && pageSysId) {
		forceTransitionTo(screenNames.appPage, {
			appsSysId,
			pageSysId,
			queryParams
		});
		return;
	}
	return;
};
