import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './uib-create-success-modal.scss';
import _capitalize from 'lodash/capitalize';
import '@servicenow/now-button';
import '@servicenow/now-pill';
import { t } from 'sn-translate';
import { audiencesBehavior } from '../../behaviors/audiencesBehavior';
import {
	AUDIENCE_LIST_REQUESTED,
	AUDIENCE_UPDATE_SELECTED
} from '../../behaviors/audiencesBehavior/constants';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import {
	CreateFlowNavigator // eslint-disable-line no-unused-vars
} from 'sn-ui-builder-components';
import {
	getTabs,
	getNavItems,
	UIB_CREATE_SUCCESS_MODAL_DISMISSED,
	actions
} from './constants';

import { subHeaderContentWrapper } from './utils';

const view = ({
	currentTab,
	pills,
	buttons,
	disabled,
	isLoading = true,
	properties: { successModalType, subHeaderContent, selectedPageIsTab }
}) => {
	return (
		<now-modal
			manage-opened={true}
			opened={true}
			size="lg"
			footer-actions={
				!isLoading
					? [
							{
								label: 'Done',
								variant: 'primary',
								clickActionType: UIB_CREATE_SUCCESS_MODAL_DISMISSED
							}
					  ]
					: null
			}
			headerLabel={!isLoading ? t('Success!') : null}>
			<div aria-busy={true} className={`flex ${isLoading ? 'loading' : ''}`}>
				{!isLoading ? (
					<div className="content">
						{subHeaderContentWrapper(
							successModalType,
							subHeaderContent,
							selectedPageIsTab
						)}
						{successModalType === 'page' ? (
							<now-tabs
								items={getTabs(selectedPageIsTab)}
								selected-item={currentTab}
							/>
						) : null}
						<CreateFlowNavigator
							{...getNavItems(selectedPageIsTab)[currentTab]}
							pills={pills}
							buttonLabels={buttons}
							buttonStates={disabled}
						/>
					</div>
				) : (
					<LoadingStage size={'sm'} loadingStageContent={t('Loading...')} />
				)}
			</div>
		</now-modal>
	);
};

createCustomElement('uib-create-success-modal', {
	view,
	styles,
	behaviors: [
		{
			behavior: audiencesBehavior
		}
	],
	initialState: {
		currentTab: 'page',
		pills: {},
		buttons: {},
		disabled: {}
	},
	properties: {
		page: {
			default: null
		},
		successModalType: {
			default: 'page'
		},
		screen: {
			default: {}
		},
		currentTab: {
			default: 'page'
		},
		subHeaderContent: {
			default: null
		},
		selectedPageIsTab: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties,
			updateState,
			dispatch
		}) => {
			const { currentTab, page, screen, selectedPageIsTab } = properties;
			const { fields, optionalParameters } = page;
			const { active, order, screenCondition, sysId } = screen;
			dispatch(AUDIENCE_LIST_REQUESTED, { sysId });
			const screenConditionPills = screenCondition
				? screenCondition.split('^')
				: [];
			const conditions = [`order: ${order}`, ...screenConditionPills];
			const pillFields = fields
				? Object.values(fields).map(({ name }) => name)
				: [];
			const parameters = optionalParameters
				? Object.values(optionalParameters).map(({ name }) => name)
				: [];
			updateState({
				currentTab,
				pills: {
					conditions,
					fields: pillFields,
					parameters
				},
				buttons: {
					conditions: t('Set conditions'),
					fields: pillFields.length
						? t('Manage required parameters')
						: t('Add required parameters'),
					parameters: parameters.length
						? t('Manage optional parameters')
						: t('Add optional parameters'),
					audiences: t('Add audience')
				},
				disabled: {
					fields: selectedPageIsTab
				}
			});
		},
		[AUDIENCE_UPDATE_SELECTED]: ({ state, updateState }) => {
			const { audienceList = [], pills, buttons } = state;
			updateState({
				pills: {
					...pills,
					audiences: audienceList.map(
						({ applicability }) => applicability.label
					)
				},
				buttons: {
					...buttons,
					audiences: audienceList.length
						? t('Manage audience')
						: t('Add audience')
				}
			});
		},
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) => {
			dispatch(UIB_CREATE_SUCCESS_MODAL_DISMISSED);
		},
		['NOW_BUTTON#CLICKED']: ({ action, dispatch, properties }) => {
			action.stopPropagation();
			const { id } = action.payload;
			const { page, screen } = properties;
			dispatch(actions[id], { page, screen, id });
		},
		['NOW_TABS#SELECTED_ITEM_SET']: ({ action, updateState }) => {
			const {
				payload: { value }
			} = action;

			updateState({
				operation: 'set',
				path: 'currentTab',
				value
			});
		}
	}
});
