import { createCustomElement } from '@servicenow/ui-core';
import 'sn-ui-builder-components';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import get from 'lodash/get';
import { createElementInstanceNameMap } from '../../utils/properties';

const view = (state) => {
	const {
		opened,
		element,
		positionTarget,
		type,
		computedType,
		elementInstanceNameMap,
		positions,
		isFlex
	} = state.properties;
	const { id: elementId } = element || {};
	return (
		<sn-uib-element-context-menu
			opened={opened}
			elementSlotStyle={`@${elementId}_information/slotStyles`}
			type={type || computedType}
			element={element}
			elementInstanceNameMap={elementInstanceNameMap}
			positions={positions}
			positionTarget={positionTarget}
			isFlex={isFlex}
		/>
	);
};

createCustomElement('uib-element-context-menu', {
	view,
	properties: {
		components: {
			default: []
		},
		opened: {
			default: false
		},
		positionTarget: {
			default: null
		},
		selectedSlotName: {
			default: null
		},
		selectedSlotParent: {
			default: null
		},
		onSelect: {
			default: null
		},
		element: {
			default: {}
		},
		layouts: {
			default: {}
		},
		positions: {
			default: null
		},
		type: {
			default: null
		},
		rootElementId: {
			default: null
		},
		isFlex: {
			computed: ({ properties: { layouts, element } }) => {
				if (!element) {
					return null;
				}
				const { slotName: selectedSlotName, elementId } = element;
				const currentItems =
					get(layouts, `${elementId}.layout.default.items`) || [];
				const currentSlot = currentItems.find(
					({ slotName }) => slotName === selectedSlotName
				);

				return get(currentSlot, 'styles.display', null) === 'flex';
			}
		},
		isRoot: {
			computed: ({ properties }) => {
				const { rootElementId, element } = properties;
				const elementId = element && element.id;
				return elementId === rootElementId;
			}
		},
		computedType: {
			computed: ({ properties }) => {
				// TODO: Have better determination of slot type
				const { layouts, element, isRoot } = properties;

				if (isRoot) {
					return 'body';
				} else if (element && element.isModal) {
					return 'modal';
				} else if (element && element.id) {
					return get(layouts, `${element.id}.type`, 'slot');
				}
				return null;
			}
		},
		elementProperties: {
			default: []
		},
		elementInstanceNameMap: {
			computed: ({ properties }) => {
				createElementInstanceNameMap(properties.elementProperties);
			}
		}
	},
	actionHandlers: {
		'UIB_ELEMENT_CONTEXT_MENU#CHANGE_FLEX_DIRECTION': ({
			action,
			dispatch
		}) => {
			const {
				element: { elementId, slotName },
				flexDirection
			} = action.payload;
			dispatch(snUXFActions.SLOT_STYLES_PATCHED, {
				elementId: elementId,
				selectedSlotName: slotName,
				styles: { 'flex-direction': flexDirection }
			});
		},
		['SN_UIB_ELEMENT_CONTROLS#COMPONENT_EDIT_BUTTON_CLICKED']: ({
			action,
			dispatch
		}) => {
			dispatch(snUXFActions.COMPONENT_EDIT_REQUESTED, action.payload);
			window.open(`/now/build/components/${action.payload.componentId}`);
		}
	}
});
