import { DevxKeyFocusBehavior } from 'sn-devx-components';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import { screenNames } from '../../../utils/routingConstants';
import { forceTransitionTo } from '../../uib-router';
import { checkForUnsavedChanges } from '../utils/checkForUnsavedChanges';

const { focusTargetId } = DevxKeyFocusBehavior;

export const panelHandlers = {
	['COLLAPSIBLE_PANEL#PANEL_TOGGLED']: ({
		action,
		properties,
		updateProperties,
		dispatch
	}) => {
		const { panel, value: expanded } = action.payload;
		if (
			panel === 'page-management' &&
			properties.pageManagementPanelExpanded !== expanded
		) {
			updateProperties({
				pageManagementPanelExpanded: expanded,
				isPanelTransitioning: true
			});
			// TODO: setTimeout...really?  See: https://code.devsnc.com/dev/emotive-builder/pull/1027
			setTimeout(() => dispatch('COLLAPSIBLE_PANEL#TRANSITION_END'), 333);
		}

		if (
			(panel === 'element-configuration' || panel === 'page-configuration') &&
			properties.elementConfigurationPanelExpanded !== expanded
		) {
			updateProperties({
				elementConfigurationPanelExpanded: expanded,
				isPanelTransitioning: true
			});
			focusTargetId(dispatch, 'quick-link-element-configuration-panel');
		}
	},
	['COLLAPSIBLE_PANEL#TRANSITION_END']: ({ updateProperties }) => {
		updateProperties({ isPanelTransitioning: false });
	},
	['UIB_SIDEBAR_NAV#HOME_CLICKED']: checkForUnsavedChanges(
		({ action, updateProperties }) => {
			action.stopPropagation();
			updateProperties({
				appsSysId: null
			});
			forceTransitionTo(screenNames.appList);
		}
	),
	['UIB_SIDEBAR_NAV#TAB_CHANGED']: ({ action, updateState }) => {
		action.stopPropagation();
		updateState({
			currentSidebarTab: action.payload.value
		});
	},
	['UIB_SIDEBAR_NAV#DATATAB_CHANGED']: ({
		state,
		action,
		updateState,
		dispatch
	}) => {
		action.stopPropagation();
		const shouldExpand =
			state.currentSidebarDataTab !== action.payload.value ||
			!state.dataBrokerPanelExpanded;
		updateState({
			currentSidebarDataTab: shouldExpand ? action.payload.value : null,
			dataBrokerPanelExpanded: shouldExpand
		});
		focusTargetId(dispatch, 'quick-link-data-broker-configuration-panel');
	},
	['SN_UIB_DATA_BROKER_CONFIG_PANEL#DATA_BROKER_SELECTED']: ({
		action,
		updateProperties
	}) => {
		action.stopPropagation();
		const {
			payload: { dataBrokerId }
		} = action;
		updateProperties({ selectedDataBrokerId: dataBrokerId });
	},
	['UIB_DATA_CONFIGURATION_SHELF#NEW_DATABROKER_INSTANCE_DETECTED']: ({
		action,
		updateProperties
	}) => {
		action.stopPropagation();
		const { dataBrokerId } = action.payload;
		updateProperties({ selectedDataBrokerId: dataBrokerId });
	},
	[snUXFActions.VIEWPORT_NAME_CHANGED]: ({
		action,
		updateProperties,
		properties
	}) => {
		const { appRoutes } = properties;
		const { id, value } = action.payload;
		updateProperties({
			appRoutes: appRoutes.map((route) => ({
				...route,
				page: {
					...route.page,
					variants: route.page.variants.map((variant) => ({
						...variant,
						viewports: variant.viewports.map((viewport) =>
							viewport.elementId === id
								? { ...viewport, tempName: value }
								: viewport
						)
					}))
				}
			}))
		});
	},
	['UNSAVED_DIALOG#DISCARD_CHANGES']: ({ updateProperties, properties }) => {
		const { appRoutes } = properties;
		updateProperties({
			appRoutes: appRoutes.map((route) => ({
				...route,
				page: {
					...route.page,
					variants: route.page.variants.map((variant) => ({
						...variant,
						viewports: variant.viewports.map((viewport) => ({
							...viewport,
							tempName: null
						}))
					}))
				}
			}))
		});
	}
};
