import { t } from 'sn-translate';

/**
 *
 */
export function showUnsupportedBrowserMessage() {
	const div = document.createElement('div');
	/* prettier-ignore */
	div.textContent = t('UI Builder does not support mobile devices or Internet Explorer or Microsoft Edge. Please use Microsoft Edge-Chromium or one of the other supported browsers on the desktop.');

	div.style.padding = '10px';
	div.style.marginBottom = '18px';
	div.style.borderRadius = '3px';
	div.style.backgroundColor = '#FAE6E6';
	div.style.borderWidth = '1px';
	div.style.borderSpacing = 'solid';
	div.style.borderColor = '#F7D8D8';
	div.style.color = '#9D0010';
	div.style.textAlign = 'center';

	if (document.body.firstElementChild) {
		document.body.insertBefore(div, document.body.firstElementChild);
	} else {
		document.body.appendChild(div);
	}

	return div;
}

/**
 * @param {Window} $window
 */
export function getIEVersion($window) {
	const ua = $window.navigator.userAgent;

	const msie = ua.indexOf('MSIE ');
	if (msie > -1) {
		return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
	}

	const trident = ua.indexOf('Trident/');
	if (trident > -1) {
		const rv = ua.indexOf('rv:');
		return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
	}

	const edge = ua.indexOf('Edge/');
	if (edge > -1) {
		return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
	}

	return -1;
}

/**
 * @param {Window} $window
 */
export function isBrowserSupportedForWindow($window) {
	const isIE11 =
		typeof $window !== 'undefined' &&
		typeof $window.MSInputMethodContext !== 'undefined' &&
		typeof $window.document.documentMode !== 'undefined';

	return (
		getIEVersion($window) < 0 &&
		!isIE11 &&
		!$window.navigator.userAgent.match(
			/(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i
		)
	);
}

export default isBrowserSupportedForWindow.bind(null, window);
