import { actionTypes } from '@servicenow/ui-core';
import { ElementTabSelectionManager } from './utils';
import get from 'lodash/get';
export default {
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
		action: { payload },
		properties,
		updateProperties
	}) => {
		const { name, value, previousValue } = payload;
		const { selectedViewportElement, selectedElementId } = properties;

		const noSelectedId = !selectedElementId;
		const previousViewportIsNull = !previousValue;
		const newValueIsDifferentFromPrevious =
			value && previousValue && previousValue.id !== value.id;
		if (
			name === 'selectedViewportElement' &&
			(noSelectedId ||
				previousViewportIsNull ||
				newValueIsDifferentFromPrevious)
		) {
			updateProperties({
				selectedComponent: get(value, 'component', null),
				selectedElementId: get(value, 'id', null)
			});
		}
		if (
			name === 'rootElementId' &&
			(!selectedElementId || previousValue === selectedElementId) &&
			!selectedViewportElement
		) {
			ElementTabSelectionManager.reset();

			updateProperties({
				selectedElementId: value
			});
		}
	}
};
