import { createCustomElement, actionTypes } from '@servicenow/ui-core';

import { snUXFActions } from 'sn-uxf-builder-middleware';
import { DevxKeyFocusBehavior } from 'sn-devx-components';
import get from 'lodash/get';

import { getHeaderActions, getMenuItems } from './utils';

import { applicationTypes } from '../../utils/routingConstants';
import * as headerActionTypes from './actions';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';

const { createFocusRef } = DevxKeyFocusBehavior;

const QUICK_LINK_PAGE_HEADER = createFocusRef('quick-link-page-header');

const view = (state) => {
	const {
		pageName,
		appScope,
		headerActions,
		pageStatus,
		menuItems,
		pageSysPolicy,
		pageScope,
		properties: {
			applicationType,
			builderType,
			runtimeUrl,
			isEmptyState,
			displayScopePicker,
			applications,
			userScope,
			isPagesLoading,
			domainTree,
			currentDomain,
			displayDomainPicker,
			displayExpandDomain,
			isDomainExpanded,
			isVariantSelected,
			parentInfo
		}
	} = state;

	const capitalizedPageStatus = pageStatus
		? pageStatus.charAt(0).toUpperCase() + pageStatus.slice(1)
		: pageStatus;
	return (
		<sn-uib-page-header
			page-scope={pageScope}
			page-sys-policy={pageSysPolicy}
			ref={QUICK_LINK_PAGE_HEADER}
			app-type={applicationType}
			app-scope={appScope}
			app-name="@uib-app/pageRegistryName"
			page-name={pageName}
			page-status={capitalizedPageStatus}
			parent-info={parentInfo}
			menu-items={menuItems}
			header-actions={headerActions}
			builder-type={builderType}
			runtime-url={runtimeUrl}
			url-test-value-changed={headerActionTypes.URL_TEST_VALUE_CHANGED}
			isEmptyState={isEmptyState}
			display-scope-picker={displayScopePicker}
			applications={applications}
			user-scope={userScope}
			is-pages-loading={isPagesLoading}
			domainTree={domainTree}
			currentDomain={currentDomain}
			display-domain-picker={displayDomainPicker}
			display-expand-domain={displayExpandDomain}
			is-domain-expanded={isDomainExpanded}
			is-variant-selected={isVariantSelected}
		/>
	);
};

const transformState = (state) => {
	const {
		isWorkspaceApp,
		selectedPage,
		page,
		appsScope,
		routeState,
		blockingRequestInFlight: isLoading
	} = state.properties;

	const pageName =
		page && !isLoading && (routeState.variantSysId || isWorkspaceApp)
			? page.name
			: selectedPage
			? selectedPage.name
			: null;
	const appScope =
		page && page.applicationName ? page.applicationName : appsScope;
	const pageStatus = get(page, 'pageStatus', null);
	const pageScope = page
		? {
				id: get(page, 'scope', null),
				label: escapeScopeLabel(get(page, 'scopeName', null))
		  }
		: null;

	const pageSysPolicy = page ? page.sysPolicy : null;

	return {
		...state,
		pageStatus,
		pageName,
		appScope: appScope ? appScope.replace(/^@/, '') : appScope,
		pageSysPolicy,
		pageScope
	};
};

const setMenuItems = (properties) => {
	const {
		isWorkspaceApp,
		selectedPage,
		variantActionsDisabled,
		isPageCopiable: screenIsCopiable,
		pageVariantsEnabled,
		page,
		selectedPageIsTab,
		applicationType,
		runtimeUrl: { isInvalidAbsoluteUrl },
		isVariantSelected,
		parentInfo,
		blockingRequestInFlight: isLoading
	} = properties;

	const pageIsSelected = selectedPage !== null;
	const screenIsSelected =
		page !== null && (isWorkspaceApp || isVariantSelected);

	return isLoading
		? []
		: getMenuItems({
				isWorkspaceApp,
				pageIsSelected,
				screenIsSelected,
				screenIsReadOnly: variantActionsDisabled,
				screenIsCopiable,
				pageVariantsEnabled,
				selectedPage,
				screenIsTab: selectedPageIsTab,
				applicationType,
				isInvalidAbsoluteUrl,
				parentInfo
		  });
};

const setHeaderActions = (properties) => {
	const {
		page,
		isPageStateDirty,
		isSavingPage,
		isWorkspaceApp,
		builderType,
		isVariantSelected,
		blockingRequestInFlight: isLoading,
		variantActionsDisabled,
		dashboardId
	} = properties;

	const pageStatus = page ? page.pageStatus : null;
	const screenIsSelected =
		page !== null && (isWorkspaceApp || isVariantSelected);

	return getHeaderActions({
		isWorkspaceApp,
		screenIsReadOnly: variantActionsDisabled,
		screenIsSelected,
		pageStatus,
		saveDisabled: variantActionsDisabled || !isPageStateDirty,
		isSavingPage,
		builderType,
		isLoading,
		dashboardId
	});
};

createCustomElement('uib-page-header', {
	view,
	transformState,
	properties: {
		appsSysId: {
			default: null
		},
		appsScope: {
			default: null
		},
		applicationType: {
			default: null
		},
		page: {
			default: null
		},
		parentInfo: {
			default: {}
		},
		isPageStateDirty: {
			default: false
		},
		isSavingPage: {
			default: false
		},
		isPageCopiable: {
			default: true
		},
		pageVariantsEnabled: {
			default: false
		},

		/**
		 * List of app routes
		 */
		appRoutes: {
			default: []
		},

		/**
		 * Are we currently in a workspace app?
		 */
		isWorkspaceApp: {
			default: false
		},
		selectedPage: {
			default: null
		},
		selectedPageIsTab: {
			default: false
		},
		routeState: {
			default: null
		},
		runtimeUrl: {
			default: {}
		},

		/**
		 * The type of application (either UI, Dashboard)
		 *
		 * @type {string}
		 */
		builderType: {
			default: 'UI'
		},

		isEmptyState: {
			default: false
		},

		displayScopePicker: {
			default: false
		},

		applications: {
			default: []
		},

		userScope: {
			default: {}
		},

		isPagesLoading: {
			default: false
		},

		domainTree: {
			default: []
		},

		currentDomain: {
			default: null
		},

		displayDomainPicker: {
			default: false
		},
		displayExpandDomain: {
			default: false
		},
		isDomainExpanded: {
			default: false
		},
		isVariantSelected: {
			default: false
		},
		blockingRequestInFlight: {
			default: false
		},
		variantActionsDisabled: {
			default: true
		},
		dashboardId: {
			default: null
		}
	},
	actionHandlers: {
		[headerActionTypes.SAVE_CLICKED]: ({ dispatch, properties, action }) => {
			action.stopPropagation();
			dispatch(snUXFActions.PAGE_SAVE_REQUESTED, {
				pageStateModel: properties.page
			});
		},

		[headerActionTypes.URL_TEST_VALUE_CHANGED]: ({ dispatch, action }) => {
			dispatch(snUXFActions.PAGE_PROPERTIES_CHANGED, action.payload);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			updateState
		}) => {
			if (
				[
					'isWorkspaceApp',
					'selectedPage',
					'variantActionsDisabled',
					'isPageCopiable',
					'pageVariantsEnabled',
					'page',
					'selectedPageIsTab',
					'applicationType',
					'runtimeUrl',
					'blockingRequestInFlight'
				].includes(action.payload.name)
			) {
				updateState({
					operation: 'set',
					path: 'menuItems',
					value: setMenuItems(properties)
				});
			}

			if (
				[
					'page',
					'isPageStateDirty',
					'isSavingPage',
					'builderType',
					'isVariantSelected',
					'variantActionsDisabled',
					'dashboardId'
				].includes(action.payload.name)
			) {
				updateState({
					operation: 'set',
					path: 'headerActions',
					value: setHeaderActions(properties)
				});
			}
		},
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties,
			updateState,
			state
		}) => {
			updateState({
				...state,
				menuItems: setMenuItems(properties),
				headerActions: setHeaderActions(properties)
			});
		},
		'SN_UIB_PAGE_HEADER#NEW_DASHBOARD_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.NEW_DASHBOARD_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#NEW_WORKSPACE_PAGE_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.NEW_WORKSPACE_PAGE_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#COPY_WORKSPACE_PAGE_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.COPY_WORKSPACE_PAGE_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#NEW_APP_PAGE_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.NEW_APP_PAGE_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#NEW_APP_DYNAMIC_TAB_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.NEW_APP_PAGE_DYNAMIC_TAB_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#NEW_APP_PAGE_VARIANT_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.NEW_APP_PAGE_VARIANT_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#COPY_APP_PAGE_VARIANT_CLICKED': ({ dispatch }) => {
			dispatch(headerActionTypes.COPY_APP_PAGE_VARIANT_CLICKED);
		},
		'SN_UIB_PAGE_HEADER#SET_DOMAIN_EXPANDED': ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();
			const { appsSysId } = properties;

			dispatch('UIB_PAGE_HEADER#SET_DOMAIN_EXPANDED', {
				...action.payload,
				appsSysId
			});
		},
		'SN_UIB_PAGE_HEADER#BACK_BUTTON_CLICKED': ({ dispatch, properties }) => {
			const payload = {
				sysId:
					properties.applicationType !== applicationTypes.dashboards
						? get(properties, 'parentInfo.screenId')
						: null,
				appsSysId: get(properties, 'routeState.appsSysId'),
				applicationType: properties.applicationType,
				pageSysId: get(properties, 'parentInfo.screenType'),
				name: get(properties, 'parentInfo.screenName'),
				screenSysId: get(properties, 'parentInfo.screenId'),
				queryParams: {
					selectedElement: get(properties, 'parentInfo.compositionElementId')
				}
			};

			// dispatch('UIB_PAGE_HEADER#BACK_BUTTON_CLICKED', payload);
			dispatch('SN_UIB_APP#PAGE_REQUESTED', payload);
		}
	}
});
