import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { applicationTypes } from '../../../utils/routingConstants';
import { checkForUnsavedChanges } from '../../uib-app/utils/checkForUnsavedChanges';

export const getElementByProperty = (element, propertyValue, path) => {
	const nodes = [element];
	let parent = null;
	while (nodes.length && !parent) {
		const node = nodes.shift();
		if (get(node, path, null) === propertyValue) {
			parent = node;
		} else {
			nodes.push(...node.children);
		}
	}
	return parent;
};

export const isolatedEditHandlers = {
	['SN_UIB_VIEWPORT#ISOLATED_EDIT']: checkForUnsavedChanges(
		({ action, dispatch }) => {
			dispatch('SN_UIB_APP#ISOLATED_EDIT', action.payload);
		}
	),
	['SN_UIB_APP#ISOLATED_EDIT']: (coeffects) => {
		const { action, properties, dispatch } = coeffects;

		const {
			appsSysId,
			pageState,
			pageMetadata,
			isDashboardBuilder
		} = properties;

		const {
			elementId,
			screenId,
			routeId,
			width,
			isolatedEditMode
		} = action.payload;

		// get appConfig, appType
		const appConfig = get(pageMetadata, 'uiTypeParameters.appConfig');
		let appType = appConfig
			? applicationTypes.app
			: applicationTypes.components;

		// get activeRoute, activeScreen
		const viewportElement = getElementByProperty(
			pageState.rootElement,
			elementId,
			'id'
		);
		const viewportRoutes = get(
			viewportElement,
			'properties.viewportRoutes.value',
			{}
		);

		const activeRoute = routeId
			? get(viewportRoutes, `${routeId}.0`, {})
			: get(viewportElement, 'properties.activeRoute.value', {});

		const activeScreen = screenId
			? activeRoute.macroponents.find((mcp) => mcp.id === screenId)
			: get(viewportElement, 'properties.activeScreen.value', {});

		// set up dashboard info
		let dashboardId = null;
		if (isDashboardBuilder) {
			appType = applicationTypes.dashboards;
			dashboardId = activeRoute.id;
		}

		if (!isEmpty(activeRoute) && !isEmpty(activeScreen)) {
			const payload = {
				sysId: activeScreen.id,
				appsSysId: appConfig || appsSysId,
				applicationType: appType,
				pageSysId: activeRoute.screenType,
				name: activeScreen.name,
				dashboardId,
				queryParams: {
					parentScreenId: pageState.sysId,
					parentElementId: elementId
				}
			};

			if (width) {
				payload.queryParams.stageWidth = Math.round(width);
			}

			dispatch('SN_UIB_APP#PAGE_REQUESTED', payload);
		} else {
			const payload = {
				appsSysId: appsSysId,
				pageSysId: activeRoute.screenType,
				applicationType: appType,
				dashboardId: get(properties, 'routeState.dashboardId', ''),
				queryParams: {
					parentScreenId: pageState.sysId,
					parentElementId: elementId
				}
			};

			if (isolatedEditMode || isEmpty(viewportRoutes)) {
				payload.queryParams.isolatedEditMode = true;
			}

			dispatch('SN_UIB_APP#PAGE_REQUESTED', payload);
		}
	}
};
