import { focusStrategy } from 'sn-ui-builder-components';
import { UNREGISTERED_SLOTTED_ELEMENT } from '../uib-default-slotted';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import {
	ElementTabSelectionManager,
	getElementKey,
	evaluateConfigTabSelection
} from './utils';
import { get } from 'lodash';

const selectElement = ({ action, properties, updateProperties }) => {
	const { elementId, component } = action.payload;
	const { unregisteredElements } = properties;
	const pageConfigTab = evaluateConfigTabSelection({
		selectedElementId: elementId,
		properties
	});
	updateProperties({
		pageConfigTab,
		selectedElementId: elementId,
		hoveredElementId: null,
		elementConfirmingDelete: null,
		selectedComponent: component,
		selectedSlotName: null,
		selectedSlotParent: null,
		elementHasError: !!unregisteredElements[elementId]
	});
};

export default {
	[UNREGISTERED_SLOTTED_ELEMENT]: ({
		action: {
			payload: { elementId }
		},
		updateProperties,
		state: {
			properties: { unregisteredElements = {} }
		}
	}) => {
		updateProperties({
			unregisteredElements: { ...unregisteredElements, [elementId]: true }
		});
	},
	[snUXFActions.COMPONENT_PLACED_SUCCEEDED]: selectElement,
	[snUXFActions.RELOCATE_ELEMENT_SUCCEEDED]: ({
		action,
		properties,
		updateProperties,
		dispatch
	}) => {
		const { elementId } = action.payload;
		selectElement({ action, updateProperties, properties });
		focusStrategy.init(dispatch, `${elementId}_ROOT`);
	},
	[snUXFActions.RESTORE_ELEMENT_SUCCEEDED]: ({
		action,
		properties,
		updateProperties,
		dispatch
	}) => {
		const { elementIds = [] } = action.payload;
		selectElement({
			action: {
				payload: {
					elementId: elementIds[0]
				}
			},
			updateProperties,
			properties
		});
		if (elementIds[0]) {
			focusStrategy.init(dispatch, `${elementIds[0]}_ROOT`);
		}
	},
	['UIB_ELEMENT#ELEMENT_SELECTED']: ({
		action,
		updateProperties,
		properties
	}) => {
		selectElement({ action, properties, updateProperties });
	},
	['UIB_APP#EMPTY_SLOT_OR_ELEMENT_SELECTED']: ({
		action,
		updateProperties,
		properties,
		dispatch
	}) => {
		switch (action.payload.type) {
			case 'slot':
				{
					const {
						selectedSlotName,
						selectedSlotParent,
						selectedSlotParentComponent
					} = action.payload;
					dispatch('UIB_SLOT_WRAPPER#SELECTED', {
						slotParent: selectedSlotParent,
						slotName: selectedSlotName,
						selectedSlotParentComponent: selectedSlotParentComponent
					});
				}
				break;
			case 'element':
				selectElement({ action, properties, updateProperties });
				break;
			default:
		}
	},
	['SN_UIB_CONFIG_PANEL#TAB_SELECTED']: ({
		updateProperties,
		action,
		properties
	}) => {
		const { name } = action.payload;
		const { selectedElementId } = properties;
		ElementTabSelectionManager.set(getElementKey(selectedElementId), name);
		updateProperties({ pageConfigTab: name });
	},
	['UIB_ELEMENT#ELEMENT_MOUSE_OVER']: ({
		action,
		properties,
		updateProperties
	}) => {
		const { elementId } = action.payload;
		const { selectedElementId } = properties;
		updateProperties({
			hoveredElementId: selectedElementId !== elementId ? elementId : null,
			hoveredSlotName: null,
			hoveredSlotParent: null
		});
	},
	['UIB_STAGE#ELEMENT_MOUSE_LEAVE']: ({ updateProperties }) => {
		updateProperties({
			hoveredElementId: null,
			hoveredSlotName: null,
			hoveredSlotParent: null
		});
	},
	['SN_UIB_ELEMENT_PLACE_CONTROL#ADD_REQUESTED']: ({
		action,
		updateProperties
	}) => {
		const { elementId, direction } = action.payload;

		updateProperties({
			addNextToElementId: elementId,
			directionToAdd: direction
		});
	},
	['SN_UIB_ELEMENT_PLACE_CONTROL#CANCEL_ADD_REQUESTED']: ({
		updateProperties
	}) => {
		updateProperties({
			addNextToElementId: null,
			directionToAdd: 'none'
		});
	},
	['SN_UIB_ELEMENT_CONTROLS#TRASH_CONFIRM_BUTTON_CLICKED']: ({
		action,
		updateProperties
	}) => {
		const { isModalDescendant, parentModalId } = action.payload;

		updateProperties({
			selectedElementId:
				isModalDescendant && parentModalId ? parentModalId : null,
			hoveredElementId: null
		});
	},
	['SN_UIB_ELEMENT_CONTEXT_MENU#CONFIGURE_COMPONENT']: ({
		action,
		updateProperties,
		dispatch,
		properties
	}) => {
		switch (action.payload.type) {
			case 'slot':
				{
					const {
						selectedSlotName,
						selectedSlotParent,
						selectedSlotParentComponent
					} = action.payload;
					dispatch('UIB_SLOT_WRAPPER#SELECTED', {
						slotParent: selectedSlotParent,
						slotName: selectedSlotName,
						selectedSlotParentComponent: selectedSlotParentComponent
					});
					dispatch('SN_UIB_ELEMENT_CONTROLS#CONFIG_BUTTON_CLICKED');
				}
				break;
			case 'element':
				selectElement({ action, properties, updateProperties });
				dispatch('SN_UIB_ELEMENT_CONTROLS#CONFIG_BUTTON_CLICKED');
				break;
			default:
				dispatch('SN_UIB_ELEMENT_CONTROLS#CONFIG_BUTTON_CLICKED');
		}
	},
	[snUXFActions.ELEMENT_ID_CHANGED]: ({ action, updateProperties }) => {
		updateProperties({ selectedElementId: action.payload.id });
	},
	[snUXFActions.ELEMENT_DELETED]: ({
		action,
		properties,
		updateProperties
	}) => {
		const elementToSelect = get(action, 'payload.elementToSelect', null);
		if (action.payload.elementId === properties.selectedElementId) {
			updateProperties({ selectedElementId: elementToSelect });
		}
	}
};
