import omit from 'lodash/omit';

const PARAMS = {
	transactionScope: 'sysparm_transaction_scope',
	query: 'sysparm_query',
	overrideVariantOpenedFrom: 'sysparm_override_variant_opened_from',
	queryNoDomain: 'sysparm_query_no_domain'
};

const setParam = (
	qp = {},
	{ transactionScope, query, overrideVariantOpenedFrom, queryNoDomain }
) => ({
	[PARAMS.transactionScope]: transactionScope || qp.transactionScope,
	[PARAMS.query]: query || qp.query,
	[PARAMS.overrideVariantOpenedFrom]:
		overrideVariantOpenedFrom || qp.overrideVariantOpenedFrom,
	[PARAMS.queryNoDomain]: queryNoDomain || qp.queryNoDomain
});

const getTransactionScope = (qp) => qp && qp[PARAMS.transactionScope];
const getQuery = (qp) => qp && qp[PARAMS.query];
const getOverrideVariantOpenedFrom = (qp) =>
	qp && qp[PARAMS.overrideVariantOpenedFrom];
const getQueryNoDomain = (qp) => qp && qp[PARAMS.queryNoDomain];

const clearTransactionScope = (qp) => qp && omit(qp, PARAMS.transactionScope);
const clearQuery = (qp) => qp && omit(qp, PARAMS.query);
const clearOverrideVariantOpenedFrom = (qp) =>
	qp && omit(qp, PARAMS.overrideVariantOpenedFrom);
const clearQueryNoDomain = (qp) => qp && omit(qp, PARAMS.queryNoDomain);

export default {
	setParam,
	getTransactionScope,
	getQuery,
	getOverrideVariantOpenedFrom,
	getQueryNoDomain,
	clearTransactionScope,
	clearQuery,
	clearOverrideVariantOpenedFrom,
	clearQueryNoDomain
};
