import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

// NDS
import '@servicenow/now-heading';
import '@servicenow/now-input';
import '@servicenow/now-text-link';

// Internal components
import '../sn-experience-settings-link';

import styles from '../../styles/forms.scss';

import {
	INPUT_CHANGED,
	PAGE_GENERAL,
	SETTINGS_UPDATED,
	TABLE_EXPERIENCE,
	URLDOC,
	WORKSPACE
} from '../../constants';

const view = ({
	properties: { sysId, path, title, description, applicationType, readonly }
}) => {
	const page =
		applicationType === WORKSPACE
			? 'UIB_Workspace_General_Settings'
			: 'UIB_General_Settings_Portal';

	const generalPage = 'UI_Builder_Experience_Settings';

	return (
		<div>
			<now-heading
				label={t('What are experience settings?')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{t(
					"These settings allow you to customize your web experience to your needs. You can apply your organization's branding, pick a theme, configure navigation systems, or enable search functionality on any pages you create. If your settings are read-only, use the scope picker to switch your experience's scope."
				)}{' '}
				<now-text-link
					label={t('Learn more on setting up your experience.')}
					href={`${URLDOC}/${generalPage}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<now-heading
				label={t('General')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{t('Edit the name, description and URL of this experience.')}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<div className="flex-column fieldset">
				<now-input
					name="title"
					label="Title"
					value={title}
					required={true}
					readonly={readonly}
				/>
				<now-input
					name="description"
					label="Description"
					value={description}
					readonly={readonly}
				/>
				<now-input
					name="path"
					label="Path"
					type="text"
					value={path}
					required={true}
					readonly={readonly}
				/>
			</div>
			<sn-experience-settings-link
				sys-id={sysId}
				table={TABLE_EXPERIENCE}></sn-experience-settings-link>
		</div>
	);
};

const inputChangedEffect = ({
	action: {
		payload: { name, fieldValue }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, { page: PAGE_GENERAL, name, value: fieldValue });
};

createCustomElement('sn-experience-settings-general', {
	view,
	properties: {
		sysId: {
			required: true,
			schema: { type: 'string' }
		},
		title: {
			required: true,
			schema: { type: 'string' }
		},
		description: {
			required: false,
			schema: { type: 'string' }
		},
		path: {
			required: true,
			schema: { type: 'string' }
		},
		readonly: {
			schema: { type: 'boolean' }
		},
		applicationType: {
			default: ''
		}
	},
	actionHandlers: {
		[INPUT_CHANGED]: {
			stopPropagation: true,
			effect: inputChangedEffect
		}
	},
	styles
});
