import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-modal';

export const CONFIRM_DIALOG_CONFIRMED = 'UIB_CONFIRM_DIALOG_CONFIRMED';
export const CONFIRM_DIALOG_CANCELED = 'UIB_CONFIRM_DIALOG_CANCELED';
export const CONFIRM_DIALOG_INTERACTION_COMPLETE =
	'UIB_CONFIRM_DIALOG_INTERACTION_COMPLETE';

const DEFAULT_ACTIONS = [
	{
		label: t('OK'),
		variant: 'primary',
		clickActionType: CONFIRM_DIALOG_CONFIRMED
	},
	{
		label: t('Cancel'),
		variant: 'secondary',
		clickActionType: CONFIRM_DIALOG_CANCELED
	}
];

const view = (state) => {
	const {
		opened,
		headerLabel,
		message,
		size,
		footerActions
	} = state.properties;
	return (
		<now-modal
			opened={opened}
			manage-opened={true}
			size={size}
			header-label={headerLabel}
			content={message}
			footer-actions={footerActions}
		/>
	);
};

createCustomElement('uib-confirm-dialog', {
	view,
	properties: {
		opened: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		size: {
			default: 'sm',
			schema: {
				type: 'string'
			}
		},
		headerLabel: {
			default: t('Please confirm'),
			schema: {
				type: 'string'
			}
		},
		message: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		footerActions: {
			default: DEFAULT_ACTIONS,
			schema: {
				type: 'array'
			}
		},
		/**
		 * A config object for the action to be dispatched on confirm
		 *
		 * @example {payload: {}, actionType: 'DO_SOMETHING'}
		 */
		confirmAction: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					payload: { type: 'object' },
					actionType: { type: 'string' }
				}
			}
		},
		/**
		 * A config object for the action to be dispatched on cancel
		 *
		 * @example {payload: {}, actionType: 'DO_SOMETHING'}
		 */
		cancelAction: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					payload: { type: 'object' },
					actionType: { type: 'string' }
				}
			}
		}
	},
	actionHandlers: {
		[CONFIRM_DIALOG_CONFIRMED]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const {
				confirmAction: { actionType, payload = {} }
			} = properties;
			actionType && dispatch(actionType, payload);
			dispatch(CONFIRM_DIALOG_INTERACTION_COMPLETE);
		},
		[CONFIRM_DIALOG_CANCELED]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const {
				cancelAction: { actionType, payload = {} }
			} = properties;
			actionType && dispatch(actionType, payload);
			dispatch(CONFIRM_DIALOG_INTERACTION_COMPLETE);
		},
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) =>
			dispatch(CONFIRM_DIALOG_INTERACTION_COMPLETE)
	}
});
