import get from 'lodash/get';
import utils from './utils';

const {
	getCurrentSelectedEventMappingHandlers,
	getExternalConfigurationButtons,
	processCurrentSelectedEventMappings,
	processCurrentSelectedEvents,
	processCurrentSelectedProperties,
	processEventHandlers,
	processEventMappingHandlers,
	findTriggeringElementsForModal
} = utils;

export default (state) => {
	const {
		elementProperties,
		elementLayouts,
		elementEvents,
		elementEventMappings,
		eventHandlers,
		elementStyles,
		selectedElementId,
		selectedElementInstanceName,
		eventMappingHandlers,
		component,
		selectedSlotName,
		selectedSlotParent,
		eventHandlerElementMap,
		clientScriptApi,
		page
	} = state.properties;

	const rootElementId = page ? page.rootElement.id : null;
	const currentProperties = elementProperties[selectedElementId] || [];
	const currentStyles = get(elementStyles, selectedElementId, {});

	const currentItems = get(
		elementLayouts,
		`${selectedSlotParent}.layout.default.items`,
		[]
	);
	const currentSlot = currentItems.find(
		({ slotName }) => slotName === selectedSlotName
	);
	const isParentElementIsContainer =
		'container' === get(get(elementLayouts, `${selectedSlotParent}`), 'type');
	const currentSlotStyles = isParentElementIsContainer
		? get(currentSlot, 'styles', {})
		: null;

	const currentElementLayout = elementLayouts[selectedElementId] || {};
	const currentEvents = elementEvents[selectedElementId] || [];
	const currentEventMappings = elementEventMappings[selectedElementId] || [];

	const currentSelectedProperties = processCurrentSelectedProperties(
		currentProperties,
		state,
		{}, // no overrides needed
		clientScriptApi
	);
	const currentSelectedEvents = processCurrentSelectedEvents(currentEvents);
	const currentSelectedEventMappings = processCurrentSelectedEventMappings(
		currentEventMappings
	);
	const localEventHandlers = eventHandlers.filter(
		(handler) => handler.ancestor === false
	);
	const currentEventHandlers = processEventHandlers(localEventHandlers);
	const ancestorEventHandlers = eventHandlers.filter(
		(handler) => handler.ancestor === true
	);
	const currentAncestorEventHandlers = processEventHandlers(
		ancestorEventHandlers
	);

	const selectedEventMappingHandlers = getCurrentSelectedEventMappingHandlers(
		currentSelectedEventMappings,
		eventMappingHandlers
	);
	const currentEventMappingHandlers = processEventMappingHandlers(
		selectedEventMappingHandlers,
		state,
		{}, // no overrides needed
		'sn-uib-element-configuration-panel'
	);

	const currentTriggeringElements = findTriggeringElementsForModal(
		selectedElementId,
		eventHandlerElementMap
	);

	const id = selectedElementId;
	const displayName = selectedElementInstanceName;
	const componentCategory = get(component, 'category', '');

	const resolvedDynamicBindings = clientScriptApi;

	return {
		...state,
		currentSelectedProperties,
		currentElementLayout,
		currentSelectedEvents,
		currentStyles,
		currentSlotStyles,
		currentSelectedEventMappings,
		currentEventHandlers,
		currentAncestorEventHandlers,
		currentEventMappingHandlers,
		currentElement:
			!selectedElementId || rootElementId === selectedElementId
				? null
				: { id, displayName, componentCategory },
		externalConfigurationButtons: getExternalConfigurationButtons(
			component,
			currentSelectedProperties,
			resolvedDynamicBindings,
			clientScriptApi
		),
		currentTriggeringElements
	};
};
