import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import 'sn-devx-components';
import '@servicenow/now-modal';

/* TODO: Request that now-modal provides a method by which to
 * disable scrolling via a property configuration
 */
const disableModalScrolling = (vnode) => {
	// If now-modal changes their doc structure this just becomes a no-op
	const nowModalRoot = vnode.elm.shadowRoot;
	const modalBody = nowModalRoot
		? nowModalRoot.querySelector('.now-modal-body')
		: null;
	if (modalBody) {
		modalBody.style.overflow = 'hidden';
	}
};

export default createCustomElement('uib-c3-modal', {
	view: ({
		properties: {
			c3PropertyConfig,
			c3ContentHeight,
			c3FooterActions,
			c3ShowModal
		}
	}) => {
		const config = c3PropertyConfig || {};
		const {
			label,
			srcUrl,
			initialValue,
			meta,
			scrollable,
			redirectFocusTo
		} = config;
		const data = { value: initialValue, meta };
		const height = c3ContentHeight;
		return srcUrl && c3ShowModal ? (
			<now-modal
				hoist
				hook-update={(vnode) => !scrollable && disableModalScrolling(vnode)}
				header-label={label}
				opened
				size="lg"
				footerActions={c3FooterActions}>
				<devx-iframe
					title={t('Editing {0}', label)}
					src={srcUrl}
					style={{ height }}
					data={data}
					redirectFocusTo={redirectFocusTo}
				/>
			</now-modal>
		) : null;
	},
	properties: {
		/**
		 * The config object containing all information and data for the C3 UX
		 * @type {object}
		 * @example { initialValue, 'original stuff', value: 'currentStuff', pendingValue: 'new stuff', ... }
		 */
		c3PropertyConfig: { default: null },
		/**
		 * The height in ems of the content inside the modal to be set on iframe
		 * @type {string}
		 * @example '40em'
		 */
		c3ContentHeight: { default: null },
		/**
		 * The state of the content within the c3Modal
		 * @type {boolean}
		 */
		c3ContentInitialized: { default: false },
		/**
		 * The footer actions to be passed to the modal
		 */
		c3FooterActions: {
			default: [
				{
					label: t('Apply'),
					variant: 'primary',
					disabled: false,
					clickActionType: 'UIB_APP#C3_DONE_CLICKED'
				},
				{
					label: t('Cancel'),
					variant: 'secondary',
					disabled: false,
					clickActionType: 'UIB_APP#C3_CANCEL_CLICKED'
				}
			]
		},
		/**
		 * The state of the modal if it's visible or hidden
		 * @type {boolean}
		 */
		c3ShowModal: { default: false }
	},
	initialState: {
		host: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name, previousValue, value, host }
			}
		}) => {
			if (name === 'c3ContentInitialized' && !previousValue && value) {
				host.shadowRoot.querySelector('now-modal').focus();
			}
		},
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) =>
			dispatch('SN_UIB_APP#C3_MODAL_CLOSED')
	}
});
