import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { effectProxy } from 'sn-uxf-builder-middleware';
import {
	AUDIENCE_CHOICES_REQUESTED,
	AUDIENCE_CHOICES_REQUEST_SUCCEEDED,
	AUDIENCE_LIST_REQUESTED,
	AUDIENCE_LIST_REQUEST_SENT,
	AUDIENCE_LIST_REQUEST_SUCCEEDED,
	AUDIENCE_ADD_URL,
	AUDIENCE_EDIT_DELETE_URL,
	AUDIENCE_ADD_REQUESTED,
	AUDIENCE_EDIT_REQUESTED,
	AUDIENCE_DELETE_REQUESTED,
	AUDIENCE_REQUEST_FAILED,
	AUDIENCE_HTTP_REQUEST_SUCCEEDED,
	AUDIENCE_LIST_UPDATE,
	AUDIENCE_UPDATE_SELECTED,
	AUDIENCE_LOADING_SET,
	AUDIENCE_HTTP_REQUEST_STARTED
} from './constants';

const addAudienceHttpEffect = createHttpEffect(AUDIENCE_ADD_URL, {
	method: 'POST',
	batch: false,
	dataParam: ['data'],
	queryParams: ['sysparm_transaction_scope'],
	startActionType: AUDIENCE_HTTP_REQUEST_STARTED,
	successActionType: AUDIENCE_HTTP_REQUEST_SUCCEEDED,
	failureActionType: AUDIENCE_REQUEST_FAILED
});

const deleteAudienceHttpEffect = createHttpEffect(AUDIENCE_EDIT_DELETE_URL, {
	method: 'DELETE',
	batch: false,
	pathParams: ['recordId'],
	queryParams: ['sysparm_transaction_scope'],
	startActionType: AUDIENCE_HTTP_REQUEST_STARTED,
	successActionType: AUDIENCE_HTTP_REQUEST_SUCCEEDED,
	failureActionType: AUDIENCE_REQUEST_FAILED
});

export const editAudienceHttpEffect = createHttpEffect(
	AUDIENCE_EDIT_DELETE_URL,
	{
		method: 'PUT',
		batch: false,
		pathParams: ['recordId'],
		queryParams: ['sysparm_transaction_scope'],
		dataParam: ['data'],
		startActionType: AUDIENCE_HTTP_REQUEST_STARTED,
		successActionType: AUDIENCE_HTTP_REQUEST_SUCCEEDED,
		failureActionType: AUDIENCE_REQUEST_FAILED
	}
);

export const audienceChoicesQuery = `query {
    GlideRecord_Query {
      sys_ux_applicability (pagination: {limit: 1000}) {
        _results {
          name {
            value
          }
          sys_id {
            value
          }
          sys_scope { 
            value
            displayValue
          }
          active { 
            value
            displayValue
          }
        }
      }
    }
  }
`;

export const fetchAudienceChoicesEffect = createGraphQLEffect(
	audienceChoicesQuery,
	{
		successActionType: AUDIENCE_CHOICES_REQUEST_SUCCEEDED
	}
);

export const fetchAudienceListEffect = createHttpEffect(
	'/api/now/uxframework/applicabilities/:sysId',
	{
		method: 'GET',
		pathParams: ['sysId'],
		successActionType: AUDIENCE_LIST_REQUEST_SUCCEEDED,
		errorActionType: AUDIENCE_REQUEST_FAILED,
		batch: false
	}
);

const audienceChoicesRequestedSucceeded = ({ action, updateState }) => {
	const { data } = action.payload;
	const audienceChoices = data.GlideRecord_Query.sys_ux_applicability._results.map(
		(audienceChoice) => {
			return {
				id: audienceChoice.sys_id.value,
				label: audienceChoice.name.value,
				sublabel: audienceChoice.sys_scope
					? audienceChoice.sys_scope.displayValue
					: null
			};
		}
	);

	updateState({
		audienceChoices
	});
};

const audienceListRequested = ({ dispatch, updateState, action }) => {
	updateState({
		isLoading: true
	});

	dispatch(AUDIENCE_LOADING_SET, { value: true });
	dispatch(AUDIENCE_LIST_REQUEST_SENT, action.payload);
};

const audienceListRequestedSucceeded = ({ action, updateState, dispatch }) => {
	const audienceList = action.payload.result
		.map((audience) => {
			const {
				id,
				active,
				order,
				sysPolicy,
				applicabilityId,
				applicabilityName,
				scopeId,
				scopeName
			} = audience;

			return {
				id,
				order,
				active,
				scope: {
					label: scopeName,
					id: scopeId
				},
				protection: sysPolicy,
				applicability: {
					id: applicabilityId,
					label: applicabilityName
				}
			};
		})
		.sort((a, b) => a.order - b.order);

	updateState({
		audienceList,
		isLoading: false
	});
	dispatch(AUDIENCE_LOADING_SET, { value: false, audienceList });
	dispatch(AUDIENCE_UPDATE_SELECTED);
};

const dispatchUpdate = ({ dispatch, action }) => {
	const { result } = action.payload;
	dispatch(AUDIENCE_LIST_UPDATE, { id: result ? result.sys_id : null });
};

const setLoadingToFalse = ({ updateState, dispatch }) => {
	updateState({
		isLoading: false
	});
	dispatch(AUDIENCE_LOADING_SET, { value: false });
};

const setLoadingToTrue = ({ updateState, dispatch }) => {
	updateState({
		isLoading: true
	});
	dispatch(AUDIENCE_LOADING_SET, { value: true });
};

export default {
	[AUDIENCE_CHOICES_REQUESTED]: effectProxy(fetchAudienceChoicesEffect),
	[AUDIENCE_CHOICES_REQUEST_SUCCEEDED]: audienceChoicesRequestedSucceeded,
	[AUDIENCE_LIST_REQUESTED]: audienceListRequested,
	[AUDIENCE_LIST_REQUEST_SENT]: effectProxy(fetchAudienceListEffect),
	[AUDIENCE_LIST_REQUEST_SUCCEEDED]: audienceListRequestedSucceeded,
	[AUDIENCE_ADD_REQUESTED]: addAudienceHttpEffect,
	[AUDIENCE_EDIT_REQUESTED]: effectProxy(editAudienceHttpEffect),
	[AUDIENCE_DELETE_REQUESTED]: deleteAudienceHttpEffect,
	[AUDIENCE_REQUEST_FAILED]: setLoadingToFalse,
	[AUDIENCE_HTTP_REQUEST_SUCCEEDED]: dispatchUpdate,
	[AUDIENCE_HTTP_REQUEST_STARTED]: setLoadingToTrue
};
