import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import 'sn-ui-builder-components';
import dictionary from './dictionary';
import get from 'lodash/get';
import { snUXFActions } from 'sn-uxf-builder-middleware';

const { actionMessageMap } = dictionary;

createCustomElement('uib-element-unsaved-dialog', {
	view: ({ properties }) => {
		const { showUnsavedChangesDialog } = properties;
		const { unsavedChangesCheckCallbackEvent } = properties;
		const callbackAction = get(
			unsavedChangesCheckCallbackEvent,
			'callbackAction',
			null
		);
		const message =
			callbackAction !== null ? actionMessageMap[callbackAction] : '';

		return showUnsavedChangesDialog ? (
			<sn-uib-component-unsaved-dialog message={message} />
		) : null;
	},
	properties: {
		isPageStateDirty: {
			default: false
		},
		unsavedChangesCheckCallbackEvent: {
			default: null
		},
		showUnsavedChangesDialog: {
			default: false,
			computed: ({ properties }) => {
				const {
					unsavedChangesCheckCallbackEvent,
					isPageStateDirty
				} = properties;

				return unsavedChangesCheckCallbackEvent !== null && isPageStateDirty;
			}
		}
	},
	actionHandlers: {
		'UNSAVED_DIALOG#SAVE_AND_CONTINUE': ({ dispatch }) => {
			dispatch(snUXFActions.PAGE_SAVE_REQUESTED);
		},
		'NOW_MODAL#OPENED_SET': ({ dispatch }) => {
			dispatch('SN_UIB_APP#UNSAVED_DIALOG_DISMISSED');
		},
		['UNSAVED_DIALOG#CANCEL']: ({ dispatch }) => {
			dispatch('SN_UIB_APP#UNSAVED_DIALOG_DISMISSED');
		},
		['UNSAVED_DIALOG#DISCARD_CHANGES']: ({ dispatch, properties }) => {
			const { unsavedChangesCheckCallbackEvent } = properties;
			const {
				callbackAction,
				callbackPayload
			} = unsavedChangesCheckCallbackEvent;

			dispatch(snUXFActions.PAGE_STATE_RESET);
			dispatch(callbackAction, callbackPayload);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			properties,
			action,
			dispatch
		}) => {
			const { unsavedChangesCheckCallbackEvent, isPageStateDirty } = properties;
			const { name, value } = action.payload;

			if (name === 'unsavedChangesCheckCallbackEvent' && value !== null) {
				const {
					callbackAction,
					callbackPayload = {}
				} = unsavedChangesCheckCallbackEvent;

				if (isPageStateDirty === false) {
					dispatch(callbackAction, {
						...callbackPayload,
						dispatchActionImmediately: true
					});
					dispatch('SN_UIB_APP#UNSAVED_DIALOG_DISMISSED');
				}
			}
		}
	}
});
