import {
	CHECKBOX_CHECKED,
	PAGE_SEARCH,
	SEARCH_COMPONENT_BOOTSTRAPPED,
	SETTINGS_UPDATED,
	TABLE_CONFIG,
	URLDOC
} from '../../constants';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

// Internal components
import '../sn-experience-settings-link';

import styles from '../../styles/forms.scss';

// NDS
import '@servicenow/now-heading';
import '@servicenow/now-input';
import '@servicenow/now-text-link';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const page = 'UIB_Portal_Search_Settings';

const view = ({
	properties: { sysId, searchPublic, searchPrivate, readonly }
}) => {
	return (
		<div>
			<now-heading
				label={t('Global search')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<p className="description">
				{
					/* prettier-ignore */
					t('You can show or hide the search functionality on any pages found in your portal. By default, all pages show search.')
				}{' '}
				<now-text-link
					label={t('Learn more')}
					href={`${URLDOC}/${page}`}
					variant="secondary"
					opens-window={true}></now-text-link>
			</p>
			<now-heading
				label={t('Public pages')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<div className="fieldset">
				<now-checkbox
					className="sub-checkbox"
					label={t('Enable global search')}
					checked={searchPublic}
					append-to-payload={{ name: 'searchPublic' }}
					readonly={readonly}
				/>
			</div>

			<now-heading
				label={t('Private pages')}
				level="1"
				variant="title-primary"
				wont-wrap
			/>
			<div className="fieldset">
				<now-checkbox
					className="sub-checkbox"
					label={t('Enable global search')}
					checked={searchPrivate}
					append-to-payload={{ name: 'searchPrivate' }}
					readonly={readonly}
				/>
			</div>

			<sn-experience-settings-link
				sys-id={sysId}
				table={TABLE_CONFIG}></sn-experience-settings-link>
		</div>
	);
};

const inputChangedEffect = ({
	action: {
		payload: { name, value }
	},
	dispatch
}) => {
	dispatch(SETTINGS_UPDATED, { page: PAGE_SEARCH, name, value });
};

createCustomElement('sn-experience-settings-search', {
	view,
	properties: {
		sysId: {
			required: true,
			schema: { type: 'string' }
		},
		searchPublic: {
			schema: { type: 'boolean' },
			default: false
		},
		searchPrivate: {
			schema: { type: 'boolean' },
			default: false
		},
		readonly: {
			schema: { type: 'boolean' }
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch(SEARCH_COMPONENT_BOOTSTRAPPED);
		},
		[CHECKBOX_CHECKED]: {
			stopPropagation: true,
			effect: inputChangedEffect
		}
	},
	styles
});
