import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './uib-screen-audiences.scss';
import '@now-par-components/sn-par-pillar';
import '@servicenow/now-input';
import '@servicenow/now-icon';
import '@servicenow/now-empty-state';
import '@servicenow/now-button';
import 'sn-ui-builder-components';
import '../uib-audiences-form';
import { t } from 'sn-translate';
import { audiencesBehavior } from '../../behaviors/audiencesBehavior';
import LoadingStage from '../uib-app/partials/LoadingStage'; // eslint-disable-line no-unused-vars
import {
	AUDIENCE_CHOICES_REQUESTED,
	AUDIENCE_LIST_REQUESTED,
	AUDIENCE_ADD_REQUESTED,
	AUDIENCE_EDIT_REQUESTED,
	AUDIENCE_DELETE_REQUESTED,
	AUDIENCE_LIST_UPDATE,
	AUDIENCE_UPDATE_SELECTED,
	AUDIENCE_LOADING_SET
} from '../../behaviors/audiencesBehavior/constants';
import {
	AUDIENCE_FORM_ADD_AUDIENCE,
	AUDIENCE_FORM_EDIT_AUDIENCE,
	AUDIENCE_FORM_DELETE_AUDIENCE
} from '../uib-audiences-form';

export const INTERNAL_PILLAR_CLICKED = 'UIB_SCREEN_AUDIENCE#PILLAR_CLICKED';

const protectedMessage = (policy, audienceScope, applications) => {
	let message, lock;
	if (policy === 'read') {
		message = (
			<span>
				This record has a<strong>read-only</strong>
				protection policy and cannot be edited.
			</span>
		);
		lock = true;
	} else if (policy === 'protected') {
		message = (
			<span>This record has a protection policy and cannot be edited.</span>
		);
		lock = true;
	} else if (
		audienceScope &&
		applications &&
		!applications.filter(({ id }) => id === audienceScope.id).length
	) {
		lock = true;
	}
	return { message, lock };
};

const view = (
	{
		audienceChoices,
		audienceList,
		selectedAudience,
		newFormOpened,
		initialLoad,
		properties: {
			applications,
			size,
			userScope,
			userDomain,
			supportDomains,
			supportScope
		}
	},
	{ dispatch }
) => {
	return (
		<div className={`wrapper --${size}`}>
			{!initialLoad ? (
				<div className={`container  --${size}`}>
					<div className="first-column">
						<div className="header">
							<div> {t('Audiences')} </div>
							<now-button-bare
								label={t('Add')}
								iconStart="plus-outline"
								size="sm"
								variant="tertiary"
								append-to-payload={{ button: 'add-audience' }}
							/>
						</div>
						{audienceList && audienceList.length ? (
							<div className="audiences-list">
								{audienceList.map((audience) => {
									return (
										<div className="wrapper">
											<sn-par-pillar
												size="sm"
												icon="user-group-fill"
												selected={
													selectedAudience &&
													selectedAudience.id === audience.id
												}
												on-click={(e) => {
													e.stopPropagation();
													dispatch(INTERNAL_PILLAR_CLICKED, { audience });
												}}
												on-keydown={(e) => {
													e.stopPropagation();
													if (e.key === 'Enter') {
														dispatch(INTERNAL_PILLAR_CLICKED, { audience });
													}
												}}
												slot="trigger">
												<div className="pillar-content">
													<div className="pillar-text">
														{audience.applicability.label}
													</div>
													<div>
														{protectedMessage(
															audience.protection,
															audience.scope,
															applications
														).lock ? (
															<now-icon icon="lock-outline" />
														) : null}
														<now-icon-presence
															presence={
																audience.active ? 'available' : 'offline'
															}
														/>
													</div>
												</div>
											</sn-par-pillar>
										</div>
									);
								})}
							</div>
						) : (
							<div className="empty-container">
								<now-empty-state
									headerText={t('No audiences, yet.')}
									bodyText={null}
									icon={'user-plus-outline'}
								/>
								<now-button
									label={t('Add audience')}
									size="sm"
									variant="secondary"
									append-to-payload={{ button: 'add-audience' }}
								/>
							</div>
						)}
					</div>
					<div className="second-column">
						{userScope && (selectedAudience || newFormOpened) ? (
							<uib-audiences-form
								audience={selectedAudience}
								protectedRecordMessage={
									selectedAudience
										? protectedMessage(
												selectedAudience.protection,
												selectedAudience.scope,
												applications
										  ).message
										: null
								}
								append-to-payload={{
									recordId: selectedAudience ? selectedAudience.id : null
								}}
								audienceChoices={audienceChoices}
								size={size}
								user-scope={userScope}
								user-domain={userDomain}
								support-domains={supportDomains}
								applications={applications}
								supportScope={supportScope}
							/>
						) : null}
					</div>
				</div>
			) : (
				<LoadingStage
					size="sm"
					loadingStageContent={t('Loading audiences...')}
				/>
			)}
		</div>
	);
};

createCustomElement('uib-screen-audiences', {
	view,
	styles,
	initialState: {
		selectedAudience: null,
		newFormOpened: false,
		nextSelectedAudience: null,
		initialLoad: true
	},
	properties: {
		screenId: {
			default: null
		},
		size: {
			default: 'lg',
			schema: {
				type: 'string',
				enum: ['lg', 'fullscreen']
			}
		},
		userScope: {
			default: null
		},
		userDomain: {
			default: null
		},
		supportDomains: {
			default: null
		},
		applications: {
			default: null
		},
		supportScope: {
			default: false
		}
	},
	behaviors: [{ behavior: audiencesBehavior }],
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: ({ properties, dispatch }) => {
			const { screenId: sysId } = properties;
			dispatch(AUDIENCE_CHOICES_REQUESTED);
			dispatch(AUDIENCE_LIST_REQUESTED, { sysId });
		},
		['NOW_BUTTON_BARE#CLICKED']: ({ action, updateState }) => {
			action.stopPropagation();
			const { button } = action.payload;
			if (button === 'add-audience') {
				updateState({
					selectedAudience: null,
					newFormOpened: true
				});
			}
		},
		['NOW_BUTTON#CLICKED']: ({ action, updateState }) => {
			action.stopPropagation();
			const { button } = action.payload;
			if (button === 'add-audience') {
				updateState({
					selectedAudience: null,
					newFormOpened: true
				});
			}
		},
		[AUDIENCE_FORM_ADD_AUDIENCE]: ({ action, properties, dispatch }) => {
			action.stopPropagation();
			const { screenId: screen } = properties;
			const {
				audienceSysId: applicability,
				order,
				active,
				applicationSysId: sys_scope
			} = action.payload;
			const data = {
				screen,
				applicability,
				order,
				active
			};
			dispatch(AUDIENCE_ADD_REQUESTED, {
				data,
				sysparm_transaction_scope: sys_scope
			});
		},
		[AUDIENCE_FORM_EDIT_AUDIENCE]: ({ action, dispatch }) => {
			action.stopPropagation();
			const {
				audienceSysId: applicability,
				order,
				active,
				applicationSysId: sysparm_transaction_scope,
				recordId
			} = action.payload;

			const data = {
				applicability,
				order,
				active
			};
			dispatch(AUDIENCE_EDIT_REQUESTED, {
				data,
				sysparm_transaction_scope,
				recordId
			});
		},
		[AUDIENCE_FORM_DELETE_AUDIENCE]: ({ action, dispatch }) => {
			action.stopPropagation();
			const {
				recordId,
				applicationSysId: sysparm_transaction_scope
			} = action.payload;
			dispatch(AUDIENCE_DELETE_REQUESTED, {
				recordId,
				sysparm_transaction_scope
			});
		},
		[AUDIENCE_LIST_UPDATE]: ({
			action,
			dispatch,
			properties,
			updateState,
			state
		}) => {
			action.stopPropagation();
			const { screenId: sysId } = properties;
			const { audienceList } = state;
			const { id } = action.payload;
			const firstAudience =
				audienceList && audienceList.length && audienceList[0];
			dispatch(AUDIENCE_LIST_REQUESTED, { sysId });
			updateState({
				selectedAudience:
					id && audienceList
						? audienceList.find((audience) => audience.id === id)
						: firstAudience,
				nextSelectedAudience: id || firstAudience.id
			});
		},
		[INTERNAL_PILLAR_CLICKED]: ({ action, updateState }) => {
			action.stopPropagation();
			const { audience } = action.payload;
			updateState({
				selectedAudience: audience,
				newFormOpened: false
			});
		},
		[AUDIENCE_UPDATE_SELECTED]: ({ action, updateState, state }) => {
			action.stopPropagation();
			const { audienceList, selectedAudience, nextSelectedAudience } = state;
			if (audienceList && audienceList.length) {
				const searchId = selectedAudience
					? selectedAudience.id
					: nextSelectedAudience;
				const searchIdAudience = audienceList.find(({ id }) => id === searchId);
				const newSelectedAudience =
					searchIdAudience ||
					audienceList.find(({ protection }) => !protection);
				updateState({
					selectedAudience: newSelectedAudience,
					newFormOpened: false,
					nextSelectedAudience: null,
					initialLoad: false
				});
			} else {
				updateState({
					selectedAudience: null,
					nextSelectedAudience: null,
					initialLoad: false
				});
			}
		},
		[AUDIENCE_LOADING_SET]: ({ action, updateState, state }) => {
			const { initialLoad } = state;
			const { value } = action.payload;
			if (initialLoad && !value) {
				updateState({
					initialLoad: value
				});
			}
		}
	}
});
