import get from 'lodash/get';

/**
 * Processor that takes in the currently configured event mapping handler value
 * and configures it for reference type
 *
 * @param {object} property - The current UXF event mapping handler value being configured
 * @param {object[]} properties - The current set of UXF handler values for the handler being configured
 * @param {object} state - The current element/data broker/page panel Seismic state
 * @returns {object} - The configured property object
 */
export default function (property, properties, state) {
	const { handlerId, selectablePropAddress } = state;

	const referenceDisplayValues = `@${selectablePropAddress}/referenceDisplayValues`;
	const referenceId = `${handlerId}${property.id}`;
	const referenceResults = `@${selectablePropAddress}/referenceResults`;
	const referenceSearching = `@${selectablePropAddress}/referenceSearching`;

	const table = get(
		property,
		'typeMeta.reference.table',
		get(property, 'typeMeta.reference', null)
	);
	let disabled = false;
	if (!table) {
		disabled = true;
	}

	return {
		...property,
		disabled,
		referenceDisplayValues,
		referenceId,
		referenceResults,
		referenceSearching,
		table
	};
}
