import { createCustomElement } from '@servicenow/ui-core';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { elementRefMap, slotRefMap } from 'sn-ui-builder-components';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import styles from './uib-element-controls-provider.scss';
import { inspectEveryNode } from '../,,/../../../../utils/tree';
import { t } from 'sn-translate';

const renderSelectedElementControls = ({
	element,
	elementConfirmingDelete,
	disabled,
	isModalDescendant,
	parentModalId,
	layoutTemplates,
	pageTemplates,
	selectedSlotName,
	selectedSlotParent,
	selectedSlotParentComponent,
	selectedSlotStyles,
	selectedElementLayoutType,
	rootElementId,
	selectedLastAppliedTemplateId,
	elementId,
	elementContextMenuOpened,
	elementContextMenuInfo,
	isSelectedLayoutModified
}) => {
	if (selectedElementLayoutType === 'slot') {
		if (!slotRefMap.get(`${selectedSlotParent}-${selectedSlotName}`)) {
			return null;
		}
	} else if (
		selectedElementLayoutType === 'leaf' ||
		selectedElementLayoutType === 'container'
	) {
		if (isEmpty(element) || !elementRefMap.get(element.id)) {
			return null;
		}
	}
	// TODO: Handle the Map.get() better. It returns undefined if not found, which breaks the destructuring
	const { ref: slotRef, resizeInfo: slotResizeInfo } =
		slotRefMap.get(`${selectedSlotParent}-${selectedSlotName}`) || {};
	const { ref: elementRef, resizeInfo } = elementRefMap.get(element.id) || {};
	return (
		<sn-uib-element-controls
			slot={`${elementId}_controls`}
			rootElementId={rootElementId}
			element={{
				...element,
				icon: get(element, 'component.icon', null),
				displayName: element.instanceName || selectedSlotName,
				id: element.id || `${selectedSlotParent}-${selectedSlotName}`,
				elementId: element.elementId || selectedSlotParent,
				isSlot: selectedElementLayoutType === 'slot',
				slotName: selectedSlotName
			}}
			layoutTemplates={layoutTemplates}
			append-to-payload={{
				isModalDescendant,
				parentModalId
			}}
			selectedSlotName={selectedSlotName}
			selectedSlotParent={selectedSlotParent}
			selectedSlotParentComponent={selectedSlotParentComponent}
			focused={true}
			keymaps={`@${element.id}_keybindings/element`}
			hovered={false}
			isVisible="@uib-select-placement/isVisible"
			disabled={disabled}
			resize-info={resizeInfo || slotResizeInfo}
			selectedSlotStyles={selectedSlotStyles}
			layoutTemplates={layoutTemplates}
			pageTemplates={pageTemplates}
			type={selectedElementLayoutType}
			confirm={elementConfirmingDelete === element.id}
			lastAppliedTemplateId={selectedLastAppliedTemplateId}
			elementContextMenuOpened={elementContextMenuOpened}
			elementContextMenuInfo={elementContextMenuInfo}
			isLayoutModified={isSelectedLayoutModified}
		/>
	);
};

const renderHoveredElementControls = ({
	element,
	disabled,
	layoutTemplates,
	pageTemplates,
	hoveredSlotName,
	hoveredSlotParent,
	selectedSlotParentComponent,
	hoveredSlotStyles,
	isModalDescendant,
	parentModalId,
	hoveredElementLayoutType,
	rootElementId,
	hoveredLastAppliedTemplateId,
	elementContextMenuOpened,
	elementContextMenuInfo,
	elementId,
	selectedSlotStyles,
	isHoveredLayoutModified
}) => {
	if (hoveredElementLayoutType === 'slot') {
		if (!slotRefMap.get(`${hoveredSlotParent}-${hoveredSlotName}`)) {
			return null;
		}
	} else if (
		hoveredElementLayoutType === 'leaf' ||
		hoveredElementLayoutType === 'container'
	) {
		if (isEmpty(element) || !elementRefMap.get(element.id)) {
			return null;
		}
	}
	// TODO: Handle the Map.get() better. It returns undefined if not found, which breaks the destructuring
	const { ref: slotRef, resizeInfo: slotResizeInfo } =
		slotRefMap.get(`${hoveredSlotParent}-${hoveredSlotName}`) || {};
	const { ref: elementRef, resizeInfo } = elementRefMap.get(element.id) || {};

	if (!element.id && !hoveredSlotName) {
		return null;
	}
	return (
		<sn-uib-element-controls
			slot={`${elementId}_controls`}
			rootElementId={rootElementId}
			element={{
				...element,
				icon: get(element, 'component.icon', null),
				displayName: element.instanceName || hoveredSlotName,
				id: element.id || `${hoveredSlotParent}-${hoveredSlotName}`,
				elementId: element.elementId || hoveredSlotParent,
				isSlot: hoveredElementLayoutType === 'slot',
				slotName: hoveredSlotName
			}}
			layoutTemplates={layoutTemplates}
			append-to-payload={{
				isModalDescendant,
				parentModalId
			}}
			selectedSlotName={hoveredSlotName}
			selectedSlotParent={hoveredSlotParent}
			selectedSlotParentComponent={selectedSlotParentComponent}
			focused={false}
			isVisible="@uib-hover-placement/isVisible"
			keymaps={`@${element.id}_keybindings/element`}
			hovered={true}
			disabled={disabled}
			resize-info={resizeInfo || slotResizeInfo}
			hoveredSlotStyles={hoveredSlotStyles}
			layoutTemplates={layoutTemplates}
			pageTemplates={pageTemplates}
			type={hoveredElementLayoutType}
			lastAppliedTemplateId={hoveredLastAppliedTemplateId}
			elementContextMenuOpened={elementContextMenuOpened}
			elementContextMenuInfo={elementContextMenuInfo}
			isLayoutModified={isHoveredLayoutModified}
		/>
	);
};

const renderSelectedElementAddControls = (
	element,
	addNextToElementId,
	directionToAdd,
	disabled,
	elementId,
	hovered
) => {
	if (
		addNextToElementId &&
		directionToAdd !== 'none' &&
		addNextToElementId !== element.id
	) {
		return null;
	}
	if (
		!elementRefMap.get(element.id) ||
		disabled ||
		get(element, 'isModal', false)
	) {
		return null;
	}
	if (element.isBody && element.slot === null) {
		return null;
	}

	const { ref: elementRef } = elementRefMap.get(element.id);

	const slotStyles = `@${element.id}_information/slotStyles`;
	return (
		<sn-uib-element-placement-controls
			slot={`${elementId}_insert-buttons`}
			element={{
				id: element.id,
				icon: element.component.icon,
				displayName: element.instanceName,
				elm_ref: elementRef.current
			}}
			focused={element.id === addNextToElementId && directionToAdd !== 'none'}
			focusedDirecion={directionToAdd}
			keymaps={`@${element.id}_keybindings/element`}
			hovered={hovered}
			disabled={disabled}
			slotStyles={slotStyles}
		/>
	);
};

const renderSelectedElementHighlight = (props) => {
	const { elementId } = props;
	return (
		<div
			slot={`${elementId}_highlight`}
			className="element-highlight-wrapper"
		/>
	);
};

const view = (
	{
		properties: {
			selectedElementId,
			hoveredElementId,
			addNextToElementId,
			directionToAdd,
			elementConfirmingDelete,
			isResizing,
			isDragging,
			flatPage,
			isPanelTransitioning,
			disabled,
			layoutTemplates,
			hoveredElementLayoutType,
			selectedElementLayoutType,
			selectedSlotName,
			selectedSlotParent,
			selectedSlotParentComponent,
			selectedSlotStyles,
			hoveredSlotName,
			hoveredSlotParent,
			hoveredSlotStyles,
			rootElementId,
			pageTemplates,
			selectedLastAppliedTemplateId,
			hoveredLastAppliedTemplateId,
			elementContextMenuOpened,
			elementContextMenuInfo
		},
		isHoveredLayoutModified,
		isSelectedLayoutModified
	},
	{ dispatch }
) => {
	const selectedElement = flatPage[selectedElementId] || {};

	const hoveredElement = flatPage[hoveredElementId] || {};
	const { isModalDescendant, parentModalId } = selectedElement;
	const hoveredId = hoveredSlotName
		? `${hoveredSlotParent}_${hoveredSlotName}`
		: hoveredElementId;
	const selectedId = selectedSlotName
		? `${selectedSlotParent}_${selectedSlotName}`
		: selectedElementId;
	return (
		<div
			className="uib-element-controls-field"
			on-click={(event) => event.stopPropagation()}
			hidden={
				isResizing || isDragging || isPanelTransitioning ? 'true' : false
			}>
			<sn-uib-element-controls-placement
				context={'hovered'}
				key={'sn-uib-element-controls-placement-hovered'}
				name="sn-uib-element-controls-placement-hovered"
				component-id="uib-hover-placement"
				names-to-check={['sn-uib-element-controls-placement-selected']}
				type={hoveredElementLayoutType}
				disabled={disabled}
				elementId={hoveredId}>
				{renderHoveredElementControls({
					element: hoveredElement,
					elementConfirmingDelete,
					disabled,
					layoutTemplates,
					pageTemplates,
					hoveredSlotName,
					hoveredSlotParent,
					selectedSlotParentComponent,
					hoveredSlotStyles,
					isModalDescendant,
					parentModalId,
					hoveredElementLayoutType,
					rootElementId,
					hoveredLastAppliedTemplateId,
					elementContextMenuOpened,
					elementContextMenuInfo,
					elementId: hoveredId,
					selectedSlotStyles,
					isHoveredLayoutModified
				})}
				{renderSelectedElementAddControls(
					hoveredElement,
					addNextToElementId,
					directionToAdd,
					disabled,
					hoveredId
				)}
				{renderSelectedElementHighlight({
					type: hoveredElementLayoutType,
					elementId: hoveredId
				})}
			</sn-uib-element-controls-placement>
			<sn-uib-element-controls-placement
				context={'selected'}
				key={'sn-uib-element-controls-placement-selected'}
				name="sn-uib-element-controls-placement-selected"
				component-id="uib-select-placement"
				type={selectedElementLayoutType}
				disabled={disabled}
				elementId={selectedId}>
				{renderSelectedElementControls({
					element: selectedElement,
					elementConfirmingDelete,
					dispatch,
					disabled,
					isModalDescendant,
					parentModalId,
					layoutTemplates,
					pageTemplates,
					selectedSlotName,
					selectedSlotParent,
					selectedSlotParentComponent,
					selectedSlotStyles,
					selectedElementLayoutType,
					rootElementId,
					selectedLastAppliedTemplateId,
					elementContextMenuOpened,
					elementContextMenuInfo,
					elementId: selectedId,
					isSelectedLayoutModified
				})}
				{renderSelectedElementAddControls(
					selectedElement,
					addNextToElementId,
					directionToAdd,
					disabled,
					selectedId
				)}
				{renderSelectedElementHighlight({
					type: selectedElementLayoutType,
					elementId: selectedId
				})}
			</sn-uib-element-controls-placement>
		</div>
	);
};
export default createCustomElement('uib-element-controls-provider', {
	view,
	properties: {
		elementContextMenuOpened: { default: false },
		elementContextMenuInfo: { default: {} },
		rootElementId: { default: null },
		applicationType: { default: null },
		elementRef: { default: null },
		selectedSlotParent: { default: null },
		selectedSlotParentComponent: { default: null },
		selectedSlotName: { default: null },
		hoveredSlotParent: { default: null },
		hoveredSlotName: { default: null },
		selectedElementId: { default: null },
		hoveredElementId: { default: null },
		addNextToElementId: { default: null },
		directionToAdd: { default: 'none' },
		isDefaultPageLayout: { default: false },
		selectedSlotStyles: {
			computed: ({
				properties: { layouts, selectedSlotName, selectedSlotParent }
			}) => {
				const currentItems = get(
					layouts,
					`${selectedSlotParent}.layout.default.items`,
					[]
				);
				const currentSlot = currentItems.find(
					({ slotName }) => slotName === selectedSlotName
				);
				return get(currentSlot, 'styles', {});
			}
		},
		hoveredSlotStyles: {
			computed: ({
				properties: { layouts, hoveredSlotName, hoveredSlotParent }
			}) => {
				const currentSlot = (
					get(layouts, `${hoveredSlotParent}.layout.default.items`) || []
				).find(({ slotName }) => slotName === hoveredSlotName);
				return get(currentSlot, 'styles', {});
			}
		},
		hoveredElementLayoutType: {
			computed: ({ properties: { layouts, hoveredElementId } }) => {
				const layoutType = get(layouts, `${hoveredElementId}.type`, 'slot');
				return layoutType;
			}
		},
		selectedElementLayoutType: {
			computed: ({ properties: { layouts, selectedElementId } }) => {
				const layoutType = get(layouts, `${selectedElementId}.type`, 'slot');
				return layoutType;
			}
		},
		selectedLastAppliedTemplateId: {
			computed: ({ properties: { layouts, selectedElementId } }) => {
				const lastAppliedTemplateId = get(
					layouts,
					`${selectedElementId}.layout.default.templateId`,
					null
				);
				return lastAppliedTemplateId;
			}
		},
		hoveredLastAppliedTemplateId: {
			computed: ({ properties: { layouts, hoveredElementId } }) => {
				const lastAppliedTemplateId = get(
					layouts,
					`${hoveredElementId}.layout.default.templateId`,
					null
				);
				return lastAppliedTemplateId;
			}
		},
		elementConfirmingDelete: { default: null },
		isDragging: { default: false },
		isResizing: { default: false },
		page: { default: null },
		flatPage: {
			computed({ properties: { page } }) {
				const obj = {};
				let parentModalId = null;
				if (!page || !page.rootElement) {
					return obj;
				}
				inspectEveryNode(page.rootElement, (element) => {
					if (element.isModal) {
						parentModalId = element.id;
					}

					const elementData = {
						...element,
						parentModalId: element.isModalDescendant
							? parentModalId
							: undefined,
						instanceName:
							element.id === page.rootElement.id
								? t('Body')
								: element.instanceName,
						isBody: element.id === page.rootElement.id
					};
					obj[element.id] = elementData;
				});
				return obj;
			}
		},
		elementTree: { default: {} },
		isPanelTransitioning: { default: false },
		disabled: { default: false },
		layouts: { default: null },
		layoutTemplates: { default: [] },
		pageTemplates: { default: [] }
	},
	initialState: {
		idToRerender: null,
		isHoveredLayoutModified: false,
		isSelectedLayoutModified: false
	},
	transformState: (state) => {
		const { layouts, hoveredElementId, selectedElementId } = state.properties;
		const hoveredLayoutInfo = get(layouts, hoveredElementId, {});
		const selectedLayoutInfo = get(layouts, selectedElementId, {});
		const { isLayoutModified: isHoveredLayoutModified } = hoveredLayoutInfo;
		const { isLayoutModified: isSelectedLayoutModified } = selectedLayoutInfo;
		return {
			...state,
			isHoveredLayoutModified,
			isSelectedLayoutModified
		};
	},

	actionHandlers: {
		// TODO: Rename actions coming from sn-uib-template-popover
		'SN_UIB_ELEMENT_CONFIGURATION_PANEL#LAYOUT_TEMPLATE_SELECTED': ({
			action,
			dispatch
		}) => {
			const { isLayoutModified, layoutTemplateId, elementId } = action.payload;
			const dispatchFn = () => {
				dispatch(snUXFActions.ELEMENT_LAYOUT_CHANGED, {
					elementId,
					layoutTemplateId
				});
			};
			if (!isLayoutModified) {
				dispatchFn();
			} else {
				dispatch('SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED', {
					type: 'element',
					applyActionHandler: dispatchFn
				});
			}
		},
		'SN_UIB_PAGE_CONFIGURATION_PANEL#PAGE_TEMPLATE_SELECTED': ({
			state,
			properties,
			action,
			dispatch
		}) => {
			const { isLayoutModified, pageTemplateId } = action.payload;
			const dispatchFn = () => {
				dispatch(snUXFActions.PAGE_TEMPLATE_SELECTED, {
					pageTemplateId
				});
			};
			if (properties.isDefaultPageLayout || !isLayoutModified) {
				dispatchFn();
			} else {
				dispatch('SN_UIB_APP#NEW_LAYOUT_SELECTION_CLICKED', {
					type: 'page',
					applyActionHandler: dispatchFn
				});
			}
		}
	},
	styles
});
