import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { t } from 'sn-translate';

const ICONS_QUERY = `
{
	GlideRecord_Query {
	  st_sys_design_system_icon(pagination: {limit: 1000}) {
		_results {
		  name {
			value
		  }
		  sys_id {
			value
		  }
		  label {
			value
		  }
		}
	  }
	}
}`;

const transformIconsResult = (graphqlResult) => {
	const {
		data: {
			GlideRecord_Query: {
				st_sys_design_system_icon: { _results }
			}
		}
	} = graphqlResult;
	return _results.map((result) => {
		return Object.entries(result).reduce((acc, [key, obj]) => {
			return { ...acc, [key]: obj.value };
		}, {});
	});
};

export const iconHandlers = {
	['UIB_APP#LOAD_ICONS']: createGraphQLEffect(ICONS_QUERY, {
		successActionType: 'UIB_APP#ICONS_FETCHED_SUCCEEDED',
		errorActionType: 'UIB_APP#ICONS_FETCHED_FAILED',
		startActionType: 'UIB_APP#ICONS_FETCHED_STARTED'
	}),
	['UIB_APP#ICONS_FETCHED_SUCCEEDED']: ({ action, updateProperties }) => {
		updateProperties({
			icons: transformIconsResult(action.payload),
			isLoadingIcons: false
		});
	},
	['UIB_APP#ICONS_FETCHED_FAILED']: ({ updateProperties }) => {
		updateProperties({
			isLoadingIcons: false,
			alertProps: {
				action: {
					type: 'dismiss'
				},
				content: t('There was an unknown error loading data'),
				status: 'critical'
			}
		});
	},
	['UIB_APP#ICONS_FETCHED_STARTED']: ({ updateProperties }) => {
		updateProperties({
			isLoadingIcons: true
		});
	}
};
