import { createCustomElement } from '@servicenow/ui-core';
import { lifecycleHandlers } from './actionHandlers/lifecycleHandlers';
import routerBehavior from './behaviors/router';
import { isolatedEditHandlers } from './actionHandlers/isolatedEditHandlers';
import { screenNames } from '../../utils/routingConstants';

const createRoutes = ({ isComponentBuilder, isDashboardBuilder }) => {
	const appBaseUrl = isComponentBuilder ? '/components' : '/apps';
	return isDashboardBuilder
		? {
				[screenNames.dashboardsDashboard]: {
					path: '/:dashboardId'
				},

				[screenNames.dashboardsHome]: {
					path: ''
				}
		  }
		: {
				[screenNames.workspacePage]: {
					path: '/workspaces/:appsSysId/pages/:pageSysId'
				},

				[screenNames.workspaceUnknown]: {
					path: '/workspaces/:appsSysId/*',
					onEnter({ routeState }) {
						return {
							...routeState,
							show404: true
						};
					}
				},

				[screenNames.workspaceHome]: {
					path: '/workspaces/:appsSysId'
				},

				[screenNames.appPage]: {
					path: `${appBaseUrl}/:appsSysId/pages/:pageSysId`
				},

				[screenNames.appVariant]: {
					path: `${appBaseUrl}/:appsSysId/pages/:pageSysId/variants/:variantSysId`
				},

				[screenNames.appUnknown]: {
					path: `${appBaseUrl}/:appsSysId/*`,
					onEnter({ routeState }) {
						return {
							...routeState,
							show404: true
						};
					}
				},

				[screenNames.appHome]: {
					path: `${appBaseUrl}/:appsSysId`
				},

				[screenNames.appList]: {
					path: '/experiences'
				}
		  };
};

createCustomElement('uib-router', {
	view: () => <slot />,
	initialState: {
		isolatedEditInfo: {
			isSavingAndContinuing: false,
			subRouteInfo: {}
		}
	},
	actionHandlers: {
		...lifecycleHandlers,
		...isolatedEditHandlers
	},

	properties: {
		appsSysId: {
			default: null,
			selectable: true
		},
		pageSysId: {
			default: null,
			selectable: true
		},
		screenTypeSysId: {
			default: null,
			selectable: true
		},
		pages: {
			default: null,
			selectable: false
		},
		routeStatus: {
			default: null,
			selectable: false
		},
		isPageStateDirty: {
			default: false
		},
		isUnsavedDialogActive: {
			default: false
		},
		bypassUnsavedChangesDialog: {
			default: false
		},
		appRoutes: {
			default: []
		},
		isComponentBuilder: {
			default: false
		},
		isDashboardBuilder: {
			default: false
		},
		pageMetadata: {
			default: {}
		},
		pageState: {
			default: {}
		},
		isDomainExpanded: {
			default: false
		}
	},

	behaviors: [
		{
			behavior: routerBehavior,
			options: {
				routesFn: createRoutes
			}
		}
	]
});
