import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import clone from 'lodash/clone';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import maxBy from 'lodash/maxBy';

// Internal components
import './components/sn-experience-settings-branding';
import './components/sn-experience-settings-general';
import './components/sn-experience-settings-navigation';
import './components/sn-experience-settings-search';
import './components/sn-experience-settings-toolbar';
import './components/sn-experience-settings-utilities';

// NDS
import '@servicenow/now-button';
import '@servicenow/now-content-tree';

import styles from './styles/layout.scss';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

import {
	BRANDING_COMPONENT_BOOTSTRAPPED,
	BRANDING_FETCH_REQUESTED,
	BRANDING_FETCH_SUCCEEDED,
	BRANDING_UPDATE_REQUESTED,
	BRANDING_UPDATE_SUCCEEDED,
	BUTTON_CLICKED,
	CANCEL_BTN,
	GENERAL_FETCH_REQUESTED,
	GENERAL_FETCH_SUCCEEDED,
	GENERAL_UPDATE_REQUESTED,
	GENERAL_UPDATE_SUCCEEDED,
	MENU_ITEM_CLICKED,
	NAVIGATION_COMPONENT_BOOTSTRAPPED,
	NAVIGATION_FETCH_REQUESTED,
	NAVIGATION_FETCH_SUCCEEDED,
	NAVIGATION_UPDATE_REQUESTED,
	NAVIGATION_UPDATE_SUCCEEDED,
	PAGE_BRANDING,
	PAGE_GENERAL,
	PAGE_NAVIGATION,
	PAGE_SEARCH,
	PAGE_TOOLBAR,
	PAGE_UTILITIES,
	PORTAL,
	SAVE_BTN,
	SEARCH_COMPONENT_BOOTSTRAPPED,
	SEARCH_FETCH_REQUESTED,
	SEARCH_FETCH_SUCCEEDED,
	SEARCH_UPDATE_REQUESTED,
	SEARCH_UPDATE_SUCCEEDED,
	SETTINGS_FETCH_REQUESTED,
	SETTINGS_FETCH_SUCCEEDED,
	SETTINGS_UPDATED,
	SETTINGS_UPDATE_REQUESTED,
	TOOLBAR_ADD_BUTTON_REQUESTED,
	TOOLBAR_COMPONENT_BOOTSTRAPPED,
	TOOLBAR_FETCH_REQUESTED,
	TOOLBAR_FETCH_SUCCEEDED,
	TOOLBAR_UPDATE_REQUESTED,
	TOOLBAR_UPDATE_SUCCEEDED,
	UTILITIES_COMPONENT_BOOTSTRAPPED,
	UTILITIES_FETCH_REQUESTED,
	UTILITIES_FETCH_SUCCEEDED,
	UTILITIES_UPDATE_REQUESTED,
	UTILITIES_UPDATE_SUCCEEDED,
	WORKSPACE
} from './constants';
import {
	brandingFetchRequestedEffect,
	brandingFetchSucceededEffect,
	brandingUpdateRequestedEffect,
	brandingUpdateSucceededEffect,
	generalFetchRequestedEffect,
	generalFetchSucceededEffect,
	generalUpdateRequestedEffect,
	generalUpdateSucceededEffect,
	navigationFetchRequestedEffect,
	navigationFetchSucceededEffect,
	navigationUpdateRequestedEffect,
	navigationUpdateSuccededEffect,
	searchFetchRequestedEffect,
	searchFetchSucceededEffect,
	searchUpdateRequestedEffect,
	searchUpdateSucceededEffect,
	settingsButtonClickedEffect,
	settingsFetchRequestedEffect,
	settingsFetchSucceededEffect,
	settingsUpdateRequestedEffect,
	settingsUpdatedEffect,
	toolbarFetchRequestedEffect,
	toolbarFetchSucceededEffect,
	toolbarUpdateRequestedEffect,
	toolbarUpdateSucceededEffect,
	utilitiesFetchRequestedEffect,
	utilitiesFetchSucceededEffect,
	utilitiesUpdateRequestedEffect,
	utilitiesUpdateSucceededEffect
} from './effects';

const items = [
	{ id: PAGE_GENERAL, icon: 'gear-outline', label: t('General') },
	{
		id: PAGE_BRANDING,
		icon: 'palette-outline',
		label: t('Branding and theming')
	},
	{
		id: PAGE_TOOLBAR,
		icon: 'menu-outline',
		label: t('Side navigation'),
		applicationType: WORKSPACE
	},
	{
		id: PAGE_NAVIGATION,
		icon: 'window-outline',
		label: t('Navigation and menus'),
		applicationType: PORTAL
	},
	{
		id: PAGE_UTILITIES,
		icon: 'wrench-outline',
		label: t('Utilities'),
		applicationType: WORKSPACE
	},
	{
		id: PAGE_SEARCH,
		icon: 'magnifying-glass-outline',
		label: t('Search'),
		applicationType: PORTAL
	}
];

const filter = (items, applicationType) => {
	return items.filter((item) => {
		if (item.applicationType && item.applicationType !== applicationType) {
			return false;
		}

		return true;
	});
};

const view = ({
	properties: { sysId, readonly },
	applicationType,
	pageSelected,
	branding,
	general,
	navigation,
	toolbar,
	search,
	utilities
}) => {
	return (
		<div className="flex-column center-space-between">
			<div className="flex-row">
				<div className="section-menu">
					<now-content-tree
						className="menu"
						items={filter(items, applicationType)}
						selected-items={[[pageSelected || PAGE_GENERAL]]}
					/>
				</div>
				<div className="section-content flex-column">
					{pageSelected === PAGE_GENERAL ? (
						<sn-experience-settings-general
							sys-id={sysId}
							readonly={readonly}
							title={general.title}
							description={general.description}
							path={general.path}
							application-type={
								applicationType
							}></sn-experience-settings-general>
					) : null}
					{pageSelected === PAGE_BRANDING ? (
						<sn-experience-settings-branding
							panel-sys-id={branding.panelSysId}
							readonly={readonly}
							themes={branding.themes}
							primary-theme-id={branding.primaryThemeId}
							application-type={
								applicationType
							}></sn-experience-settings-branding>
					) : null}
					{pageSelected === PAGE_TOOLBAR ? (
						<sn-experience-settings-toolbar
							sys-id={toolbar.sysId}
							readonly={readonly}
							routes={toolbar.routes}
							buttons={toolbar.buttons}></sn-experience-settings-toolbar>
					) : null}
					{pageSelected === PAGE_NAVIGATION ? (
						<sn-experience-settings-navigation
							header-sys-id={navigation.headerSysId}
							readonly={readonly}
							menu={navigation.menu}
							menu-sys-id={navigation.menuSysId}
							footer-top={navigation.footerTop}
							footer-bottom={navigation.footerBottom}
							footer-sys-id={
								navigation.footerSysId
							}></sn-experience-settings-navigation>
					) : null}
					{pageSelected === PAGE_UTILITIES ? (
						<sn-experience-settings-utilities
							header-sys-id={utilities.headerSysId}
							readonly={readonly}
							search-sys-id={utilities.searchSysId}
							global-search={utilities.globalSearch}
							notifications={utilities.notifications}
							selected-search-source={utilities.selectedSearchSource}
							search-sources={
								utilities.searchSources
							}></sn-experience-settings-utilities>
					) : null}
					{pageSelected === PAGE_SEARCH ? (
						<sn-experience-settings-search
							sys-id={search.sysId}
							readonly={readonly}
							search-public={search.searchPublic}
							search-private={
								search.searchPrivate
							}></sn-experience-settings-search>
					) : null}
				</div>
			</div>
			<div className="footer flex-row flex-end">
				<div className="flex-column">
					<div className="spacer"></div>
					<div className="flex-end">
						<now-button
							className="btn"
							label={t('Cancel')}
							append-to-payload={{ name: CANCEL_BTN }}></now-button>
						<now-button
							className="btn"
							name="save"
							label={t('Save all changes')}
							variant="primary"
							append-to-payload={{ name: SAVE_BTN }}
							disabled={
								readonly ||
								(!general.updated &&
									!branding.updated &&
									!toolbar.updated &&
									!utilities.updated &&
									!navigation.updated &&
									!search.updated)
							}></now-button>
					</div>
				</div>
			</div>
		</div>
	);
};

createCustomElement('uib-experience-settings-connected', {
	renderer: { type: snabbdom },
	view,
	properties: {
		sysId: { required: true },
		readonly: {
			schema: { type: 'boolean' }
		}
	},
	initialState: {
		applicationType: '',
		panelSysId: '',
		branding: {
			panelSysId: '',
			primaryThemeId: '',
			themes: [],
			updated: false
		},
		general: {
			title: '',
			description: '',
			path: '',
			updated: false
		},
		navigation: {
			menu: false,
			menuSysId: '',
			footerTop: false,
			footerBottom: false,
			footerSysId: '',
			updated: false
		},
		pageSelected: PAGE_GENERAL,
		toolbar: {
			sysId: '',
			updated: false,
			buttons: []
		},
		search: {
			sysId: '',
			searchPublic: false,
			searchPrivate: false,
			updated: false
		},
		utilities: {
			globalSearch: false,
			headerSysId: '',
			notifications: false,
			searchSources: [],
			searchSysId: '',
			selectedSearchSource: '',
			updated: false
		}
	},
	actionHandlers: {
		[BRANDING_COMPONENT_BOOTSTRAPPED]: ({
			properties: { sysId },
			dispatch,
			state: { branding }
		}) => {
			if (!branding.updated) {
				dispatch(BRANDING_FETCH_REQUESTED, { sysId });
			}
		},
		[BRANDING_FETCH_REQUESTED]: brandingFetchRequestedEffect,
		[BRANDING_FETCH_SUCCEEDED]: brandingFetchSucceededEffect,
		[BRANDING_UPDATE_REQUESTED]: brandingUpdateRequestedEffect,
		[BRANDING_UPDATE_SUCCEEDED]: brandingUpdateSucceededEffect,
		[BUTTON_CLICKED]: settingsButtonClickedEffect,
		[COMPONENT_BOOTSTRAPPED]: ({ properties: { sysId }, dispatch }) => {
			dispatch(SETTINGS_FETCH_REQUESTED, { sysId });
			dispatch(GENERAL_FETCH_REQUESTED, { sysId });
		},
		[GENERAL_FETCH_REQUESTED]: generalFetchRequestedEffect,
		[GENERAL_FETCH_SUCCEEDED]: generalFetchSucceededEffect,
		[GENERAL_UPDATE_REQUESTED]: generalUpdateRequestedEffect,
		[GENERAL_UPDATE_SUCCEEDED]: generalUpdateSucceededEffect,
		[NAVIGATION_COMPONENT_BOOTSTRAPPED]: ({
			properties: { sysId },
			dispatch,
			state: { navigation }
		}) => {
			if (!navigation.updated) {
				dispatch(NAVIGATION_FETCH_REQUESTED, { sysId });
			}
		},
		[NAVIGATION_FETCH_REQUESTED]: navigationFetchRequestedEffect,
		[NAVIGATION_FETCH_SUCCEEDED]: navigationFetchSucceededEffect,
		[NAVIGATION_UPDATE_REQUESTED]: navigationUpdateRequestedEffect,
		[NAVIGATION_UPDATE_SUCCEEDED]: navigationUpdateSuccededEffect,
		[SEARCH_COMPONENT_BOOTSTRAPPED]: ({
			properties: { sysId },
			dispatch,
			state: { search }
		}) => {
			if (!search.updated) {
				dispatch(SEARCH_FETCH_REQUESTED, { sysId });
			}
		},
		[SEARCH_FETCH_REQUESTED]: searchFetchRequestedEffect,
		[SEARCH_FETCH_SUCCEEDED]: searchFetchSucceededEffect,
		[SEARCH_UPDATE_REQUESTED]: searchUpdateRequestedEffect,
		[SEARCH_UPDATE_SUCCEEDED]: searchUpdateSucceededEffect,
		[SETTINGS_FETCH_REQUESTED]: settingsFetchRequestedEffect,
		[SETTINGS_FETCH_SUCCEEDED]: settingsFetchSucceededEffect,
		[TOOLBAR_COMPONENT_BOOTSTRAPPED]: ({
			properties: { sysId },
			dispatch,
			state: { toolbar }
		}) => {
			if (!toolbar.updated) {
				dispatch(TOOLBAR_FETCH_REQUESTED, { sysId });
			}
		},
		[TOOLBAR_FETCH_REQUESTED]: toolbarFetchRequestedEffect,
		[TOOLBAR_FETCH_SUCCEEDED]: toolbarFetchSucceededEffect,
		[TOOLBAR_UPDATE_REQUESTED]: toolbarUpdateRequestedEffect,
		[TOOLBAR_UPDATE_SUCCEEDED]: toolbarUpdateSucceededEffect,
		[TOOLBAR_ADD_BUTTON_REQUESTED]: ({
			state: {
				toolbar: { buttons }
			},
			updateState
		}) => {
			const max = maxBy(buttons, 'order');

			buttons.push({
				sysId: -1,
				label: '',
				icon: '',
				route: '',
				group: '',
				viewportRoute: null,
				order: max ? max.order + 100 : 100,
				new: true
			});
			updateState([
				{
					path: `${PAGE_TOOLBAR}.buttons`,
					value: clone(buttons),
					operation: 'set'
				}
			]);
			updateState([
				{ path: `${PAGE_GENERAL}.updated`, value: true, operation: 'set' }
			]);
		},
		[UTILITIES_COMPONENT_BOOTSTRAPPED]: ({
			properties: { sysId },
			dispatch,
			state: { utilities }
		}) => {
			if (!utilities.updated) {
				dispatch(UTILITIES_FETCH_REQUESTED, { sysId });
			}
		},
		[UTILITIES_FETCH_REQUESTED]: utilitiesFetchRequestedEffect,
		[UTILITIES_FETCH_SUCCEEDED]: utilitiesFetchSucceededEffect,
		[UTILITIES_UPDATE_REQUESTED]: utilitiesUpdateRequestedEffect,
		[UTILITIES_UPDATE_SUCCEEDED]: utilitiesUpdateSucceededEffect,
		[SETTINGS_UPDATE_REQUESTED]: settingsUpdateRequestedEffect,
		[MENU_ITEM_CLICKED]: ({ action: { payload }, updateState }) => {
			updateState({ pageSelected: payload.item.id });
		},
		[SETTINGS_UPDATED]: settingsUpdatedEffect
	},
	styles
});
