export const checkForUnsavedChanges = (enqueuedEvent) => (coeffects) => {
	const { action, dispatch, properties } = coeffects;
	const { pageState } = properties;
	const { payload = {}, type: callbackAction } = action;
	const { dispatchActionImmediately = false } = payload;

	action.stopPropagation();

	if (dispatchActionImmediately || pageState === null) {
		enqueuedEvent(coeffects);
		dispatch('UIB_APP#UPDATE_UNSAVED_CHANGES_CHECK_CALLBACK_EVENT', {
			unsavedChangesCheckCallbackEvent: null
		});
	} else {
		dispatch('UIB_APP#UPDATE_UNSAVED_CHANGES_CHECK_CALLBACK_EVENT', {
			unsavedChangesCheckCallbackEvent: {
				callbackAction,
				callbackPayload: payload
			}
		});
	}
};
