const getPageTemplates = memoizeDeepN(function (templates, rootElementId) {
	const doesNotUseCurrentMacroponent = ({ rootMacroponentId }) =>
		rootMacroponentId !== rootElementId;

	const pageTemplates = filterTemplatesByType('page');

	return templates.filter(
		(template) =>
			pageTemplates(template) && doesNotUseCurrentMacroponent(template)
	);
});

const getLayoutTemplates = memoizeDeepN(function (templates) {
	const layoutTemplates = filterTemplatesByType('layout');
	return templates.filter(layoutTemplates);
});

const getSelectedPage = memoizeDeepN(function (
	applicationType,
	appRoutes,
	pageSysId,
	dashboardId,
	pages,
	workspacePageId
) {
	if (
		applicationType === applicationTypes.app ||
		applicationType === applicationTypes.components
	) {
		const pages = flattenAppRoutes(appRoutes).map((route) => route.page);
		const page = pages.find((page) => page.sysId === pageSysId);
		return page;
	} else if (applicationType === applicationTypes.dashboards) {
		const flattened = flattenDashboardRoutes(appRoutes);
		const route = flattened.find((route) => route.id === dashboardId);
		return (
			route && {
				...route
			}
		);
	} else if (applicationType === applicationTypes.workspace) {
		const page = pages && pages.find(({ sysId }) => workspacePageId === sysId);
		return page;
	}
});

const getSelectedPageVariants = memoizeDeepN(function (selectedPage) {
	return selectedPage && selectedPage.variants ? selectedPage.variants : [];
});

