		...uxfHandlers,
		...panelHandlers,
		...elementHandlers,
		...modalHandlers,
		...dialogHandlers,
		...referenceHandlers,
		...tablesHandlers,
		...fieldHandlers,
		...extendedPropertiesHandlers,
