import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';

// Third party
import get from 'lodash/get';

import {
	NAVIGATION_FETCH_SUCCEEDED,
	NAVIGATION_UPDATE_SUCCEEDED,
	SETTINGS_FETCH_ERROR_OCCURRED,
	SETTINGS_UPDATE_SUCCEEDED
} from '../constants';

const props = `headerSysId
          menu
          menuSysId
          footerTop
          footerBottom
          footerSysId`;

const query = `
query snExperienceSettingsConnected($sysId: ID!) {
    snExpSettings {
      settings {
        navigation(sysId: $sysId) {
          ${props}
        }
      }
    }
  }`;

export const navigationFetchRequestedEffect = createGraphQLEffect(query, {
	variableList: ['sysId'],
	successActionType: NAVIGATION_FETCH_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const navigationFetchSucceededEffect = ({
	action: { payload, stopPropagation },
	updateState
}) => {
	stopPropagation();
	const {
		headerSysId,
		menu,
		menuSysId,
		footerTop,
		footerBottom,
		footerSysId
	} = get(payload, `data.snExpSettings.settings.navigation`, {});

	updateState({
		navigation: {
			headerSysId,
			menu,
			menuSysId,
			footerTop,
			footerBottom,
			footerSysId,
			updated: false
		}
	});
};

const mutation = `
mutation snExperienceSettingsConnected($sysId: ID! $menu: Boolean! $footerTop: Boolean! $footerBottom: Boolean!) {
  snExpSettings {
    settings {
      navigationUpdate(sysId:$sysId menu:$menu footerTop:$footerTop footerBottom:$footerBottom) {
        ${props}
      }
    }
  }
}`;

export const navigationUpdateRequestedEffect = createGraphQLEffect(mutation, {
	variableList: ['sysId', 'menu', 'footerTop', 'footerBottom'],
	successActionType: NAVIGATION_UPDATE_SUCCEEDED,
	errorActionType: SETTINGS_FETCH_ERROR_OCCURRED
});

export const navigationUpdateSuccededEffect = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	const {
		headerSysId,
		menu,
		menuSysId,
		footerTop,
		footerBottom,
		footerSysId
	} = get(payload, `data.snExpSettings.settings.navigationUpdate`, []);
	updateState({
		navigation: {
			headerSysId,
			menu,
			menuSysId,
			footerTop,
			footerBottom,
			footerSysId,
			updated: false
		}
	});
	dispatch(SETTINGS_UPDATE_SUCCEEDED);
};
