/**
 * Processor that takes in the currently configured property
 * and configures is for dynamic or static mode
 * @param {object} property - The current UXF element property being configured
 * @param {object[]} properties - The current set of UXF properties for the element being configured
 * @param {object} state - The current element configuration panel Seismic state
 * @return {object} - The configured property object
 */
export default function (property) {
	// TODO: this isStatic key will eventually be supported by UXF and passed down
	// as part of the property metadata. For now, we are setting isStatic to true
	// or false based on the presence of '@' at the beginning of the property value.
	const isBoundProperty = (property) =>
		property && typeof property === 'string' && property.startsWith('@');

	let isStatic = true;
	if (isBoundProperty(property.value)) {
		isStatic = false;
	}

	return {
		...property,
		isStatic
	};
}
