import { t } from 'sn-translate';
import { snUXFActions } from 'sn-uxf-builder-middleware';
import * as uibPageHeader from '../../uib-page-header';
import { urlRoot, getRuntimeUrl } from '../../../utils/urlBuilders';
import { flattenAppRoutes } from '../../../utils/appRoutes';
import isEmpty from 'lodash/isEmpty';
import getHelpResourceUrl from '../utils/getHelpResourceUrl';
import { checkForUnsavedChanges } from '../utils/checkForUnsavedChanges';

const findRouteForCurrentPage = (appRoutes, pageSysId) =>
	appRoutes.find((appRoute) => {
		return appRoute.page.sysId === pageSysId;
	});

const editDashboard = (updateProperties, dashboardId, appRoutes) => {
	const dashboard = appRoutes.find((route) => route.id === dashboardId);
	const { routeType, name, scope, id, sysPolicy } = dashboard;
	updateProperties({
		pageConfigurationModalInfo: {
			route: {
				routeType,
				page: {
					name,
					scope,
					sysId: id
				},
				sysId: id,
				sysPolicy
			}
		}
	});
};

const newDashboardClicked = ({ dispatch }) => {
	dispatch('SN_UIB_APP#CREATE_PAGE_REQUESTED');
};

const editDashboardClicked = ({ updateProperties, properties }) => {
	const { appRoutes, routeState } = properties;
	const { dashboardId } = routeState;
	editDashboard(updateProperties, dashboardId, appRoutes);
};

const editExperienceClicked = ({ updateState }) => {
	updateState({
		operation: 'set',
		path: 'showExperienceModal',
		value: true
	});
};

const newWorkspacePageClicked = ({ updateProperties }) => {
	updateProperties({
		showNewPageModal: true,
		pageCreateType: 'createPage'
	});
};

const editWorkspacePageClicked = ({ updateProperties }) => {
	updateProperties({
		showNewPageModal: true,
		pageCreateType: 'editPage'
	});
};

const copyWorkspacePageClicked = ({ action, updateProperties }) => {
	const { payload = {} } = action;
	const { page = null } = payload;
	updateProperties({
		showNewPageModal: true,
		pageCreateType: 'copyPage',
		newPageModalInfo: page
	});
};

const deleteWorkspacePageClicked = ({ properties, updateProperties }) => {
	const {
		routeState,
		pageState: { scope, name }
	} = properties;
	const { pageSysId } = routeState;
	const objectToDelete = {
		name,
		sysId: pageSysId,
		scope
	};
	updateProperties({
		showDeleteConfirmation: true,
		deleteConfirmationProps: {
			headerLabel: t('Delete page'),
			objectToDelete,
			deleteActionHandler: (dispatch, objectToDelete) => {
				dispatch(snUXFActions.PAGE_DELETE_REQUESTED, {
					sysId: objectToDelete.sysId,
					scope: objectToDelete.scope
				});
			}
		}
	});
};

const newAppPageClicked = ({ dispatch }) => {
	dispatch('SN_UIB_APP#CREATE_PAGE_REQUESTED');
};

const newAppPageDynamicTabClicked = ({ updateProperties }) => {
	updateProperties({
		showNewPageModal: true,
		isDynamicTabCreateClicked: true
	});
};

const editAppPageClicked = ({ updateProperties, properties }) => {
	const route = findRouteForCurrentPage(
		properties.selectedPageIsTab
			? flattenAppRoutes(properties.appRoutes)
			: properties.appRoutes,
		properties.routeState.pageSysId
	);
	if (route === undefined || route === null) {
		return;
	}
	const {
		name,
		scope,
		sysId,
		routeType,
		optionalParameters,
		fields,
		page,
		icon,
		order,
		sysPolicy
	} = route;
	updateProperties({
		pageConfigurationModalInfo: {
			route: {
				routeType,
				icon,
				order,
				optionalParameters,
				fields,
				sysPolicy,
				page: {
					name,
					scope,
					sysId: page.sysId
				},
				sysId
			}
		}
	});
};

const newAppPageVariantClicked = ({ updateProperties, properties }) => {
	const { routeState = {}, selectedPage, selectedPageIsTab } = properties;
	const { pageSysId } = routeState;
	let order = 0;

	if (selectedPageIsTab) {
		const { variants = [] } = selectedPage;
		if (!isEmpty(variants)) {
			const sortedVariants = variants.sort((a, b) => b.order - a.order);
			order = sortedVariants[0].order + 100;
		}
	}

	updateProperties({
		newVariantModalInfo: {
			pageSysId,
			variantName: '',
			order
		}
	});
};

const editAppPageVariantClicked = ({ updateProperties, properties }) => {
	const { routeState } = properties;
	const { variantSysId: screenSysId } = routeState;

	updateProperties({
		screenConfigurationModalInfo: {
			screenSysId
		}
	});
};

const copyAppPageVariantClicked = ({ updateProperties, properties }) => {
	const { pageState, routeState } = properties;
	const {
		name: currentPageName,
		uiTypeParameters: { order }
	} = pageState;
	const { variantSysId: currentPageSysId, pageSysId } = routeState;

	updateProperties({
		newVariantModalInfo: {
			pageSysId,
			copyFrom: currentPageSysId,
			variantName: `${currentPageName} ${t('copy')}`,
			order
		}
	});
};

const backButtonClicked = ({ action, dispatch }) => {
	dispatch('SN_UIB_APP#PAGE_REQUESTED', action.payload);
};

const setDomainExpanded = ({ action, dispatch, properties }) => {
	action.stopPropagation();
	const { appsSysId } = properties;

	dispatch(snUXFActions.SET_DOMAIN_EXPANDED, {
		...action.payload,
		appsSysId
	});
};

const goToPage = ({ properties }) => {
	const runtimeUrl = getRuntimeUrl(properties, true);
	window.open(runtimeUrl.absolute, '_blank');
};

const goToScreenCollection = ({ properties }) => {
	const {
		appRoutes,
		routeState: { pageSysId }
	} = properties;
	const parentRoute = appRoutes.find((route) => route.page.sysId === pageSysId);
	if (!parentRoute) {
		return;
	}
	window.open(
		`${urlRoot()}/sys_ux_screen_type.do?sys_id=${parentRoute.page.sysId}`,
		'_blank'
	);
};

const goToScreen = ({ properties }) => {
	window.open(
		`${urlRoot()}/sys_ux_screen.do?sys_id=${properties.pageState.sysId}`,
		'_blank'
	);
};

const goToMacroponent = ({ properties }) => {
	window.open(
		`${urlRoot()}/sys_ux_macroponent.do?sys_id=${
			properties.pageState.rootElement.id
		}`,
		'_blank'
	);
};

const goToAppConfig = ({ properties }) => {
	const { appsSysId } = properties;
	window.open(
		`${urlRoot()}/sys_ux_app_config.do?sys_id=${appsSysId}`,
		'_blank'
	);
};

const activateClicked = ({ updateProperties }) => {
	updateProperties({
		showActivationConfirmation: true
	});
};

const deactivateClicked = ({ updateProperties }) => {
	updateProperties({
		showActivationConfirmation: true
	});
};

const RELEASE_VERSION = 'quebec';
const openHelpLearnAbout = () => {
	window.open(getHelpResourceUrl('uib-overview'), '_blank');
};

const openHelpIntro = () => {
	window.open(getHelpResourceUrl('uib-quickstart'), '_blank');
};

const openHelpComponentDocumentation = () => {
	window.open(
		`https://developer.servicenow.com/dev.do#!/reference/now-experience/${RELEASE_VERSION}/components`,
		'_blank'
	);
};
const openHelpWhatsNew = () => {
	window.open(getHelpResourceUrl('uib-release-notes'), '_blank');
};
const openHelpBuildingCustom = () => {
	window.open(getHelpResourceUrl('custom-components'), '_blank');
};
const openHelpLearnAboutDashboard = () => {
	window.open(getHelpResourceUrl('dashboard-builder'), '_blank');
};

export default {
	[uibPageHeader.NEW_DASHBOARD_CLICKED]: checkForUnsavedChanges(
		newDashboardClicked
	),
	[uibPageHeader.EDIT_DASHBOARD_CLICKED]: editDashboardClicked,
	[uibPageHeader.NEW_WORKSPACE_PAGE_CLICKED]: checkForUnsavedChanges(
		newWorkspacePageClicked
	),
	[uibPageHeader.EDIT_WORKSPACE_PAGE_CLICKED]: editWorkspacePageClicked,
	[uibPageHeader.COPY_WORKSPACE_PAGE_CLICKED]: checkForUnsavedChanges(
		copyWorkspacePageClicked
	),
	[uibPageHeader.DELETE_WORKSPACE_PAGE_CLICKED]: deleteWorkspacePageClicked,
	[uibPageHeader.NEW_APP_PAGE_CLICKED]: checkForUnsavedChanges(
		newAppPageClicked
	),
	[uibPageHeader.NEW_APP_PAGE_VARIANT_CLICKED]: checkForUnsavedChanges(
		newAppPageVariantClicked
	),
	[uibPageHeader.NEW_APP_PAGE_DYNAMIC_TAB_CLICKED]: checkForUnsavedChanges(
		newAppPageDynamicTabClicked
	),
	[uibPageHeader.EDIT_APP_PAGE_CLICKED]: editAppPageClicked,
	[uibPageHeader.EDIT_EXPERIENCE_CLICKED]: editExperienceClicked,
	[uibPageHeader.EDIT_APP_PAGE_VARIANT_CLICKED]: editAppPageVariantClicked,
	[uibPageHeader.COPY_APP_PAGE_VARIANT_CLICKED]: checkForUnsavedChanges(
		copyAppPageVariantClicked
	),
	[uibPageHeader.GO_TO_PAGE]: goToPage,
	[uibPageHeader.GO_TO_SCREEN_COLLECTION]: goToScreenCollection,
	[uibPageHeader.GO_TO_SCREEN]: goToScreen,
	[uibPageHeader.GO_TO_MACROPONENT]: goToMacroponent,
	[uibPageHeader.GO_TO_APP_CONFIG]: goToAppConfig,
	[uibPageHeader.ACTIVATE_CLICKED]: activateClicked,
	[uibPageHeader.DEACTIVATE_CLICKED]: deactivateClicked,
	[uibPageHeader.LEARN_ABOUT_LINK_CLICKED]: openHelpLearnAbout,
	[uibPageHeader.INTRO_LINK_CLICKED]: openHelpIntro,
	[uibPageHeader.COMPONENT_DOCUMENTATION_LINK_CLICKED]: openHelpComponentDocumentation,
	[uibPageHeader.WHATS_NEW_LINK_CLICKED]: openHelpWhatsNew,
	[uibPageHeader.BUILDING_CUSTOM_LINK_CLICKED]: openHelpBuildingCustom,
	[uibPageHeader.LEARN_ABOUT_DASHBOARD_BUILDER_CLICKED]: openHelpLearnAboutDashboard,
	[uibPageHeader.BACK_BUTTON_CLICKED]: backButtonClicked,
	[uibPageHeader.SET_DOMAIN_EXPANDED]: checkForUnsavedChanges(setDomainExpanded)
};
