import { evaluateConfigTabSelection } from './utils';

const selectSlot = ({ updateProperties, payload, properties, dispatch }) => {
	const {
		selectedSlotName,
		selectedSlotParent,
		selectedSlotParentComponent
	} = payload;

	const { hoveredSlotName, hoveredSlotParent } = properties;

	const hoveredSlotInformation = {};

	if (
		hoveredSlotName === selectedSlotName &&
		hoveredSlotParent === selectedSlotParent
	) {
		hoveredSlotInformation.hoveredSlotName = null;
		hoveredSlotInformation.hoveredSlotParent = null;
	}
	const pageConfigTab = evaluateConfigTabSelection({
		selectedSlotName,
		properties
	});

	updateProperties({
		pageConfigTab,
		selectedElementId: null,
		selectedSlotName,
		selectedSlotParent,
		selectedSlotParentComponent: null,
		...hoveredSlotInformation
	});
};

export default {
	['UIB_SLOT_WRAPPER#MOUSE_OVER']: ({
		action,
		properties,
		updateProperties
	}) => {
		const { slotName, slotParent } = action.payload;
		const { selectedSlotName, selectedSlotParent } = properties;

		if (slotName === selectedSlotName && slotParent === selectedSlotParent) {
			updateProperties({
				hoveredSlotName: null,
				hoveredSlotParent: null,
				hoveredElementId: null
			});
		} else {
			updateProperties({
				hoveredSlotName: slotName,
				hoveredSlotParent: slotParent,
				hoveredElementId: null
			});
		}
	},
	['UIB_SLOT_WRAPPER#SELECTED']: ({
		action,
		properties,
		updateProperties,
		dispatch
	}) => {
		const { payload } = action;
		const {
			slotName: selectedSlotName,
			slotParent: selectedSlotParent,
			selectedSlotParentComponent: selectedSlotParentComponent
		} = payload;
		selectSlot({
			payload: {
				selectedSlotName,
				selectedSlotParent,
				selectedSlotParentComponent
			},
			properties,
			updateProperties,
			dispatch
		});
	},
	['UIB_SLOT#SLOT_SELECTED']: ({ action, properties, updateProperties }) => {
		const { payload } = action;
		selectSlot({ payload, properties, updateProperties });
	}
};
