import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-alert';
import '@servicenow/now-button';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { createRef, Fragment } from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import cuid from 'cuid';
import get from 'lodash/get';
import { t } from 'sn-translate';
import * as snUIBuilderComponents from 'sn-ui-builder-components';
import { escapeScopeLabel } from '../../utils/escapeScopeLabel';
import { applicationTypes } from '../../utils/routingConstants';
import { inspectEveryNode } from '../../utils/tree';
import workspaceLookup from '../../utils/workspaceLookup';
import '../uib-default-slotted';
import { capabilityMatrix } from '../../utils/capabilityMatrix';
import { checkForUnsavedChanges } from '../uib-app/utils/checkForUnsavedChanges';

import '../uib-element';
import '../uib-empty-slot';
import '../uib-placeholder';
import './components/uib-element-controls-provider';
// eslint-disable-next-line no-unused-vars
import RouteNotFound, {
	shouldShowRouteNotFound
} from './partials/RouteNotFound';
import styles from './_uib-stage.scss';
import { memoizeDeepN } from 'seismic-memo-utils';
import { SHARED_PAGE_DEFINITION_SYS_ID } from './../../globals/constants';

const createFlagPage = memoizeDeepN(function (page) {
	const obj = {};
	let parentModalId = null;
	if (!page || !page.rootElement) {
		return obj;
	}
	inspectEveryNode(page.rootElement, (element) => {
		if (element.isModal) {
			parentModalId = element.id;
		}

		const elementData = {
			...element,
			parentModalId: element.isModalDescendant ? parentModalId : undefined,
			instanceName:
				element.id === page.rootElement.id ? t('Body') : element.instanceName
		};
		obj[element.id] = elementData;
	});
	return obj;
});

const {
	snUIBSplashPage,
	strategyManager,
	onNextFrame,
	snUibElementControlsPlacement,
	ELEMENT_TOOLTIP_MESSAGE,
	stageDragMap
} = snUIBuilderComponents;
const { CHILD_DATA_SET } = snUIBSplashPage;
const { elementRefMap } = snUibElementControlsPlacement;

export const UIB_STAGE_UNLOCK_TO_CREATE_OVERRIDE_CLICKED =
	'UIB_STAGE#UNLOCAK_TO_CREATE_OVERRIDE_CLICKED';

const getStageDimensions = memoizeDeepN(function (height, top) {
	return {
		top,
		height
	};
});

export default createCustomElement('uib-stage', {
	view: (state, { updateState, dispatch }) => {
		const { lastDroppedElementId, isStageFocused, stageManagerRef } = state;
		const {
			properties: {
				pageSysId,
				isWorkspaceApp,
				routeName,
				routeState,
				disabled,
				isDefaultPageLayout,
				flatPage,
				keyboardDrag,
				isKeyboardNav,
				toolboxComponents,
				applicationType,
				showLandingPageInfoLink,
				rootElementSelectable,
				selectedComponent,
				selectedPage,
				shouldShowSplashPage,
				layoutTemplates,
				ElementControlsTagName, // eslint-disable-line no-unused-vars
				rootElementId,
				pageTemplates,
				isDashboardBuilder,
				userScope,
				userDomain,
				applications,
				domains,
				supportDomains,
				selectedPageIsTab,
				pageScopeName,
				pageScopeId,
				pageDomainName,
				pageDomainId,
				pageDefinitionScopeId,
				pageDefinitionScopeName,
				disableStateTransactionOverride,
				pageProtectionPolicy,
				pageDefProtectionPolicy,
				isDomainExpanded,
				isSharedPageDef,
				supportScope,
				isPageStateDirty,
				page
			},
			stageEl,
			maximizeStage
		} = state;

		const layoutRenderers = state.properties.layoutRenderers || {};

		if (shouldShowRouteNotFound(routeName, routeState, applicationType)) {
			return (
				<RouteNotFound
					routeName={routeName}
					routeState={routeState}
					applicationType={applicationType}
				/>
			);
		}

		if (!pageSysId && shouldShowSplashPage) {
			const customText =
				applicationType === applicationTypes.dashboards
					? {
							/* prettier-ignore */
							customTitleText: t("Let's create a new {0}", selectedPageIsTab ? 'tab' : 'dashboard'),
							/* prettier-ignore */
							customButtonText: t('Create a new {0}', selectedPageIsTab ? 'tab' : 'dashboard'	)
					  }
					: {};
			return (
				<div className="splash-page-container">
					<sn-uib-splash-page
						application="@uib-app/pageRegistryName"
						showLandingPageInfoLink={showLandingPageInfoLink}
						{...customText}
						isDashboardBuilder={isDashboardBuilder}
						selectedPageIsTab={selectedPageIsTab}
					/>
				</div>
			);
		}

		if (shouldShowSplashPage) {
			return (
				<div className="splash-page-container">
					<sn-uib-splash-page
						showLandingPageInfoLink={false}
						/* prettier-ignore */
						custom-title-text={t("Let's create a variant for {0}", selectedPage.name)}
						custom-button-text={t('Create a new variant')}
						custom-button-action={'SN_UIB_APP#NEW_VARIANT_MODAL_OPENED'}
						isDashboardBuilder={isDashboardBuilder}
						selectedPageIsTab={selectedPageIsTab}
					/>
				</div>
			);
		}

		const dragRef = createRef();
		onNextFrame(() => {
			stageDragMap.set('stageRef', dragRef);
		});
		// This is used to set the container for scrolling while dragging an item into the stage.
		strategyManager.set('stage', 'ref', dragRef);
		strategyManager.set('stage-content-wrapper', 'ref', stageManagerRef);
		elementRefMap.set('stageWrapper', stageManagerRef);
		const setStageEl = (el) => onNextFrame(() => updateState({ stageEl: el }));
		const { stageWidth } = get(routeState, 'queryParams', {});
		const isolatedEditMode = !!stageWidth;

		let stageDimensions = {};
		if (stageEl) {
			const stageElRef = stageEl.querySelector('.stage-content-wrapper');
			const height = stageElRef.clientHeight;
			const stageTopPadding = parseInt(getComputedStyle(stageElRef).paddingTop);
			const top =
				stageElRef.scrollTop > stageTopPadding
					? stageElRef.scrollTop - stageTopPadding
					: stageElRef.scrollTop;

			stageDimensions = getStageDimensions(height, top);
		}

		const resizeIcon = isolatedEditMode ? (
			<now-button-bare
				className="stage-resize"
				icon-start="expand-outline"
				tooltipContent={t('Expand Stage')}
				size="md"
			/>
		) : null;

		const stageContent = (
			<div
				class={{
					'stage-content-wrapper': true,
					'isolated-edit-mode': isolatedEditMode
				}}
				ref={stageManagerRef}>
				{resizeIcon}
				{isWorkspaceApp ? <div className="safe-padding-hack" /> : null}
				<ElementControlsTagName
					applicationType={applicationType}
					rootElementId={rootElementId}
					selected-element-id="@ui-state/selectedElementId"
					hovered-element-id="@ui-state/hoveredElementId"
					add-next-to-element-id="@ui-state/addNextToElementId"
					direction-to-add="@ui-state/directionToAdd"
					selectedSlotParent="@ui-state/selectedSlotParent"
					selectedSlotName="@ui-state/selectedSlotName"
					selectedSlotParentComponent="@ui-state/selectedSlotParentComponent"
					hoveredSlotName="@ui-state/hoveredSlotName"
					hoveredSlotParent="@ui-state/hoveredSlotParent"
					scroll-container={stageManagerRef}
					element-confirming-delete="@uib-app/elementConfirmingDelete"
					element-context-menu-opened="@uib-app/elementContextMenuOpened"
					is-resizing="@kb-nav-provider/isResizing"
					is-dragging="@dnd-provider/isDragging"
					last-dropped-element-id={lastDroppedElementId}
					flat-page={flatPage}
					disabled={disabled}
					is-panel-transitioning="@uib-app/isPanelTransitioning"
					layoutTemplates={layoutTemplates}
					pageTemplates={pageTemplates}
					layouts={'@uxf/elementLayouts'}
					isDefaultPageLayout={'@uxf/isDefaultPageLayout'}
				/>
				<sn-uxf-renderer
					key={`renderer-${rootElementId}`}
					page="@uxf/pageState"
					class={{
						short: disabled
					}}
					ref={dragRef}
					selectedComponent={selectedComponent}
					stageDimensions={stageDimensions}
					selectedElementId="@ui-state/selectedElementId"
					selectedSlotParent="@ui-state/selectedSlotParent"
					selectedSlotName="@ui-state/selectedSlotName"
					selectedSlotParentComponent="@ui-state/selectedSlotParentComponent"
					hidden-elements="@uxf/hiddenElements"
					renderers={{
						// If we are using the default page layout we don't need to worry about loading these
						...(isDefaultPageLayout ? {} : layoutRenderers),
						element: (options) => {
							let childElements = options.children;

							if (options.parentElementId === null && !rootElementSelectable) {
								return options.children;
							}

							const component = toolboxComponents
								? toolboxComponents.find(
										(componentConfig) =>
											componentConfig.id === options.componentId
								  )
								: null;

							if (options.hasError) {
								dispatch('UIB_DEFAULT_SLOTTED#PLACED_IN_UNREGISTERED', {
									elementId: options.elementId
								});

								childElements = [
									<sn-uib-missing-component element-id={options.elementId} />
								];
							}

							return (
								<uib-element
									selected-element-id="@ui-state/selectedElementId"
									hovered-element-id="@ui-state/hoveredElementId"
									is-workspace={isWorkspaceApp}
									is-resizing="@kb-nav-provider/isResizing"
									is-dragging="@dnd-provider/isDragging"
									element-confirming-delete="@uib-app/elementConfirmingDelete"
									element-id={options.elementId}
									parentElementId={options.parentElementId}
									elementInstanceName={options.instanceName}
									elementComponentIcon={options.componentIcon}
									allowIsolatedEditing={options.allowIsolatedEditing}
									component={component}
									disabled={disabled}>
									{childElements}
								</uib-element>
							);
						},

						emptySlot: (options) => {
							const { slotName = null } = options;
							if (isWorkspaceApp) {
								if (options.parentElementId === null || isDefaultPageLayout) {
									return (
										<uib-placeholder
											element-id={options.elementId}
											is-dragging="@dnd-provider/isDragging"
											page-type={t('page')}
											dragged-element="@dnd-provider/draggedElement"
											slot={slotName}
										/>
									);
								}
							}
							return (
								<uib-default-slotted
									element-id={options.elementId}
									slot={slotName}>
									<uib-empty-slot
										is-dragging="@dnd-provider/isDragging"
										keymaps={`@${options.elementId}_keybindings/emptySlot`}
										element-id={options.elementId}
										slotName={slotName}
									/>
								</uib-default-slotted>
							);
						},
						emptyRegion: (options) => {
							if (isDefaultPageLayout && !isolatedEditMode && isWorkspaceApp) {
								return (
									<uib-placeholder
										element-id={options.elementId}
										is-dragging="@dnd-provider/isDragging"
										page-type={t('page')}
										style={{ width: '100%' }}
										dragged-element="@dnd-provider/draggedElement"
										slot={options.slotName}
										slotName={options.slotName}
									/>
								);
							}

							// eslint-disable-next-line no-unused-vars
							const RegionTag = workspaceLookup(
								isWorkspaceApp,
								'regionTag',
								'sn-uib-region-empty-slot'
							);
							return (
								<RegionTag
									element-id={options.elementId}
									slotName={options.slotName}
									isModalDescendant={options.isModalDescendant}
									activeId={'@dnd-provider/activeId'}
									slot={options.slotName}
									disabled={disabled}
								/>
							);
						},
						slotWrapper: ({ children, slotName, slotParent, styles }) => {
							if (isWorkspaceApp) {
								return <Fragment>{children}</Fragment>;
							}
							return (
								<sn-uib-slot-wrapper
									key={`slotWrapper-${slotName}-${slotParent}`}
									elementRef={`@${slotParent}_${slotName}_information/elementRef`}
									slot={slotName}
									slotName={slotName}
									styles={styles}
									slotParent={slotParent}
									selectedSlotName="@ui-state/selectedSlotName"
									selectedSlotParent="@ui-state/selectedSlotParent"
									hoveredSlotName="@ui-state/hoveredSlotName"
									selectedSlotParentComponent="@ui-state/selectedSlotParentComponent"
									hoveredSlotParent="@ui-state/hoveredSlotParent">
									{children}
								</sn-uib-slot-wrapper>
							);
						}
					}}
					stage-manager-ref={stageManagerRef}
				/>
			</div>
		);

		return (
			<sn-uib-aria-live>
				<main
					className="stage"
					on-mouseleave={(event) => {
						event.stopPropagation();

						if (
							event.relatedTarget &&
							event.relatedTarget.tagName.toLowerCase() ===
								'uib-element-controls-provider'
						) {
							return;
						}
						dispatch('UIB_STAGE#ELEMENT_MOUSE_LEAVE');
					}}
					on-focusin={() => {
						updateState({ isStageFocused: true });
					}}
					on-focusout={() => {
						updateState({ isStageFocused: false });
					}}
					hook-insert={({ elm }) => setStageEl(elm)}>
					<div className="stage-container">
						{page ? (
							isWorkspaceApp ? (
								<div className="uib-stage-alert-container">
									<sn-uib-transaction-alert
										recordProtectionPolicy={pageProtectionPolicy}
										applications={applications}
										userScope={userScope}
										request-scope-name={pageScopeName}
										request-scope-id={pageScopeId}
										requestItemType="Landing Page"
										support-scope={supportScope}
										allow-cancel-transaction-in-progress={
											isPageStateDirty === false
										}
									/>
								</div>
							) : (
								<div className="uib-stage-alert-container">
									<sn-uib-transaction-alert
										recordProtectionPolicy={pageProtectionPolicy}
										user-scope={userScope}
										user-domain={userDomain}
										applications={applications}
										support-domains={supportDomains}
										support-scope={supportScope}
										domains={domains}
										isDomainExpanded={isDomainExpanded}
										request-scope-name={pageScopeName}
										request-scope-id={pageScopeId}
										request-domain-name={pageDomainName}
										request-domain-id={pageDomainId}
										request-page-definition-scope-id={pageDefinitionScopeId}
										request-page-definition-scope-name={pageDefinitionScopeName}
										pageDefProtectionPolicy={pageDefProtectionPolicy}
										recordIsOverridenInCurrentDomain={
											disableStateTransactionOverride
										}
										isSharedPageDef={isSharedPageDef}
										requestItemType={
											isDashboardBuilder ? 'Dashboard' : 'Variant'
										}
										allow-cancel-transaction-in-progress={
											isPageStateDirty === false
										}
									/>
								</div>
							)
						) : null}

						{isolatedEditMode && !maximizeStage ? (
							<div
								style={{
									width: `${stageWidth}px`,
									overflow: 'auto'
								}}>
								{stageContent}
							</div>
						) : (
							stageContent
						)}
					</div>
					{/* Tooltip for informing keyboard users to use keyboard to navigate elements on stage */}
					<now-tooltip
						id="uib-stage-element-tooltip"
						content={ELEMENT_TOOLTIP_MESSAGE}
						target-ref={state.stageEl}
						manage-opened
						opened={!keyboardDrag && isKeyboardNav && isStageFocused}
					/>
				</main>
			</sn-uib-aria-live>
		);
	},
	properties: {
		appCapabilities: {
			computed({ properties: { applicationType } }) {
				return capabilityMatrix[applicationType] || {};
			}
		},
		isDragging: {
			default: false
		},
		page: {
			default: null
		},
		pageSysId: {
			default: null
		},
		routeName: {
			default: null
		},
		routeState: {
			default: {}
		},
		isPageReadOnly: {
			default: false,
			computed({ properties: { page } }) {
				return !!page && !!page.sysPolicy && page.readOnly;
			}
		},
		disabled: {
			default: false
		},
		isPageCopiable: {
			default: true,
			computed({ properties: { page } }) {
				return !!page && !!page.sysPolicy && page.canCopy;
			}
		},
		isWorkspaceApp: {
			default: true
		},
		isPagesLoading: {
			default: false
		},
		isDefaultPageLayout: {
			default: false
		},
		renderToolbar: {
			default: false
		},
		shouldShowSplashPage: {
			default: false
		},
		selectedElementId: {
			default: null
		},
		rootElementId: {
			default: null
		},
		layoutRenderers: {
			default: {}
		},
		layoutTemplates: {
			default: []
		},
		pageTemplates: {
			default: []
		},
		isKeyboardNav: {
			default: false
		},
		pages: {
			default: []
		},
		flatPage: {
			computed({ properties: { page } }) {
				return createFlagPage(page);
			}
		},
		ElementControlsTagName: {
			computed({ properties: { renderToolbar } }) {
				if (renderToolbar) {
					return 'uib-workspace-element-controls-provider';
				}
				return () => null;
			}
		},
		/**
		 * list of component configs
		 *
		 * @type {Array}
		 */
		toolboxComponents: {
			default: []
		},
		applicationType: {
			default: null
		},
		showLandingPageInfoLink: {
			default: false
		},
		rootElementSelectable: {
			default: false
		},
		actionNames: { default: {} },
		selectedComponent: { default: {} },
		selectedPageHasVariants: {
			default: false
		},
		selectedPage: {
			default: null
		},
		parentInfo: {
			default: null
		},
		isDashboardBuilder: {
			default: false
		},
		isMacroponentReadOnly: {
			default: false
		},
		userScope: {
			default: null
		},
		userDomain: {
			default: null
		},
		supportDomains: {
			default: null
		},
		applications: {
			default: null
		},
		domains: {
			default: null
		},
		selectedPageIsTab: {
			default: false
		},
		isEmptyState: {
			default: false
		},
		pageScopeName: {
			default: null,
			computed({ properties: { page } }) {
				const scopeName = get(page, 'scopeName', null);
				return scopeName !== null ? escapeScopeLabel(scopeName) : null;
			}
		},
		pageScopeId: {
			default: null,
			computed({ properties: { page } }) {
				return get(page, 'scope', null);
			}
		},
		pageDomainName: {
			default: null,
			computed({ properties: { page } }) {
				return get(page, 'domainName', null);
			}
		},
		pageDomainId: {
			default: null,
			computed({ properties: { page } }) {
				return get(page, 'domainId', null);
			}
		},
		pageDefinitionScopeId: {
			default: null,
			computed({ properties: { page } }) {
				return get(page, 'rootElement.scope', null);
			}
		},
		pageDefinitionScopeName: {
			default: null,
			computed({ properties: { page } }) {
				const scopeName = get(page, 'rootElement.scopeName', null);
				return scopeName !== null ? escapeScopeLabel(scopeName) : null;
			}
		},
		isPageStateDirty: {
			default: false,
			selectable: true
		},
		disableStateTransactionOverride: {
			default: false
		},
		pageProtectionPolicy: {
			default: null,
			computed({ properties: { page } }) {
				return get(page, 'sysPolicy', null);
			}
		},
		pageDefProtectionPolicy: {
			default: null,
			computed({ properties: { page } }) {
				return get(page, `rootElement.sysPolicy`, null);
			}
		},
		isDomainExpanded: {
			default: false
		},
		isSharedPageDef: {
			default: false,
			computed({ properties: { page } }) {
				return page && page.usingSharedPageDefinition;
			}
		},
		supportScope: {
			default: false
		}
	},
	actionHandlers: {
		[CHILD_DATA_SET]: ({ action, dispatch }) => {
			dispatch('SN_UIB_PAGE_MANAGEMENT_PANEL#PAGE_CREATE_CLICKED', {
				type: action.payload.value
			});
		},
		['SN_UIB_TRANSACTION_ALERT#COPY_CLICKED']: ({
			action,
			dispatch,
			properties
		}) => {
			action.stopPropagation();

			const { isWorkspaceApp } = properties;

			if (isWorkspaceApp) {
				dispatch('SN_UIB_APP#CLONE_PAGE_REQUESTED');
			} else {
				dispatch('SN_UIB_APP#NEW_VARIANT_MODAL_OPENED', {
					copyFrom: 'current'
				});
			}
		},
		['SN_UIB_PAGE_MANAGEMENT_PANEL#PAGE_CREATE_CLICKED']: ({ dispatch }) => {
			dispatch('SN_UIB_APP#CREATE_PAGE_REQUESTED');
		},
		['SN_UIB_ELEMENT_CONTROLS#TRASH_CONFIRM_BUTTON_CLICKED']: ({
			dispatch,
			action,
			state: {
				properties: { actionNames }
			}
		}) => {
			dispatch(actionNames.DELETED, action.payload);
		},
		['UIB_DND_DROPZONE#DROPPED']: ({ updateState, action: { payload } }) => {
			if (typeof payload.dataTransfer === 'object' && payload.dataTransfer.id) {
				updateState({
					lastDroppedElementId: `${payload.dataTransfer.id}_${cuid()}`
				});
			}
		},
		['EMPTY_SLOT#DROPPED']: ({ updateState, action: { payload } }) => {
			if (payload.parentId) {
				updateState({ lastDroppedElementId: `${payload.parentId}_${cuid()}` });
			}
		},
		['NOW_BUTTON#CLICKED']: ({ action, dispatch }) => {
			const context = get(action, 'payload.context', null);

			if (context === 'screenConfig') {
				dispatch('UIB_STAGE#SCREEN_CONFIG_CLICKED');
				dispatch('COLLAPSIBLE_PANEL#PANEL_TOGGLED', {
					panel: 'page-configuration',
					value: true
				});
			}
		},
		['NOW_BUTTON_BARE#CLICKED']: ({
			action: { stopPropagation },
			state,
			updateState
		}) => {
			stopPropagation();
			const { maximizeStage } = state;
			updateState({
				maximizeStage: !maximizeStage
			});
		},
		['SN_UIB_TRANSACTION_ALERT#UNLOCK_TO_OVERRIDE_CLICKED']: ({
			properties,
			action,
			dispatch
		}) => {
			action.stopPropagation();
			dispatch(UIB_STAGE_UNLOCK_TO_CREATE_OVERRIDE_CLICKED, {
				...properties.page
			});
		},
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ updateState }) => {
			updateState({ stageManagerRef: createRef() });
		},
		// This is to prevent exiting edit mode on a transaction with unsaved changes
		['SN_UIB_TRANSACTION_ALERT#CANCEL_CLICKED']: checkForUnsavedChanges(
			() => {}
		)
	},
	initialState: {
		lastDroppedElementId: null,
		stageEl: null,
		stageManagerRef: createRef(),
		isStageFocused: false,
		maximizeStage: false
	},
	behaviors: [focusBehavior],
	styles
});
