import { snUXFActions } from 'sn-uxf-builder-middleware';
import { forceTransitionTo, forceReplaceWith } from '../../uib-router';
import {
	fetchNewAppConfigEffect,
	flattenQueryResult
} from '../../../utils/appRouteState';
import PAGE_TITLE, { PAGE_TITLE_SUFFIX } from '../../../utils/pageTitle';
import { pageListSorter } from '../../../utils/pageListSort';
import {
	fetchDashboardsListEffect,
	annotateDashboards
} from '../../../utils/dashboards-list';
import { applicationTypes, screenNames } from '../../../utils/routingConstants';
import { flattenAppRoutes } from '../../../utils/appRoutes';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { getElementByProperty } from '../../../utils/tree.js';
import omit from 'lodash/omit';
import { capabilities } from '../../../utils/capabilityMatrix';
import { globalEditConfirmDialogProps } from '../../uib-confirm-dialog/globalEditConfirm';
import { CONFIRM_DIALOG_CONFIRMED } from '../../uib-confirm-dialog';
import { t } from 'sn-translate';
import { getFlaggedPropsAndOutOfSync } from '../../../utils/getFlaggedPropsAndOutOfSync';

const UPDATE_APP_ROUTE_STATE = 'SN_UIB_APP#UPDATE_APP_ROUTE_STATE';
const UPDATE_APP_ROUTE_STATE_REQUESTED =
	'SN_UIB_APP#UPDATE_APP_ROUTE_STATE_REQUESTED';
const UPDATE_APP_ROUTE_STATE_SUCCEEDED =
	'SN_UIB_APP#UPDATE_APP_ROUTE_STATE_SUCCEEDED';
const UPDATE_APP_ROUTE_STATE_FAILED =
	'SN_UIB_APP#UPDATE_APP_ROUTE_STATE_FAILED';
const UPDATE_DASHBOARDS_LIST = 'SN_UIB_APP#UPDATE_DASHBOARDS_LIST';
const UPDATE_DASHBOARDS_LIST_SUCCEEDED =
	'SN_UIB_APP#UPDATE_DASHBOARDS_LIST_SUCCEEDED';
const UPDATE_DASHBOARDS_LIST_FAILED =
	'SN_UIB_APP#UPDATE_DASHBOARDS_LIST_FAILED';
const UPDATE_DASHBOARDS_LIST_STARTED =
	'SN_UIB_APP#UPDATE_DASHBOARDS_LIST_STARTED';

const DASHBOARDS_CREATE_TAB_REQUEST = 'SN_UXF#DASHBOARDS_CREATE_TAB_REQUEST';
const DASHBOARDS_CREATE_TAB_SUCCEEDED =
	'SN_UXF#DASHBOARDS_CREATE_TAB_SUCCEEDED';
const DASHBOARDS_CREATE_TAB_FAILED = 'SN_UXF#DASHBOARDS_CREATE_TAB_FAILED';
const DASHBOARDS_CREATE_TAB_STARTED = 'SN_UXF#DASHBOARDS_CREATE_TAB_STARTED';

let alertTimeout = null;

const successAlertHandler = (
	successMsg,
	addlPropertiesToUpdate = {},
	postAlertAction = () => {},
	updateAppRouteMeta = {}
) =>
	alertHandler(
		true,
		successMsg,
		addlPropertiesToUpdate,
		postAlertAction,
		updateAppRouteMeta
	);

const unsavedChangesPostAlertAction = ({ state, updateState }) => {
	const { unsavedChangesCheckCallbackEvent } = state;

	if (unsavedChangesCheckCallbackEvent !== null) {
		const {
			callbackAction,
			callbackPayload
		} = unsavedChangesCheckCallbackEvent;

		updateState({
			unsavedChangesCheckCallbackEvent: {
				callbackAction,
				callbackPayload: {
					...callbackPayload,
					dispatchActionImmediately: true
				}
			}
		});
	}
};

const failureAlertHandler = (
	successMsg,
	addlPropertiesToUpdate = {},
	postAlertAction = () => {}
) => alertHandler(false, successMsg, addlPropertiesToUpdate, postAlertAction);

const alertHandler = (
	success,
	successMsg,
	addlPropertiesToUpdate = {},
	postAlertAction = () => {},
	updateAppRouteMeta = {}
) => (coeffects) => {
	const { dispatch, updateProperties, properties } = coeffects;
	if (alertTimeout) {
		clearTimeout(alertTimeout);
	}

	alertTimeout = setTimeout(() => {
		dispatch('SN_UIB_APP#ALERT_CLEARED_BY_TIMEOUT');
	}, 5000);

	const { keepInterstitial } = updateAppRouteMeta;

	const interstitialUpdate = keepInterstitial
		? {}
		: {
				showPageInterstitial: false,
				pageInterstitialType: null,
				pageInterstitialContent: ''
		  };

	updateProperties({
		alertProps: successMsg
			? {
					action: {
						type: 'dismiss'
					},
					content: successMsg,
					status: success ? 'positive' : 'warning',
					type: 'save',
					icon: success ? 'circle-check-outline' : 'circle-exclamation-outline'
			  }
			: null,
		...interstitialUpdate,
		...addlPropertiesToUpdate
	});

	const { appsSysId, applicationType } = properties;
	if (
		applicationType === applicationTypes.app ||
		applicationType === applicationTypes.components
	) {
		dispatch(
			UPDATE_APP_ROUTE_STATE,
			{ sysId: appsSysId },
			{ ...updateAppRouteMeta }
		);
	} else if (applicationType === applicationTypes.dashboards) {
		dispatch(UPDATE_DASHBOARDS_LIST, {}, { ...updateAppRouteMeta });
	}

	postAlertAction(coeffects);
};

const interstitialHandler = (contentFn, showLoadingModal) => (coeffects) => {
	const { updateProperties } = coeffects;

	updateProperties({
		showDeleteConfirmation: false,
		deleteConfirmationProps: null,
		showPageInterstitial: true,
		pageInterstitialType: showLoadingModal ? 'modal' : 'stage',
		pageInterstitialContent: contentFn(coeffects)
	});
};

const clearedPageInterstitial = {
	showPageInterstitial: false,
	pageInterstitialType: null,
	pageInterstitialContent: null
};

export const uxfHandlers = {
	[snUXFActions.SHOW_FAILURE_ALERT]: (coeffects) => {
		const {
			action: {
				stopPropagation,
				payload: { message }
			}
		} = coeffects;
		stopPropagation();
		failureAlertHandler(message)(coeffects);
	},
	[snUXFActions.PAGE_CREATE_REQUESTED]: interstitialHandler(({ action }) => {
		const {
			payload: { name }
		} = action;
		return t('Creating {0} page', name);
	}),
	[snUXFActions.PAGE_COPY_REQUESTED]: interstitialHandler(({ action }) => {
		const {
			payload: { name }
		} = action;
		return t('Creating {0} page', name);
	}),
	[snUXFActions.METADATA_UPDATE_REQUESTED]: interstitialHandler(
		({ action }) => {
			const {
				payload: { name }
			} = action;
			return t('Updating {0} page', name);
		}
	),
	[snUXFActions.PAGE_DELETE_REQUESTED]: interstitialHandler(({ action }) => {
		const {
			payload: { name }
		} = action;
		return t('Deleting {0} page', name);
	}),
	[snUXFActions.PAGE_DELETE_SUCCEEDED]: ({
		action,
		dispatch,
		properties,
		updateProperties
	}) => {
		const {
			appsSysId,
			routeStatus,
			applicationType,
			pages,
			pageState
		} = properties;
		const { deleteSysId } = action.meta;

		alertTimeout = setTimeout(() => {
			dispatch('SN_UIB_APP#ALERT_CLEARED_BY_TIMEOUT');
		}, 5000);

		updateProperties({
			routeStatus: {
				...routeStatus,
				isDeletingPage: true
			},
			alertProps: {
				action: {
					type: 'dismiss'
				},
				content: t('Page deleted'),
				status: 'positive'
			},
			showDeleteConfirmation: false,
			deleteConfirmationProps: null,
			...clearedPageInterstitial,
			selectedElementId: null
		});

		if (deleteSysId === pageState.sysId) {
			const updatedPages = pages.filter(({ sysId }) => sysId !== deleteSysId);
			const transitionId =
				updatedPages.length > 0 ? updatedPages[0].sysId : null;
			if (transitionId !== null) {
				forceTransitionTo(`${applicationType}.page`, {
					appsSysId,
					deleteSysId,
					pageSysId: transitionId
				});
			} else {
				forceTransitionTo(`${applicationType}`, { appsSysId, deleteSysId });
			}
		}
	},
	[snUXFActions.PAGE_UPDATE_STARTED]: interstitialHandler(
		({ properties, action }) => {
			const {
				pageState: { name },
				pageInterstitialContent
			} = properties;
			const { customStartMessage, noInterstitialMessage } = action.meta;
			return noInterstitialMessage
				? pageInterstitialContent
				: customStartMessage || t('Saving {0}...', name);
		}
	),
	[snUXFActions.PAGE_UPDATE_SUCCEEDED]: (coeffects) => {
		const { dispatch } = coeffects;
		const { applicationType } = coeffects.properties;
		const { customSuccessMessage } = coeffects.action.meta;
		const pageType = {
			[applicationTypes.workspace]: 'Page',
			[applicationTypes.app]: 'Variant',
			[applicationTypes.components]: 'Variant',
			[applicationTypes.dashboards]: 'Dashboard'
		}[applicationType];

		if (coeffects.action.meta.forceActivate === true) {
			successAlertHandler(
				customSuccessMessage || t('{0} saved and activated', pageType),
				{
					showActivationConfirmation: false
				},
				unsavedChangesPostAlertAction
			)(coeffects);
		} else if (coeffects.action.meta.forceDeactivate === true) {
			successAlertHandler(
				customSuccessMessage || t('{0} saved and deactivated', pageType),
				{
					showActivationConfirmation: false
				},
				unsavedChangesPostAlertAction
			)(coeffects);
		} else {
			successAlertHandler(
				customSuccessMessage || null,
				{
					showActivationConfirmation: false
				},
				unsavedChangesPostAlertAction
			)(coeffects);
		}

		if (applicationType === applicationTypes.dashboards) {
			dispatch(UPDATE_DASHBOARDS_LIST);
		}
	},
	[snUXFActions.PAGE_UPDATE_FAILED]: failureAlertHandler(
		t('Failed to save page'),
		{
			showActivationConfirmation: false
		}
	),
	[snUXFActions.METADATA_UPDATE_FAILED]: ({ updateProperties }) => {
		updateProperties({
			showPageInterstitial: false,
			pageInterstitialType: null,
			pageInterstitialContent: ''
		});
	},
	[snUXFActions.METADATA_UPDATE_SUCCEEDED]: ({
		updateProperties,
		action,
		properties,
		dispatch
	}) => {
		const {
			result: { isActive, name }
		} = action.payload;

		const handler = successAlertHandler(t('{0} updated', name));
		handler({ dispatch, updateProperties, properties });
	},
	[snUXFActions.PAGE_LIST_REQUESTED]: ({ updateProperties }) => {
		updateProperties({
			isPagesLoading: true
		});
	},
	[snUXFActions.PAGE_LIST_REQUEST_SUCCEEDED]: ({
		updateProperties,
		properties,
		action
	}) => {
		const { routeName, routeState } = properties;
		const { result: pages = [] } = action.payload;

		updateProperties({
			isPagesLoading: false,
			isPagePresent: !!pages.length
		});

		if (pages.length === 0) {
			return;
		}

		const isPageIdPresent = pages.find(
			(page) => page.id === routeState.pageSysId
		);

		switch (routeName) {
			case screenNames.workspaceHome:
				forceReplaceWith(screenNames.workspacePage, {
					appsSysId: routeState.appsSysId,
					pageSysId: pages.sort(pageListSorter)[0].id
				});
				break;

			case screenNames.workspacePage:
				// Not strictly necessary, but forces an update to page title on load
				if (isPageIdPresent) {
					forceReplaceWith(screenNames.workspacePage, routeState);
				} else {
					forceReplaceWith(screenNames.workspacePage, {
						...routeState,
						show404: true
					});
				}
				break;
		}
	},
	[snUXFActions.PAGE_CREATE_SUCCEEDED]: ({
		action,
		state,
		properties,
		updateProperties,
		dispatch
	}) => {
		const { appsSysId } = state.properties;
		const { id } = action.payload.result;

		updateProperties({
			routeStatus: {
				...properties.routeStatus,
				isCreatingPage: true
			},
			showPageInterstitial: false,
			pageInterstitialType: null,
			pageInterstitialContent: ''
		});

		forceTransitionTo(screenNames.workspacePage, { appsSysId, pageSysId: id });
	},
	[snUXFActions.PAGE_COPY_SUCCEEDED]: ({
		action,
		state,
		properties,
		updateProperties,
		dispatch
	}) => {
		const { appsSysId, applicationType } = state.properties;
		const { id } = action.payload.result;

		updateProperties({
			routeStatus: {
				...properties.routeStatus,
				isCreatingPage: true
			},
			showPageInterstitial: false,
			pageInterstitialType: null,
			pageInterstitialContent: ''
		});

		forceTransitionTo(`${applicationType}.page`, { appsSysId, pageSysId: id });
	},
	[snUXFActions.PAGE_REQUEST_HTTP_SUCCEEDED]: ({
		action,
		dispatch,
		properties: {
			appRoutes,
			pageSysId,
			appsSysId,
			isComponentBuilder,
			isDashboardBuilder
		},
		updateProperties
	}) => {
		const { result } = action.payload;

		if (result.name) {
			PAGE_TITLE.set(result.name, PAGE_TITLE_SUFFIX);
		}

		let propsToUpdate = {
			isPagesLoading: false
		};

		if (!isDashboardBuilder && !isComponentBuilder) {
			const route = flattenAppRoutes(appRoutes).find(
				(route) => get(route, 'page.sysId') === pageSysId
			);
			const isSharedRoute = route && route.appConfig !== appsSysId;

			if (isSharedRoute) {
				propsToUpdate = {
					...propsToUpdate,
					showConfirmDialog: true,
					confirmDialogProps: globalEditConfirmDialogProps(true)
				};
			}
		}

		updateProperties(propsToUpdate);

		dispatch('UIB_APP#RESET_ROUTE_STATUS');
	},
	[snUXFActions.PAGE_REQUEST_HTTP_FAILED]: ({ properties, dispatch }) => {
		const {
			applicationType,
			routeState: { appsSysId, pageSysId }
		} = properties;
		if (applicationType === applicationTypes.workspace) {
			forceTransitionTo(screenNames.workspaceHome, {
				appsSysId
			});
		} else if (
			applicationType === applicationTypes.app ||
			applicationType === applicationTypes.components
		) {
			forceTransitionTo(screenNames.appPage, {
				appsSysId,
				pageSysId: pageSysId
			});
		} else {
			forceTransitionTo(screenNames.dashboardsHome, {});
			dispatch('UIB_APP#RESET_ROUTE_STATUS');
		}
	},
	[snUXFActions.ISOLATED_EDIT_MODE_REQUEST_SUCCEEDED]: ({
		updateProperties
	}) => {
		updateProperties({
			isPagesLoading: false
		});
	},
	[snUXFActions.ISOLATED_EDIT_MODE_REQUEST_FAILED]: (coeffects) => {
		const { updateProperties } = coeffects;
		updateProperties({
			isPagesLoading: false
		});

		failureAlertHandler(
			t('Failed to load tab in isolation mode.'),
			{}
		)(coeffects);
	},
	[snUXFActions.PAGE_COMPONENTS_REQUESTED]: ({ dispatch }) => {
		dispatch('UIB_APP#LOAD_TABLES');
	},
	[snUXFActions.NEW_PAGE_CREATE_SUCCEEDED]: (coeffects) => {
		const pageSysId = get(coeffects, 'action.payload.result.screenType', '');
		const variantSysId = get(coeffects, 'action.payload.result.id', '');
		successAlertHandler(
			null,
			{},
			({ action, properties, dispatch }) => {
				const {
					appsSysId,
					createAsTab,
					selectedPageIsTab,
					rawParentInfo,
					routeState
				} = properties;
				const {
					id: variantSysId,
					screenType: pageSysId
				} = action.payload.result;

				const {
					request: {
						data: { parentMacroponentCompositionElementId }
					}
				} = action.meta;

				let parentVariantSysId = get(routeState, 'variantSysId', '');
				if (selectedPageIsTab) {
					parentVariantSysId = get(rawParentInfo, 'screenId', '');
				}
				dispatch('SN_UIB_APP#SET_BYPASS_UNSAVED_CHANGED_DIALOG', {
					bypassUnsavedChangesDialog: true
				});
				setTimeout(() => {
					const payload = {
						appsSysId,
						pageSysId,
						variantSysId
					};

					if (createAsTab) {
						payload.queryParams = {
							parentScreenId: parentVariantSysId,
							parentElementId: parentMacroponentCompositionElementId
						};
					}
					forceTransitionTo(screenNames.appVariant, payload);
				}, 0);
			},
			{
				successModalType: 'page',
				openSuccessModal: true,
				pageSysId,
				variantSysId
			}
		)(coeffects);
	},
	[snUXFActions.NEW_PAGE_SAVE_STARTED]: (coeffects) => {
		const {
			meta: { showLoadingModal }
		} = coeffects.action;
		const { selectedPageIsTab } = coeffects.properties;
		interstitialHandler(({ action }) => {
			const {
				request: {
					data: { name }
				},
				routeConfigType
			} = action.meta;
			switch (routeConfigType) {
				case 'fields':
					return t('Saving required parameters...');
				case 'parameters':
					return t('Saving optional parameters...');
			}
			return t('Saving {0} {1}...', selectedPageIsTab ? 'tab' : 'page', name);
		}, showLoadingModal)(coeffects);
	},
	[snUXFActions.NEW_PAGE_SAVE_SUCCEEDED]: (coeffects) => {
		const {
			action,
			properties: { selectedPageIsTab }
		} = coeffects;
		const { routeConfigType, returnToSuccessModal } = action.meta;

		let successMessage = t('{0} updated', selectedPageIsTab ? 'Tab' : 'Page');
		let subHeaderContent;
		if (routeConfigType === 'fields') {
			successMessage = t('Changes to required parameters have been saved');
			/* prettier-ignore */
			subHeaderContent = t('Required parameters have been added. Do you want to do anything else?');
		} else if (routeConfigType === 'parameters') {
			successMessage = t('Changes to optional parameters have been saved');
			/* prettier-ignore */
			subHeaderContent = t('Optional parameters have been added. Do you want to do anything else?');
		}
		const meta = returnToSuccessModal
			? {
					openSuccessModal: true,
					currentTab: 'page',
					keepInterstitial: true,
					subHeaderContent
			  }
			: {};
		successAlertHandler(
			returnToSuccessModal ? null : successMessage,
			{
				showActivationConfirmation: false,
				pageConfigurationModalInfo: null
			},
			({ state, dispatch, updateState }) => {
				const { unsavedChangesCheckCallbackEvent } = state;

				if (unsavedChangesCheckCallbackEvent !== null) {
					const {
						callbackAction,
						callbackPayload
					} = unsavedChangesCheckCallbackEvent;

					dispatch(callbackAction, callbackPayload);
					updateState({
						unsavedChangesCheckCallbackEvent: null
					});
				}
			},
			meta
		)(coeffects);
	},
	[snUXFActions.NEW_PAGE_DELETE_STARTED]: interstitialHandler(
		({ action, properties: { selectedPageIsTab } }) => {
			const { name } = action.meta;
			return t('Deleting {0} {1}...', selectedPageIsTab ? 'tab' : 'page', name);
		}
	),

	[snUXFActions.NEW_PAGE_DELETE_SUCCEEDED]: (coeffects) => {
		const {
			action: { stopPropagation },
			properties: { selectedPageIsTab }
		} = coeffects;
		stopPropagation();
		successAlertHandler(
			selectedPageIsTab ? t('Tab deleted') : t('Page deleted'),
			{
				showDeleteConfirmation: false,
				deleteConfirmationProps: null,
				showPageInterstitial: false,
				pageInterstitialType: null,
				pageInterstitialContent: null
			},
			({
				action: {
					stopPropagation,
					meta: { pageSysId }
				},
				properties: {
					appsSysId,
					applicationType,
					appRoutes,
					currentPages,
					selectedPageIsTab,
					queryParams
				},
				state: { parentInfo }
			}) => {
				stopPropagation();
				let updatedAppRoutes = [];
				let updatedCurrentPages = [];

				if (selectedPageIsTab) {
					updatedCurrentPages = currentPages.filter(
						({ id }) => id !== pageSysId
					);
				} else {
					updatedAppRoutes = appRoutes.filter(
						({ page: { sysId } }) => sysId !== pageSysId
					);
				}

				// page pr tab was deleted,
				// navigate to first variant in first page or tab
				// that has a variant
				routeToNextValidState({
					routes: selectedPageIsTab ? updatedCurrentPages : updatedAppRoutes,
					selectedPageIsTab,
					appsSysId,
					applicationType,
					parentInfo
				});
			}
		)(coeffects);
	},
	[snUXFActions.NEW_PAGE_DELETE_FAILED]: (coeffects) => {
		const { selectedPageIsTab } = coeffects.properties;

		failureAlertHandler(
			t('Failed to delete {0}', selectedPageIsTab ? 'tab' : 'page'),
			{
				showDeleteConfirmation: false,
				deleteConfirmationProps: null
			}
		)(coeffects);
	},
	[snUXFActions.PAGE_DELETE_FAILED]: failureAlertHandler(
		t('Failed to delete page')
	),
	[snUXFActions.NEW_PAGE_CREATE_FAILED]: (coeffects) => {
		const { selectedPageIsTab } = coeffects.properties;

		failureAlertHandler(
			t('Failed to create {0}', selectedPageIsTab ? 'tab' : 'page'),
			{
				showNewPageModal: false,
				isDynamicTabCreateClicked: false
			}
		)(coeffects);
	},
	[snUXFActions.PAGE_CREATE_FAILED]: failureAlertHandler(
		t('Failed to create page')
	),
	[snUXFActions.NEW_PAGE_SAVE_FAILED]: (coeffects) => {
		const { selectedPageIsTab } = coeffects.properties;

		failureAlertHandler(
			t('Failed to update {0}', selectedPageIsTab ? 'tab' : 'page'),
			{
				successModalInfo: null
			}
		)(coeffects);
	},
	[snUXFActions.NEW_PAGE_VARIANT_CREATE_SUCCEEDED]: (coeffects) => {
		// To clear variant cache
		successAlertHandler(
			null,
			{},
			({ action, state, dispatch }) => {
				const { appsSysId } = state.properties;
				const id = action.payload.result.id;
				const { pageSysId } = action.meta;
				dispatch('SN_UIB_APP#SET_BYPASS_UNSAVED_CHANGED_DIALOG', {
					bypassUnsavedChangesDialog: true
				});

				setTimeout(() => {
					forceTransitionTo(screenNames.appVariant, {
						appsSysId,
						pageSysId,
						variantSysId: id
					});
				}, 0);
			},
			{
				successModalType: 'variant',
				openSuccessModal: true,
				variantSysId: get(coeffects, 'action.payload.result.id')
			}
		)(coeffects);
	},
	[snUXFActions.NEW_PAGE_VARIANT_CREATE_OVERRIDE_STARTED]: ({
		updateProperties
	}) =>
		updateProperties({
			isLoading: true
		}),
	[snUXFActions.NEW_PAGE_VARIANT_CREATE_OVERRIDE_SUCCEEDED]: successAlertHandler(
		null,
		{
			isLoading: false
		},
		({ action, state, updateProperties }) => {
			const { routeState } = state.properties;
			const id = action.payload.result.id;
			forceTransitionTo(screenNames.appVariant, {
				...routeState,
				variantSysId: id
			});
		},
		{
			successModalType: 'variant',
			openSuccessModal: true
		}
	),
	[snUXFActions.NEW_PAGE_VARIANT_CREATE_OVERRIDE_FAILED]: failureAlertHandler(
		t('Failed to create screen override'),
		{
			isLoading: false
		}
	),
	[snUXFActions.NEW_PAGE_VARIANT_CREATE_FAILED]: failureAlertHandler(
		t('Failed to create variant'),
		{
			newVariantModalInfo: null
		}
	),
	[snUXFActions.NEW_PAGE_VARIANT_DELETE_STARTED]: interstitialHandler(
		({ action }) => {
			const { name } = action.meta;
			return t('Deleting variant {0}...', name);
		}
	),
	[snUXFActions.NEW_PAGE_VARIANT_DELETE_SUCCEEDED]: successAlertHandler(
		t('Variant deleted'),
		{
			showDeleteConfirmation: false,
			deleteConfirmationProps: null
		},
		({ state, action }) => {
			const {
				appsSysId,
				applicationType,
				appRoutes,
				routeState: { queryParams }
			} = state.properties;
			const { pageSysId, variantSysId } = action.meta;

			const currentRoute = flattenAppRoutes(appRoutes).find(
				(route) => route.page.sysId === pageSysId
			);
			if (!currentRoute) {
				routeToNextValidState({
					routes: appRoutes,
					appsSysId,
					applicationType,
					deletedVariantId: variantSysId,
					queryParams
				});
				return;
			}
			const currentPageOtherVariant = currentRoute.page.variants.find(
				(v) => v.sysId !== variantSysId
			);

			if (currentPageOtherVariant) {
				forceTransitionTo(screenNames.appVariant, {
					appsSysId,
					pageSysId,
					variantSysId: currentPageOtherVariant.sysId,
					queryParams
				});
			} else {
				forceTransitionTo(screenNames.appPage, {
					queryParams,
					appsSysId,
					pageSysId
				});
			}
		}
	),
	[snUXFActions.NEW_PAGE_VARIANT_DELETE_FAILED]: failureAlertHandler(
		t('Failed to delete variant'),
		{
			showDeleteConfirmation: true,
			deleteConfirmationProps: null
		}
	),
	[UPDATE_APP_ROUTE_STATE]: ({ action, state, properties, dispatch }) => {
		const { applicationType } = properties;
		const sysparm_query_no_domain =
			action.payload.sysparm_query_no_domain !== undefined
				? action.payload.sysparm_query_no_domain
				: state.isDomainExpanded;

		if (
			applicationType === applicationTypes.app ||
			applicationType === applicationTypes.components
		) {
			dispatch(
				UPDATE_APP_ROUTE_STATE_REQUESTED,
				{
					...action.payload,
					sysparm_query_no_domain
				},
				action.meta
			);
		}
	},
	[UPDATE_APP_ROUTE_STATE_REQUESTED]: fetchNewAppConfigEffect(
		UPDATE_APP_ROUTE_STATE_SUCCEEDED,
		UPDATE_APP_ROUTE_STATE_FAILED
	),
	[UPDATE_APP_ROUTE_STATE_SUCCEEDED]: ({
		updateProperties,
		action,
		properties
	}) => {
		let page, screen;
		const flattenedResult = flattenQueryResult(action.payload.result);
		const {
			openSuccessModal,
			successModalType,
			currentTab,
			keepInterstitial: removeInterstitialNow,
			subHeaderContent,
			pageSysId,
			variantSysId
		} = action.meta;
		const { routeState, successModalInfo = {} } = properties;
		const appRoutes = flattenedResult.appConfig.routes;
		if (openSuccessModal) {
			page = flattenAppRoutes(appRoutes).find(
				({ page }) => page.sysId === (pageSysId || routeState.pageSysId)
			);
			screen = page.page.variants.find(
				({ sysId }) => sysId === (variantSysId || routeState.variantSysId)
			);
		}
		const interstitialUpdate = removeInterstitialNow
			? {
					showPageInterstitial: false,
					pageInterstitialType: null,
					pageInterstitialContent: ''
			  }
			: {};
		const newProps = openSuccessModal
			? {
					appRoutes,
					showNewPageModal: false,
					isDynamicTabCreateClicked: false,
					newVariantModalInfo: null,
					successModalInfo: {
						successModalType,
						...successModalInfo,
						currentTab: currentTab || successModalType,
						page,
						screen,
						subHeaderContent,
						render: true
					},
					...interstitialUpdate
			  }
			: {
					appRoutes,
					...interstitialUpdate
			  };

		updateProperties({
			...newProps
		});
	},
	[UPDATE_APP_ROUTE_STATE_FAILED]: failureAlertHandler(
		t('Failed to fetch app route data')
	),
	[UPDATE_DASHBOARDS_LIST]: fetchDashboardsListEffect(
		UPDATE_DASHBOARDS_LIST_SUCCEEDED,
		UPDATE_DASHBOARDS_LIST_FAILED,
		UPDATE_DASHBOARDS_LIST_STARTED
	),
	[UPDATE_DASHBOARDS_LIST_STARTED]: interstitialHandler(() =>
		t('Refreshing Dashboards List...')
	),
	[UPDATE_DASHBOARDS_LIST_SUCCEEDED]: ({
		updateProperties,
		action,
		dispatch
	}) => {
		updateProperties({
			appRoutes: annotateDashboards(action.payload.result.dashboards),
			...clearedPageInterstitial,
			isPagesLoading: false
		});
		if (action.meta && action.meta.routeName) {
			const { routeName, routeState } = action.meta;
			forceTransitionTo(routeName, routeState);
		}
		dispatch('UIB_APP#RESET_ROUTE_STATUS');
	},
	[UPDATE_DASHBOARDS_LIST_FAILED]: failureAlertHandler(
		t('Failed to update dashboards list')
	),
	[snUXFActions.ELEMENT_ID_CHANGED]: ({ action, updateProperties }) => {
		updateProperties({ selectedElementId: action.payload.id });
	},
	[snUXFActions.DATA_BROKER_INSTANCE_ID_CHANGED]: ({
		action,
		updateProperties
	}) => {
		updateProperties({ selectedDataBrokerId: action.payload.id });
	},
	[snUXFActions.SCREEN_PROPERTIES_UPDATE_STARTED]: interstitialHandler(
		({ action }) => {
			const {
				meta: { pageName, configType }
			} = action;
			return configType === 'conditions'
				? t('Saving Conditions...')
				: t('Saving {0}...', pageName);
		},
		true
	),
	[snUXFActions.SCREEN_PROPERTIES_UPDATE_SUCCEEDED]: (coeffects) => {
		const {
			pageName,
			configType,
			returnToSuccessModal
		} = coeffects.action.meta;
		const successMessage =
			configType === 'conditions'
				? t('Changes to Conditions have been saved')
				: t('Changes to {0} have been saved', pageName);

		const meta = returnToSuccessModal
			? {
					openSuccessModal: true,
					currentTab: 'variant',
					keepInterstitial: true,
					/* prettier-ignore */
					subHeaderContent: t('Screen conditions have been set. Do you want to do anything else?')
			  }
			: {};

		successAlertHandler(
			returnToSuccessModal ? null : successMessage,
			{
				showActivationConfirmation: false
			},
			() => {},
			meta
		)(coeffects);
	},
	[snUXFActions.SCREEN_PROPERTIES_UPDATE_FAILED]: failureAlertHandler(
		t('Failed to save variant'),
		{
			showActivationConfirmation: false,
			successModalInfo: null
		}
	),
	[snUXFActions.DASHBOARDS_SCREENS_LOADED]: ({ updateProperties }) => {
		updateProperties({
			isPagesLoading: false
		});
	},
	[snUXFActions.DASHBOARDS_CREATE_REQUEST_SUCCEEDED]: successAlertHandler(
		t('Dashboard created'),
		{},
		({ action, updateProperties, properties }) => {
			const {
				result: { id }
			} = action.payload;
			updateProperties({
				appRoutes: [...properties.appRoutes, action.payload.result]
			});
			setTimeout(() => {
				forceTransitionTo(screenNames.dashboardsDashboard, { dashboardId: id });
			}, 0);
		}
	),
	[snUXFActions.DASHBOARDS_CREATE_REQUEST_FAILED]: failureAlertHandler(
		t('Failed to create Dashboard')
	),
	[snUXFActions.DASHBOARDS_CREATE_REQUEST_STARTED]: interstitialHandler(
		({ action }) => {
			const {
				meta: {
					request: {
						data: { name }
					}
				}
			} = action;
			return t('Creating {0}...', name);
		}
	),
	[snUXFActions.DASHBOARDS_EDIT_REQUEST_SUCCEEDED]: (coeffects) => {
		const {
			action: { stopPropagation },
			properties: { selectedPageIsTab }
		} = coeffects;
		stopPropagation();
		successAlertHandler(
			t('{0} saved', selectedPageIsTab ? 'Tab' : 'Dashboard'),
			{}
		)(coeffects);
	},
	[snUXFActions.DASHBOARDS_EDIT_REQUEST_FAILED]: (coeffects) => {
		const {
			action: { stopPropagation },
			properties: { selectedPageIsTab }
		} = coeffects;
		stopPropagation();
		failureAlertHandler(
			t('Failed to save {0}', selectedPageIsTab ? 'Tab' : 'Dashboard'),
			{}
		)(coeffects);
	},
	[snUXFActions.DASHBOARDS_EDIT_REQUEST_STARTED]: interstitialHandler(
		({ action }) => {
			const {
				meta: { pageName }
			} = action;
			return t('Saving {0}...', pageName);
		}
	),
	[snUXFActions.DASHBOARDS_DELETE_REQUEST_SUCCEEDED]: (coeffects) => {
		const {
			action: { stopPropagation },
			properties: { selectedPageIsTab }
		} = coeffects;
		stopPropagation();
		successAlertHandler(
			t('{0} deleted', selectedPageIsTab ? 'Tab' : 'Dashboard'),
			{},
			({ action, properties, state: { parentInfo } }) => {
				const {
					currentPages,
					routeState: { dashboardId }
				} = properties;
				const {
					meta: { deletedDashboardId }
				} = action;
				const nextState = nextDashboardState({
					deletedDashboardId,
					dashboardId,
					currentPages,
					selectedPageIsTab,
					parentInfo
				});
				if (nextState) {
					forceTransitionTo(nextState.routeName, nextState.routeState);
				}
			}
		)(coeffects);
	},
	[snUXFActions.DASHBOARDS_DELETE_REQUEST_FAILED]: failureAlertHandler(
		t('Failed to delete Dashboard')
	),
	[snUXFActions.DASHBOARDS_DELETE_REQUEST_STARTED]: interstitialHandler(
		({ properties, updateProperties }) => {
			const {
				routeState: { dashboardId },
				pages
			} = properties;
			const pageName =
				(pages.find((page) => page._appRouteId === dashboardId) || {}).name ||
				'Dashboard';
			updateProperties({
				showDeleteConfirmation: false,
				deleteConfirmationProps: null
			});
			return t('Deleting {0}...', pageName);
		}
	),
	[snUXFActions.PAGE_DATA_RECEIVED]: ({ updateProperties, properties }) => {
		const {
			routeState = {},
			appCapabilities,
			appRoutes,
			selectedPageId
		} = properties;
		const selectedElementId = get(routeState, 'queryParams.selectedElement');
		const pageState = get(properties, 'pageState');
		const pageProperties = get(pageState, 'rootElement.macroponentProperties');

		const { outOfSyncFields: outOfSync } =
			pageState && !pageState.usingSharedPageDefinition
				? getFlaggedPropsAndOutOfSync(
						appCapabilities[capabilities.routeFieldsParams],
						appRoutes,
						selectedPageId,
						pageProperties
				  )
				: { outOfSyncFields: null };

		if (
			selectedElementId &&
			pageState &&
			properties.selectedElementId !== selectedElementId
		) {
			const component = get(
				getElementByProperty(pageState.rootElement, selectedElementId, 'id'),
				'component'
			);
			if (component) {
				const modifiedRouteState = omit(
					routeState,
					'queryParams.selectedElement'
				);
				updateProperties({
					selectedViewportElement: { id: selectedElementId, component },
					routeState: modifiedRouteState,
					syncDialogProps: outOfSync
						? { opened: true, outOfSync, name: pageState.name }
						: null
				});
			}
		} else {
			updateProperties({
				selectedViewportElement: null,
				syncDialogProps: !isEmpty(outOfSync)
					? { opened: true, outOfSync, name: pageState.name }
					: null
			});
		}
	},
	[DASHBOARDS_CREATE_TAB_STARTED]: interstitialHandler(({ action }) => {
		const {
			meta: {
				request: {
					data: { name }
				}
			}
		} = action;
		return t('Creating {0}...', name);
	}),
	[DASHBOARDS_CREATE_TAB_FAILED]: failureAlertHandler(
		t('Failed to create Tab')
	),
	[DASHBOARDS_CREATE_TAB_SUCCEEDED]: successAlertHandler(
		t('Tab created'),
		{},
		({ action }) => {
			const {
				result: {
					id,
					macroponents: [screen, ...rest]
				}
			} = action.payload;
			setTimeout(() => {
				forceTransitionTo(screenNames.dashboardsDashboard, {
					dashboardId: id,
					screenId: screen.id,
					isNewlyCreatedTab: true
				});
			}, 0);
		}
	),
	[snUXFActions.DASHBOARDS_CREATE_OVERRIDE_REQUEST_STARTED]: ({
		updateProperties
	}) =>
		updateProperties({
			isLoading: true
		}),
	[snUXFActions.DASHBOARDS_CREATE_OVERRIDE_REQUEST_SUCCEEDED]: successAlertHandler(
		t('Override create succeeded'),
		{
			isLoading: false
		},
		({ action, properties }) => {
			const { routeState } = properties;
			const overrideId = action.payload.result.id;
			forceTransitionTo(screenNames.dashboardsDashboard, {
				dashboardId: routeState.dashboardId,
				overrideId,
				queryParams: {
					overrideId
				}
			});
		}
	),
	[snUXFActions.DASHBOARDS_CREATE_OVERRIDE_REQUEST_FAILED]: failureAlertHandler(
		t('Failed to create dashboard override'),
		{
			isLoading: false
		}
	),
	['UIB_APP#SHOW_SHARED_ROUTE_ALERT']: (coeffects) => {
		const {
			action: { stopPropagation },
			updateProperties
		} = coeffects;
		stopPropagation();

		updateProperties({
			showConfirmDialog: true,
			confirmDialogProps: {
				headerLabel: t('Shared route'),
				message: (
					<div>
						<p>{t('This is a shared route.')}</p>
						<p>
							{
								/* prettier-ignore */
								t('Any changes to this route will be automatically applied across all experience, page, variant, or macroponent.')
							}
						</p>
					</div>
				),
				size: 'sm',
				footerActions: [
					{
						label: t('Okay'),
						variant: 'primary',
						clickActionType: CONFIRM_DIALOG_CONFIRMED
					}
				]
			}
		});
	}
};

const nextDashboardState = ({
	deletedDashboardId,
	dashboardId,
	currentPages,
	selectedPageIsTab,
	parentInfo
}) => {
	const nextDashboard = currentPages.filter(
		(route) => route.id !== deletedDashboardId
	)[0];
	if (nextDashboard) {
		return nextDashboard.id === dashboardId
			? {}
			: {
					routeName: screenNames.dashboardsDashboard,
					routeState: { dashboardId: nextDashboard.id }
			  };
	} else {
		if (selectedPageIsTab) {
			return {
				routeName: screenNames.dashboardsHome,
				routeState: {
					queryParams: {
						parentScreenId: parentInfo.screenId,
						isolatedEditMode: true,
						parentElementId: parentInfo.compositionElementId
					}
				}
			};
		}
		return { routeName: screenNames.dashboardsHome };
	}
};

const routeToNextValidState = ({
	routes,
	selectedPageIsTab,
	appsSysId,
	deletedVariantId,
	parentInfo,
	queryParams = {}
}) => {
	// we need to perform similar functionality for tabs and pages
	// but they use a slightly different structure so adjusting for that
	// with the path variables below
	const variantsPath = selectedPageIsTab ? 'children' : 'page.variants';
	const pageSysIdPath = selectedPageIsTab ? 'pageSysId' : 'page.sysId';
	const variantIdPath = selectedPageIsTab ? 'id' : 'sysId';

	const firstRouteWithVariant = routes.find(
		(route) =>
			get(route, variantsPath, []).filter(
				(v) =>
					!deletedVariantId || get(v, variantIdPath, '') !== deletedVariantId
			).length > 0
	);
	const firstRoute = routes[0];
	if (firstRouteWithVariant) {
		const variantToSelect = get(firstRouteWithVariant, variantsPath, []).find(
			(v) => !deletedVariantId || get(v, variantIdPath, '') !== deletedVariantId
		);
		const variantSysId = get(variantToSelect, variantIdPath, '');
		const pageSysId = get(firstRouteWithVariant, pageSysIdPath, '');
		forceTransitionTo(screenNames.appVariant, {
			appsSysId,
			pageSysId,
			variantSysId,
			queryParams
		});
	} else if (firstRoute) {
		const pageSysId = get(firstRoute, pageSysIdPath, '');
		forceTransitionTo(screenNames.appPage, {
			appsSysId,
			pageSysId,
			queryParams
		});
	} else {
		if (selectedPageIsTab) {
			forceTransitionTo(screenNames.appHome, {
				appsSysId,
				queryParams: {
					parentScreenId: parentInfo.screenId,
					isolatedEditMode: true,
					parentElementId: parentInfo.compositionElementId
				}
			});
		} else {
			forceTransitionTo(screenNames.appHome, { appsSysId, queryParams });
		}
	}
};
