import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-alert';
import { t } from 'sn-translate';

const createPayload = (changedData, userPayload) => {
	let outputPayload = { __data: changedData };
	if (typeof userPayload === 'object') {
		outputPayload = { ...outputPayload, ...userPayload };
	}
	return outputPayload;
};

const view = ({ properties }) => {
	const { message, changedData } = properties;

	return (
		<Fragment>
			{changedData ? (
				<now-alert
					icon="exclamation-triangle-fill"
					content={message}
					action={{ type: 'acknowledge', label: t('Update page') }}
				/>
			) : null}
		</Fragment>
	);
};

export default createCustomElement('uib-data-changed-alert', {
	view,
	properties: {
		message: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		actions: {
			schema: {
				type: 'object'
			},
			default: {}
		},
		changedData: {
			schema: {
				type: 'object'
			},
			default: null
		}
	},
	actionHandlers: {
		['NOW_ALERT#ACTION_CLICKED']: ({ action, dispatch, properties }) => {
			action.stopPropagation();
			for (const action of Object.entries(properties.actions)) {
				const [actionType, payload] = action;
				const payloadToSend = createPayload(properties.changedData, payload);
				dispatch(actionType, payloadToSend);
			}
		}
	}
});
