import find from 'lodash/find';

export function createDataBrokerInstanceNameMap(dataBrokerInstances) {
	return dataBrokerInstances.reduce((acc, cur) => {
		const { id, instanceName } = cur;
		acc[id] = instanceName;
		return acc;
	}, {});
}

export const getSelectablePropsFromPageState = ({
	properties: {
		dataBrokerInstances,
		ancestorDataBrokerInstances,
		selectedDataBrokerId
	}
}) => {
	const allDataBrokers = [
		...dataBrokerInstances,
		...ancestorDataBrokerInstances
	];
	const dataBrokerInstance = find(allDataBrokers, {
		id: selectedDataBrokerId
	});
	if (isDataBrokerTypeClientState(allDataBrokers, selectedDataBrokerId)) {
		try {
			const selectableProps = JSON.parse(dataBrokerInstance.selectableProps);
			return selectableProps.reduce((acc, elem) => [...acc, elem.name], []);
		} catch (e) {
			throw new Error(e);
		}
	}
};

export const isDataBrokerTypeClientState = (
	dataBrokerInstances,
	dataBrokerDataId
) => {
	const dataBrokerInstance = find(dataBrokerInstances, {
		id: dataBrokerDataId
	});
	if (
		dataBrokerInstance &&
		dataBrokerInstance.definition &&
		dataBrokerInstance.definition.type === 'CLIENT_STATE'
	) {
		return true;
	}
	return false;
};
