import isNull from 'lodash/isNull';
import isUndefined from 'lodash/isUndefined';

/**
 * @typedef ValidatorCoeffects
 * @property {object} state
 * @property {object} properties
 * @property {*} value
 * @property {object} options
 * @return {boolean}
 */

/**
 * @param {ValidatorCoeffects}
 * @return {boolean}
 */
export function isRequired({ value }) {
	return !!value;
}

/**
 * @param {String}
 * @return {boolean}
 */
export function isNonEmptyString({ value = '' }) {
	return value.replace(/\s/g, '').length > 0;
}

/**
 * @param {ValidatorCoeffects}
 * @return {boolean}
 */
export function isRequiredTypeahead({ state, properties, value, options }) {
	const listOptions = state[options.path] || properties[options.path];
	const lastSelection = properties[options.id];
	const lastSelectionIsValid = lastSelection && lastSelection.label;

	if (Array.isArray(listOptions) && value !== null) {
		if (
			listOptions.length > 0 &&
			typeof value === 'string' &&
			lastSelectionIsValid
		) {
			return (
				value.toLowerCase() === lastSelection.label.toLowerCase() &&
				!!listOptions.find(
					(listOption) => value.toLowerCase() === listOption.label.toLowerCase()
				)
			);
		} else {
			return listOptions.some((option) => option.id === value.id);
		}
	} else {
		return true;
	}
}

export function isAScreenCondition({ value }) {
	let isValid = true;
	const isEmpty = isNull(value) || isUndefined(value) || value === '';
	if (isEmpty) {
		return true;
	}

	// Use the same validation logic employed on the glide business rule
	// for list and forms screen condition saving
	const encodedQuery = value;
	const conditions = encodedQuery.split(/\^OR|\^/);
	const alphaNumRegex = /^[0-9a-zA-Z_]+$/;
	conditions.forEach((condition) => {
		const pair = condition.split(/!=|=/);
		if (
			pair.length <= 1 ||
			!pair[0].match(alphaNumRegex) ||
			!pair[1].match(alphaNumRegex)
		) {
			isValid = false;
		}
	});
	return isValid;
}
