import { t } from 'sn-translate';
import '@servicenow/now-modal';
import 'sn-ui-builder-components';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { snUXFActions } from 'sn-uxf-builder-middleware';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const dictionary = {
	activate: t('Activate'),
	deactivate: t('Deactivate'),
	activateProcessing: t('Activating'),
	deactivateProcessing: t('Deactivating'),
	confirmMessage: {
		/* prettier-ignore */
		activate: t('By activating this page, it will be viewable by end users. Are you sure you want to activate it?'),
		/* prettier-ignore */
		deactivate: t('By deactivating this page, it will no longer be visible to end-users. Are you sure you want to deactivate it?')
	}
};

const events = {
	activate: 'SN_UIB_ACTIVATION_DIALOG#ACTIVATE',
	deactivate: 'SN_UIB_ACTIVATION_DIALOG#DEACTIVATE',
	cancel: 'SN_UIB_ACTIVATION_DIALOG#CANCEL'
};

/**
 * @param root0
 * @param root0.targetStatus
 * @param root0.pageName
 */
function InterstitialPartial({ targetStatus, pageName }) {
	const label = `${dictionary[`${targetStatus}Processing`]} ${pageName}`;
	return (
		<div>
			<sn-uib-component-interstitial
				content={<sn-uib-loader label={label} />}
			/>
		</div>
	);
}

/**
 * @param root0
 * @param root0.targetStatus
 * @param root0.pageName
 */
function ConfirmPartial({ targetStatus, pageName }) {
	const heading = `${dictionary[targetStatus]} ${pageName}?`;
	const footerActions = JSON.stringify([
		{
			variant: 'primary',
			label: dictionary[targetStatus],
			clickActionType: events[targetStatus]
		},
		{
			variant: 'tertiary',
			label: t('Cancel'),
			clickActionType: events.cancel
		}
	]);

	const message = dictionary.confirmMessage[targetStatus];

	return (
		<now-modal
			opened={true}
			size="md"
			header-label={heading}
			footerActions={footerActions}>
			<slot name="defaultSlot">
				<div>
					<p>{message}</p>
				</div>
			</slot>
		</now-modal>
	);
}

const partialMap = {
	pre: ConfirmPartial,
	updating: InterstitialPartial
};

export default createCustomElement('uib-activate-dialog', {
	view: (state) => {
		const { pageStatus, pageName, targetStatus, step } = state;
		return partialMap[step]({ pageStatus, pageName, targetStatus });
	},
	properties: {
		// pre, updating, post
		step: {
			default: 'pre'
		},
		pageStatus: {
			default: null
		},
		pageState: {
			default: null
		},
		pageName: {
			default: null
		},
		targetStatus: {
			default: null
		},
		isPageStateDirty: {
			default: false
		}
	},
	transformState: (state) => {
		const { pageState, step } = state.properties;
		const pageStatus = pageState ? pageState.pageStatus : null;
		const pageName = pageState ? pageState.name : null;
		const targetStatus = pageStatus === 'active' ? 'deactivate' : 'activate';
		return {
			...state,
			pageStatus,
			pageName,
			targetStatus,
			step
		};
	},
	actionHandlers: {
		['NOW_MODAL#OPENED_SET']: ({ dispatch }) => {
			dispatch('SN_UIB_APP#ACTIVATION_DIALOG_DISMISSED');
		},
		['SN_UIB_ACTIVATION_DIALOG#CANCEL']: ({ dispatch }) => {
			dispatch('SN_UIB_APP#ACTIVATION_DIALOG_DISMISSED');
		},
		['SN_UIB_ACTIVATION_DIALOG#ACTIVATE']: ({
			dispatch,
			updateProperties,
			properties
		}) => {
			const { pageState } = properties;
			updateProperties({
				step: 'updating'
			});
			dispatch(snUXFActions.PAGE_SAVE_REQUESTED, {
				pageStateModel: pageState,
				forceActivate: true
			});
		},
		['SN_UIB_ACTIVATION_DIALOG#DEACTIVATE']: ({
			dispatch,
			updateProperties,
			properties
		}) => {
			const { pageState } = properties;
			updateProperties({
				step: 'updating'
			});
			dispatch(snUXFActions.PAGE_SAVE_REQUESTED, {
				pageStateModel: pageState,
				forceDeactivate: true
			});
		}
	}
});
