import { t } from 'sn-translate';
import '@servicenow/now-modal';

export default function LoginModal({ isLoggedIn, onLogin }) {
	if (typeof onLogin !== 'function') {
		throw new TypeError(
			`[LoginModal] Expected 'onLogin' property to be a function`
		);
	}

	if (isLoggedIn) {
		return null;
	}

	return (
		<now-modal
			className="login-modal"
			header-label={t('Login')}
			size="md"
			manageOpened
			opened>
			<iframe
				title={t('Login')}
				className="login-modal--content"
				frameBorder="0"
				scrolling="no"
				src="/amb_login.do"
				on-load={({ target: iframe }) => {
					const {
						g_ck: token,
						location: { pathname }
					} = iframe.contentWindow;

					if (pathname === '/amb_welcome.do') {
						onLogin(token);
					}
				}}
			/>
			<now-button-iconic
				icon="circle-question-fill"
				size="md"
				bare
				variant="primary"
				tooltip-content="You were logged out due to inactivity"
			/>
		</now-modal>
	);
}
