import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';

const APP_CONFIG_QUERY = `
query($sysId: String) {
	glideRecord: GlideRecord_Query {
		sys_ux_app_config(sys_id:$sysId) {
			results: _results {
				name {
					displayValue
					value
				}
				sys_id {
					displayValue
					value
				}
				sys_scope {
					displayValue
					value
				}
				_query {
					GlideRecord_Query {
						sys_ux_page_registry(queryConditions: "admin_panel=$$parent.sys_id$$") {
							results: _results {
								path {
									value
								}
							}
						}
					}
				}
			}
		}
	}
}
`;

const WORKSPACE_CONFIG_QUERY = `
	query($sysId: String) {
		glideRecord: GlideRecord_Query {
			sys_aw_master_config(sys_id:$sysId) {
				results: _results {
					name {
						displayValue
						value
					}
					sys_id {
						displayValue
						value
					}
					sys_scope {
						displayValue
						value
					}
					compiled_page_registry {
						displayValue
						value
					}
				}
			}
		}
	}
`;

export const fetchAppConfigEffect = createGraphQLEffect(APP_CONFIG_QUERY, {
	variableList: ['sysId'],
	successActionType: 'UIB_ROUTER#APPLICATION_CONFIG_REQUEST_SUCCEEDED',
	errorActionType: 'UIB_ROUTER#CONFIG_REQUEST_FAILED'
});

export const fetchWorkspaceConfigEffect = createGraphQLEffect(
	WORKSPACE_CONFIG_QUERY,
	{
		variableList: ['sysId'],
		successActionType: 'UIB_ROUTER#WORKSPACE_CONFIG_REQUEST_SUCCEEDED',
		errorActionType: 'UIB_ROUTER#CONFIG_REQUEST_FAILED'
	}
);

export const fetchDashboardsConfigEffect = createHttpEffect(
	'/api/now/builder/dashboards',
	{
		method: 'GET',
		successActionType: 'UIB_ROUTER#DASHBOARDS_CONFIG_REQUEST_SUCCEEDED',
		errorActionType: 'UIB_ROUTER#CONFIG_REQUEST_FAILED',
		batch: false
	}
);
