import { applicationTypes } from './routingConstants';

export const capabilities = {
	dataBrokerManagement: 'dataBrokerManagement',
	dataBrokerExecution: 'dataBrokerExecution',
	screenVariants: 'screenVariants',
	screenApplicability: 'screenApplicability',
	routeFieldsParams: 'routeFieldsParams',
	clientScripts: 'clientScripts',
	clientStateManagement: 'clientStateManagement',
	elementEventMappings: 'elementEventMappings',
	elementStyling: 'elementStyling',
	componentTreeNavigation: 'componentTreeNavigation',
	layoutTemplates: 'layoutTemplates',
	rowColumnLayout: 'rowColumnLayout',
	componentsShown: 'componentsShown',
	autowiringEventMappingsForFilters: 'autowiringEventMappingsForFilters',
	screenPropertyManagement: 'screenPropertyManagement',
	screenEventHandling: 'screenEventHandling',
	tabSets: 'tabSets',
	dynamicTabs: 'dynamicTabs',
	macroponentExtension: 'macroponentExtension',
	intellisense: 'intellisense',
	openMenu: 'openMenu',
	mainRouteLabel: 'mainRouteLabel',
	routeConfiguration: 'routeConfiguration',
	builderType: 'builderType',
	scriptedProperties: 'scriptedProperties',
	runtimePagePreview: 'runtimePagePreview',
	editInstanceNameInHeader: 'editInstanceNameInHeader',
	scopePicker: 'scopePicker',
	domainPicker: 'domainPicker',
	expandDomainScope: 'expandDomainScope',
	sharedPageDefinitionPicker: 'sharedPageDefinitionPicker',
	recordTransactions: 'recordTransactions',
	urlPreview: 'urlPreview',
	hideComponentPropertyBindings: 'hideComponentPropertyBindings'
};

export const capabilityMatrix = (() => {
	const rawMatrix = [
		// Capability Name                  App   Dash  Comp   WS
		[capabilities.dataBrokerManagement, true, false, true, false],
		[capabilities.dataBrokerExecution, true, true, true, false],
		[capabilities.screenVariants, true, false, true, false],
		[capabilities.screenApplicability, true, true, true, false],
		[capabilities.routeFieldsParams, true, false, true, false],
		[capabilities.clientScripts, true, false, true, false],
		[capabilities.clientStateManagement, true, false, true, false],
		[capabilities.elementEventMappings, true, false, true, false],
		[capabilities.elementStyling, true, true, true, false],
		[capabilities.componentTreeNavigation, true, true, true, false],
		[capabilities.rootElementSelectable, true, true, true, false],
		[capabilities.layoutTemplates, true, true, true, false],
		[capabilities.rowColumnLayout, false, false, false, true],
		[capabilities.fixedElementToolbar, true, true, true, false],
		[
			capabilities.componentsShown,
			null,
			'dashboard',
			'components',
			'landing-page'
		],
		[capabilities.autowiringEventMappingsForFilters, false, true, false, false],
		[capabilities.screenPropertyManagement, true, false, true, false],
		[capabilities.screenEventHandling, true, false, true, false],
		[capabilities.tabSets, true, true, true, false],
		[capabilities.dynamicTabs, true, false, true, false],
		[capabilities.macroponentExtension, true, true, true, false],
		[capabilities.intellisense, true, false, true, false],
		[capabilities.openMenu, true, true, true, false],
		[capabilities.mainRouteLabel, 'page', 'dashboard', 'page', 'landing page'],
		[capabilities.routeConfiguration, true, false, true, false],
		[capabilities.builderType, 'UI', 'Dashboard', 'Component', 'UI'],
		[capabilities.scriptedProperties, true, false, true, false],
		[capabilities.runtimePagePreview, true, false, false, false],
		[capabilities.editInstanceNameInHeader, true, true, true, false],
		[capabilities.scopePicker, true, true, true, false],
		[capabilities.domainPicker, true, true, true, false],
		[capabilities.expandDomainScope, true, true, true, false],
		[capabilities.sharedPageDefinitionPicker, true, false, false, false],
		[capabilities.recordTransactions, true, true, false, true],
		[capabilities.urlPreview, true, false, false, false],
		[capabilities.hideComponentPropertyBindings, true, false, false, false]
	];

	const init = {
		[applicationTypes.app]: {},
		[applicationTypes.dashboards]: {},
		[applicationTypes.components]: {},
		[applicationTypes.workspace]: {}
	};
	const reducer = (acc, [cap, app, db, comp, ws]) => ({
		[applicationTypes.app]: { ...acc[applicationTypes.app], [cap]: app },
		[applicationTypes.dashboards]: {
			...acc[applicationTypes.dashboards],
			[cap]: db
		},
		[applicationTypes.components]: {
			...acc[applicationTypes.components],
			[cap]: comp
		},
		[applicationTypes.workspace]: {
			...acc[applicationTypes.workspace],
			[cap]: ws
		}
	});

	return rawMatrix.reduce(reducer, init) || {};
})();
